import { Pipe, PipeTransform } from '@angular/core';
import { DatePipe } from '@angular/common';

@Pipe({
  name: 'vendorFilter'
})
export class VendorFilterPipe implements PipeTransform {

  patientNm: any;
  private datePipe: DatePipe;
  transform(
    vendorsList: any,
    inputvendorName: string,
    inputvendorId: string,
    inputaccountNum: string,
    inputMobileNo: string,
    inputEmail: string,
  ) {
    if (vendorsList && vendorsList.length) {
      return vendorsList.filter(vendor => {
        if (
          (inputvendorName && !vendor.vendorNm) ||
          (inputvendorId && !vendor.vendorRefNo) ||
          ( inputaccountNum && !vendor.accountNo) ||
          (inputMobileNo && !vendor.contactNo) ||
          ( inputEmail && !vendor.emailId)
        ) {
          return false;
        }
        if (vendor.vendorNm) {
          if (
            inputvendorName &&
            vendor.vendorNm.toLowerCase().indexOf(inputvendorName.toLowerCase()) ===
              -1
          ) {
            return false;
          }
        }



        // sale order no
        if (vendor.vendorRefNo) {
          if (
            inputvendorId &&
            vendor.vendorRefNo.toLowerCase().indexOf(inputvendorId.toLowerCase()) ===
              -1
          ) {
            return false;
          }
        }

        // mobile no

        if (vendor.accountNo) {
          if (
            inputaccountNum &&
            vendor.accountNo.toLowerCase().indexOf(inputaccountNum.toLowerCase()) ===
              -1
          ) {
            return false;
          }
        }



        // vendor status
        if (vendor.contactNo) {
          if (
            inputMobileNo &&
            vendor.contactNo.toLowerCase().indexOf(inputMobileNo.toLowerCase()) === -1
          ) {
            return false;
          }
        }
        if (vendor.emailId) {
          if (
            inputEmail &&
            vendor.emailId.toLowerCase().indexOf(inputEmail.toLowerCase()) === -1
          ) {
            return false;
          }
        }
        return true;
      });
    } else {
        return vendorsList;
    }
  }

}
