import { Pipe, PipeTransform } from '@angular/core';
import { DatePipe } from '@angular/common';

@Pipe({
    name: 'posearchfilter'
})
export class PoSearchFilterPipe implements PipeTransform {
    patientNm: any;
    private datePipe: DatePipe;


    transform(
        allPosList: any,
        orderDt: string,
        inputPoNum: string,
        inputVendorNm: string,
        inputMobileNum: string,
        inputOrderStatus: string,
        inputTotalAmt: string,
        expDeliveryDt: string
    ) {
        if (allPosList && allPosList.length) {
            return allPosList.filter(patient => {
                // expDeliveryDt type
                if (patient.purchaseOrderNo) {

                    if (
                        inputPoNum &&
                        patient.purchaseOrderNo
                            .toLowerCase()
                            .indexOf(inputPoNum.toLowerCase()) === -1
                    ) {
                        return false;
                    }
                }
                // patient category
                if (patient.vendorNm) {
                    if (
                        inputVendorNm &&
                        patient.vendorNm
                            .toLowerCase()
                            .indexOf(inputVendorNm.toLowerCase()) === -1
                    ) {
                        return false;
                    }
                }
                // patient status
                if (patient.orderStatus) {
                    if (
                        inputOrderStatus &&
                        patient.orderStatus
                            .toLowerCase()
                            .indexOf(inputOrderStatus.toLowerCase()) === -1
                    ) {
                        return false;
                    }
                }
                // patient name
                if (patient.mobileNo) {
                    if (
                        inputMobileNum &&
                        patient.mobileNo.toLowerCase().indexOf(inputMobileNum.toLowerCase()) ===
                        -1
                    ) {
                        return false;
                    }
                }

                // patient visitDate
                if (patient.orderDt) {
                    const datePipe = new DatePipe('en-US');
                    patient.orderDt = datePipe.transform(patient.orderDt, 'yyyy/MM/dd');
                    let date = patient.orderDt.split('/');
                    const  filterDate = date[2] + '/' + date[1] + '/' + date[0];
                    if (orderDt && filterDate.toLowerCase().indexOf(orderDt.toLowerCase()) === -1) {
                        return false;
                    }
                }
                // patient category
                if (patient.totalAmt) {
                    if (
                        inputTotalAmt &&
                        patient.totalAmt &&
                        patient.totalAmt.toString()
                            .toLowerCase()
                            .indexOf(inputTotalAmt.toLowerCase()) === -1
                    ) {
                        return false;
                    }
                }
                // patient visitDate
                if (patient.deliveryExpDt) {
                    const datePipe = new DatePipe('en-US');
                    patient.deliveryExpDt = datePipe.transform(patient.deliveryExpDt, 'yyyy/MM/dd');
                    let date = patient.deliveryExpDt.split('/');
                    const  filterDate = date[2] + '/' + date[1] + '/' + date[0];
                    if (expDeliveryDt && patient.deliveryExpDt &&
                      filterDate.toLowerCase().indexOf(expDeliveryDt.toLowerCase()) === -1) {
                        return false;
                    }
                }
                // if (inputVendorNm &&  (inputVendorNm.length === 10 || inputVendorNm.length === 2 || inputVendorNm.length === 3 ||
                //   inputVendorNm.length === 5 || inputVendorNm.length === 6 ) ) {
                //   const splitedArray = inputVendorNm.split('/') || [];
                //   if (splitedArray) {
                //     const reversArray = splitedArray.reverse();
                //     if (reversArray && patient.visitDt.indexOf(reversArray.join('/')) === -1) {
                //       return false;
                //     }
                //   }
                // }
                return true;
            });
        } else {
            return allPosList;
        }
    }
}
