import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { WarehouseCommonComponent } from './containers/warehouse-common/warehouse-common.component';
import { WarehouseSaleComponent } from './containers/warehouse-sale/warehouse-sale.component';
import { SalesHistoryComponent } from './containers/sales-history/sales-history.component';
import { WarehouseRegistrationComponent } from './containers/warehouse-registration/warehouse-registration.component';
import { AddToInventoryComponent } from './containers/add-to-inventory/add-to-inventory.component';
import { AddToListComponent } from './containers/add-to-list/add-to-list.component';
import { InventoryListComponent } from './containers/inventory-list/inventory-list.component';
import { WarehousePurchaseComponent } from './containers/warehouse-purchase/warehouse-purchase.component';
import { PurchaseHistoryComponent } from './containers/purchase-history/purchase-history.component';
import { SaleOrderInvoiceBreakdownComponent } from './containers/sale-order-invoice-breakdown/sale-order-invoice-breakdown.component';
import { SettingsComponent } from './containers/settings/settings.component';
import { AddNewVendorComponent } from './containers/add-new-vendor/add-new-vendor.component';
import { PartListComponent } from './containers/part-list/part-list.component';
import { ProductsListComponent } from './containers/products-list/products-list.component';
import { PurchaseOrderComponent } from './containers/purchase-order/purchase-order.component';
import { ListOfVendorsComponent } from './containers/list-of-vendors/list-of-vendors.component';
import { ListOfCustomersComponent } from './containers/list-of-customers/list-of-customers.component';
import { WarehouseLocationsComponent } from './containers/warehouse-locations/warehouse-locations.component';
import { ListOfProductsComponent } from '@app/modules/warehouse/containers/list-of-products/list-of-products.component';
import { AddProductComponent } from '@app/modules/warehouse/containers/add-product/add-product.component';
import { ExcelUploadComponent } from './containers/excel-upload/excel-upload.component';
import {
  PurchaseOrderInvoiceBreakdownComponent
} from './containers/purchase-order-invoice-breakdown/purchase-order-invoice-breakdown.component';
import { VendorInvoiceHistoryComponent } from '@app/modules/warehouse/containers/vendor-invoice-history/vendor-invoice-history.component';
import { CreateVendorInvoiceComponent } from '@app/modules/warehouse/containers/create-vendor-invoice/create-vendor-invoice.component';
import { SaleOrderComponent } from './containers/sale-order/sale-order.component';
import {
  WarehouseCreateLocationDialogueComponent
 } from './containers/warehouse-create-location-dialogue/warehouse-create-location-dialogue.component';
import { VendorPaymentModeComponent } from '@app/modules/warehouse/containers/vendor-payment-mode/vendor-payment-mode.component';
// import { PicklistComponent } from './picklist/picklist.component';
import {PicklistComponent} from './containers/picklist/picklist.component';
import { CreateStorageLocationComponent } from './containers/create-storage-location/create-storage-location.component';
import {StockTransferComponent} from './containers/stock-transfer/stock-transfer.component';
import { PaymentSaleComponent } from './containers/payment-sale/payment-sale.component';
import { GenerateStorageSpaceComponent } from './containers/generate-storage-space/generate-storage-space.component';
import { ManageStockComponent } from './containers/stock-transfer/manage-stock/manage-stock.component';
import { TaxCategoryComponent } from './containers/tax-category/tax-category.component';
import { VendorInvoiceDetailsComponent } from './containers/vendor-invoice-details/vendor-invoice-details.component';
// import { DevComponent } from './containers/dev/dev.component';
import { TransferHistoryComponent } from './containers/stock-transfer/transfer-history/transfer-history.component';
import { PickItemsComponent } from './containers/picklist/pick-items/pick-items.component';
import { BarcodeComponent } from './containers/barcode/barcode.component';
import { InventoryExcelUploadComponent } from './containers/inventory-excel-upload/inventory-excel-upload.component';
import { CartonBoxComponent } from './containers/carton-box/carton-box.component';
import { CartonDialogComponent } from './containers/carton-dialog/carton-dialog.component';
import { SaleReturnsHistoryComponent } from './containers/sale-returns-history/sale-returns-history.component';
import { SaleReturnComponent } from './containers/sale-return/sale-return.component';
import { CreditNoteHistoryComponent } from './containers/sales-history/credit-note-history/credit-note-history.component';


export const warehouseRoutings: Routes = [
  { path: '', component: WarehouseCommonComponent },
  {
    path: 'settings', component: SettingsComponent,
    children: [
      {
        path: 'warehouse-productsList',
        component: ProductsListComponent
      },
      {
        path: 'list-of-products',
        component: ListOfProductsComponent
      },
      {
        path: 'warehouse-locations',
        component: WarehouseLocationsComponent
      },
      {
        path: 'tax-category',
        component: TaxCategoryComponent
      },
      {
        path: 'carton-box',
        component: CartonBoxComponent
      },
      {
        path: 'carton-dialog',
        component: CartonDialogComponent
      },
      { path: '', redirectTo: 'warehouse-locations', pathMatch: 'prefix' }
    ]
  },
  {
    path: 'purchase', component: WarehousePurchaseComponent,
    children: [

      {
        path: 'purchase-order',
        component: PurchaseOrderComponent
      },
      {
        path: 'list-of-vendors',
        component: ListOfVendorsComponent
      },
      {
        path: 'vendor-invoice', // govindh
        // component: VendorInvoiceHistoryComponent
        component: CreateVendorInvoiceComponent
      },
      { path: '', redirectTo: 'purchase-order', pathMatch: 'prefix' }
    ]
  },
  {
    path: 'sale', component: WarehouseSaleComponent,
    children: [
      {
        path: 'sale-order',
        component: SaleOrderComponent
      },
      {
        path: 'list-of-customers',
        component: ListOfCustomersComponent
      },
      {
        path: 'sale-return',
        component: SaleReturnComponent
      },
      {
        path: 'sale-returns-history',
        component: SaleReturnsHistoryComponent
      },
      {
        path: 'credit-note-history',
        component: CreditNoteHistoryComponent
      },


      // {
      //   path: 'picklist',
      //   component: PicklistComponent
      // },
      // {
      //   path: 'stock-transfer',
      //   component: StockTransferComponent,
      //   children: [
      //     {
      //       path: 'manage-stock',
      //       component: ManageStockComponent
      //     }
      //   ]
      // },
      // {
      //   path: 'manage-stock',
      //   component: ManageStockComponent
      // },
      { path: '', redirectTo: 'sale-order', pathMatch: 'prefix' }
    ]
  },
  {
    path: 'picklist',
    component: PicklistComponent
  },
  {
    path: 'PickItems',
    component: PickItemsComponent
  },
  {
    path: 'inventory', component: AddToInventoryComponent,
    children: [

      {
        path: 'add-to-list',
        component: AddToListComponent
      },
      {
        path: 'inventory-list',
        component: InventoryListComponent
      },
      {
        path: 'stock-transfer', component: StockTransferComponent,
        // children: [
        //   {
        //     path: 'manage-stock',
        //     component: ManageStockComponent
        //   }
        // ]
      },
      {
        path: 'manage-stock',
        component: ManageStockComponent
      },
      {
        path: 'transfer-history',
        component: TransferHistoryComponent
      },
      { path: '', redirectTo: 'inventory-list', pathMatch: 'prefix' }
    ]
  },
  {
    path: 'payment-screen',
    component: PaymentSaleComponent
   },
   {
    path: 'excel-upload',
    component: ExcelUploadComponent
  },
   {
    path: 'vendor-invoice-payment-screen',
    component: VendorPaymentModeComponent
   },
   {
    path: 'stock-transfer', component: StockTransferComponent,
    // children: [
    //   {
    //     path: 'manage-stock',
    //     component: ManageStockComponent
    //   }
    // ]
  },
  {
    path: 'manage-stock',
    component: ManageStockComponent
  },
  {
    path: 'transfer-history',
    component: TransferHistoryComponent
  },
   {
    path: 'warehouse-registration',
    component: WarehouseRegistrationComponent
  },
  {
    path: 'sale-order-invoice-breakdown',
    component: SaleOrderInvoiceBreakdownComponent
  },
  {
    path: 'purchase-order-invoice-breakdown',
    component: PurchaseOrderInvoiceBreakdownComponent
  },
  {
    path: 'vendor-invoice-details',
    component: VendorInvoiceDetailsComponent
  },
  {
    path: 'warehouse-partsList',
    component: PartListComponent
  },
  {
    path: 'warehouse-create-location',
    component: WarehouseCreateLocationDialogueComponent
  },
  {
    path: 'create-storage-location',
    component: CreateStorageLocationComponent
  },
  {
    path: 'generate-storage-space',
    component: GenerateStorageSpaceComponent
  },
  {
    path: 'add-new-vendor',
    component: AddNewVendorComponent
  },
  {
    path: 'add-product',
    component: AddProductComponent
  },
  {
    path: 'purchase-history',
    component: PurchaseHistoryComponent
  },
  {
    path: 'vendor-invoice-history', // govindh
    // component: CreateVendorInvoiceComponent
    component: VendorInvoiceHistoryComponent
  },
  {
    path: 'warehouse-sale-history',
    component: SalesHistoryComponent
  },
  {
    path: 'barcode',
    component: BarcodeComponent
  },
  {
    path: 'inventory-excel-upload',
    component: InventoryExcelUploadComponent
  },
  { path: '', redirectTo: 'sale', pathMatch: 'prefix' },

];
@NgModule({
  imports: [RouterModule.forChild(warehouseRoutings)],
  exports: [RouterModule],
  declarations: []
})
export class WarehouseRoutingModule { }
