import {
  map,
  catchError,
  debounceTime,
  distinctUntilChanged,
  tap,
  filter,
  switchMap
} from 'rxjs/operators';
import { Component, OnInit, Inject } from '@angular/core';
import { UserSession } from '@core/models/user-session';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { WarehouseApiService } from '@core/services/warehouse-api.service';
import { UserService } from '@core/services';
import { MatDatepicker } from '@angular/material/datepicker';
import { ToastrService } from 'ngx-toastr';
import { Subject, Observable, of, concat } from 'rxjs';
import { DatePipe } from '@angular/common';
import { Router } from '../../../../../../node_modules/@angular/router';
import { HttpErrorResponse } from '@angular/common/http';
import { MatSnackBar } from '@angular/material/snack-bar';
import { MOMENT } from '@core/services/lib.providers';
import { WarehouseService } from '../../warehouse.service';
import { MatDialog } from '@angular/material';
import { ConfirmationDialogComponent } from '@shared/components/confirmation-dialog/confirmation-dialog.component';

@Component({
  selector: 'app-purchase-order',
  templateUrl: './purchase-order.component.html',
  styleUrls: ['./purchase-order.component.scss']
})
export class PurchaseOrderComponent implements OnInit {
  purchaseOrderForm: FormGroup;
  private sessionData: UserSession;
  vendorsList: any;
  vendorSelectData: any;
  isEditable = false;
  isConform = true;
  partNmValue: any;
  ispartValue = true;
  isMailPDF = true;
  isaddInput = false;
  quentity: any;
  dateMin = new Date();
  partsList: any;
  vendorList: any;
  orderData: any;
  partTableList = [];
  selectedPartsList: any;
  public modeOfDelivery = [];
  addressList: any;
  totalAmt: number;
  vendorEmaild: any;
  shedNm: any;
  currentSelectedVendor$ = new Subject();
  product$: Observable<any>;
  deliveryAddress$ = new Subject<string>();
  shedData: any;
  serviceAmt: number;
  gstTaxAmt: number;
  negQty = false;
  submit = false;
  invalidTax = false;
  public grossAmt: number;
  partNmComparator = true;
  public partNmOrNo;
  partNmOrNo$: Observable<any>;
  partNameListinput$ = new Subject<string>();
  purchase_neworder_validation_messages = {
    vendorNm: [{ type: 'required', message: '* Vendor Name is required' }],
    partNm: [{ type: 'required', message: '* Part Name is required' }],
    quantity: [{ type: 'required', message: '* Quantity is required' }],
    shippingType: [
      { type: 'required', message: '* shipping type is required' }
    ],
    deliveryExpDt: [
      { type: 'required', message: '* Delivery Date is required' }
    ],
    _deliveryadd: [
      { type: 'required', message: '* Delivery Address  is required' }
    ],
    unitAmt: [
      {
        type: 'pattern',
        message: 'cccccccccc'
      }
    ]
  };
  currencyType: {};
  constructor(
    private wareHouseApiService: WarehouseApiService,
    private userService: UserService,
    private toaster: ToastrService,
    private formbuilder: FormBuilder,
    private datePipe: DatePipe,
    private router: Router,
    private snackBar: MatSnackBar,
    private warehouseDataService: WarehouseService,
    public dialog: MatDialog,
    private matdialog: MatDialog,
    @Inject(MOMENT) private moment
  ) {
    this.sessionData = userService.getUser();
  }

  ngOnInit() {
    this.fetchCurrencyType();
    this.warehouseDataService.purchaseData.next('purchase-order');
    this.initForm();
    this.fetchVendors(null);
    this.modeDelivery();
    this.fetchShedRef();
    this.fetchPartNmOrNo();
  }
  fetchCurrencyType() {
    const payload = {
      orgId: this.sessionData.orgId,
      orgGrpId: this.sessionData.orgGroupId,
      createdUsrId: this.sessionData.regId
    };
    this.wareHouseApiService.fetchCurrencyType(payload).subscribe(
      response => {
        if (response && response.responseCode === 'E200') {
          this.currencyType = response.data;
        } else if (response && response.responseCode === 'E400') {
          this.snackBar.open(response.responseMessage, 'dismiss', {
            duration: 500
          });
        }
      },
      (errorResponse: HttpErrorResponse) => {
        if (errorResponse.status > 0) {
          this.snackBar.open(errorResponse.error.responseMessage, 'dismiss', {
            duration: 500
          });
        } else {
          // network failure
          this.snackBar.open('Network slow', 'dismiss', {
            duration: 500
          });
        }
      }
    );
  }
  initForm() {
    this.purchaseOrderForm = this.formbuilder.group({
      vendorNm: this.formbuilder.control(null, Validators.required),
      partNm: this.formbuilder.control('', Validators.required),
      quantity: this.formbuilder.control('', Validators.required),
      shippingType: this.formbuilder.control('Roadways', Validators.required),
      deliveryExpDt: this.formbuilder.control(new Date(), Validators.required),
      indentRaisedQty: '',
      unitAmt: '',
      _deliveryadd: this.formbuilder.control('', Validators.required)
    });
    this.purchaseOrderForm.get('vendorNm').valueChanges.subscribe(val => {
      if (val) {
        this.vendorEmaild = val.emailId;
        this.fetchVendors(val);
      }
    });
    this.purchaseOrderForm.get('_deliveryadd').valueChanges.subscribe(val => {
      if (typeof val === 'object') {
        this.addressList = val;
      }
    });
    this.purchaseOrderForm.get('quantity').valueChanges.subscribe(res => {
      if (res === 0) {
        this.isaddInput = false;
        this.negQty = true;
      } else {
        this.quentity = res;
        if (this.partNmValue.partId) {
          this.isaddInput = true;
        }
        this.negQty = false;
      }
    });
    this.purchaseOrderForm.get('partNm').valueChanges.subscribe(res => {
      if (res) {
        this.partNmValue = res;
        if (this.quentity) {
          this.isaddInput = true;
        }
        this.ispartValue = false;
      }
    });
  }
  modeDelivery() {
    this.modeOfDelivery = ['Roadways', 'Railways', 'Airways', 'Waterways'];
  }
  datePickerToggler(datePicker: MatDatepicker<Date>) {
    datePicker[datePicker.opened ? 'close' : 'open']();
  }

  vendorComparer(o1, o2) {
    return o1 && o2 && o1.vendorInfoId === o2.vendorInfoId;
  }

  fetchVendors(data) {
    const payload = {
      orgId: this.sessionData.orgId,
      orgGrpId: this.sessionData.orgGroupId,
      createdUsrId: this.sessionData.regId,
      dropDown: !data,
      vendorInfoId: data ? (data.vendorInfoId ? data.vendorInfoId : null) : null
    };

    this.wareHouseApiService.fetchVendors(payload).subscribe(
      (response): any => {
        if (response && response.responseCode === 'E200') {
          if (response.data[0].emailId) {
            this.isMailPDF = true;
          }

          if (data && data.vendorInfoId) {
            const index = this.vendorsList.findIndex(
              val => val.vendorInfoId === data.vendorInfoId
            );
            this.vendorsList[index] = response.data[0];
            this.purchaseOrderForm
              .get('vendorNm')
              .patchValue(this.vendorsList[index], { emitEvent: false });
            this.currentSelectedVendor$.next(response.data[0]);
          } else {
            this.vendorsList = response.data;
          }
          this.partNmOrNo = null;
        } else if (response && response.responseCode === 'E400') {
        }
      },
      (errorResponse: HttpErrorResponse) => {
        if (errorResponse.status > 0) {
          this.snackBar.open(errorResponse.error.responseMessage, 'dismiss', {
            duration: 500
          });
        } else {
          // network failure
          this.snackBar.open('Network slow', 'dismiss', {
            duration: 500
          });
        }
      }
    );
  }

  fetchShedRef() {
    const payload = {
      createdUsrId: this.sessionData.regId,
      orgGrpId: this.sessionData.orgGroupId,
      orgId: this.sessionData.orgId
    };
    this.wareHouseApiService.fetchShedRef(payload).subscribe(
      response => {
        if (response && response.responseCode === 'E200') {
          this.shedData = response.data;
          this.purchaseOrderForm
            .get('_deliveryadd')
            .patchValue(this.shedData[0].shedNm);
          this.addressList = this.shedData[0];
          this.shedData.forEach(res => {
            if (res.isHeadOffice) {
              this.purchaseOrderForm.get('_deliveryadd').patchValue(res.shedNm);
              this.addressList = res;
            }
          });
        } else if (response && response.responseCode === 'E400') {
          this.snackBar.open(response.responseMessage, 'close', {
            duration: 2
          });
        }
      },
      (errorResponse: HttpErrorResponse) => {
        if (errorResponse.status > 0) {
          this.snackBar.open(errorResponse.error.responseMessage, 'dismiss', {
            duration: 3000
          });
        } else {
          // network failure
          this.snackBar.open('Network slow', 'dismiss', {
            duration: 3000
          });
        }
      }
    );
  }

  add() {
    const qty = this.purchaseOrderForm.get('quantity').value;
    const vendorName = this.purchaseOrderForm.get('vendorNm').value;
    const partName = this.purchaseOrderForm.get('partNm').value;
    if (qty && vendorName && partName) {
      this.submit = true;
      this.selectedPartsList = this.purchaseOrderForm.get('partNm').value;
      this.partTableList.filter((val: any) => {
        if (this.selectedPartsList.partId === val.partId) {
          this.partNmComparator = false;
        }
      });
      if (this.partNmComparator) {
        this.partsList.filter(val => {
          if (val.partId === this.selectedPartsList.partId && val.taxCatCode === this.selectedPartsList.taxCatCode && val.gstTaxPercntg === this.selectedPartsList.gstTaxPercntg) {
            const partTable = {};
            val.indentRaisedQty = this.purchaseOrderForm.get('quantity').value;
            val.serviceAmt = val.indentRaisedQty * val.unitAmt;
            const taxAmtpercentage = val.gstTaxPercntg * val.unitAmt;
            const totalTaxAmt = taxAmtpercentage / 100;
            val.gstTaxAmt = totalTaxAmt * val.indentRaisedQty;
            val.totalAmt = val.serviceAmt + val.gstTaxAmt;
            val.totalAmt.toFixed(2);
            this.selectedPartsList.indentRaisedQty = this.purchaseOrderForm.get(
              'quantity'
            ).value;
            partTable['createdUsrId'] = val.createdUsrId;
            partTable['gstTaxAmt'] = val.gstTaxAmt;
            partTable['gstTaxPercntg'] = val.gstTaxPercntg;
            partTable['indentRaisedQty'] = val.indentRaisedQty;
            partTable['orgGrpId'] = val.orgGrpId;
            partTable['orgId'] = val.orgId;
            partTable['partId'] = val.partId;
            partTable['partNm'] = val.partNm;
            partTable['partNo'] = val.partNo;
            partTable['partProductInd'] = val.partProductInd;
            partTable['serviceAmt'] = val.serviceAmt;
            partTable['totalAmt'] = val.totalAmt;
            partTable['totalTaxAmt'] = totalTaxAmt;
            partTable['unitAmt'] = val.unitAmt;
            partTable['uom'] = val.uom;

            this.grossAmt = Number(this.grossAmt || 0) + Number(val.totalAmt);
            // this.partTableList.push(val);
            this.partTableList.push(partTable);
            this.purchaseOrderForm.get('partNm').patchValue('');
            this.purchaseOrderForm.get('quantity').patchValue('');
          }
        });
      } else {
        this.snackBar.open(
          this.selectedPartsList.partNm + ' already added',
          'dismiss',
          {
            duration: 1000
          }
        );
      }
      this.partNmComparator = true;
    } else {
      this.submit = false;
      this.negQty = false;
    }
    this.isConform = false;
    this.isaddInput = false;
  }

  // to get part details
  fetchPartNmOrNo() {
    this.partNmOrNo$ = concat(
      of([]), // default items
      this.partNameListinput$.pipe(
        debounceTime(200),
        distinctUntilChanged(),
        tap(console.log),
        filter(checkStringCount => {
          if (checkStringCount && checkStringCount.length > 1) {
            return true;
          }
        }),
        //  tap(() => this.people3Loading = true),
        switchMap(this.fetchParts.bind(this))
      )
    );
  }

  fetchParts(data) {
    const vendorNm = this.purchaseOrderForm.get('vendorNm').value;
    const payload = {
      orgId: this.sessionData.orgId,
      orgGrpId: this.sessionData.orgGroupId,
      createdUsrId: this.sessionData.regId,
      //partProductInd: 'PA',
      vendorInfoId: vendorNm.vendorInfoId,
      isDropDown: false,
      partNmOrNo: data
    };
    return this.wareHouseApiService.fetchPartDetails(payload).pipe(
      map((response: any) => {
        if (response.responseCode === 'E200') {
          return response.data;
        } else if (response.response === 'E400') {
        }
      }),
      catchError(error => {
        return of([]);
      })
    );
  }

  fetchPartDetails(event) {
    const vendorNm = this.purchaseOrderForm.get('vendorNm').value;
    const payload = {
      orgId: this.sessionData.orgId,
      orgGrpId: this.sessionData.orgGroupId,
      createdUsrId: this.sessionData.regId,
      //partProductInd: 'PA',
      vendorInfoId: vendorNm.vendorInfoId,
      isDropDown: false
    };

    this.wareHouseApiService.fetchPartDetails(payload).subscribe(
      response => {
        this.partsList = response.data;
        if (response && response.responseCode === 'E200') {
        } else if (response && response.responseCode === 'E400') {
          this.toaster.error(response.responseMessage);
        }
      },
      (errorResponse: HttpErrorResponse) => {
        if (errorResponse.status > 0) {
          this.snackBar.open(errorResponse.error.responseMessage, 'dismiss', {
            duration: 500
          });
        } else {
          this.snackBar.open('Network slow', 'dismiss', {
            duration: 500
          });
        }
      }
    );
  }

  editOrder() {
    this.isEditable = true;
  }

  calculateAmount(i, tax) {
    if (tax > 100) {
      this.invalidTax = true;
    }
    const listOfData = this.partTableList[i];
    listOfData.serviceAmt =
      listOfData.unitAmt.toFixed(2) * listOfData.indentRaisedQty;
    const taxAmtpercentage: any = (
      listOfData.gstTaxPercntg * listOfData.unitAmt
    ).toFixed(2);
    const totalTaxAmt: any = (taxAmtpercentage / 100).toFixed(2);
    listOfData['gstTaxAmt'] = totalTaxAmt * listOfData.indentRaisedQty;
    listOfData.totalAmt = (
      listOfData.serviceAmt + listOfData.gstTaxAmt
    ).toFixed(2);
    this.grossAmt = 0;
    this.partTableList.filter(val => {
      this.grossAmt = Number(this.grossAmt) + Number(val.totalAmt);
      // if (this.grossAmt === 0) {
      //   this.grossAmt = 0.00;
      // }
    });
  }
  /**
   * @description This method is used to check whether the Pdf as to send to he vendor
   */
  confirmOrder() {
    const obj = {
      emailRequired: false,
      vendorMailId: null
    };
    if (this.isMailPDF) {
      const dialogRef = this.dialog.open(ConfirmationDialogComponent, {
        data: { title: 'Do you want to send PDF to vendor Email ?' },
        disableClose: true
      });
      dialogRef.afterClosed().subscribe(result => {
        if (result) {
          obj.emailRequired = true;
          obj.vendorMailId = this.vendorEmaild;
        } else {
          obj.emailRequired = false;
          obj.vendorMailId = ' ';
        }
        this.insertPurchaseOrder(obj);
      });
    } else {
      this.insertPurchaseOrder(obj);
      obj.emailRequired = false;
      obj.vendorMailId = null;
    }
    this.isConform = true;
    this.currentSelectedVendor$ = null;
    this.fetchPartNmOrNo();
  }
  /**
   * @description This method is used toinsert purchaseOrder
   */
  insertPurchaseOrder(obj) {
    const date = this.purchaseOrderForm.get('deliveryExpDt').value;
    const vendorNm = this.purchaseOrderForm.get('vendorNm').value;
    const payload = {
      orgId: this.sessionData.orgId,
      orgGrpId: this.sessionData.orgGroupId,
      createdUsrId: this.sessionData.regId,
      shippingType: this.purchaseOrderForm.get('shippingType').value,
      vendorInfoId: vendorNm.vendorInfoId,
      totalAmt: this.totalAmtCal(this.partTableList),
      serviceAmt: this.totalServiceAmt(this.partTableList),
      totalTaxAmt: this.totalGstTaxAmt(this.partTableList),
      purchaseOrderItemDetailsList: this.partTableList,
      shedId: this.addressList.shedId,
      orderDt: this.datePipe.transform(new Date(), 'yyyy-MM-dd'),
      vendorNm: this.partsList[0].defaultVendorNm,
      deliveryExpDt: this.datePipe.transform(date, 'yyyy-MM-dd'),
      ...obj
    };
    this.wareHouseApiService.insertPurchaseOrder(payload).subscribe(
      response => {
        if (response && response.responseCode === 'E200') {
          this.purchaseOrderForm.get('vendorNm').setValue(null);
          this.partTableList = [];
          this.partsList = null;
          this.router.navigate(['/warehouse/purchase/purchase-order']);
        } else if (response && response.responseCode === 'E400') {
          this.toaster.error(response.responseMessage);
        }
      },
      (errorResponse: HttpErrorResponse) => {
        if (errorResponse.status > 0) {
          this.snackBar.open(errorResponse.error.responseMessage, 'dismiss', {
            duration: 3000
          });
        } else {
          this.snackBar.open('Network slow', 'dismiss', {
            duration: 3000
          });
        }
      }
    );
  }

  cancelButton() {
    //this.router.navigate(['/warehouse/purchase/purchase-order']);
    this.purchaseOrderForm.reset();
    this.purchaseOrderForm.patchValue({
      shippingType: 'Roadways',
      deliveryExpDt: new Date()
    });
    this.currentSelectedVendor$.next(null);
    this.partsList = null;
    this.partTableList = [];
    this.isConform = true;
    this.isaddInput = false;
    this.fetchPartNmOrNo();
    this.fetchShedRef();
  }

  deletePart(part) {
    const dialogRef = this.dialog.open(ConfirmationDialogComponent, {
      data: { title: 'Are you sure want to do delete ?' },
      disableClose: true
    });
    dialogRef.afterClosed().subscribe(result => {
      if (result) {
        this.partTableList.filter(val => {
          if (val.partNm === part.partNm) {
            const index = this.partTableList.indexOf(part);
            this.partTableList.splice(index, 1);
          }
        });
        if (this.partTableList.length === 0) {
          this.isConform = true;
        }
        this.grossAmt = this.grossAmt - part.totalAmt;
      }
    });
  }

  totalAmtCal(partList): any {
    this.totalAmt = 0;
    partList.forEach(element => {
      this.totalAmt += +element.totalAmt;
    });
    return this.totalAmt.toFixed(2);
  }

  totalServiceAmt(partList) {
    this.serviceAmt = 0;
    partList.forEach(element => {
      this.serviceAmt += +element.serviceAmt;
    });
    return this.serviceAmt;
  }

  totalGstTaxAmt(partList) {
    this.gstTaxAmt = 0;
    partList.forEach(element => {
      this.gstTaxAmt += +element.gstTaxAmt;
    });
    return this.gstTaxAmt;
  }
}
