import { Pipe, PipeTransform } from '@angular/core';
import { DatePipe } from '@angular/common';

@Pipe({
    name: 'partssearchfilter'
})
export class PartsSearchFilterPipe implements PipeTransform {
    patientNm: any;
    private datePipe: DatePipe;


    transform(
        allPartsList: any,
        inputPartNo: string,
        inputPartNm: string,
        inputQty: string,
        inputUOM: string,
        inputUnitPrice: string,
        inputTax: string,
        inputTotalAmt: string
    ) {
        if (allPartsList && allPartsList.length) {
            return allPartsList.filter(patient => {
                // inputTotalAmt type
                if (patient.partNm) {

                    if (
                        inputPartNm &&
                        patient.partNm
                            .toLowerCase()
                            .indexOf(inputPartNm.toLowerCase()) === -1
                    ) {
                        return false;
                    }
                }
                // patient category
                if (patient.indentRaisedQty) {
                    if (
                        inputQty &&
                        patient.indentRaisedQty.toString()
                            .toLowerCase()
                            .indexOf(inputQty.toLowerCase()) === -1
                    ) {
                        return false;
                    }
                }
                // patient status
                if (patient.unitAmt) {
                    if (
                        inputUnitPrice &&
                        patient.unitAmt.toString()
                            .toLowerCase()
                            .indexOf(inputUnitPrice.toLowerCase()) === -1
                    ) {
                        return false;
                    }
                }
                // patient name
                if (patient.uom) {
                    if (
                        inputUOM &&
                        patient.uom.toLowerCase().indexOf(inputUOM.toLowerCase()) ===
                        -1
                    ) {
                        return false;
                    }
                }

                // patient visitDate
                if (patient.partNo) {
                    if (
                        inputPartNo &&
                        patient.partNo &&
                        patient.partNo
                            .toLowerCase()
                            .indexOf(inputPartNo.toLowerCase()) === -1
                    ) {
                        return false;
                    }
                }
                // patient category
                if (patient.gstTaxPercntg) {
                    if (
                        inputTax &&
                        patient.gstTaxPercntg &&
                        patient.gstTaxPercntg.toString()
                            .toLowerCase()
                            .indexOf(inputTax.toLowerCase()) === -1
                    ) {
                        return false;
                    }
                }
                // patient visitDate
                if (patient.totalAmt) {
                    if (
                        inputTotalAmt &&
                        patient.totalAmt &&
                        patient.totalAmt.toString()
                            .toLowerCase()
                            .indexOf(inputTotalAmt.toLowerCase()) === -1
                    ) {
                        return false;
                    }
                }
                // if (inputQty &&  (inputQty.length === 10 || inputQty.length === 2 || inputQty.length === 3 ||
                //   inputQty.length === 5 || inputQty.length === 6 ) ) {
                //   const splitedArray = inputQty.split('/') || [];
                //   if (splitedArray) {
                //     const reversArray = splitedArray.reverse();
                //     if (reversArray && patient.visitDt.indexOf(reversArray.join('/')) === -1) {
                //       return false;
                //     }
                //   }
                // }
                return true;
            });
        } else {
            return allPartsList;
        }
    }
}
