import { Component, OnInit, Input, OnDestroy, AfterViewInit, ChangeDetectorRef } from '@angular/core';
import { MatSidenav } from '@angular/material';
import { Subscription } from 'rxjs';
import { WarehouseService } from '@app/modules/warehouse/warehouse.service';
import { AutoUnsubscribe } from 'ngx-auto-unsubscribe';
import { UserSession } from '@core/models/user-session';
import { UserService } from '@core/services/user.service';
import { WarehouseApiService } from '@core/services/warehouse-api.service';
import {MatSnackBar} from '@angular/material';

@AutoUnsubscribe()
@Component({
  selector: 'app-customer-view',
  templateUrl: './customer-view.component.html',
  styleUrls: ['./customer-view.component.scss']
})
export class CustomerViewComponent implements OnInit, OnDestroy, AfterViewInit {

  @Input()
  sideNavRef: MatSidenav;
  private sessionData: UserSession;
  private customerListDataSubscription: Subscription;
  public customerList: any;
  public customersList: any;
  public side = true;
  constructor(
    private wareHouseDataService: WarehouseService,
    userService: UserService,
    private snackBar: MatSnackBar,
    private wareHouseApiService: WarehouseApiService,
    private cdr: ChangeDetectorRef
  ) {
    this.sessionData = userService.getUser();
   }
  ngOnDestroy() {

  }
  ngOnInit() {
  }
  ngAfterViewInit() {
    this.customerListDataSubscription = this.wareHouseDataService.vendorData.subscribe(res => {
      if (res) {
        this.customerList = res;
        this.fetchCustomerDetails();
        this.cdr.detectChanges();
      }
    });
  }

  // to get specific customer details
  fetchCustomerDetails() {
    const payload = {
      orgId: this.sessionData.orgId,
      orgGrpId: this.sessionData.orgGroupId,
      createdUsrId: this.sessionData.regId,
      orgCustomerId: this.customerList.orgCustomerId
    };
    this.wareHouseApiService.fetchCustomerRegList(payload).subscribe(response => {
      if (response && response.responseCode === 'E200') {
        this.customersList = response.data[0];
      } else if (response && response.responseCode === 'E400') {
      }
    });
  }
  closeSideNav() {
    this.sideNavRef.close();
   this.side = false;
   this.wareHouseDataService.vendorData.next('');
  }
}
