import { Component, OnInit } from '@angular/core';
import {
  Validators,
  FormControl,
  FormBuilder,
  AbstractControl,
  FormGroup
} from '@angular/forms';
import { WarehouseService } from '@app/modules/warehouse/warehouse.service';
import { UtilService, UserService } from '@core/services';
import { Router } from '@angular/router';
import { WarehouseApiService } from '@core/services/warehouse-api.service';
import { MatDialog, MatSidenav, MatDatepicker, MatSnackBar } from '@angular/material';
import { UserSession } from '@core/models/user-session';
import { Subscription } from 'rxjs';
import { HttpErrorResponse } from '@angular/common/http';
import { DatePipe } from '@angular/common';

@Component({
  selector: 'app-vendor-invoice-history',
  templateUrl: './vendor-invoice-history.component.html',
  styleUrls: ['./vendor-invoice-history.component.scss']
})
export class VendorInvoiceHistoryComponent implements OnInit {
  patientSearchForm: FormGroup;
  partsList: any;
  public inputinvoiceNumber;
  public inputpoNumber;
  public inputvendorName;
  public inputinvoiceDate;
  public inputtotalAmount;
  public inputpaidAmount;
  public inputdueAmount;
  public inputmodeDelivery;
  public apiSubscription: Subscription;
  private sessionData: UserSession;
  inputValue: string;
  fromDt: any;
  payDisable = false;
  toDt: any;
  public showPaymenttable = false;
  showPaymentTableData = false;
  indexNestedTable: number;
  patientList: any;
  placeholderValue: string;
  patientControl = new FormControl();
  patientData: any;
  patientmobile: any;
  public fromDate = new Date;
  public toDate = new Date;
  public maxDate = new Date;
  transactionDetails = [];
  searchType: any;
  patientSearch: any;
  patientSearchControl: AbstractControl;
  checked = this.utilService.generateUniqueId();
  start_time = new Date();
  isClicked = false;
  currentElement: any;
  isFetchingData = false;
  vendorList: any;
  public currencyType: any;
  date = new Date();
  constructor(
    userService: UserService,
    private dialog: MatDialog,
    private wareHouseApiService: WarehouseApiService,
    private router: Router,
    private snackBar: MatSnackBar,
    private formbuilder: FormBuilder,
    private datePipe: DatePipe,
    private utilService: UtilService,
    private warehoueDataService: WarehouseService
  ) {
    this.sessionData = userService.getUser();
  }

  ngOnInit() {

  this.fetchCurrencyType();
    this.warehoueDataService.paymentsList.subscribe(res => {
      if (res) {
        this.vendorList = res;
        this.fromDate = this.vendorList.fromDt;
        this.toDate = this.vendorList.toDt;
      }
    });
    this.fetchVendorInvoiceDetails();
    this.patientSearchControl = new FormControl('vendorNm');
    this.placeholderValue = 'Search by Vendor Name';
    this.searchType = 'vendorNm';
    this.patientSearchControl.valueChanges.subscribe(value => {
      this.searchType = value;
      switch (this.searchType) {
        case 'vendorNm':
          this.placeholderValue = 'Search by Vendor Name';
          this.patientData = '';
          this.fetchVendorInvoiceDetails();
          break;
        case 'invoiceNumber':
          this.placeholderValue = 'Search by Invoice Number';
          this.patientData = '';
          this.fetchVendorInvoiceDetails();
          break;
        default:
          this.placeholderValue = 'Search by Vendor Name';
          this.patientData = '';
          break;
      }
    });
  }
  initForm() {
    this.patientSearchForm = this.formbuilder.group({
      patientSearchControl: this.formbuilder.control(null, Validators.required),
      fromDt: '',
      toDt: '',
      serviceTable: ['', Validators.required]
    });
  }

  fetchCurrencyType() {
    const payload = {
      orgId: this.sessionData.orgId,
      orgGrpId: this.sessionData.orgGroupId,
      createdUsrId: this.sessionData.regId,
    };
    this.wareHouseApiService.fetchCurrencyType(payload).subscribe(response => {
      if (response && response.responseCode === 'E200') {
        this.currencyType = response.data;
      } else if (response && response.responseCode === 'E400') {
        this.snackBar.open(response.responseMessage, 'dismiss', {
          duration: 500,
        });
      }
    }, (errorResponse: HttpErrorResponse) => {
      if (errorResponse.status > 0) {
        this.snackBar.open(errorResponse.error.responseMessage, 'dismiss', {
          duration: 500,
        });
      } else {
        // network failure
        this.snackBar.open('Network slow', 'dismiss', {
          duration: 500,
        });
      }
    });
  }
  datePickerToggler(datePicker: MatDatepicker<Date>) {
    datePicker[datePicker.opened ? 'close' : 'open']();
  }
  onChangeDates() {
    if (!this.fromDate || !this.toDate) {
      return false;
    }
    this.fetchVendorInvoiceDetails();
  }
  fetchVendorInvoiceDetails() {
    const payload = {
      orgId: this.sessionData.orgId,
      orgGrpId: this.sessionData.orgGroupId,
      createdUsrId: this.sessionData.regId,
      fromDt: this.datePipe.transform(this.fromDate, 'yyyy-MM-dd'),
      toDt: this.datePipe.transform(this.toDate, 'yyyy-MM-dd')
    };

    if (this.searchType === 'vendorNm') {
      payload['vendorNm'] = this.patientData;
    } else if (this.searchType === 'invoiceNumber') {
      payload['vendorInvoiceNo'] = this.patientData;
    }
    this.wareHouseApiService.fetchVendorInvoiceDetails(payload).subscribe(response => {
      this.vendorList = response.data;
      if (this.vendorList.length) {
        this.isFetchingData = true;
      } else {
        this.isFetchingData = false;
      }
      this.vendorList.forEach(element => {
        element['showPlus'] = false;
      });
      if (response && response.responseCode === 'E200') {
      } else if (response && response.responseCode === 'E400') {
        this.snackBar.open(response.responseMessage, 'dismiss', {
          duration: 500,
        });
      }
    });
  }

  showCategory(index, listDetails) {
    this.indexNestedTable = index;
    if (this.vendorList[index].showPlus === false) {
      this.vendorList.forEach(element => {
        element['showPlus'] = false;
      });
    this.vendorList[index].showPlus = true;
    } else {
      this.vendorList[index].showPlus = false;
    }
    this.getTrans(listDetails);
    this.showPaymentTableData = true;
  }

    getTrans(listDetails) {
     const payload = {
      orgId: this.sessionData.orgId,
      orgGrpId: this.sessionData.orgGroupId,
      createdUsrId: this.sessionData.regId,
      vendorInvoiceId: listDetails.vendorInvoiceId
    };
    this.wareHouseApiService.fetchVendorTransactions(payload).subscribe(response => {
      if (response && response.responseCode === 'E200') {
        const data = [];
        data.push(response.data);
        this.transactionDetails = data;

      } else if (response && response.responseCode === 'E400') {

      }
    });
  }
  paymentMethod(vendor) {
    if (vendor.totalDueAmt <= 0) {
      this.payDisable = true;
    } else {
    this.fromDt = this.datePipe.transform(this.fromDate, 'yyyy-MM-dd');
    this.toDt = this.datePipe.transform(this.toDate, 'yyyy-MM-dd');
    vendor.fromDt = this.fromDt;
    vendor.toDt = this.toDt;
    this.warehoueDataService.paymentsList.next(vendor);
    this.router.navigate(['/warehouse/vendor-invoice-payment-screen']);
    }
  }

  previousTransactions() {
    this.isClicked = true;
  }
  invoiceDetails(invoice) {
    this.fromDt = this.datePipe.transform(this.fromDate, 'yyyy-MM-dd');
    this.toDt = this.datePipe.transform(this.toDate, 'yyyy-MM-dd');
    invoice.fromDt = this.fromDt;
    invoice.toDt = this.toDt;
    this.warehoueDataService.paymentsList.next(invoice);
    this.router.navigate(['/warehouse/vendor-invoice-details']);
  }
  showDiv(index) {
    this.currentElement = index;

  }
}
