import { Component, OnInit, ViewChild, ChangeDetectorRef } from '@angular/core';
import { UserSession } from '@core/models/user-session';
import { WarehouseApiService } from '@core/services/warehouse-api.service';
import { UserService } from '@core/services/user.service';
import { ToastrService } from 'ngx-toastr';
import { FormControl, FormGroup, FormBuilder, Validators, AbstractControl } from '@angular/forms';
import { WarehouseService } from '@app/modules/warehouse/warehouse.service';
import { Router, ActivatedRoute, NavigationEnd } from '@angular/router';
import { MatDialog, MatSidenav, MatSnackBar } from '../../../../../../node_modules/@angular/material';
import { CommentsDialogComponent } from '@shared/components/comments-dialog/comments-dialog.component';
import { UtilService } from '@core/services';
import { HttpErrorResponse } from '../../../../../../node_modules/@angular/common/http';

@Component({
  selector: 'app-list-of-vendors',
  templateUrl: './list-of-vendors.component.html',
  styleUrls: ['./list-of-vendors.component.scss']
})
export class ListOfVendorsComponent implements OnInit {
  private sessionData: UserSession;
  vendorSearchControl: AbstractControl;
  placeholderValue: string;
  vendorData: any;
  inputvendorName: '';
  inputvendorId: '';
  inputaccountNum: '';
  inputMobileNo: '';
  inputEmail: '';
  isFetchingData = true;
  checked = this.utilService.generateUniqueId();
  vendorsList: any;
  searchType: any;
  public side = false;
  @ViewChild('sidenav')
  sidenav: MatSidenav;
  public isOrganisation: true;
  patientData: any;
  // public side = false;
  constructor(
    userService: UserService,
    private toaster: ToastrService,
    private dialog: MatDialog,
    private wareHouseApiService: WarehouseApiService,
    private warehouseService: WarehouseService,
    private router: Router,
    private utilService: UtilService,
    private snackBar: MatSnackBar,
    private changeDetection: ChangeDetectorRef
  ) {
    this.sessionData = userService.getUser();
  }


  ngOnInit() {
    this.warehouseService.vendorData.next(null);
    this.warehouseService.purchaseData.next('list-of-vendors');
    this.getVendorsList();
    this.vendorSearchControl = new FormControl('mobile');
    this.placeholderValue = 'Search  by Vendor Name';
    this.searchType = 'vendorNm';
      this.vendorSearchControl.valueChanges.subscribe(value => {
      this.searchType = value;
      switch (this.searchType) {
        case 'vendorId':
          this.placeholderValue = 'Search  by Vendor ID';
          this.vendorData = '';
          this.getVendorsList();
          break;
        default:
          this.placeholderValue = 'Search  by Vendor Name';
          this.vendorData = '';
          this.getVendorsList();
          break;
      }
    });
  }

  getVendorsList() {
    const payload = {
      orgId: this.sessionData.orgId,
      orgGrpId: this.sessionData.orgGroupId,
      createdUsrId: this.sessionData.regId,
    };
    if (this.searchType === 'vendorNm') {
      payload['vendorNm'] = this.vendorData;
    } else if (this.searchType === 'vendorId') {
      payload['vendorRefNo'] = this.vendorData;
    }
    this.wareHouseApiService.fetchVendors(payload).subscribe(response => {
      if (response && response.responseCode === 'E200') {
        this.vendorsList = response.data;
        if (this.vendorsList.length) {
          this.isFetchingData = true;
        } else {
          this.isFetchingData = false;
        }
      } else if (response && response.responseCode === 'E400') {
        this.snackBar.open(response.responseMessage, 'close', {
          duration: 1000,
        });
      }
      this.changeDetection.detectChanges();
    });
  }

  // EDIT VENDOR FUNCTIONALITY

  editVendor(vendor) {
    this.warehouseService.vendorData.next(vendor);
    this.router.navigate(['/warehouse/add-new-vendor']);
  }

  deleteVendor(vendor, index) {
    const dialogRef = this.dialog.open(CommentsDialogComponent, {
      data: {
        title: 'Delete Vendor',
        data: 'Reason for Deleting Vendor'
      },
      disableClose: true
    });
    dialogRef.afterClosed().subscribe(result => {
      if (result) {
        const deletePayload = {
          orgId: this.sessionData.orgId,
          orgGrpId: this.sessionData.orgGroupId,
          createdUsrId: this.sessionData.regId,
          vendorInfoId: vendor.vendorInfoId,
          deleteReason: result
        };
        this.wareHouseApiService.deactivateVendor(deletePayload).subscribe(
          (response: any) => {
            if (response && response.responseCode === 'E200') {
              this.getVendorsList();
            } else if (response && response.responseCode === 'E400') {
              this.snackBar.open(response.responseMessage, 'close', {
                duration: 3000,
              });
            }
          },
          (errorResponse: HttpErrorResponse) => {
            if (errorResponse.status > 0) {
              this.snackBar.open(errorResponse.error.responseMessage, 'dismiss', {
                duration: 3000,
              });
            } else {
              // network failure
              this.snackBar.open('Network slow', 'dismiss', {
                duration: 3000,
              });
            }
          }
        );
      }
    });

  }
  // history() {
  //   this.router.navigate(['/warehouse/add-new-vendor']);
  // }
  openSidenav(vendor) {
    this.side = true;
    this.warehouseService.vendorData.next(vendor);
    this.sidenav.open();
  }

  getALlVendors() {
    if (!this.vendorData) {
      this.getVendorsList();
    }
  }
}
