import { Component, OnInit, ChangeDetectorRef } from '@angular/core';
import { FormControl, FormGroup, FormBuilder, Validators, AbstractControl } from '@angular/forms';
import { UserSession } from '@core/models/user-session';
import { DatePipe } from '@angular/common';
import { MatSnackBar } from '@angular/material/snack-bar';
import { MatDialog, MatDatepicker } from '@angular/material';
import { Router } from '@angular/router';
import { UserService, UtilService } from '@core/services';
import { WarehouseService } from '../../warehouse.service';
import { HttpErrorResponse } from '@angular/common/http';
// tslint:disable-next-line: semicolon
import { WarehouseApiService } from '@core/services/warehouse-api.service';
@Component({
  selector: 'app-sale-returns-history',
  templateUrl: './sale-returns-history.component.html',
  styleUrls: ['./sale-returns-history.component.scss']
})
export class SaleReturnsHistoryComponent implements OnInit {
  private sessionData: UserSession;
  public fromDate = new Date();
  public toDate = new Date();
  customerSearchControl: AbstractControl;
  customerData: any;
  searchType: any;
  saleHisoryList: any;
  saleorderId = null;
  customerId = null;
  dateMax = new Date();
  placeholderValue: string;
  public saleHisoryItemList: any;
  public fetchSaleOrders: any[] = [];
  // warehouseApiService: any;
  public saleOrderData: any;
  checked = this.utilService.generateUniqueId();
  minDate: Date;
  indexNum: any;
  public showBatches = false;
  constructor(
    private router: Router,
    userService: UserService,
    private utilService: UtilService,
    private warehouseService: WarehouseService,
    private snackBar: MatSnackBar,
    private datePipe: DatePipe,
    public dialog: MatDialog,
    private warehouseApiService: WarehouseApiService,
    private changeDetection: ChangeDetectorRef
  ) {
    this.sessionData = userService.getUser(); }
/** This method is triggerd at the time of page loding and also initializing the valibles in side this method */
  ngOnInit() {
    this.fromDate = this.warehouseService.getSaleReturnsFromDate();
    this.toDate = this.warehouseService.getSaleReturnsToDate();
    this.placeholderValue = 'Search  by Customer ID';
    this.fetchSaleorderHistoryFn(this.searchType, this.customerData);
    if (!this.fromDate) {
      this.fromDate = new Date();
      this.toDate = new Date();
    }
    this.customerSearchControl = new FormControl('mobile');
    /** This method verifies the search control changes using filters like customerId and SaleorderId */
    this.customerSearchControl.valueChanges.subscribe(value => {
      this.searchType = value;
      switch (this.searchType) {
        case 'SO':
          this.placeholderValue = 'Search  by Sale Order ID';
          this.customerData = '';
          this.fetchSaleorderHistoryFn('', '');
          break;
          default:
          this.placeholderValue = 'Search  by Customer ID';
          this.customerData = '';
          this.fetchSaleorderHistoryFn('', '');
          break;
      }
    });
  }
  /** This method is used for date picker to perform the open and close actions */
  datePickerToggler(datePicker: MatDatepicker<Date>) {
    datePicker[datePicker.opened ? 'close' : 'open']();
  }
  /** This method is used for chage the from Date and toDate */
  onChangeDates() {
    if (!this.fromDate || !this.toDate) {
      return false;
    }
    this.minDate = this.fromDate;
    this.fetchSaleorderHistoryFn(this.searchType, this.customerData);
    this.warehouseService.setSaleReturnsFromDate(this.fromDate);
    this.warehouseService.setSaleReturnsToDate(this.toDate);
  }
/** This method is used for get(Fetch) the data  based on from date and Todate and also based on search control like customerId, sale orderId*/
  fetchSaleorderHistoryFn( searchType: any, customerData: any) {
    const payload = {
    createdUsrId:  this.sessionData.regId,
    toDt: this.datePipe.transform(this.toDate, 'yyyy-MM-dd'),
    fromDt: this.datePipe.transform(this.fromDate, 'yyyy-MM-dd'),
    orderStatus: 'string',
    orgId: this.sessionData.orgId,
    orgGrpId: this.sessionData.orgGroupId,
      };
      if (searchType === 'SO') {
        payload['saleOrderNo'] = customerData;
      } else {
        payload['customerId'] = customerData;
      }
      this.warehouseApiService.fetchSaleOrdersHisotoryFn(payload).subscribe(
        response => {
          if (response && response.responseCode === 'E200') {
            this.saleHisoryList = response.data;
            this.saleHisoryList.forEach(element => {
              element['showPlus'] = false;
            });
          } else if (response && response.responseCode === 'E400') {
            this.snackBar.open(response.responseMessage, 'dismiss', {
              duration: 1500
            });
          }
          this.changeDetection.detectChanges();
        },
        (errorResponse: HttpErrorResponse) => {
          if (errorResponse.status > 0) {
            this.snackBar.open(errorResponse.error.responseMessage, 'dismiss', {
              duration: 1500
            });
          } else {
            // network failure
            this.snackBar.open('Network slow', 'dismiss', {
              duration: 1500
            });
          }
        }
      );
    }
  /**  @@description: this method is used to get the retun history based on saleorderId and also customerId */
  /** sale return history item details function strat here */
    fetchSalesItemReturnDeatils(row, index) {
      if (this.saleHisoryList[index].isShow === true) {
        this.saleHisoryList[index].isShow = false;
      } else {
        this.saleHisoryList[index].isShow = true;
      }
      this.saleHisoryList.forEach(el => {
        el['isShow'] = false;
      });
      this.saleHisoryList[index].isShow = true;
    const payload = {
    createdUsrId:  this.sessionData.regId,
    toDt: this.datePipe.transform(this.toDate, 'yyyy-MM-dd'),
    fromDt: this.datePipe.transform(this.fromDate, 'yyyy-MM-dd'),
    orgId: this.sessionData.orgId,
    orgGrpId: this.sessionData.orgGroupId,
    saleOrderReturnId: row.saleOrderReturnId,
    saleOrderNo: row ? row.saleOrderNo : null,
    saleOrderId: row ? row.saleOrderId : null,
    customerId: row ? row.customerId : null,
      };
      this.warehouseApiService.fetchSalesItem(payload).subscribe(
        response => {
          if (response && response.responseCode === 'E200') {
            this.saleHisoryItemList = response.data;
            this.fetchSaleOrders = this.saleHisoryItemList.fetchReturnsItemsList;
            this.fetchSaleOrders.forEach(val => {
            return val.showBatchDetails = false;
            });
          } else if (response && response.responseCode === 'E400') {
            this.snackBar.open(response.responseMessage, 'dismiss', {
              duration: 1500
            });
          }
          this.changeDetection.detectChanges();
        },
        (errorResponse: HttpErrorResponse) => {
          if (errorResponse.status > 0) {
            this.snackBar.open(errorResponse.error.responseMessage, 'dismiss', {
              duration: 1500
            });
          } else {
            // network failure
            this.snackBar.open('Network slow', 'dismiss', {
              duration: 1500
            });
          }
        }
      );
    }
    /** This method is used for toogle the saleorders/customer orders based on index fires */
    accordionToggle(index) {
      this.saleHisoryList[index].isShow = !this.saleHisoryList[index].isShow;
    }
  /** Show batch details functions start here */
  showBatchDetails (index) {
  this.indexNum = index;
  this.fetchSaleOrders[index].showBatchDetails = ! this.fetchSaleOrders[index].showBatchDetails;
  this.showBatches = true;

}
}
