import { Component, OnInit, ChangeDetectorRef, ViewChild } from '@angular/core';
import { Router } from '@angular/router';
import { FormControl, AbstractControl } from '@angular/forms';
import { WarehouseApiService } from '@core/services/warehouse-api.service';
import { UtilService, ReportsService } from '@core/services';
import { WarehouseService } from '@app/modules/warehouse/warehouse.service';
import { UserSession } from '@core/models/user-session';
import { UserService } from '@core/services/user.service';
import {
  MatDatepicker,
  MatSnackBar,
  MatDialog,
  MatSidenav
} from '@angular/material';
import { DatePipe } from '@angular/common';
import { PicklistOrderStatusComponent } from '../picklist-order-status/picklist-order-status.component';
import { CommentsDialogComponent } from '@shared/components/comments-dialog/comments-dialog.component';
import { HttpErrorResponse } from '@angular/common/http';
@Component({
  selector: 'app-sales-history',
  templateUrl: './sales-history.component.html',
  styleUrls: ['./sales-history.component.scss']
})
export class SalesHistoryComponent implements OnInit {
  private sessionData: UserSession;
  index: number;
  customerSearchControl: AbstractControl;
  customerData: any;
  inputOrderStatus: '';
  pickData = [];
  searchType: any;
  productDtlsList: any;
  inputDate: '';
  inputSONumber: '';
  inputPhoneNumber: '';
  inputPaidAmt: '';
  inputDueAmt: '';
  inputTotalAmt: '';
  inputCustomerName: '';
  patientControl = new FormControl();
  minDate: any;
  side = false;
  hideData = false;
  packingList: any;
  dateMax = new Date();
  types = [
    'Order Generated',
    'Pick List Prepared',
    'Ready for Shipment',
    'Shipped',
    'cancelled'
  ];
  date = this.dateMax;
  public showPaymenttable = false;
  showPaymentTableData = false;
  OrderStatusDropDown = false;
  public fromDate = new Date();
  public toDate = new Date();
  placeholderValue: string;
  checked = this.utilService.generateUniqueId();
  saleList: any;
  saleslist: any;
  transactionDetails = [];
  dummyarray = [];
  pickStatus = false;
  public currencyType: any;
  @ViewChild('sidenav')
  sidenav: MatSidenav;

  constructor(
    private router: Router,
    userService: UserService,
    private utilService: UtilService,
    private warehouseService: WarehouseService,
    private snackBar: MatSnackBar,
    private datePipe: DatePipe,
    private snackbar: MatSnackBar,
    private warehouseApiService: WarehouseApiService,
    public dialog: MatDialog,
    private changeDetection: ChangeDetectorRef,
    private reportsApiService: ReportsService
  ) {
    this.sessionData = userService.getUser();
  }

  ngOnInit() {
    this.fetchCurrencyType();
    this.warehouseService.creditData.next(null);
    this.fromDate = this.warehouseService.getSaleHistoryFromDate();
    this.toDate = this.warehouseService.getSaleHistoryToDate();
    this.warehouseService.vendorData.subscribe(res => {
      if (res) {
        this.saleslist = res;
      }
    });
    if (!this.fromDate) {
      this.fromDate = new Date();
      this.toDate = new Date();
    }
    this.getCustomersList(this.searchType, this.customerData);
    this.customerSearchControl = new FormControl('mobile');
    this.placeholderValue = 'Search  by SO Number';
    this.searchType = 'N';
    this.customerSearchControl.valueChanges.subscribe(value => {
      this.searchType = value;
      switch (this.searchType) {
        case 'I':
          this.placeholderValue = 'Search  by Customer Name';
          this.OrderStatusDropDown = false;
          this.customerData = null;
          this.customerData = '';
          break;
        case 'Y':
          this.placeholderValue = 'Search  by Order Status';
          this.OrderStatusDropDown = true;
          this.customerData = null;
          this.customerData = '';
          break;
        default:
          this.placeholderValue = 'Search  by SO Number';
          this.OrderStatusDropDown = false;
          this.customerData = '';

          break;
      }
    });
  }

  fetchCurrencyType() {
    const payload = {
      orgId: this.sessionData.orgId,
      orgGrpId: this.sessionData.orgGroupId,
      createdUsrId: this.sessionData.regId
    };
    this.warehouseApiService.fetchCurrencyType(payload).subscribe(
      response => {
        if (response && response.responseCode === 'E200') {
          this.currencyType = response.data;
        } else if (response && response.responseCode === 'E400') {
          this.snackBar.open(response.responseMessage, 'dismiss', {
            duration: 500
          });
        }
        this.changeDetection.detectChanges();
      },
      (errorResponse: HttpErrorResponse) => {
        if (errorResponse.status > 0) {
          this.snackBar.open(errorResponse.error.responseMessage, 'dismiss', {
            duration: 500
          });
        } else {
          // network failure
          this.snackBar.open('Network slow', 'dismiss', {
            duration: 500
          });
        }
      }
    );
  }
  getCustomersList(searchType, customerData) {
    const payload = {
      orgId: this.sessionData.orgId,
      orgGrpId: this.sessionData.orgGroupId,
      createdUsrId: this.sessionData.regId,
      fromDt: this.datePipe.transform(this.fromDate, 'yyyy-MM-dd'),
      toDt: this.datePipe.transform(this.toDate, 'yyyy-MM-dd'),
      customerNm: '',
      orderStatus: '',
      saleOrderNo: ''
    };
    if (searchType === 'Y') {
      payload.orderStatus = customerData;
    } else if (searchType === 'I') {
      payload.customerNm = customerData;
    } else if (searchType === 'N') {
      payload.saleOrderNo = customerData;
    }
    this.warehouseApiService.fetchSaleOrders(payload).subscribe(
      response => {
        if (response && response.responseCode === 'E200') {
          this.saleList = response.data;
          this.packingList = response.data;
          this.saleList.forEach(element => {
            element['showPlus'] = false;
          });
        } else if (response && response.responseCode === 'E400') {
          this.snackBar.open(response.responseMessage, 'dismiss', {
            duration: 1500
          });
        }
        this.changeDetection.detectChanges();
      },
      (errorResponse: HttpErrorResponse) => {
        if (errorResponse.status > 0) {
          this.snackBar.open(errorResponse.error.responseMessage, 'dismiss', {
            duration: 1500
          });
        } else {
          // network failure
          this.snackBar.open('Network slow', 'dismiss', {
            duration: 1500
          });
        }
      }
    );
  }
  onChangeDates() {
    this.showPaymentTableData = false;
    if (!this.fromDate || !this.toDate) {
      return false;
    }
    this.minDate = this.fromDate;
    this.getCustomersList(this.searchType, this.customerData);
    this.warehouseService.setSaleHistoryFromDate(this.fromDate);
    this.warehouseService.setSaleHistoryToDate(this.toDate);
  }

  /**
   * @function showPayment
   * @description to show the payment details based on the row selection
   * @param listDetails
   * @param index
   */
  showPayment(listDetails, index) {
    this.index = index;
    if (this.saleList[index].showPlus === false) {
      this.saleList.forEach(element => {
        element['showPlus'] = false;
      });
    this.saleList[index].showPlus = true;
    } else {
      this.saleList[index].showPlus = false;
    }
    this.getTrans(listDetails);
    this.showPaymentTableData = true;
  }
  picklist(sale) {
    this.pickData = [];
    this.pickStatus = true;
    this.pickData.push(sale);
  }
  getTrans(listDetails) {
    const payload = {
      orgId: this.sessionData.orgId,
      orgGrpId: this.sessionData.orgGroupId,
      createdUsrId: this.sessionData.regId,
      invoiceNO: listDetails.invoiceNo,
      orgCustomerId: listDetails.orgCustomerId
    };
    this.warehouseApiService
      .fetchCustomerTransactions(payload)
      .subscribe(response => {
        if (response && response.responseCode === 'E200') {
          const data = [];
          data.push(response.data);
          this.transactionDetails = data;
        } else if (response && response.responseCode === 'E400') {
        }
      });
  }

  sendDataToPicklist(inventory) {
    this.warehouseService.setSaleOrderId(inventory.saleOrderId);
    const payload = {
      createdUsrId: this.sessionData.regId,
      orgGrpId: this.sessionData.orgGroupId,
      orgId: this.sessionData.orgId,
      // saleOrderDetailedId: inventory.saleOrderDetailedId
      saleOrderId: inventory.saleOrderId
    };
    this.warehouseApiService.fetchPickListDetails(payload).subscribe(
      response => {
        if (response.responseCode === 'E200') {
          const data = [];
          data['customerNm'] = inventory.customerNm;
          data['saleOrderNo'] = inventory.saleOrderNo;
          data['saleOrderId'] = inventory.saleOrderId;
          data.push(response.data);
         this.warehouseService.saleOrderData.next(data);
         this.router.navigate(['warehouse/PickItems']);
        } else if (response.responseCode === 'E400') {
          this.snackbar.open(response.responseMessage, 'undo', {
            duration: 3000
          });
        }
      },
      () => {
        this.snackbar.open('somethin went wroing', 'undo', { duration: 3000 });
      }
    );
  }

  // date picker
  datePickerToggler(datePicker: MatDatepicker<Date>) {
    datePicker[datePicker.opened ? 'close' : 'open']();
  }
  goToPicklistDialog(pickDataStatus) {
    const typeCheckObj = {};
    typeCheckObj['saleOrderId'] = pickDataStatus.saleOrderId;
    const dialogRef = this.dialog.open(PicklistOrderStatusComponent, {
      data: typeCheckObj,
      height: '345px',
      width: '575px',
      disableClose: true
    });
    dialogRef.afterClosed().subscribe(result => {
      if (result) {
        if (result === true) {
          this.getCustomersList('', '');
          this.snackBar.open('orderStatus has been updated', 'dismiss', {
            duration: 800
          });
        }
      }
    });
  }
  saleOrderInvoice(sale) {
    this.warehouseService.vendorData.next(sale);
    this.router.navigate(['/warehouse/sale-order-invoice-breakdown']);
  }

  goToPaymentScreen(sale) {
    if (sale.totalDueAmt <= 0) {
      this.hideData = true;
      this.snackbar.open('Due Amount is Zero', 'dismiss', { duration: 2000 });
    } else {
      this.warehouseService.vendorData.next(sale);
      this.router.navigate(['/warehouse/payment-screen']);
    }
  }

  deleteOrder(sale) {
    const dialogRef = this.dialog.open(CommentsDialogComponent, {
      data: {
        title: 'Cancel Order',
        data: 'Reason for Cancelling Order'
      },
      disableClose: true
    });
    dialogRef.afterClosed().subscribe(result => {
      if (result) {
        const deletePayload = {
          orgId: this.sessionData.orgId,
          orgGrpId: this.sessionData.orgGroupId,
          createdUsrId: this.sessionData.regId,
          saleOrderId: sale.saleOrderId,
          cancelReason: result
        };
        this.warehouseApiService.cancelSaleOrder(deletePayload).subscribe(
          (response: any) => {
            if (response.responseCode === 'E200') {
              this.getCustomersList('', '');
              this.snackBar.open(response.responseMessage, 'dismiss', {
                duration: 1000
              });
            } else if (response.response === 'E400') {
              this.snackBar.open(response.responseMessage, 'dismiss', {
                duration: 500
              });
            }
          },
          err => {
            this.snackBar.open('Something went wrong', 'dismiss', {
              duration: 500
            });
          }
        );
      }
    });
  }
  openSideNav(inventory) {
    this.side = true;
    this.warehouseService.saleHistory.next(inventory);
    this.sidenav.open(this.productDtlsList);
    /**
     * Sending Picklist Data in Packing list
     */
    // const payload = {
    //   createdUsrId: this.sessionData.regId,
    //   orgGrpId: this.sessionData.orgGroupId,
    //   orgId: this.sessionData.orgId,
    //   saleOrderId: inventory.saleOrderId
    // };
    // this.warehouseApiService.fetchPickListDetails(payload).subscribe(
    //   (response) => {
    //     if (response && response.responseCode === 'E200') {
    //       this.productDtlsList = response.data;
    //       this.warehouseService.pickListData.next(this.productDtlsList);
    //     }
    //   },
    //   () => { }
    // );
  }

  // Below function is used to Print Sale order
  saleOrderPrint(saleOrderId) {
    const queryParams = {
      orgId: this.sessionData.orgId,
      loggedInUserId: this.sessionData.regId,
      saleOrderId: saleOrderId
    };
    this.reportsApiService.fetchWarehouseSaleOrderPrint(queryParams).subscribe(
      response => {
        if (response instanceof ArrayBuffer) {
          const saleOrderReport = new Blob([response], {
            type: 'application/pdf'
          });
          const reportURL = URL.createObjectURL(saleOrderReport);
          const win = window.open('', '_blank');
          win.location.href = reportURL;
          setTimeout(() => {
            win.print();
          }, 1000);
        } else if (response.responseCode) {
          this.snackBar.open('Something went wrong', 'dismiss', {
            duration: 800
          });
        }
      },
      () => {
        this.snackBar.open('Something went wrong', 'dismiss', {
          duration: 800
        });
      }
    );
  }
}
