import {
  Component,
  OnInit,
  OnDestroy,
  ViewChild,
  ElementRef
} from "@angular/core";
import { Router } from "@angular/router";
import { Subject } from "rxjs";
import { WarehouseService } from "../../warehouse.service";
import { UserSession } from "@core/models/user-session";
import { UserService } from "@core/services";
import { WarehouseApiService } from "@core/services/warehouse-api.service";
import { MatDialog, MatSnackBar } from "@angular/material";
import { HttpErrorResponse } from "@angular/common/http";
import { takeUntil } from "rxjs/operators";
import { GenerateStorageSpaceComponent } from "../generate-storage-space/generate-storage-space.component";
import { ConfirmationDialogComponent } from "@shared/components/confirmation-dialog/confirmation-dialog.component";

export interface DialogData {
  prefix: string;
  noOfStorage: string;
  suffix: any;
  range: any;
}
@Component({
  selector: "app-create-storage-location",
  templateUrl: "./create-storage-location.component.html",
  styleUrls: ["./create-storage-location.component.scss"]
})
export class CreateStorageLocationComponent implements OnInit, OnDestroy {
  destroyed$ = new Subject();
  private sessionData: UserSession;
  locationDetails: any;
  alleyResponse: any;
  public isAllay = false;
  oncheck: any;
  oncheckColumn: any;
  oncheckRack: any;
  @ViewChild("rownumber")
  rowNumber: ElementRef;
  locationNm: any;
  rackDisabled = false;
  public createStorageDetails: any;
  public checked = false;
  public checkedColumn = false;
  public checkedRack = false;
  public isEnable: number;
  public isEnableColumn: number;
  public isEnableRack: number;
  public saveEnable = true;
  public closeEnable = true;
  public isCancel = false;
  // public isRow = false;
  public isDisable = true;
  public rightArrow: number;
  public rightArrowColumn: number;
  public rightArrowRack: number;
  public enableEdit = false;
  public updatePayload: any;
  public isEdit = true;
  public updatePayloadArrRow = [];
  public updatePayloadArrRack = [];
  public updatePayloadArrColumn = [];
  public rowCheckData = [];
  public colCheckData = [];
  public rackCheckData = [];
  columnObj: any;
  columnResponse: any;
  public isColumn = false;
  deletePayload: {
    createdUsrId: number;
    orgGrpId: number;
    orgId: number;
    locationId: any;
  };
  tobeDeleted: any;
  tobeDeletedCol: any;
  tobeDeletedRack: any;
  clickRowData: any;
  clickColumnData: any;
  clickRackData: any;
  rackObj: any;
  rackResponse: any;
  public isRack = false;
  keUpRow: any;
  checkrowDataEdit: any;
  checkcolumnDataEdit: any;

  constructor(
    private router: Router,
    userService: UserService,
    private snackBar: MatSnackBar,
    public dialog: MatDialog,
    private wareHouseService: WarehouseService,
    private warehouseApiService: WarehouseApiService
  ) {
    this.sessionData = userService.getUser();
  }

  ngOnInit() {
    this.wareHouseService.createStorage
      .pipe(takeUntil(this.destroyed$))
      .subscribe(res => {
        if (res) {
          this.locationDetails = res;
        }
      });
    this.fetchRow();
    this.wareHouseService.createStorageColumn.next(null);
  }

  // fetching data for alleys
  fetchRow() {
    const createPayload = {
      createdUsrId: this.sessionData.regId,
      orgGrpId: this.sessionData.orgGroupId,
      orgId: this.sessionData.orgId,
      shedId: this.locationDetails.shedId
    };
    this.warehouseApiService
      .fetchShedRowxref(createPayload)
      .pipe(takeUntil(this.destroyed$))
      .subscribe(
        response => {
          if (response && response.responseCode === "E200") {
            this.alleyResponse = response.data;
            if (this.alleyResponse && this.alleyResponse.length) {
              this.isAllay = true;
            } else {
              this.isAllay = false;
            }
          } else if (response && response.responseCode === "E400") {
            this.snackBar.open(response.responseMessage, "dismiss", {
              duration: 500
            });
          }
        },
        (errorResponse: HttpErrorResponse) => {
          if (errorResponse.status > 0) {
            this.snackBar.open(errorResponse.error.responseMessage, "dismiss", {
              duration: 1500
            });
          } else {
            // network failure
            this.snackBar.open("Network slow", "dismiss", {
              duration: 1500
            });
          }
        }
      );
  }

  // fetch data for columns
  fetchColumn() {
    const createPayload = {
      createdUsrId: this.sessionData.regId,
      orgGrpId: this.sessionData.orgGroupId,
      orgId: this.sessionData.orgId,
      shedId: this.locationDetails.shedId,
      rowId: this.columnObj.rowId
    };
    this.warehouseApiService
      .fetchShedColumnRef(createPayload)
      .pipe(takeUntil(this.destroyed$))
      .subscribe(
        response => {
          if (response && response.responseCode === "E200") {
            this.columnResponse = response.data;
            if (this.columnResponse && this.columnResponse.length) {
              this.isColumn = true;
            } else {
              this.isColumn = false;
            }
            this.rackResponse = [];
          } else if (response && response.responseCode === "E400") {
            this.snackBar.open(response.responseMessage, "dismiss", {
              duration: 500
            });
          }
        },
        (errorResponse: HttpErrorResponse) => {
          if (errorResponse.status > 0) {
            this.snackBar.open(errorResponse.error.responseMessage, "dismiss", {
              duration: 1500
            });
          } else {
            // network failure
            this.snackBar.open("Network slow", "dismiss", {
              duration: 1500
            });
          }
        }
      );
  }

  // fetch data for racks
  fetchRack() {
    const createRackPayload = {
      createdUsrId: this.sessionData.regId,
      orgGrpId: this.sessionData.orgGroupId,
      orgId: this.sessionData.orgId,
      shedId: this.locationDetails.shedId,
      columnId: this.rackObj.columnId,
      rowId: this.columnObj.rowId
    };
    this.warehouseApiService
      .fetchShedTierxref(createRackPayload)
      .pipe(takeUntil(this.destroyed$))
      .subscribe(
        response => {
          if (response && response.responseCode === "E200") {
            this.rackResponse = response.data;
            if (this.rackResponse && this.rackResponse.length) {
              this.isRack = true;
            } else {
              this.isRack = false;
            }
          } else if (response && response.responseCode === "E400") {
            this.snackBar.open(response.responseMessage, "dismiss", {
              duration: 500
            });
          }
        },
        (errorResponse: HttpErrorResponse) => {
          if (errorResponse.status > 0) {
            this.snackBar.open(errorResponse.error.responseMessage, "dismiss", {
              duration: 1500
            });
          } else {
            // network failure
            this.snackBar.open("Network slow", "dismiss", {
              duration: 1500
            });
          }
        }
      );
  }

  update() {
    if (this.rowCheckData.length) {
      this.updateRow();
    }
    if (this.colCheckData.length) {
      this.updateColumn();
    }
    if (this.rackCheckData.length) {
      this.updateRack();
    }
    this.isEnableColumn = -1;
    this.isEnable = -1;
    this.isEnableRack = -1;
  }

  // for updating row or alley name  api
  updateRow() {
    this.warehouseApiService
      .insertOrUpdateShedRow(this.updatePayloadArrRow)
      .subscribe(
        response => {
          if (response && response.responseCode === "E200") {
            this.updatePayloadArrRow = [];
            this.rowCheckData = [];
            this.saveEnable = true;
            this.closeEnable = true;
            this.isDisable = true;
            setTimeout(() => {
              this.fetchRow();
            }, 300);
          } else if (response && response.responseCode === "E400") {
            this.snackBar.open(response.responseMessage, "dismiss", {
              duration: 500
            });
          } else {
            // network failure
            this.snackBar.open("Network slow", "dismiss", {
              duration: 500
            });
          }
        },
        (errorResponse: HttpErrorResponse) => {
          if (errorResponse.status > 0) {
            this.snackBar.open(errorResponse.error.responseMessage, "dismiss", {
              duration: 1500
            });
          } else {
            // network failure
            this.snackBar.open("Network slow", "dismiss", {
              duration: 1500
            });
          }
        }
      );
  }

  // to update column
  updateColumn() {
    this.warehouseApiService
      .insertOrUpdateShedColumnRef(this.updatePayloadArrColumn)
      .subscribe(
        response => {
          if (response && response.responseCode === "E200") {
            this.fetchColumn();
            this.updatePayloadArrColumn = [];
            this.colCheckData = [];
            this.saveEnable = true;
            this.closeEnable = true;
            this.isDisable = true;
            setTimeout(() => {
              this.fetchColumn();
            }, 300);
          } else if (response && response.responseCode === "E400") {
            this.snackBar.open(response.responseMessage, "dismiss", {
              duration: 500
            });
          } else {
            // network failure
            this.snackBar.open("Network slow", "dismiss", {
              duration: 500
            });
          }
        },
        (errorResponse: HttpErrorResponse) => {
          if (errorResponse.status > 0) {
            this.snackBar.open(errorResponse.error.responseMessage, "dismiss", {
              duration: 1500
            });
          } else {
            // network failure
            this.snackBar.open("Network slow", "dismiss", {
              duration: 1500
            });
          }
        }
      );
  }

  // to update rack
  updateRack() {
    this.warehouseApiService
      .insertOrUpdateShedTierxref(this.updatePayloadArrRack)
      .subscribe(
        response => {
          if (response && response.responseCode === "E200") {
            this.fetchRack();
            this.updatePayloadArrRack = [];
            this.rackCheckData = [];
            this.saveEnable = true;
            this.closeEnable = true;
            this.isDisable = true;
            setTimeout(() => {
              this.fetchRack();
            }, 300);
          } else if (response && response.responseCode === "E400") {
            this.snackBar.open(response.responseMessage, "dismiss", {
              duration: 500
            });
          } else {
            // network failure
            this.snackBar.open("Network slow", "dismiss", {
              duration: 500
            });
          }
        },
        (errorResponse: HttpErrorResponse) => {
          if (errorResponse.status > 0) {
            this.snackBar.open(errorResponse.error.responseMessage, "dismiss", {
              duration: 1500
            });
          } else {
            // network failure
            this.snackBar.open("Network slow", "dismiss", {
              duration: 1500
            });
          }
        }
      );
  }

  delete() {
    const dialogRef = this.dialog.open(ConfirmationDialogComponent, {
      data: { title: "Are You Sure You Want To Delete ?" },

      disableClose: true
    });
    dialogRef.afterClosed().subscribe(result => {
      if (result) {
        if (this.rowCheckData.length) {
          this.deleteRow();
        }
        if (this.colCheckData.length) {
          this.deleteColumn();
        }
        if (this.rackCheckData.length) {
          this.deleteRack();
        }
        this.isCancel = false;
      }
    });
  }

  deleteRow() {
    this.deletePayload = {
      createdUsrId: this.sessionData.regId,
      orgGrpId: this.sessionData.orgGroupId,
      orgId: this.sessionData.orgId,
      locationId: this.tobeDeleted.rowId
    };
    this.warehouseApiService.deleteShedRowXref(this.deletePayload).subscribe(
      response => {
        if (response && response.responseCode === "E200") {
          this.alleyResponse.splice(this.oncheck, 1);
          this.fetchRow();
          this.rowCheckData = [];
          this.isDisable = true;
          setTimeout(() => {
            this.fetchRow();
          }, 300);
        } else if (response && response.responseCode === "E400") {
          this.snackBar.open(response.responseMessage, "dismiss", {
            duration: 500
          });
        } else {
          // network failure
          this.snackBar.open("Network slow", "dismiss", {
            duration: 500
          });
        }
      },
      (errorResponse: HttpErrorResponse) => {
        if (errorResponse.status > 0) {
          this.snackBar.open(errorResponse.error.responseMessage, "dismiss", {
            duration: 1500
          });
        } else {
          // network failure
          this.snackBar.open("Network slow", "dismiss", {
            duration: 1500
          });
        }
      }
    );
  }

  // delete column data
  deleteColumn() {
    const deletePayloadCol = {
      createdUsrId: this.sessionData.regId,
      orgGrpId: this.sessionData.orgGroupId,
      orgId: this.sessionData.orgId,
      locationId: this.tobeDeletedCol.columnId
    };
    this.warehouseApiService.deleteShedColumnRef(deletePayloadCol).subscribe(
      response => {
        if (response && response.responseCode === "E200") {
          this.columnResponse.splice(this.oncheckColumn, 1);
          setTimeout(() => {
            this.fetchColumn();
          }, 300);
          this.colCheckData = [];
          this.isDisable = true;
        } else if (response && response.responseCode === "E400") {
          this.snackBar.open(response.responseMessage, "dismiss", {
            duration: 500
          });
        } else {
          // network failure
          this.snackBar.open("Network slow", "dismiss", {
            duration: 500
          });
        }
      },
      (errorResponse: HttpErrorResponse) => {
        if (errorResponse.status > 0) {
          this.snackBar.open(errorResponse.error.responseMessage, "dismiss", {
            duration: 1500
          });
        } else {
          // network failure
          this.snackBar.open("Network slow", "dismiss", {
            duration: 1500
          });
        }
      }
    );
  }

  // delete rack data
  deleteRack() {
    const deletePayloadRack = {
      createdUsrId: this.sessionData.regId,
      orgGrpId: this.sessionData.orgGroupId,
      orgId: this.sessionData.orgId,
      locationId: this.tobeDeletedRack.tierId
    };
    this.warehouseApiService.deleteShedTierXref(deletePayloadRack).subscribe(
      response => {
        if (response && response.responseCode === "E200") {
          this.rackResponse.splice(this.oncheckRack, 1);
          setTimeout(() => {
            this.fetchRack();
          }, 300);
          this.rackCheckData = [];
          this.isDisable = true;
        } else if (response && response.responseCode === "E400") {
          this.snackBar.open(response.responseMessage, "dismiss", {
            duration: 500
          });
        } else {
          // network failure
          this.snackBar.open("Network slow", "dismiss", {
            duration: 500
          });
        }
      },
      (errorResponse: HttpErrorResponse) => {
        if (errorResponse.status > 0) {
          this.snackBar.open(errorResponse.error.responseMessage, "dismiss", {
            duration: 500
          });
        } else {
          // network failure
          this.snackBar.open("Network slow", "dismiss", {
            duration: 500
          });
        }
      }
    );
  }

  // goes back to warehouse locations
  goBack() {
    this.router.navigate(["/warehouse/settings/warehouse-locations"]);
  }

  // for checkbox functionality
  onChecked(index, event, data) {
    // conditions kept for multiple check disabling
    this.checkrowDataEdit = data;
    if (event.target.checked) {
      const res = this.rowCheckData.find(item => item === data.rowNo);
      if (!res) {
        this.rowCheckData.push(data.rowNo);
        this.oncheck = index;
      }
    } else {
      const i = this.rowCheckData.findIndex(item => item === data.rowNo);
      this.rowCheckData.splice(i, 1);
      if (!this.rowCheckData.length) {
        this.oncheck = null;
      }
    }

    if (this.rowCheckData.length === 1) {
      this.isDisable = false;
    } else {
      this.isDisable = true;
    }
    if (this.colCheckData.length <= 1 || this.rackCheckData.length <= 1) {
      this.isDisable = false;
    }
    if (this.rowCheckData.length) {
      this.checked = true;
      this.isDisable = false;
    } else {
      this.checked = false;
      this.isDisable = true;
    }
    this.closeEnable = true;
    this.saveEnable = true;
    this.isEnable = -1;
    this.isEnableColumn = -1;
    this.isEnableRack = -1;

    this.tobeDeleted = data;
    // this.isDisable = false;
  }

  // for column checkbox
  onCheckedColumn(index, event, data) {
    this.checkcolumnDataEdit = data;
    if (event.target.checked) {
      const res = this.colCheckData.find(item => item === data.columnNo);
      if (!res) {
        this.colCheckData.push(data.columnNo);
        this.oncheckColumn = index;
      }
    } else {
      const i = this.colCheckData.findIndex(item => item === data.columnNo);
      this.colCheckData.splice(i, 1);
      if (!this.colCheckData.length) {
        this.oncheckColumn = null;
      }
    }
    // if (this.colCheckData.length === 1) {
    //   this.isDisable = false;
    // } else {
    //   this.isDisable = true;
    // }

    if (this.colCheckData.length) {
      this.checked = true;
      this.isDisable = false;
    } else {
      this.checked = false;
      this.isDisable = true;
    }
    this.isEnable = -1;
    this.isEnableColumn = -1;
    this.isEnableRack = -1;
    this.closeEnable = true;
    this.saveEnable = true;
    // if (this.rowCheckData.length <= 1 || this.rackCheckData.length <= 1 ) {
    //   this.isDisable = false;
    // }
    this.tobeDeletedCol = data;
    this.checkedColumn = true;
  }
  // on alley box click
  onClickAlley(i, event) {
    if (event.bubbles === true) {
      this.rackDisabled = false;
      console.log(this.rackDisabled, "rackdisabled");
    }
  }
  // on shelve click
  onClickShelve(i, event) {
    if (event.bubbles === true) {
      this.rackDisabled = false;
      console.log(this.rackDisabled, "rackdisabled");
    }
  }
  // onrack box click
  onClickRack(i, event) {
    console.log(event.bubbles, "clickfunevents");
    if (event.bubbles === true) {
      this.rackDisabled = true;
      console.log(this.rackDisabled, "rackdisabled");
    }
  }

  // for rack checkbox
  onCheckedRack(index, event, data) {
    if (event.target.checked) {
      const res = this.rackCheckData.find(item => item === data.tierNo);
      if (!res) {
        this.rackCheckData.push(data.tierNo);
        this.oncheckRack = index;
      }
    } else {
      const i = this.rackCheckData.findIndex(item => item === data.tierNo);
      this.rackCheckData.splice(i, 1);
      if (!this.rackCheckData.length) {
        this.oncheckRack = null;
      }
    }
    // if (this.rackCheckData.length === 1) {
    //   this.isDisable = false;
    // } else {
    //   this.isDisable = true;
    // }

    if (this.rackCheckData.length) {
      this.checked = true;
      this.isDisable = false;
    } else {
      this.checked = false;
      this.isDisable = true;
    }

    // if (this.rowCheckData.length <= 1 || this.colCheckData.length <= 1 ) {
    //   this.isDisable = false;
    // }
    // this.oncheckRack = index;
    this.tobeDeletedRack = data;
    this.checkedRack = true;

    this.closeEnable = true;
    this.saveEnable = true;
    this.isEnable = -1;
    this.isEnableColumn = -1;
    this.isEnableRack = -1;
  }

  // for label click row
  clickOnlabel(i, data) {
    this.checked = false;
    //this.closeEnable = false;
    this.isCancel = true;
    // this.clickRowData = data;
    this.rightArrow = i;
    this.alleyResponse.forEach(element => {
      if (element.rowId === data.rowId || !this.alleyResponse) {
        data["clickedOn"] = "row";
        this.columnObj = data;
        this.wareHouseService.createStorageColumn.next(this.columnObj);
        this.fetchColumn();
      }
    });
  }

  // for label click column
  clickOnlabelColumn(i, data) {
    this.checkedColumn = false;
    //this.closeEnable = false;
    this.isCancel = true;
    // this.clickColumnData = data;
    this.rightArrowColumn = i;
    this.columnResponse.forEach(element => {
      if (element.columnId === data.columnId || !this.columnResponse) {
        data["clickedOn"] = "column";
        this.rackObj = data;
        this.wareHouseService.createStorageColumn.next(this.rackObj);
        this.fetchRack();
      }
    });
  }
  // for label click rack
  // clickOnlabelRack(i, data) {
  // this.checkedRack = false;
  // this.clickRackData = data;
  // this.rightArrowRack = i;
  // }

  // for row key up
  keyUpEnable(data, index) {
    this.saveEnable = false;
    this.closeEnable = false;
    this.keUpRow = data;
    const alleyObj = {};
    this.alleyResponse.forEach(el => {
      if (el.rowId === data.rowId) {
        alleyObj["rowNo"] = data.rowNo;
        alleyObj["rowId"] = data.rowId;
        alleyObj["shedId"] = data.shedId;
        alleyObj["createdUsrId"] = this.sessionData.regId;
        alleyObj["orgGrpId"] = this.sessionData.orgGroupId;
        alleyObj["orgId"] = this.sessionData.orgId;
        this.updatePayloadArrRow.push(alleyObj);
      }
    });
  }

  // for column key up
  keyUpEnableColumn(data, i) {
    // this.saveEnableColumn = false;
    this.saveEnable = false;
    this.closeEnable = false;
    const columnObj = {};
    this.columnResponse.forEach(el => {
      if (el.columnId === data.columnId) {
        columnObj["columnNo"] = data.columnNo;
        columnObj["columnId"] = data.columnId;
        columnObj["shedId"] = data.shedId;
        columnObj["rowId"] = data.rowId;
        columnObj["createdUsrId"] = this.sessionData.regId;
        columnObj["orgGrpId"] = this.sessionData.orgGroupId;
        columnObj["orgId"] = this.sessionData.orgId;
        this.updatePayloadArrColumn.push(columnObj);
      }
    });
  }
  // for rack/tier key up
  keyUpEnableRack(data, i) {
    this.saveEnable = false;
    this.closeEnable = false;
    const rackObj = {};
    this.rackResponse.forEach(el => {
      if (el.tierId === data.tierId) {
        rackObj["tierNo"] = data.tierNo;
        rackObj["columnId"] = data.columnId;
        rackObj["shedId"] = data.shedId;
        rackObj["tierId"] = data.tierId;
        rackObj["createdUsrId"] = this.sessionData.regId;
        rackObj["orgGrpId"] = this.sessionData.orgGroupId;
        rackObj["orgId"] = this.sessionData.orgId;
        this.updatePayloadArrRack.push(rackObj);
      }
    });
  }

  createStorageSpace() {
    const dialogRef = this.dialog.open(GenerateStorageSpaceComponent, {
      width: "500px"
    });
    // this.locationDetails['clickedOn'] = 'row';
    this.wareHouseService.createStorage.next(this.locationDetails);
    dialogRef.afterClosed().subscribe(result => {
      if (result) {
        this.isCancel = false;
        this.fetchRow();
        this.fetchColumn();
        this.fetchRack();
      }
    });
  }

  close() {
    this.saveEnable = true;
    this.closeEnable = true;
    if (this.oncheck >= 0) {
      this.isEnable = -1;
      this.fetchRow();
      this.rowCheckData = [];
    }
    if (this.oncheckColumn >= 0) {
      this.isEnableColumn = -1;
      this.fetchColumn();
      this.colCheckData = [];
    }
    if (this.oncheckRack >= 0) {
      this.isEnableRack = -1;
      this.fetchRack();
      this.rackCheckData = [];
    }
    if (this.isCancel) {
      this.isRack = false;
      this.rackResponse = [];
      this.isColumn = false;
      this.columnResponse = [];
      this.fetchRow();
      this.wareHouseService.createStorageColumn.next(null);
    }
    this.isCancel = false;
    this.checked = false;
    this.isDisable = true;
  }

  // for editing row or alley name
  editRowNm() {
    this.isCancel = false;
    this.isEdit = false;
    this.isDisable = !this.isDisable;
    this.isEnable = this.oncheck;
    this.isEnableColumn = this.oncheckColumn;
    this.isEnableRack = this.oncheckRack;
    this.saveEnable = false;
    this.closeEnable = false;
  }

  ngOnDestroy() {
    this.destroyed$.next();
    this.destroyed$.complete();
  }
}
