import { Component, OnInit } from '@angular/core';
import { MatDatepicker, MatDialog, MatSnackBar } from '@angular/material';
import { FormGroup, FormBuilder, Validators, FormArray, FormControl } from '@angular/forms';
import { Observable, concat, of, Subject, Subscription } from 'rxjs';
import { UserSession } from '@core/models/user-session';
import { UserService } from '@core/services/user.service';
import { DatePipe } from '@angular/common';
import { Router } from '@angular/router';
import { WarehouseApiService } from '@core/services/warehouse-api.service';
import { WarehouseService } from '@app/modules/warehouse/warehouse.service';
import { HttpErrorResponse } from '@angular/common/http';
import {
  debounceTime,
  distinctUntilChanged,
  filter,
  switchMap,
  map,
  catchError,
  tap
} from 'rxjs/operators';
import { LocationDialogComponent } from '../location-dialog/location-dialog.component';
import { BarcodeComponent } from '../barcode/barcode.component';
@Component({
  selector: 'app-add-to-list',
  templateUrl: './add-to-list.component.html',
  styleUrls: ['./add-to-list.component.scss']
})
export class AddToListComponent implements OnInit {
  vendorInfo: any;
  isCopied = false;
  today = new Date();
  addToListForm: FormGroup;
  public isEditable: Boolean;
  editInventory = false;
  private partDataSubscription: Subscription;
  partName$: Observable<any>;
  vendorNm$: Observable<any>;
  partNameListinput$ = new Subject<string>();
  vendorNmListinput$ = new Subject<string>();
  copiedData = [];
  vendorsList: any;
  private sessionData: UserSession;
  inventoryList: any;
  partId: any;
  public currencyType: any;
  partName: any;

  constructor(
    private formbuilder: FormBuilder,
    private userService: UserService,
    private snackBar: MatSnackBar,
    private router: Router,
    private datePipe: DatePipe,
    private wareHouseApiService: WarehouseApiService,
    private wareHouseDataService: WarehouseService,
    public dialog: MatDialog,
  ) {
    this.sessionData = userService.getUser();
  }

  ngOnInit() {
    this.wareHouseDataService.purchaseData.next('add-to-list');
    this.fetchCurrencyType();
    this.initForm();
    this.fetchPartDetails();
    //this.fetchVendorsList();
   // this.fetchVendorDetails();
   this.fetchVendors(null);
    this.partDataSubscription = this.wareHouseDataService.vendorData.subscribe(
      res => {
        if (res) {
          this.inventoryList = res;
          this.isEditable = true;
          this.getEditData(this.inventoryList);
        }
      }
    );
    this.wareHouseDataService.vendorData.next(null);
  }
  datePickerToggler(datePicker: MatDatepicker<Date>) {
    datePicker[datePicker.opened ? 'close' : 'open']();
  }
  initForm() {
    this.addToListForm = this.formbuilder.group({
      partNameOrNumber: this.formbuilder.control(''),
      vendorNm: this.formbuilder.control(''),
      partDescription: this.formbuilder.control(''),
      vendorInvoiceItemsList: this.formbuilder.array([])
    });
    // this.addToListForm.get('vendorNm').valueChanges.subscribe(val => {
    //   if (val) {
    //    // this.vendorEmaild = val.emailId;
    //     this.fetchVendors(val);
    //   }
    // });
  }

  get manageFormArray() {
    return <FormArray>this.addToListForm.get('vendorInvoiceItemsList');
  }
  remove(i) {
    this.manageFormArray.removeAt(i);
  }

  fetchCurrencyType() {
    const payload = {
      orgId: this.sessionData.orgId,
      orgGrpId: this.sessionData.orgGroupId,
      createdUsrId: this.sessionData.regId
    };
    this.wareHouseApiService.fetchCurrencyType(payload).subscribe(
      response => {
        if (response && response.responseCode === 'E200') {
          this.currencyType = response.data;
        } else if (response && response.responseCode === 'E400') {
          this.snackBar.open(response.responseMessage, 'dismiss', {
            duration: 500
          });
        }
      },
      (errorResponse: HttpErrorResponse) => {
        if (errorResponse.status > 0) {
          this.snackBar.open(errorResponse.error.responseMessage, 'dismiss', {
            duration: 500
          });
        } else {
          // network failure
          this.snackBar.open('Network slow', 'dismiss', {
            duration: 500
          });
        }
      }
    );
  }
  // to fetch part details by entering specific part
  fetchPartDetails() {
    this.partName$ = concat(
      of([]), // default items
      this.partNameListinput$.pipe(
        debounceTime(200),
        distinctUntilChanged(),
        tap(console.log),
        filter(checkStringCount => {
          if (checkStringCount && checkStringCount.length > 1) {
            return true;
          }
        }),
        switchMap(this.fetchParts.bind(this))
      )
    );
  }

    // to fetch Vendor details by entering specific vendor
    fetchVendorDetails() {
      this.vendorNm$ = concat(
        of([]), // default items
        this.vendorNmListinput$.pipe(
          debounceTime(200),
          distinctUntilChanged(),
          tap(console.log),
          filter(checkStringCount => {
            if (checkStringCount && checkStringCount.length > 1) {
              return true;
            }
          }),
          switchMap(this.fetchVendors.bind(this))
        )
      );
    }
  /** navigate to barcode */
  openBarcode(data) {
    // this.router.navigate(['/warehouse/barcode']);
    const dialogRef = this.dialog.open(BarcodeComponent, {
      width: '700px',
      height: '500px'
    });
    dialogRef.afterClosed().subscribe(result => {
      if (result) {
        data.patchValue({ barcode: result });
        console.log(result);
      }
    });
  }
  fetchParts(string) {
    const payload = {
      createdUsrId: this.sessionData.regId,
      orgGrpId: this.sessionData.orgGroupId,
      orgId: this.sessionData.orgId,
      isDropDown: false,
      partNmOrNo: string
    };
    return this.wareHouseApiService.fetchPartDetails(payload).pipe(
      map((response: any) => {
        if (response.responseCode === 'E200') {
          return response.data;
        } else if (response.response === 'E400') {
        }
      }),
      catchError(error => {
        return of([]);
      })
    );
  }

  openDialog(data) {
    console.log(data.value);
    const dialogRef = this.dialog.open(LocationDialogComponent, {
      width: '700px',
      height: '200px'
    });
    dialogRef.afterClosed().subscribe(result => {
      if (result) {
        console.log(result);
        data.patchValue({
          location:
            result.shedNm +
            '/' +
            result.rowNo +
            '/' +
            result.columnNo +
            '/' +
            result.tierNo
        });
        data.value.rowId = result.rowId;
        data.value.columnId = result.columnId;
        data.value.tierId = result.tierId;
        data.value.shedId = result.shedId;
      }
    });
  }

  // the following method will display or patch the part related information

  onAddPart(part) {
    if (!part.receivedDt) {
      part.receivedDt = new Date();
    }
    let location;
    if (part.shedNm) {
    location = part.shedNm + '/' + part.rowNo + '/' + part.columnNo + '/' + part.tierNo;
    }
    const creds = this.addToListForm.controls
      .vendorInvoiceItemsList as FormArray;
    creds.push(
      this.formbuilder.group({
        createdUsrId: this.sessionData.regId,
        orgId: this.sessionData.orgId,
        orgGrpId: this.sessionData.orgGroupId,
        partId: part.partId,
        vendorInfoId: '',
        partProductInd: part.partProductInd,
        inventoryId: part.inventoryId,
        batchNo: [part.batchNo, Validators.required],
        versionId: part.versionId,
        columnId: part.columnId,
        receivedDt: [part.receivedDt, Validators.required],
        rowId: part.rowId,
        shedId: part.shedId,
        tierId: part.tierId,
        partNm: part.partNm,
        barcode: [part.barcode, Validators.required],
        uom: part.uom,
        qty: [part.qty, Validators.required],
        unitAmt: [part.unitAmt, Validators.required],
        vendorNm: '',
        description: part.description,
        expireDt: [
          this.datePipe.transform(part.expireDt, 'yyyy-MM-dd'),
          // Validators.required
        ],
        serviceAmt: part.serviceAmt,
        mrp: [part.mrp, Validators.required],
        location: [location, Validators.required],
        copyDetails: this.formbuilder.array([])
      })
    );
    this.addToListForm.patchValue({ partDescription: part.description });
  }
  addNewFields(creds, fieldData) {
    creds.push(
      this.formbuilder.group({
        createdUsrId: this.sessionData.regId,
        orgId: this.sessionData.orgId,
        orgGrpId: this.sessionData.orgGroupId,
        partId: fieldData.value.partId,
        partNm: fieldData.value.partNm,
        partProductInd: fieldData.value.partProductInd,
        inventoryId: fieldData.value.inventoryId,
        versionId: fieldData.value.versionId,
        receivedDt: [fieldData.value.receivedDt, Validators.required],
        columnId: fieldData.value.columnId,
        rowId: fieldData.value.rowId,
        shedId: fieldData.value.shedId,
        tierId: fieldData.value.tierId,
        batchNo: [fieldData.value.batchNo, Validators.required],
        barcode: [fieldData.value.barcode, Validators.required],
        uom: fieldData.value.uom,
        qty: [fieldData.value.qty, Validators.required],
        unitAmt: [fieldData.value.unitAmt, Validators.required],
        description: fieldData.value.description,

        vendorNm: '',
        expireDt: [fieldData.value.expireDt],
        serviceAmt: fieldData.value.serviceAmt,
        mrp: [fieldData.value.mrp, Validators.required],
        location: [fieldData.value.location, Validators.required],
        vendorInfoId: fieldData.value.vendorInfoId
      })
    );
  }

  getEditData(part) {
    this.editInventory = true;
    this.onAddPart(part);
    this.addToListForm.patchValue({ partNameOrNumber: part.partNmOrNo });
    // this.addToListForm.patchValue({ vendorName: part.vendorNm });
    this.addToListForm.get('vendorNm').patchValue(part.vendorNm);
    this.vendorInfo = this.addToListForm.get('vendorNm').value;
    this.addToListForm.patchValue({ partDescription: part.description });
  }
  /**
   * @description comparing two objects for update VendorName
   * @function dilationTypeComparator
   * @param {object, object}
   */
  dilationTypeComparator(o1, o2) {
    return o1.vendorInfoId === o2.vendorInfoId;
  }

  // add to inventory method
  addToInventory() {
    let url = 'insertWarehouseInventory';
    let payload;
    const vendorItemsFilterList = [];
    const vendorItemList = this.addToListForm.get('vendorInvoiceItemsList')
      .value;
    vendorItemList.forEach(obj => {
      if (obj.copyDetails.length > 0) {
        obj.copyDetails.forEach(elemen => {
          vendorItemsFilterList.push(elemen);
        });
      }
    });
    vendorItemsFilterList.forEach(eleme => {
      vendorItemList.push(eleme);
    });
    vendorItemList.forEach(el => {
      el.vendorNm = this.addToListForm.value.vendorNm.vendorNm;
      el.vendorInfoId = this.addToListForm.value.vendorNm.vendorInfoId;
      el.description = this.addToListForm.value.partDescription;
      el.expireDt = this.datePipe.transform(el.expireDt, 'yyyy-MM-dd');
      el.receivedDt = this.datePipe.transform(el.receivedDt, 'yyyy-MM-dd');
      el.partNmOrNo = this.addToListForm.value.partNameOrNumber.partNmOrNo;
    });
    //  payload = vendorItemList;
    if (this.editInventory === true) {
      url = 'updateWarehouseInventory';
      payload = vendorItemList[0];
    } else {
      payload = vendorItemList;
    }
    this.wareHouseApiService[url](payload).subscribe(response => {
      if (response && response.responseCode === 'E200') {
        this.router.navigate(['/warehouse/inventory/inventory-list']);
      } else if (response && response.responseCode === 'E400') {
        this.snackBar.open(response.responseMessage, 'dismiss', {
          duration: 1000
        });
      }
    });
  }

  // to get vendor list
  fetchVendorsList() {
    const payload = {
    orgId: this.sessionData.orgId,
    orgGrpId: this.sessionData.orgGroupId,
    createdUsrId: this.sessionData.regId,
    };

    this.wareHouseApiService.fetchVendors(payload).subscribe((response: any) => {
    if (response && response.responseCode === 'E200') {
    this.vendorsList = response.data;
    } else if (response && response.responseCode === 'E400') {
    }
    });
    }

    vendorComparer(o1, o2) {
      return o1 && o2 && o1.vendorInfoId === o2.vendorInfoId;
    }

    fetchVendors(data) {
      const payload = {
        orgId: this.sessionData.orgId,
        orgGrpId: this.sessionData.orgGroupId,
        createdUsrId: this.sessionData.regId,
        dropDown: !data,
        vendorInfoId: data ? (data.vendorInfoId ? data.vendorInfoId : null) : null
      };

      this.wareHouseApiService.fetchVendors(payload).subscribe(
        (response): any => {
          if (response && response.responseCode === 'E200') {
            // if (response.data[0].emailId) {
            //   this.isMailPDF = true;
            // }

            if (data && data.vendorInfoId) {
              const index = this.vendorsList.findIndex(
                val => val.vendorInfoId === data.vendorInfoId
              );
              this.vendorsList[index] = response.data[0];
              // this.purchaseOrderForm
              //   .get('vendorNm')
              //   .patchValue(this.vendorsList[index], { emitEvent: false });
              // this.currentSelectedVendor$.next(response.data[0]);
            } else {
              this.vendorsList = response.data;
            }
            // this.partNmOrNo = null;
          } else if (response && response.responseCode === 'E400') {
          }
        },
        (errorResponse: HttpErrorResponse) => {
          if (errorResponse.status > 0) {
            this.snackBar.open(errorResponse.error.responseMessage, 'dismiss', {
              duration: 500
            });
          } else {
            // network failure
            this.snackBar.open('Network slow', 'dismiss', {
              duration: 500
            });
          }
        }
      );
    }

    fetchVendors11(string) {
      const payload = {
        orgId: this.sessionData.orgId,
        orgGrpId: this.sessionData.orgGroupId,
        createdUsrId: this.sessionData.regId,
        vendorNm: string
      };
      return this.wareHouseApiService.fetchVendors(payload).pipe(
        map((response: any) => {
          if (response.responseCode === 'E200') {
            return response.data;
          } else if (response.response === 'E400') {
          }
        }),
        catchError(error => {
          return of([]);
        })
      );
    }

     // for deleting row of copy details
  delete(i, j) {
    const control = <FormArray>this.addToListForm.get('vendorInvoiceItemsList')['controls'][i].get('copyDetails');
 control.removeAt(j);
   }

  reset(creds) {
    creds.patchValue({ barcode: '' });
    creds.patchValue({ batchNo: '' });
    creds.patchValue({ expireDt: '' });
    creds.patchValue({ uom: '' });
    creds.patchValue({ qty: '' });
    creds.patchValue({ unitAmt: '' });
    creds.patchValue({ mrp: '' });
    creds.patchValue({ location: '' });
    creds.patchValue({ receivedDt: '' });
  }

  cancel() {
    this.wareHouseDataService.vendorData.next(null);
    this.router.navigate(['/warehouse/inventory/inventory-list']);
  }
}
