import { Component, OnInit, Inject, OnDestroy } from '@angular/core';
import { MAT_DIALOG_DATA, MatDialogRef, MatSnackBar } from '@angular/material';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
@Component({
  selector: 'app-sale-return-comform-model',
  templateUrl: './sale-return-comform-model.component.html',
  styleUrls: ['./sale-return-comform-model.component.scss']
})
export class SaleReturnComformModelComponent implements OnInit {
  formData: any;
  saleReturnData: FormGroup;
  constructor(
    private readonly fb: FormBuilder,
    @Inject(MAT_DIALOG_DATA)
    private readonly injectedData,
    private readonly dialogRef: MatDialogRef<SaleReturnComformModelComponent>,
    private readonly snackBar: MatSnackBar,
  ) {

  }
  ngOnInit() {
    this.initForm();
  }

  initForm() {
   const { required, pattern } = Validators;
    this.saleReturnData = this.fb.group({
      returnRemarks: [null, required],
      isCreditNote: [null, required]
    });
  }
  salesReturnRequest() {
    // if (!this.saleReturnData.valid) {
    //   return;
    // }
    if (this.saleReturnData.valid) {
      this.formData = this.saleReturnData.value;
      this.dialogRef.close(this.formData);
  }
  }
  closePartsModel() {
    this.dialogRef.close();
  }
}
