import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators, } from '@angular/forms';
import { Subscription } from 'rxjs';
import { ToastrService } from 'ngx-toastr';
import { AutoUnsubscribe } from 'ngx-auto-unsubscribe';
import { UserSession } from '@core/models/user-session';
import { UserService } from '@core/services/user.service';
import { WarehouseApiService } from '@core/services/warehouse-api.service';
import { WarehouseService } from '@app/modules/warehouse/warehouse.service';
import { MatDatepicker, MatSnackBar } from '@angular/material';
import { DatePipe } from '@angular/common';
import { MatDialog } from '@angular/material';
import { Router } from '@angular/router';
import { UploadDocComponent } from './../upload-doc/upload-doc.component';
import { HttpErrorResponse } from '@angular/common/http';
@Component({
  selector: 'app-add-new-vendor',
  templateUrl: './add-new-vendor.component.html',
  styleUrls: ['./add-new-vendor.component.scss']
})
export class AddNewVendorComponent implements OnInit {
  addVendorForm: FormGroup;
  public apiSubscription: Subscription;
  private sessionData: UserSession;
  private vendorDataSubscription: Subscription;
  vendorList: any;
  vendorsList: any;
  inventoryFormSubmitted = false;
  uploadTable = false;
  uploadDocumentsList: any = [];
  checkValue: boolean;
  types = ['Individual', 'Organization'];
  private orgType: any;
  dateOfBirth = new Date();
  public isEditable: Boolean;
  vendor_details_validation_messages = {
    firstName: [
      { type: 'required', message: '* First Name is required' },
      { type: 'pattern',
       message: 'Enter Valid Name'},
       {type: 'maxlength', message: '* First Name is too long'}
    ],
    lastName: [
      { type: 'required', message: '* Last Name is required' },
      { type: 'pattern',
      message: 'Enter Valid Name'},
      {type: 'maxlength', message: '* Last Name is too long'}

    ],
    middleNm: [
      {type: 'maxlength', message: '* Middle Name is too long'}
    ],

    HonorificName: [
      { type: 'required', message: '* Honorific is required' },
    ],
    PhoneNumber: [
      { type: 'required', message: '* Contact Number is required' },
      {
        type: 'pattern',
        message: 'Enter Valid Number'
      }
    ],
    RegistrationType: [
      { type: 'required', message: '* Register Type is required' },
    ],
    EmailId: [
      { type: 'required', message: '* Email Id  is required' },
      {
        type: 'pattern',
        message: 'Enter Valid Email'
      }
    ],
    Address: [
      { type: 'required', message: '* Address  is required' },
    ],
    City: [
      { type: 'required', message: '* City  is required' },
    ],
    State: [
      { type: 'required', message: '* State  is required' },
    ],
    Country: [
      { type: 'required', message: '* Country  is required' },
    ],
    ZipCode: [
      { type: 'required', message: '* Pincode  is required' },
      { type: 'maxlength', message: '* Pincode is too long'}
    ],
    Organization: [
      { type: 'required', message: '* Organization Name  is required' },

    ],
    faxNo: [
      {type: 'required', message: '*Fax Number is required'},
      {type: 'maxlength', message: '* Fax Number is too long'},
    ]
    ,
    taxIdentifier: [
      {type: 'maxlength', message: '* Tax Identifier is too long'}
    ]


  };
  constructor(
    private formbuilder: FormBuilder,
    userService: UserService,
    private toaster: ToastrService,
    private wareHouseApiService: WarehouseApiService,
    private wareHouseDataService: WarehouseService,
    private datePipe: DatePipe,
    public dialog: MatDialog,
    private snackBar: MatSnackBar,
    private router: Router
  ) {
    this.sessionData = userService.getUser();
  }

  ngOnInit() {
    this.initForm();
    this.vendorDataSubscription = this.wareHouseDataService.vendorData.subscribe(res => {
      if (res) {
        this.vendorList = res;
        this.uploadDocumentsList = this.vendorList.uploadDocumentsList ? null : [];
        this.isEditable = true;
        this.uploadTable = true;
        // this.getFetchData();
        this.getEditData();
      }
    });
    this.wareHouseDataService.vendorData.next(null);

  }

  initForm() {

    this.addVendorForm = this.formbuilder.group({
      honorificNm: this.formbuilder.control('', Validators.required),
      registerType: this.formbuilder.control('', Validators.required),
      firstNm: this.formbuilder.control(null, [Validators.required,
        Validators.pattern(/^[a-zA-Z ]*$/), Validators.maxLength(250)]),
      middleNm: this.formbuilder.control('', [Validators.maxLength(250)]),
      lastNm: this.formbuilder.control(null, [Validators.required,
        Validators.pattern(/^[a-zA-Z ]*$/), Validators.maxLength(250)]),
      contactNo: this.formbuilder.control(null, [
        Validators.minLength(10),
        Validators.maxLength(10),
        Validators.pattern(/(5|6|7|8|9)\d{9}$/),
        Validators.required
      ]),
      alternateContactNo: this.formbuilder.control(''),
      faxNo: this.formbuilder.control('', [Validators.required, Validators.maxLength(25)]),
      taxIdentifier: this.formbuilder.control('', Validators.maxLength(30)),
      // emailId:'',
      emailId: this.formbuilder.control(null, [Validators.pattern(/^[a-z]+[a-z0-9._]+@[a-z]+\.[a-z.]{2,5}$/)] ),
      accountNo: this.formbuilder.control(''),
      vendorAddress: this.formbuilder.control('', Validators.required),
      vendorCity: this.formbuilder.control('', Validators.required),
      vendorState: this.formbuilder.control('', Validators.required),
      vendorCountry: this.formbuilder.control('', Validators.required),
      zipcode: this.formbuilder.control('', [Validators.required, Validators.maxLength(6)]),
      birthEstablishDt: '',
      ifscCode: '',
      bankNm: '',
      branchNm: '',

    });
    this.addVendorForm.get('registerType').valueChanges.subscribe(val => {

      if (val === 'Organization') {
       this.reset();
        this.checkValue = true;
        this.addVendorForm.addControl('orgNm', this.formbuilder.control(null, Validators.required));
        this.addVendorForm.addControl('contactPersonEmail', this.formbuilder.control(null,
         [Validators.required, Validators.pattern(/^[a-z]+[a-z0-9._]+@[a-z]+\.[a-z.]{2,5}$/)] ));
        this.addVendorForm.addControl('contactPersonPhoneNo', this.formbuilder.control(null, Validators.required));
      } else {
        this.reset();
        this.checkValue = false;
        this.addVendorForm.removeControl('orgNm');
        this.addVendorForm.removeControl('contactPersonEmail');
        this.addVendorForm.removeControl('contactPersonPhoneNo');
      }
    });


  }
  datePickerToggler(datePicker: MatDatepicker<Date>) {
    datePicker[datePicker.opened ? 'close' : 'open']();
  }

  // the below method for resetting the values except register type field
  reset() {
    this.addVendorForm.get('firstNm').reset();
    this.addVendorForm.get('lastNm').reset();
    this.addVendorForm.get('honorificNm').reset();
    this.addVendorForm.get('contactNo').reset();
    this.addVendorForm.get('alternateContactNo').reset();
    this.addVendorForm.get('faxNo').reset();
    this.addVendorForm.get('accountNo').reset();
    this.addVendorForm.get('vendorAddress').reset();
    this.addVendorForm.get('vendorCity').reset();
    this.addVendorForm.get('vendorState').reset();
    this.addVendorForm.get('vendorCountry').reset();
    this.addVendorForm.get('emailId').reset();
    this.addVendorForm.get('zipcode').reset();
    this.addVendorForm.get('birthEstablishDt').reset();
    this.addVendorForm.get('bankNm').reset();
  }
  submitForm() {
    this.inventoryFormSubmitted = true;

    if (this.addVendorForm.valid) {

    const regPayload = {
      orgId: this.sessionData.orgId,
      orgGrpId: this.sessionData.orgGroupId,
      createdUsrId: this.sessionData.regId,
      ...this.addVendorForm.value
    };
    if (this.addVendorForm.get('registerType').value === 'Organization') {
      regPayload['isOrganisation'] = true;
    } else {
      regPayload['isOrganisation'] = false;
    }
    regPayload['birthEstablishDt'] = this.datePipe.transform(
      this.addVendorForm.value.birthEstablishDt,
      'yyyy-MM-dd'
    );
    regPayload.uploadDocumentsList = this.uploadDocumentsList;
    this.wareHouseApiService.insertOrUpdateVendorDetails(regPayload).subscribe(response => {
      if (response && response.responseCode === 'E200') {
        // this.snackBar.open(response.responseMessage, 'close', {
        //   duration: 1000,
        // });
        this.router.navigate(['/warehouse/purchase/list-of-vendors']);
      } else if (response && response.responseCode === 'E400') {
        this.snackBar.open(response.responseMessage, 'close', {
          duration: 2,
        });
      }
    },
      (errorResponse: HttpErrorResponse) => {
        if (errorResponse.status > 0) {
          this.snackBar.open(errorResponse.error.responseMessage, 'dismiss', {
            duration: 3000,
          });
        } else {
          // network failure
          this.snackBar.open('Network slow', 'dismiss', {
            duration: 3000,
          });
        }
      });
    // window.location.reload(/warehouse/purchase/list-of-vendors);`
    }
  }
  clearFormData() {
    this.addVendorForm.reset();
    this.isEditable = false;
    this.uploadDocumentsList = [];
  }


  // to fetch specific row data mainly for upload doc list(using same fetch api with different request)

  getFetchData() {
    const payload = {
      orgId: this.sessionData.orgId,
      orgGrpId: this.sessionData.orgGroupId,
      createdUsrId: this.sessionData.regId,
      vendorInfoId: this.vendorList.vendorInfoId
    };
    this.wareHouseApiService.fetchVendors(payload).subscribe(response => {
      if (response && response.responseCode === 'E200') {
        // this.uploadDocumentsList = '';
        this.vendorsList = response.data;
        console.log(this.vendorsList);
        console.log(this.vendorList);
        this.uploadDocumentsList = this.vendorsList[0].uploadDocumentsList;
        // this.toaster.success('success', response.responseMessage);
      } else if (response && response.responseCode === 'E400') {
        this.toaster.error(response.responseMessage);
      }
    });
  }

  getEditData() {
    if (this.vendorList.isOrganisation) {
      this.orgType = 'Organisation';
    } else {
      this.orgType = 'Individual';
    }
    this.addVendorForm.patchValue({ registerType: this.orgType });
    this.addVendorForm.patchValue({ honorificNm: this.vendorList.honorificNm});
    this.addVendorForm.patchValue({ firstNm: this.vendorList.firstNm });
    this.addVendorForm.patchValue({ middleNm: this.vendorList.middleNm });
    this.addVendorForm.patchValue({ taxIdentifier: this.vendorList.taxIdentifier});
    this.addVendorForm.patchValue({ ifscCode: this.vendorList.ifscCode});
    this.addVendorForm.patchValue({ lastNm: this.vendorList.lastNm });
    this.addVendorForm.patchValue({ emailId: this.vendorList.emailId });
    this.addVendorForm.patchValue({ contactNo: this.vendorList.contactNo });
    this.addVendorForm.patchValue({ alternateContactNo: this.vendorList.alternateContactNo });
    this.addVendorForm.patchValue({ faxNo: this.vendorList.faxNo });
    this.addVendorForm.patchValue({ accountNo: this.vendorList.accountNo });
    this.addVendorForm.patchValue({ vendorAddress: this.vendorList.vendorAddress });
    this.addVendorForm.patchValue({ vendorCity: this.vendorList.vendorCity });
    this.addVendorForm.patchValue({ vendorProvince: this.vendorList.vendorProvince });
    this.addVendorForm.patchValue({ vendorState: this.vendorList.vendorState });
    this.addVendorForm.patchValue({ vendorCountry: this.vendorList.vendorCountry });
    this.addVendorForm.patchValue({ zipcode: this.vendorList.zipcode });
    this.addVendorForm.patchValue({ birthEstablishDt: this.vendorList.birthEstablishDt });
    this.addVendorForm.patchValue({ bankNm: this.vendorList.bankNm });
    this.addVendorForm.patchValue({ branchNm: this.vendorList.branchNm });
    this.addVendorForm.patchValue({ orgNm: this.vendorList.orgNm });
    this.addVendorForm.patchValue({ contactPersonEmail: this.vendorList.contactPersonEmail });
    this.addVendorForm.patchValue({ contactPersonPhoneNo: this.vendorList.contactPersonPhoneNo });

  }
  // for uploading docs
  goToUploadPage() {
    const dialogRef = this.dialog.open(UploadDocComponent, {
      data: {
        title: 'ATTACH FILES'
      },
      disableClose: true
    });
    dialogRef.afterClosed().subscribe(result => {
      if (result) {
        // this.uploadTable = true;
        this.uploadDocumentsList.push(result);
        this.uploadTable = true;
      }
    });
  }
  // for deleting doc
  deleteFile(i) {
    this.uploadDocumentsList.splice(i, 1);
  }
  // for editing file
  editFile(i) {
    // this.uploadTable = false;
    const typeCheckObj = {};
    typeCheckObj['typeCheck'] = 'edit';
    const dialogRef = this.dialog.open(UploadDocComponent, {
      data: {
        data: typeCheckObj
      },
      disableClose: true
    });
    dialogRef.afterClosed().subscribe(result => {
      if (result) {
        this.uploadDocumentsList.splice(i, 1);
        this.uploadDocumentsList.push(result);
      }
    });
  }
  // API FOR UPDATE VENDOR DETAILS

  submitUpdateForm() {
    this.inventoryFormSubmitted = true;
   if (this.addVendorForm.valid)    {
    const regPayload = {
      orgId: this.sessionData.orgId,
      orgGrpId: this.sessionData.orgGroupId,
      createdUsrId: this.sessionData.regId,
      vendorInfoId: this.vendorList.vendorInfoId,
      versionId: this.vendorList.versionId,
      ...this.addVendorForm.value
    };
    regPayload.uploadDocumentsList = this.uploadDocumentsList;
    this.wareHouseApiService.insertOrUpdateVendorDetails(regPayload).subscribe(response => {
      if (response && response.responseCode === 'E200') {
        this.router.navigate(['/warehouse/purchase/list-of-vendors']);
      } else if (response && response.responseCode === 'E400') {
        this.snackBar.open(response.responseMessage, 'close', {
          duration: 1000,
        });
      }
    },
      (errorResponse: HttpErrorResponse) => {
        if (errorResponse.status > 0) {
          this.snackBar.open(errorResponse.error.responseMessage, 'dismiss', {
            duration: 3000,
          });
        } else {
          // network failure
          this.snackBar.open('Network slow', 'dismiss', {
            duration: 3000,
          });
        }
      });
   }
  }

  cancelButton() {
    this.router.navigate(['/warehouse/purchase/list-of-vendors']);
    this.clearFormData();
    this.wareHouseDataService.vendorData.next(null);
  }
}
