import {
  Component,
  OnInit
} from '@angular/core';
import { FormGroup, FormBuilder } from '@angular/forms';
import { WarehouseApiService } from '@core/services/warehouse-api.service';
import { MatSnackBar, MatDialog } from '@angular/material';
import { UserService } from '@core/services';
import { UserSession } from '@core/models/user-session';
import { WarehouseService } from '../../warehouse.service';
import { Subscription } from 'rxjs';
import { Router } from '@angular/router';
import { HttpErrorResponse } from '@angular/common/http';
import { ConfirmationDialogComponent } from '@shared/components/confirmation-dialog/confirmation-dialog.component';
@Component({
  selector: 'app-picklist',
  templateUrl: './picklist.component.html',
  styleUrls: ['./picklist.component.scss']
})
export class PicklistComponent implements OnInit {
  ItemsForPickList = [];
  index: number;
  showDetails = false;
  // showEditPicklisst = false;
  PicklistHeading: string;
  formGroup: FormGroup;
  locationsForm: FormGroup;
  PicklistDetails: any;
  _indexReqStock: number;
  private sessionData: UserSession;
  dataFromInputFn = [];
  collectStockText: string;
  dataFromPickIcon = [];
  vendorData: any;
  date = new Date();
  selectedPeople3 = [];
  subscribePickData = Subscription;
  enableDisableQty = false;
  locationSearchName: any;
  shedId: number;
  rowId: number;
  columnId: number;
  searchLocationKey: string;
  tierId: number;
  shedNm: string;
  rowNo: string;
  columnNo: string;
  tierLocationNm = '';
  partIdForItemsPickList: any;
  stockTobePicked: any;
  saleOrderDetailedId: any;
  requiredQuantity: any;
  number: number;
  listQuantity: number;
  dateToSaleHistory = new Date();
  storagShelfs: {};
  storagColumns: {};
  storagRows: {};
  storagSheds: {};
  saleOrderId: number;
  headings: any;
  stockIndex: any;
  public isCollectStock = true;
  constructor(
    private fb: FormBuilder,
    private warehouseApiService: WarehouseApiService,
    private snackBar: MatSnackBar,
    private userService: UserService,
    private warehouseService: WarehouseService,
    private router: Router,
    private dialog: MatDialog
  ) {
    this.sessionData = this.userService.getUser();
  }
  ngOnInit() {
    this.initForm();
    this.warehouseService.pickListIndex.subscribe(res => {
      if (res >= 0) {
        this.index = res;
      }
    });
    this.saleOrderId = this.warehouseService.getSaleOrderId();
    this.headings = this.warehouseService.getPickItemsHeadins();
    this.warehouseService.fetchpicklistDetails.subscribe(res => {
      if (res) {
        this.PicklistDetails = res;
        this.stockTobePicked = res.stockToBePicked;
        this.stockIndex = this.PicklistDetails.index;
      }
    });
    this.warehouseService.pickIconData.subscribe(res => {
      if (res) {
        const data = res;
        this.dataFromPickIcon = data[0];
      }
    });
    this.saleOrderDetailedId = this.warehouseService.getSaleOrderDetailId();
    this.dateToSaleHistory = this.warehouseService.getSaleHistoryFromDate();
    this.fetchShedRef();
    this.fetchpicklistDetails();
  }

  initForm() {
    // search and select dropdown form
    this.formGroup = this.fb.group({
      search1: '',
      search2: '',
      search3: '',
      search4: ''
    });
    //  fetch locations form
    this.locationsForm = this.fb.group({
      itemLocation: '',
      expireDate: '',
      availableQty: '',
      qty: ''
    });
  }

  // for disabled of search locations
  onChanges() {
    const s1 = this.formGroup.get('search1');
    const s2 = this.formGroup.get('search2');
    const s3 = this.formGroup.get('search3');
    const s4 = this.formGroup.get('search4');
    s2.disable();
    s3.disable();
    s4.disable();
    s1.valueChanges.subscribe(value => {
      if (value) {
        s2.enable();
      } else {
        s2.disable();
      }
    });
    s2.valueChanges.subscribe(value => {
      if (value) {
        s3.enable();
      } else {
        s3.disable();
      }
    });
    s3.valueChanges.subscribe(value => {
      if (value) {
        s4.enable();
      } else {
        s4.disable();
      }
    });
  }

  // picklist qty input keypress event
  limitValueForReqStock(event, list, index) {
    list.color = false;
    const number = Number(event.target.value);
    if (
      index === this._indexReqStock && (number > list.availableQty || number > this.stockTobePicked)
    ) {
      list.color = true;
      list.qty = '';
      this.snackBar.open('Please enter correct quantity', 'Cancel', {
        duration: 3000
      });
    } else if (
      index === this._indexReqStock &&
      number <= (list.previousQty || this.stockTobePicked)
    ) {
      list.color = false;
    }
  }

  // back to pick list main page
  closeEditPicklisst() {
    this.ItemsForPickList = [];
    this.dataFromInputFn = [];
    this.PicklistDetails = {};
    this.formGroup.reset();
    this.router.navigate(['warehouse/PickItems']);
  }

  // it will call after checkbox changes in picklist details
  checkDataToCollection(isChecked, list, index) {
    if (!list.availableQty) {
      list.checkLocation = false;
    }
    if (isChecked.checked) {
      this._indexReqStock = index;
      this.ItemsForPickList[0]['checkedContents'] = true;
      list.saleOrderDetailedId = this.PicklistDetails.saleOrderDetailedId;
    } else {
      if (list.picklistInfoId !== null) {
        this.deletePickListLocations(list, index);
      }
      this._indexReqStock = null;
      const Total = (list.qty || 0) - (list.previous || 0);
      this.PicklistDetails.stockToBePicked += Total;
      list.previous = list.qty;
      this.dataFromInputFn.splice(index, 1);
      this.ItemsForPickList[0]['checkedContents'] = false;
    }
  }
  qtyFocus(list, index) {
    if (list.qty && this.headings.PicklistHeading === 'UPDATE PICK LIST') {
      this.listQuantity = list.qty;
    }
    this._indexReqStock = index;
  }
  deletePickListLocations(data, index) {
    const dialogRef = this.dialog.open(ConfirmationDialogComponent, {
      data: { title: 'Are you sure  you want to delete picked stock ?' },
      disableClose: true
    });
    dialogRef.afterClosed().subscribe(result => {
      if (result) {
        const payload = {
          createdUsrId: this.sessionData.regId,
          orgGrpId: this.sessionData.orgGroupId,
          orgId: this.sessionData.orgId,
          picklistInfoId: data.picklistInfoId,
          inventoryId: data.inventoryId,
          qty: data.qty
        };
        this.warehouseApiService.deletePickListLocations(payload).subscribe(
          response => {
            if (response.responseCode === 'E200') {
              this.stockTobePicked += this.ItemsForPickList[0][index].qty;
              this.ItemsForPickList[0].splice(index, 1);
              this.fetchpicklistDetails();
            } else if (response.responseCode === 'E400') {
              this.snackBar.open(response.responseMessage, 'Cancel', {
                duration: 3000
              });
            }
          },
          () => {
            this.snackBar.open('something went wrong', 'Cancel', {
              duration: 3000
            });
          }
        );
      }
    });
  }
  // focus out event for req stock here we are moving checked values up
  DataToCollection(input, list, _index) {
    this.requiredQuantity = list.qty;
    let Total = (list.qty || 0) - (list.previous || 0);
    if (this.listQuantity) {
      Total -= this.listQuantity;
    }
    this.stockTobePicked -= Total;
    list.previous = list.qty;
    const {
      availableQty,
      checkLocation,
      previous,
      color,
      partId,
      partNm,
      partNmOrNo,
      partNo,
      picklistInfoId,
      disableReqQty,
      saleOrderId,
      checkedContents,
      ...remaining
    } = list;
    remaining['createdUsrId'] = this.sessionData.regId;
    remaining['orgGrpId'] = this.sessionData.orgGroupId;
    remaining['orgId'] = this.sessionData.orgId;
    remaining['picklistLocation'] = list.location;
    remaining['saleOrderId'] = this.saleOrderId;
    remaining['picklistInfoId']  = list.picklistInfoId;
    remaining['saleOrderDetailedId'] = list.saleOrderDetailedId;
    if (input.target.value) {
      list.checkLocation = true;
      this.dataFromInputFn.push(remaining);
    } else {
      list.picklistInfoId = null;
      list.checkLocation = false;
    }
  }

  // move to next data of pick list
  nextPicklistDetails() {
    ++this.index;
    const length = this.dataFromPickIcon.length;
    if (this.index <= length - 1) {
      this.PicklistDetails = this.dataFromPickIcon[this.index];
      this.stockTobePicked = this.PicklistDetails.stockToBePicked;
      this.fetchpicklistDetails();
    }
  }
  fetchpicklistDetails() {
    this.ItemsForPickList = [];
    let payload = {};
    if (this.tierLocationNm === '') {
      payload = {
        createdUsrId: this.sessionData.regId,
        orgGrpId: this.sessionData.orgGroupId,
        orgId: this.sessionData.orgId,
        partId: this.PicklistDetails.partId,
        saleOrderDetailedId: this.PicklistDetails.saleOrderDetailedId,
        saleOrderId: this.PicklistDetails.saleOrderId || this.saleOrderId
      };
    } else if (this.tierLocationNm !== '') {
      payload = {
        createdUsrId: this.sessionData.regId,
        orgGrpId: this.sessionData.orgGroupId,
        orgId: this.sessionData.orgId,
        partId: this.PicklistDetails.partId,
        saleOrderDetailedId: this.PicklistDetails.saleOrderDetailedId,
        saleOrderId: this.PicklistDetails.saleOrderId || this.saleOrderId,
        shedId: this.shedId,
        rowId: this.rowId,
        columnId: this.columnId,
        tierId: this.tierId
      };
    }
    this.warehouseApiService.fetchItemsForPickList(payload).subscribe(
      response => {
        if (response.responseCode === 'E200') {
          this.tierLocationNm = '';
          this.ItemsForPickList.push(response.data);
          this.ItemsForPickList[0].forEach(element => {
            element['previous'] = null;
            element['checkedContents'] = false;
            element['disableReqQty'] = false;
            element['color'] = false;
            if (element.picklistInfoId !== null) {
              element['checkLocation'] = true;
            } else {
              element['checkLocation'] = false;
            }
          });
        } else if (response.responseCode === 'E400') {
          this.snackBar.open('NO LOCATIONS FOUND IN INVENTORY', 'Cancel', {
            duration: 3000
          });
        }
      },
      (errorResponse: HttpErrorResponse) => {
        if (errorResponse.status > 0) {
          this.snackBar.open(errorResponse.error.responseMessage, 'dismiss', {
            duration: 3000
          });
        } else {
          // network failure
          this.snackBar.open('Network slow', 'dismiss', {
            duration: 2500
          });
        }
      }
    );
  }
  // to fetch part details by entering specific location
  fetchShedRef() {
    const payload = {
      orgId: this.sessionData.orgId,
      orgGrpId: this.sessionData.orgGroupId,
      createdUsrId: this.sessionData.regId
    };
    this.warehouseApiService.fetchShedRef(payload).subscribe(response => {
      if (response && response.responseCode === 'E200') {
        //     // const shedName = response.shedNm;
        //     const sheds = [];
        //    this.data.forEach(el => {
        //    sheds.push({shedName : el.shedNm});
        // });
        this.storagSheds = response.data;
        // for ( i = 0; i <= this.storagSheds.length; i++) {
        //  const sheds = this.storagSheds.shedNm;
        // }
      }
    });
  }
  locationSearch1(value) {
    if (value) {
      this.shedNm = value.shedNm;
      this.shedId = value.shedId;
      this.fetchRowRef();
    }
  }
  fetchRowRef() {
    const payload = {
      orgId: this.sessionData.orgId,
      orgGrpId: this.sessionData.orgGroupId,
      createdUsrId: this.sessionData.regId,
      shedId: this.shedId
    };
    this.warehouseApiService.fetchShedRowxref(payload).subscribe(
      response => {
        if (response && response.responseCode === 'E200') {
          this.storagRows = response.data;
        } else if (response && response.responseCode === 'E400') {
          this.snackBar.open(response.responseMessage, 'close', {
            duration: 2500
          });
        }
      },
      (errorResponse: HttpErrorResponse) => {
        if (errorResponse.status > 0) {
          this.snackBar.open(errorResponse.error.responseMessage, 'dismiss', {
            duration: 3000
          });
        } else {
          // network failure
          this.snackBar.open('Network slow', 'dismiss', {
            duration: 3000
          });
        }
      }
    );
  }
  locationSearch2(value) {
    if (value) {
      this.rowNo = value.rowNo;
      this.rowId = value.rowId;
      this.fetchShedColumnRef();
    }
  }
  fetchShedColumnRef() {
    const payload = {
      orgId: this.sessionData.orgId,
      orgGrpId: this.sessionData.orgGroupId,
      createdUsrId: this.sessionData.regId,
      shedId: this.shedId,
      rowId: this.rowId
    };
    this.warehouseApiService.fetchShedColumnRef(payload).subscribe(
      response => {
        if (response && response.responseCode === 'E200') {
          this.storagColumns = response.data;
        } else if (response && response.responseCode === 'E400') {
          this.snackBar.open(response.responseMessage, 'close', {
            duration: 2500
          });
        }
      },
      (errorResponse: HttpErrorResponse) => {
        if (errorResponse.status > 0) {
          this.snackBar.open(errorResponse.error.responseMessage, 'dismiss', {
            duration: 3000
          });
        } else {
          // network failure
          this.snackBar.open('Network slow', 'dismiss', {
            duration: 3000
          });
        }
      }
    );
  }
  locationSearch3(value) {
    if (value) {
      this.columnNo = value.columnNo;
      this.columnId = value.columnId;
      this.fetchShedTierxref();
    }
  }
  fetchShedTierxref() {
    const payload = {
      orgId: this.sessionData.orgId,
      orgGrpId: this.sessionData.orgGroupId,
      createdUsrId: this.sessionData.regId,
      shedId: this.shedId,
      rowId: this.rowId,
      columnId: this.columnId
    };
    this.warehouseApiService.fetchShedTierxref(payload).subscribe(
      response => {
        if (response && response.responseCode === 'E200') {
          this.storagShelfs = response.data;
        } else if (response && response.responseCode === 'E400') {
          this.snackBar.open(response.responseMessage, 'close', {
            duration: 2500
          });
        }
      },
      (errorResponse: HttpErrorResponse) => {
        if (errorResponse.status > 0) {
          this.snackBar.open(errorResponse.error.responseMessage, 'dismiss', {
            duration: 3000
          });
        } else {
          // network failure
          this.snackBar.open('Network slow', 'dismiss', {
            duration: 3000
          });
        }
      }
    );
  }
  locationSearch4(value) {
    if (value) {
      this.tierLocationNm = value.tierNo;
      this.tierId = value.tierId;
      this.fetchpicklistDetails();
    }
  }
  insertUpdatePickList() {
    const data = [];
    this.dataFromInputFn.forEach(item => {
      if (data.findIndex(i => i.inventoryId === item.inventoryId) === -1) {
        data.push(item);
      }
    });
    this.dataFromInputFn = data;
    if (!this.requiredQuantity) {
      this.snackBar.open('select atleast one quantity', 'Cancel', {
        duration: 3000
      });
    } else {
      // this.dataFromInputFn.forEach(r => {
      //   r.saleOrderDetailedId = this.saleOrderDetailedId;
      // });
      if (this.headings.PicklistHeading === 'UPDATE PICK LIST') {
        this.dataFromInputFn.forEach(el => {
          // const prev = el.previousQty;
          // const qt = el.qty;
          el.previousQty = this.listQuantity;
          // el.picklistInfoId =
          // el.qty = prev;
        });
      }
      const payload = this.dataFromInputFn;
      this.warehouseApiService.insertOrUpdatePickList(payload).subscribe(
        response => {
          if (response.responseCode === 'E200') {
            this.dataFromInputFn = [];
            this.router.navigate(['warehouse/PickItems']);
            // this.showEditPicklisst = false;
            // this.fetcLocations = [];
          } else if (response.responseCode === 'E400') {
            this.snackBar.open(response.responseMessage, 'Cancel', {
              duration: 3000
            });
          }
        },
        () => {
          this.snackBar.open('something went wrong', 'Cancel', {
            duration: 3000
          });
        }
      );
    }
  }
}
