import {
  Component,
  OnInit,
  OnDestroy
} from '@angular/core';
import { FormGroup, FormBuilder, Validators, FormArray, FormControl, AbstractControl } from '@angular/forms';
import { MatSnackBar } from '@angular/material';
import { Observable, concat, of, Subject, from, Subscription } from 'rxjs';
import { AutoUnsubscribe } from 'ngx-auto-unsubscribe';
import { UserSession } from '@core/models/user-session';
import { UserService } from '@core/services/user.service';
import { WarehouseApiService } from '@core/services/warehouse-api.service';
import { WarehouseService } from '@app/modules/warehouse/warehouse.service';
import { MatDatepicker } from '@angular/material';
import { MatDialog } from '@angular/material';
import { UploadDocComponent } from './../upload-doc/upload-doc.component';
import { Router } from '@angular/router';
import { HttpErrorResponse } from '@angular/common/http';

@AutoUnsubscribe()
@Component({
  selector: 'app-warehouse-registration',
  templateUrl: './warehouse-registration.component.html',
  styleUrls: ['./warehouse-registration.component.scss']
})
export class WarehouseRegistrationComponent implements OnInit, OnDestroy {
  customerDetailsForm: FormGroup;
  public apiSubscription: Subscription;
  private customerDataSubscription: Subscription;
  types = ['Individual', 'Organization'];
  private sessionData: UserSession;
  isOrganisation: boolean;
  checked: Boolean = false;
  dateMax = new Date();
  public isEditable: Boolean;
  orgType: any;
  inventoryFormSubmitted = false;
  public OpenNewAddress = [];
  public shippingAddressesList = [];
  public showAddress: Boolean = true;
  public showBillAddress: Boolean = false;
  isTypeOfAddress: boolean;
  customerList: any;
  isindex: false;
  isEnableCheckbox: boolean;
  uploadTable = false;
  uploadDocumentsList: any = [];
  billingAddressList = [];
  customersList: any;
  vendor_details_validation_messages = {
    firstName: [
      { type: 'required', message: '* First Name is required' },
      {
        type: 'pattern',
        message: 'Enter Valid Name'
      }
    ],
    lastName: [
      { type: 'required', message: '* Last Name is required' },
    ],
    HonorificName: [
      { type: 'required', message: '* Honorific is required' },
    ],
    PhoneNumber: [
      { type: 'required', message: '* Phone Number is required' },
    ],
    RegistrationType: [
      { type: 'required', message: '* Register Type is required' },
    ],
    EmailId: [
      { type: 'required', message: '* Email-Id  is required' },
      {
        type: 'pattern',
        message: 'Enter Valid Email'
      }
    ],
    contactPersonEmail: [
      { type: 'required', message: '* Email-Id  is required' },
      {
        type: 'pattern',
        message: 'Enter Valid Email'
      }
    ],
    Address: [
      { type: 'required', message: '* Address  is required' },
    ],
    City: [
      { type: 'required', message: '* City  is required' },
    ],
    State: [
      { type: 'required', message: '* State  is required' },
    ],
    Country: [
      { type: 'required', message: '* Country  is required' },
    ],
    ZipCode: [
      { type: 'required', message: '* Pincode  is required' },
    ],
    Organization: [
      { type: 'required', message: '* Organization Name  is required' },

    ],
    taxIdentifier: [
      { type: 'maxlength', message: 'Tax Identifier is too long' }
    ],
    creditLimit: [
      { type: 'maxlength', message: 'Credit Limit is too long' }
    ],
    billAddress: [
      { type: 'required', message: '* Address  is required' }
    ],
    billAddressCityNm: [
      { type: 'required', message: '* City is required' }
    ],
    billAddressStateNm: [
      { type: 'required', message: '* State is required' }
    ],
    billAddressCountryNm: [
      { type: 'required', message: '* Country is required' }
    ],
    billAddressZipcode: [
      { type: 'required', message: '* Zip Code is required' }
    ],

  };
  constructor(
    private formbuilder: FormBuilder,
    userService: UserService,
    private snackBar: MatSnackBar,
    private wareHouseApiService: WarehouseApiService,
    private wareHouseDataService: WarehouseService,
    public dialog: MatDialog,
    private router: Router,
  ) {
    this.sessionData = userService.getUser();
  }

  datePickerToggler(datePicker: MatDatepicker<Date>) {
    datePicker[datePicker.opened ? 'close' : 'open']();
  }

  ngOnInit() {
    this.initForm();
    this.addNewAddress();
    this.customerDataSubscription = this.wareHouseDataService.vendorData.subscribe(res => {
      if (res) {
        this.customerList = res;
        this.isEditable = true;
        this.uploadTable = true;
        this.getFetchData();
        this.getEditData();
      }
    });
    this.wareHouseDataService.vendorData.next(null);
  }

  ngOnDestroy() {
  }

  initForm() {
    this.customerDetailsForm = this.formbuilder.group({
      shippingAddressesList: this.formbuilder.array([]),
      registerType: this.formbuilder.control(null, Validators.required),
      honorificNm: this.formbuilder.control(null, Validators.required),
      orgNm: this.formbuilder.control(null, Validators.required),
      checkAddress: '',
      firstNm: this.formbuilder.control(null, Validators.required),
      middleNm: this.formbuilder.control(''),
      lastNm: this.formbuilder.control(null, Validators.required),
      birthEstablishDate: this.formbuilder.control(''),
      alternateContactNo: this.formbuilder.control(''),
      mobileNo: this.formbuilder.control(null, Validators.required),
      faxNumber: this.formbuilder.control(''),
      contactPersonPhoneNo: this.formbuilder.control(null, Validators.required),

      // contactPersonEmail: [
      //   null,
      //   Validators.required,
      //   Validators.pattern(/^[a-z]+[a-z0-9._]+@[a-z]+\.[a-z.]{2,5}$/)
      // ],
      emailId: this.formbuilder.control(null, [Validators.required, Validators.pattern(/^[a-z]+[a-z0-9._]+@[a-z]+\.[a-z.]{2,5}$/)]),
      contactPersonEmail: this.formbuilder.control(null,
        [Validators.required, Validators.pattern(/^[a-z]+[a-z0-9._]+@[a-z]+\.[a-z.]{2,5}$/)]),
      taxIdentifier: this.formbuilder.control('', Validators.maxLength(30)),
      creditLimit: this.formbuilder.control('', Validators.maxLength(12)),
      paymentTerms: this.formbuilder.control(''),
      // customerStatus: this.formbuilder.control(''),
      billAddress: this.formbuilder.control('', Validators.required),
      billAddressCityNm: this.formbuilder.control('', Validators.required),
      billAddressStateNm: this.formbuilder.control('', Validators.required),
      billAddressCountryNm: this.formbuilder.control('', Validators.required),
      billAddressZipcode: this.formbuilder.control('', Validators.required)
    });

    this.customerDetailsForm.get('registerType').valueChanges.subscribe(val => {
      if (val === 'Organization') {
        this.reset();
        this.isOrganisation = true;
        this.customerDetailsForm.addControl('orgNm', this.formbuilder.control(null, Validators.required));
        this.customerDetailsForm.addControl('contactPersonEmail', this.formbuilder.control(null,
          [Validators.required, Validators.pattern(/^[a-z]+[a-z0-9._]+@[a-z]+\.[a-z.]{2,5}$/)]));
        this.customerDetailsForm.addControl('contactPersonPhoneNo', this.formbuilder.control(null, Validators.required));
      } else {
        this.reset();
        this.isOrganisation = false;
        this.customerDetailsForm.removeControl('orgNm');
        this.customerDetailsForm.removeControl('contactPersonEmail');
        this.customerDetailsForm.removeControl('contactPersonPhoneNo');
      }
    });

  }

  reset() {
    this.customerDetailsForm.get('firstNm').reset();
    this.customerDetailsForm.get('middleNm').reset();
    this.customerDetailsForm.get('lastNm').reset();
    this.customerDetailsForm.get('emailId').reset();
    this.customerDetailsForm.get('shippingAddressesList').reset();
    this.customerDetailsForm.get('mobileNo').reset();
    this.customerDetailsForm.get('honorificNm').reset();
  }
  submitRegistrationForm() {
    this.inventoryFormSubmitted = true;
    if (this.customerDetailsForm.valid) {

      const regPayload = {
        orgId: this.sessionData.orgId,
        orgGrpId: this.sessionData.orgGroupId,
        createdUsrId: this.sessionData.regId,
        ...this.customerDetailsForm.value
      };
      if (this.customerDetailsForm.get('registerType').value === 'Organization') {
        regPayload['isOrganisation'] = true;
      } else {
        regPayload['isOrganisation'] = false;
      }
      regPayload.uploadDocumentsList = this.uploadDocumentsList;
      this.wareHouseApiService.insertCustomerReg(regPayload).subscribe(response => {
        if (response && response.responseCode === 'E200') {
          this.snackBar.open(response.responseMessage, 'dismiss', {
            duration: 500,
          });
          this.router.navigate(['/warehouse/sale/list-of-customers']);
        } else if (response && response.responseCode === 'E400') {
          this.snackBar.open(response.responseMessage, 'dismiss', {
            duration: 500,
          });
        }
      },
        (errorResponse: HttpErrorResponse) => {
          if (errorResponse.status > 0) {
            this.snackBar.open(errorResponse.error.responseMessage, 'dismiss', {
              duration: 500,
            });
          } else {
            // network failure
            this.snackBar.open('Network slow', 'dismiss', {
              duration: 500,
            });
          }
        }
      );
    }
  }

  getFetchData() {
    const payload = {
      orgId: this.sessionData.orgId,
      orgGrpId: this.sessionData.orgGroupId,
      createdUsrId: this.sessionData.regId,
      orgCustomerId: this.customerList.orgCustomerId
    };
    this.wareHouseApiService.fetchCustomerRegList(payload).subscribe(response => {
      if (response && response.responseCode === 'E200') {
        this.customersList = response.data;
        this.customerDetailsForm.get('shippingAddressesList').patchValue(this.customersList[0].shippingAddressesList);
        this.uploadDocumentsList = this.customersList[0].uploadDocumentsList;
      } else if (response && response.responseCode === 'E400') {
        this.snackBar.open(response.responseMessage, 'dismiss', {
          duration: 500,
        });
      }
    });
  }

  getEditData() {
    if (this.customerList.isOrganisation) {
      this.orgType = 'Organization';
    } else {
      this.orgType = 'Individual';
    }
    this.customerDetailsForm.patchValue({ registerType: this.orgType });
    this.customerDetailsForm.patchValue({ honorificNm: this.customerList.honorificNm });
    this.customerDetailsForm.patchValue({ firstNm: this.customerList.firstNm });
    this.customerDetailsForm.patchValue({ middleNm: this.customerList.middleNm });
    this.customerDetailsForm.patchValue({ lastNm: this.customerList.lastNm });
    this.customerDetailsForm.patchValue({ birthEstablishDate: this.customerList.birthEstablishDate });
    this.customerDetailsForm.patchValue({ faxNumber: this.customerList.faxNumber });
    this.customerDetailsForm.patchValue({ creditLimit: this.customerList.creditLimit });
    this.customerDetailsForm.patchValue({ orgNm: this.customerList.orgNm });
    // this.customerDetailsForm.patchValue({ customerStatus: this.customerList.customerStatus });
    this.customerDetailsForm.patchValue({ alternateContactNo: this.customerList.alternateContactNo });
    this.customerDetailsForm.patchValue({ mobileNo: this.customerList.mobileNo });
    this.customerDetailsForm.patchValue({ emailId: this.customerList.emailId });
    this.customerDetailsForm.patchValue({ taxIdentifier: this.customerList.taxIdentifier });
    this.customerDetailsForm.patchValue({ paymentTerms: this.customerList.paymentTerms });
    this.customerDetailsForm.patchValue({ billAddress: this.customerList.billAddress });
    this.customerDetailsForm.patchValue({ billAddressCityNm: this.customerList.billAddressCityNm });
    this.customerDetailsForm.patchValue({ billAddressStateNm: this.customerList.billAddressStateNm });
    this.customerDetailsForm.patchValue({ billAddressCountryNm: this.customerList.billAddressCountryNm });
    this.customerDetailsForm.patchValue({ billAddressZipcode: this.customerList.billAddressZipcode });
    this.customerDetailsForm.patchValue({ contactPersonEmail: this.customerList.contactPersonEmail });
    this.customerDetailsForm.patchValue({ contactPersonPhoneNo: this.customerList.contactPersonPhoneNo });
  }

  // API FOR UPDATE FUNCTIONALITY
  submitUpdateForm() {
    const regPayload = {
      orgId: this.sessionData.orgId,
      orgGrpId: this.sessionData.orgGroupId,
      createdUsrId: this.sessionData.regId,
      orgCustomerId: this.customerList.orgCustomerId,
      ...this.customerDetailsForm.value
    };
    if (this.customerDetailsForm.get('registerType').value === 'Organization') {
      regPayload['isOrganisation'] = true;
    } else {
      regPayload['isOrganisation'] = false;
    }
    regPayload.uploadDocumentsList = this.uploadDocumentsList;
    this.wareHouseApiService.updateCustomerReg(regPayload).subscribe(response => {
      if (response && response.responseCode === 'E200') {
        this.snackBar.open(response.responseMessage, 'dismiss', {
          duration: 500,
        });
        this.router.navigate(['/warehouse/sale/list-of-customers']);
      } else if (response && response.responseCode === 'E400') {
        this.snackBar.open(response.responseMessage, 'dismiss', {
          duration: 500,
        });
      }
    },
      (errorResponse: HttpErrorResponse) => {
        if (errorResponse.status > 0) {
          this.snackBar.open(errorResponse.error.responseMessage, 'dismiss', {
            duration: 500,
          });
        } else {
          // network failure
          this.snackBar.open('Network slow', 'dismiss', {
            duration: 500,
          });
        }
      }
    );
    this.clearFormData();
    this.wareHouseDataService.vendorData.next(null);
  }

  clearFormData() {
    this.customerDetailsForm.reset();
    this.isEditable = false;
    this.uploadDocumentsList = [];
  }

  deliveryAddress() {
    this.isTypeOfAddress = false;
  }
  billingAddress() {
    this.isTypeOfAddress = true;
  }
  addNewAddress() {
    const creds = this.customerDetailsForm.controls.shippingAddressesList as FormArray;
    creds.push(this.formbuilder.group({
      shipAddress: this.formbuilder.control(null, Validators.required),
      shipAddressCityNm: this.formbuilder.control(null, Validators.required),
      shipAddressStateNm: this.formbuilder.control(null, Validators.required),
      shipAddressCountryNm: this.formbuilder.control(null, Validators.required),
      shipAddressZipcode: this.formbuilder.control(null, Validators.required),
      checkAddress: ''
    }));
  }
  get exArray() {
    return (<FormArray>this.customerDetailsForm.get('shippingAddressesList')).controls;

  }
  // getBillingAddress
  getBillAddress(event, i) {
    if (event.checked === true) {
      this.checkBill(i);
    } else if (event.checked === false) {
      this.customerDetailsForm.get('billAddress').reset();
      this.customerDetailsForm.get('billAddressCityNm').reset();
      this.customerDetailsForm.get('billAddressStateNm').reset();
      this.customerDetailsForm.get('billAddressCountryNm').reset();
      this.customerDetailsForm.get('billAddressZipcode').reset();
    }
  }
  checkBill(i) {
    this.customerDetailsForm.patchValue({ billAddress: this.exArray[i].value.shipAddress });
    this.customerDetailsForm.patchValue({ billAddressCityNm: this.exArray[i].value.shipAddressCityNm });
    this.customerDetailsForm.patchValue({ billAddressStateNm: this.exArray[i].value.shipAddressStateNm });
    this.customerDetailsForm.patchValue({ billAddressCountryNm: this.exArray[i].value.shipAddressCountryNm });
    this.customerDetailsForm.patchValue({ billAddressZipcode: this.exArray[i].value.shipAddressZipcode });
  }
  // for adding address fields
  showAddressFields(i) {
    this.isindex = i;
    this.showAddress = !this.showAddress;
  }
  checkIndex(index) {
    if (index === this.isindex) {
      return true;
    } else {
      return false;
    }
  }
  // for adding bill address fields
  showBillAddressFields() {
    this.showBillAddress = !this.showBillAddress;
  }
  goToUploadPage() {
    const dialogRef = this.dialog.open(UploadDocComponent, {
      data: {
        //  typeCheck: 'edit,
        title: 'ATTACH FILES'
      },
      disableClose: true
    });
    dialogRef.afterClosed().subscribe(result => {
      if (result) {
        this.uploadTable = true;
        this.uploadDocumentsList.push(result);
      }
    });
  }
  // for deleting doc
  deleteFile(i) {
    this.uploadDocumentsList.splice(i, 1);
  }
  // for editing file
  editFile(i) {
    const typeCheckObj = {};
    typeCheckObj['typeCheck'] = 'edit';

    const dialogRef = this.dialog.open(UploadDocComponent, {
      data: {
        data: typeCheckObj
      },
      disableClose: true
    });
    dialogRef.afterClosed().subscribe(result => {
      if (result) {
        this.uploadDocumentsList.splice(i, 1);
        this.uploadDocumentsList.push(result);
      }
    });
  }
  // cancel method
  cancel() {
    this.router.navigate(['/warehouse/sale/list-of-customers']);
    this.wareHouseDataService.vendorData.next(null);
  }
  deleteAddress(i) {
    const deleteAddress1 = this.customerDetailsForm.controls.shippingAddressesList as FormArray;
    deleteAddress1.removeAt(i);
  }
  EnableCheckbox() {
    this.isEnableCheckbox = true;
  }

}
