import { Component, OnInit, ElementRef, ViewChild, Inject } from '@angular/core';
import * as S3 from 'aws-sdk/clients/s3';
import { WarehouseService } from '@app/modules/warehouse/warehouse.service';
import { UtilService } from '@core/services';
import { UserSession } from '@core/models/user-session';
import { FormBuilder, FormGroup } from '@angular/forms';
import { UserService } from '@core/services/user.service';
import { WarehouseApiService } from '@core/services/warehouse-api.service';
import { MatSnackBar } from '../../../../../../node_modules/@angular/material';
import { Router } from '@angular/router';
import { from, Subscription } from 'rxjs';
import { DatePipe } from '@angular/common';
import { HttpErrorResponse } from '@angular/common/http';
import {ActivatedRoute} from '@angular/router';
@Component({
  selector: 'app-excel-upload',
  templateUrl: './excel-upload.component.html',
  styleUrls: ['./excel-upload.component.scss']
})
export class ExcelUploadComponent implements OnInit {
  public bucket = new S3(this.warehouseService.getBucketsConfig());
  private sessionData: UserSession;
  public fileUpload: any;
  paramData: any;
  uploadPart = false;
  public url;
  @ViewChild('fileInput') fileInput: ElementRef;
  public fileUploadSubscription: Subscription;
  enableUploadIcon: boolean;
  fileList: any;
  uploadForm: FormGroup;
  partDetails: any;
  obj: { documentNm: any; documentUrl: string; documentUploadDt: string; };
  files: any;
  constructor(
    private warehouseService: WarehouseService,
    private wareHouseApiService: WarehouseApiService,
    userService: UserService,
    private formBuilder: FormBuilder,
    private utilService: UtilService,
    private snackBar: MatSnackBar,
    private router: Router,
    private datePipe: DatePipe,
    private activatedRoute: ActivatedRoute
  ) {
    this.sessionData = userService.getUser();
    this.activatedRoute.queryParams.subscribe(
      (params) => {
    this.paramData = params;
      });

   }

  ngOnInit() {
    this.getPartDetails();
    this.uploadForm = this.formBuilder.group({
      profile: ['']
    });
  }

 /**To know whether the file has been selected or not */

 onFileSelect(event) {
  this.enableUploadIcon = false;
  if (event.target.files.length > 0) {
    this.enableUploadIcon = true;
    const file = event.target.files[0];
    this.uploadForm.get('profile').setValue(file);
  }
 }

 uploadfile() {
  this.files = '';
}
 /** function for uploading file */

 fileUploadFn() {
  const formData = new FormData();
  formData.append('multipart', this.uploadForm.get('profile').value);
  formData.append('orgId', this.sessionData.orgId.toString());
  formData.append('orgGrpId', this.sessionData.orgGroupId.toString());
  formData.append('createdUsrId', this.sessionData.regId.toString());
  formData.append('timezone' , this.sessionData.orgTimeZone.toString());
  this.wareHouseApiService.partExcelUpload(formData).subscribe(response => {
    if (response && response.responseCode === 'E200') {
      // this.uploadPart = true;
      this.getPartDetails();
    } else if (response && response.responseCode === 'E400') {
      this.snackBar.open(response.responseMessage, 'close', {
        duration: 2,
      });
    }
  },
    (errorResponse: HttpErrorResponse) => {
      if (errorResponse.status > 0) {
        this.snackBar.open(errorResponse.error.responseMessage, 'dismiss', {
          duration: 3000,
        });
      } else {
        // network failure
        this.snackBar.open('Network slow', 'dismiss', {
          duration: 3000,
        });
      }
    });

// }
 }



// Api for fetchind data of uploaded part

getPartDetails() {
  const payload = {
    orgId: this.sessionData.orgId,
    orgGrpId: this.sessionData.orgGroupId,
    createdUsrId: this.sessionData.regId,
  };
  this.wareHouseApiService.fetchTemporaryUploadedParts(payload).subscribe((response: any) => {
    if (response && response.responseCode === 'E200') {
      if (response.data.length > 0) {
      this.partDetails = response.data;
      // if (this.uploadPart === true) {
      this.partDetails.forEach((val, index) => {
        if (val.partUploadRemarks) {
      this.partDetails[index]['remarks'] = JSON.parse(val.partUploadRemarks);
        }
      });
    // }
  }
    } else if (response && response.responseCode === 'E400') {
      this.snackBar.open(response.responseMessage, 'close', {
        duration: 2,
      });
    }
    // this.changeDetection.detectChanges();

  },
    (errorResponse: HttpErrorResponse) => {
      if (errorResponse.status > 0) {
        this.snackBar.open(errorResponse.error.responseMessage, 'dismiss', {
          duration: 3000,
        });
      } else {
        // network failure
        this.snackBar.open('Network slow', 'dismiss', {
          duration: 3000,
        });
      }
    });
}

// change function

changedDetails(event, part) {
if (part.partNm === event) {
part.remarks.partNm = '';
} else if (part.partNo === event) {
  part.remarks.partNo = '';
} else if (part.uom === event) {
  part.remarks.uom = '';
} else if (part.partsPerPacket === event) {
  part.remarks.partsPerPacket = '';
} else if (part.unitAmt === event) {
  part.remarks.unitAmt = '';
} else {
}
}
// function for updating parts
updatedPartsUpload() {
  this.partDetails.forEach(element => {
    delete element.vendorInfoId;
  });

  const payload = [
    ...this.partDetails
];

  this.wareHouseApiService.partExcelUploadForUpdate(payload).subscribe((response): any => {
    if (response && response.responseCode === 'E200') {
      this.snackBar.open(response.responseMessage, 'dismiss', {
        duration: 1000,
      });
      this.getPartDetails();
      this.gotoPartOrProduct();
    } else if (response && response.responseCode === 'E400') {
      this.snackBar.open(response.responseMessage, 'close', {
        duration: 2,
      });
    }
  },
    (errorResponse: HttpErrorResponse) => {
      if (errorResponse.status > 0) {
        this.snackBar.open(errorResponse.error.responseMessage, 'dismiss', {
          duration: 3000,
        });
      } else {
        // network failure
        this.snackBar.open('Network slow', 'dismiss', {
          duration: 3000,
        });
      }
    });
}
// function for deleting part
deletePart(part, i) {
  const payload = {
    orgId: this.sessionData.orgId,
    orgGrpId: this.sessionData.orgGroupId,
    createdUsrId: this.sessionData.regId,
    partUploadInfoId: part.partUploadInfoId
  };
  this.wareHouseApiService.deleteUploadedParts(payload).subscribe((response): any => {
    if (response && response.responseCode === 'E200') {
     this.partDetails.splice(i, 1);
     this.snackBar.open(response.responseMessage, '', {
      duration: 2000,
    });
    } else if (response && response.responseCode === 'E400') {
      this.snackBar.open(response.responseMessage, 'close', {
        duration: 2,
      });
    }
    // this.changeDetection.detectChanges();

  },
    (errorResponse: HttpErrorResponse) => {
      if (errorResponse.status > 0) {
        this.snackBar.open(errorResponse.error.responseMessage, 'dismiss', {
          duration: 3000,
        });
      } else {
        // network failure
        this.snackBar.open('Network slow', 'dismiss', {
          duration: 3000,
        });
      }
    });

}
 // cancel method
//  cancel(params) {
//   this.router.navigate(['/warehouse/warehouse-partsList']);
//  }
 /**
  * Navigate to add Part Or Add Product Page While Clicking Back button
  */
 gotoPartOrProduct() {
if (this.paramData.addProduct) {
  this.router.navigate(['/warehouse/add-product']);
} else {
  this.router.navigate(['/warehouse/warehouse-partsList']);
}
}
}
