import { Component, OnInit, Inject, ChangeDetectorRef } from '@angular/core';
import { Router } from '@angular/router';
import { UserSession } from '@core/models/user-session';
import { UserService, CLONEDEEP } from '@core/services';
import { WarehouseApiService } from '@core/services/warehouse-api.service';
import { FormGroup, FormBuilder, FormArray, Validators} from '@angular/forms';
import { MatDatepicker, MatDialog } from '@angular/material';
import { Observable, Subject, concat, of } from 'rxjs';
import {MatSnackBar} from '@angular/material';
import { DatePipe } from '@angular/common';
import { debounceTime, distinctUntilChanged, tap, filter, switchMap, map, catchError } from 'rxjs/operators';
import { LocationDialogComponent } from '../location-dialog/location-dialog.component';
import { WarehouseService } from '../../warehouse.service';
import { HttpErrorResponse } from '@angular/common/http';
import { BarcodeComponent } from '../barcode/barcode.component';
import { UtilService } from '@core/services';


@Component({
 selector: 'app-create-vendor-invoice',
 templateUrl: './create-vendor-invoice.component.html',
 styleUrls: ['./create-vendor-invoice.component.scss']
})
export class CreateVendorInvoiceComponent implements OnInit {
 private sessionData: UserSession;
 public partNm;
 vendorInvoiceForm: FormGroup;
 vendorItemList: any;
 public purchaseOrderNo;
 poPartDetails: any;
 vendorsList: any[] = [];
 itemList = [];
 selectedVendorInfoId: any;
 selectedVendor: any;
 checked = this.utilService.generateUniqueId();
 partsList: any;
 partName$: Observable<any>;
 partNameListinput$ = new Subject<string>();
 partList: any;
 showTable = true;
 minDate = new Date();
 selectedPartList: any;
//  partTableList: any[] = [];
 vendorItemsFilterList: any[] = [];
 totalAmt: number;
 totalTaxAmt: number;
 totalServiceAmt: number;
 public TransportModes: any[] = [{'deliverycode': 'RDW', 'deliveryName': 'Roadways'},
                                  { 'deliverycode': 'RLW', 'deliveryName': 'Railways'},
                                  { 'deliverycode': 'WW', 'deliveryName': 'Waterways' },
                                  { 'deliverycode': 'AW', 'deliveryName': 'Airways'}];
 public currencyType: any;
 grossAmt: number;
 totalTaxPercntg: number;
 today = new Date();
 isOpenDialog = false;
 storagSheds: any;
  purchaseDetails: Boolean = false;
  manualDetails: Boolean = true;
  selectedPurchaseOrderList: any;
  locationData: any;
 constructor(
 private router: Router,
 userService: UserService,
 private wareHouseApiService: WarehouseApiService,
 private formBuilder: FormBuilder,
 private utilService: UtilService,
 private snackBar: MatSnackBar,
 private datePipe: DatePipe,
 public dialog: MatDialog,
 private changeDetection: ChangeDetectorRef,
 private warehouseDataService: WarehouseService
 ) {
 this.sessionData = userService.getUser();
 }

 ngOnInit() {
  this.fetchCurrencyType();
 this.warehouseDataService.purchaseData.next('vendor-invoice');
 this.initForm();
 this.fetchVendorsList();
 this.fetchPartDetails();
//  this.vendorInvoiceForm.patchValue({ shippingType: 'Roadways'});
 }
 initForm() {
 this.vendorInvoiceForm = this.formBuilder.group({
 shippingType: [this.TransportModes[0]],
 partName: this.purchaseDetails ? null : this.formBuilder.control(null, Validators.required),
//  poPartName: '',
 vendorInfoId: this.formBuilder.control(null, Validators.required),
 vendorInvoiceNo: this.formBuilder.control(null, Validators.required),
 orderDt: this.formBuilder.control(null, Validators.required),
 deliveryDt: this.formBuilder.control(new Date(), Validators.required),
 vendorInvoiceItemsList: this.formBuilder.array([])
 });
 }

 fetchCurrencyType() {
  const payload = {
    orgId: this.sessionData.orgId,
    orgGrpId: this.sessionData.orgGroupId,
    createdUsrId: this.sessionData.regId,
  };
  this.wareHouseApiService.fetchCurrencyType(payload).subscribe(response => {
    if (response && response.responseCode === 'E200') {
      this.currencyType = response.data;
    } else if (response && response.responseCode === 'E400') {
      this.snackBar.open(response.responseMessage, 'dismiss', {
        duration: 500,
      });
    }
  }, (errorResponse: HttpErrorResponse) => {
    if (errorResponse.status > 0) {
      this.snackBar.open(errorResponse.error.responseMessage, 'dismiss', {
        duration: 500,
      });
    } else {
      // network failure
      this.snackBar.open('Network slow', 'dismiss', {
        duration: 500,
      });
    }
  });
}
 get manageFormArray() {
 return <FormArray>this.vendorInvoiceForm.get('vendorInvoiceItemsList');
 }

 remove(i) {
 this.manageFormArray.removeAt(i);
 }

 fetchPartDetails() {
 this.partName$ = concat(
 of([]), // default items
 this.partNameListinput$.pipe(
 debounceTime(200),
 distinctUntilChanged(),
 tap(console.log),
 filter(checkStringCount => {
 if (checkStringCount && checkStringCount.length > 1) {
 return true;
 }
 }),
 // tap(() => this.people3Loading = true),
 switchMap(this.fetchParts.bind(this))
 )
 );
 }

 // function to get part details through PO ID
 getPartDetails(poNum) {
  const payload = {
    orgId: this.sessionData.orgId,
    orgGrpId: this.sessionData.orgGroupId,
    createdUsrId: this.sessionData.regId,
    purchaseOrderNo: poNum
    };

    this.wareHouseApiService.fetchPoDetailsForInvoice(payload).subscribe((response: any) => {
    if (response && response.responseCode === 'E200') {
      if (response.data.length > 0) {
      this.selectedPurchaseOrderList = response.data[0];
      this.selectedPartList = response.data[0].vendorInvoiceItemsList;
      this.manualDetails = true;
    this.changeDetection.detectChanges();
      this.selectedPartList.forEach(element => {
        this.partDetailsToTable(element);
        // this.calculatePurchaseOrderPrice(element);
      });
    this.vendorInvoiceForm.patchValue({ vendorInfoId: response.data[0].vendorNm});
      } else {
        this.snackBar.open(response.responseMessage, 'error', {
        duration: 2000,
        });
      }
    } else if (response && response.responseCode === 'E400') {
    }
    });

 }



  fetchParts(string) {
    const payload = {
      createdUsrId: this.sessionData.regId,
      orgGrpId: this.sessionData.orgGroupId,
      orgId: this.sessionData.orgId,
      vendorInfoId: this.selectedVendorInfoId,
      partNmOrNo: string,
    };
    return this.wareHouseApiService.fetchPartDetails(payload).pipe(
      map((response: any) => {
        if (response.responseCode === 'E200') {
          return response.data;
        } else if (response.response === 'E400') {
        }
      }),
      catchError(error => {
        return of([]);
      })
    );
  }
  openDialog(data) {
    // this.locationData = '';
    const dialogRef = this.dialog.open(LocationDialogComponent, {
      width: '1000px',
      height: '200px',
    });
    dialogRef.afterClosed().subscribe(result => {
      // this.locationData = result;
      data.patchValue({location: result.shedNm + '/' + result.rowNo + '/' + result.columnNo + '/' + result.tierNo });
      data.value.rowId = result.rowId;
      data.value.columnId = result.columnId;
      data.value.tierId = result.tierId;
      data.value.shedId = result.shedId;
    });
  }

  // function for getting dynamic tool tip for specific location
  getToolTipData(data) {
    // console.log(data.value.location);
   return data.value.location;
  }

  onAddPart(part) {
    let location;
    if (part.shedNm) {
    location = part.shedNm + '/' + part.rowNo + '/' + part.columnNo + '/' + part.tierNo;
    }
    const creds = this.vendorInvoiceForm.controls.vendorInvoiceItemsList as FormArray;
        creds.push(this.formBuilder.group({
          createdUsrId: this.sessionData.regId,
          orgId: this.sessionData.orgId,
          orgGrpId: this.sessionData.orgGroupId,
          partId: part.partId,
          columnId: part.columnId,
          rowId: part.rowId,
          shedId: part.shedId,
          tierId: part.tierId,
          partNm: part.partNm,
          partDescription: part.partDescription,
          partProductInd: part.partProductInd,
          batchNo: [part.batchNo, Validators.required],
          barcode: '',
          gstTaxAmt: '',
          uom: part.uom,
          qty: [part.qty, Validators.required],
          unitAmt: [part.unitAmt, Validators.required],
          expireDt: [
            this.datePipe.transform(part.expireDt, 'yyyy-MM-dd')
          ],
          serviceAmt: '',
          mrp: [part.mrp, Validators.required],
          gstTaxPercntg: [part.gstTaxPercntg, Validators.required],
          totalAmt: '',
          location: location,
          copyDetails: this.formBuilder.array([])
        }));
  }

  // function for adding part details in the table
  partDetailsToTable(result) {
    const calEndResult = this.calculatePurchaseOrderPrice(result);
    const location =
    result.shedNm + '/' + result.rowNo + '/' + result.columnNo + '/' + result.tierNo;
    const creds = this.vendorInvoiceForm.controls.vendorInvoiceItemsList as FormArray;
        creds.push(this.formBuilder.group({
          createdUsrId: this.sessionData.regId,
          orgId: this.sessionData.orgId,
          orgGrpId: this.sessionData.orgGroupId,
          partId: result.partId,
          columnId: result.columnId,
          rowId: result.rowId,
          shedId: result.shedId,
          tierId: result.tierId,
          partNm: result.partNm,
          partDescription: result.partDescription,
          partProductInd: result.partProductInd,
          batchNo: [result.batchNo, Validators.required],
          barcode: '',
          gstTaxAmt: calEndResult.gstTaxAmount,
          uom: result.uom,
          qty: [result.qty, Validators.required],
          unitAmt: [result.unitAmt, Validators.required],
          expireDt: [
            this.datePipe.transform(result.expireDt, 'yyyy-MM-dd')
          ],
          serviceAmt: calEndResult.serviceAmt,
          mrp: [result.mrp, Validators.required],
          gstTaxPercntg: [result.gstTaxPercntg, Validators.required],
          totalAmt: calEndResult.totalAmount,
          location: location,
          copyDetails: this.formBuilder.array([])
        }));
  }
  addNewFields(creds, fieldData) {
    creds.push(this.formBuilder.group({
      createdUsrId: this.sessionData.regId,
      orgId: this.sessionData.orgId,
      orgGrpId: this.sessionData.orgGroupId,
      partId: fieldData.value.partId,
      batchNo: [fieldData.value.batchNo, Validators.required],
      partProductInd: fieldData.value.partProductInd,
      partDescription: fieldData.value.partDescription,
      barcode: fieldData.value.barcode,
      uom: fieldData.value.uom,
      qty: [fieldData.value.qty, Validators.required],
      unitAmt: [fieldData.value.unitAmt, Validators.required],
      expireDt: [
        this.datePipe.transform(fieldData.value.expireDt, 'yyyy-MM-dd')
      ],
      gstTaxAmt: '',
      serviceAmt: fieldData.value.serviceAmt,
      mrp: [fieldData.value.mrp, Validators.required],
      gstTaxPercntg: [fieldData.value.gstTaxPercntg, Validators.required],
      totalAmt: fieldData.value.totalAmt,
      columnId: fieldData.value.columnId,
      rowId: fieldData.value.rowId,
      shedId: fieldData.value.shedId,
      tierId: fieldData.value.tierId,
      location: fieldData.value.location
    }));
 }
 fetchVendorsList() {
 const payload = {
 orgId: this.sessionData.orgId,
 orgGrpId: this.sessionData.orgGroupId,
 createdUsrId: this.sessionData.regId,
 };

 this.wareHouseApiService.fetchVendors(payload).subscribe((response: any) => {
 if (response && response.responseCode === 'E200') {
 this.vendorsList = response.data;
 } else if (response && response.responseCode === 'E400') {
 }
 });
 }
 onAddVendor(vendor) {
 this.selectedVendorInfoId = vendor.vendorInfoId;

  }
  datePickerToggler(datePicker: MatDatepicker<Date>) {
    datePicker[datePicker.opened ? 'close' : 'open']();
  }
  AddToInventory() {
    const tempFormData = Object.assign({}, { ...this.vendorInvoiceForm.value });
      this.vendorInvoiceForm.patchValue({
        vendorInfoId: tempFormData.vendorInfoId.vendorInfoId
      });
    this.vendorItemList = this.vendorInvoiceForm.get('vendorInvoiceItemsList').value;
    this.vendorItemList.forEach(obj => {
      if (obj.copyDetails.length > 0) {
        obj.copyDetails.forEach(element => {
        this.vendorItemsFilterList.push(element);
        });
      }
    });
    this.vendorItemsFilterList.forEach(element => {
      this.vendorItemList.push(element);
    });
 // for calculating total amounts
 this.totalAmt = 0;
 this.totalTaxAmt = 0;
 this.totalServiceAmt = 0;
 this.totalTaxPercntg = 0;
 this.vendorItemList.forEach(val => {
 this.totalAmt += val.totalAmt;
 this.totalServiceAmt += val.serviceAmt;
 this.totalTaxPercntg += val.gstTaxPercntg;
 this.totalTaxAmt += val.gstTaxAmt;
 this.grossAmt = this.totalAmt - this.totalTaxAmt;
 });
 this.insertVendorInvoice();
 }
 insertVendorInvoice() {
 const payload = {
 orgId: this.sessionData.orgId,
 orgGrpId: this.sessionData.orgGroupId,
 createdUsrId: this.sessionData.regId,
 purchaseOrderId: this.selectedPurchaseOrderList ? this.selectedPurchaseOrderList.purchaseOrderId : '',
 purchaseOrderNo: this.purchaseOrderNo,
 ...this.vendorInvoiceForm.value,
 vendorInfoId: this.purchaseDetails ? this.selectedPurchaseOrderList.vendorInfoId : this.vendorInvoiceForm.value.vendorInfoId,
 orderDt: this.datePipe.transform(
 this.vendorInvoiceForm.value.orderDt,
 'yyyy-MM-dd'
 ),
 deliveryDt: this.datePipe.transform(
 this.vendorInvoiceForm.value.deliveryDt,
 'yyyy-MM-dd'
 ),
 vendorInvoiceItemsList: this.vendorItemList,
 totalAmt: this.totalAmt,
 totalServiceAmt: this.totalServiceAmt,
 totalTaxPercntg: this.totalTaxPercntg,
 totalTaxAmt: this.totalTaxAmt,
 partName: ''
 };
payload.vendorInvoiceItemsList.forEach(obj => {
    obj.receivedDt = payload.deliveryDt;
});
 payload.shippingType = payload.shippingType ? payload.shippingType.deliveryName : '';
 this.wareHouseApiService.insertVendorInvoice(payload).subscribe(response => {
 if (response && response.responseCode === 'E200') {
 this.vendorInvoiceForm.reset();
 this.purchaseOrderNo = '';
 this.showTable = false;
//  this.router.navigate(['/warehouse/purchase/vendor-invoice']);
 } else if (response && response.responseCode === 'E400') {
 this.snackBar.open(response.responseMessage, 'dismiss', {
 duration: 1000,
 });
 }
 });
 }
 // calculate price
 calculatePrice(creds) {
  const unitPrice = creds.value.unitAmt || 0;
  const qty = creds.value.qty;
  creds.value.serviceAmt = qty * unitPrice;
  creds.patchValue({serviceAmt : creds.value.serviceAmt});
  this.calculateTaxAmount(creds);
  }
  calculateTaxAmount(creds) {
    const serviceAmount = creds.value.serviceAmt;
    const taxPercent = creds.value.gstTaxPercntg;
    if (taxPercent > 100) {
    this.snackBar.open('Tax should not be greater than 100', 'dismiss', {
    duration: 3000,
    });
    creds.patchValue({gstTaxPercntg: ''});
    }
    creds.value.gstTaxAmt = serviceAmount * (taxPercent / 100);
    creds.patchValue({gstTaxAmt: creds.value.gstTaxAmt});
    const totalAmt = serviceAmount + creds.value.gstTaxAmt;
    creds.patchValue({totalAmt: totalAmt});
    }

  /** navigate to barcode */
  openBarcode(data) {
    const dialogRef = this.dialog.open(BarcodeComponent, {
      width: '700px',
      height: '500px',
    });
    dialogRef.afterClosed().subscribe(result => {
      if (result) {
      data.patchValue({barcode: result });
      }
    });
  }

  // function for enabling purchase order input
  showPurchaseDetails() {
    this.purchaseDetails = true;
    this.manualDetails = false;
    this.showTable = true;
    this.initForm();
  //  this.vendorInvoiceForm.reset();
  }

  showManualDetails() {
    this.purchaseDetails = false;
    this.manualDetails = true;
    this.initForm();
  //  this.vendorInvoiceForm.reset();
   this.purchaseOrderNo = '';
  }

  // for deleting row of copy details
  delete(i, j) {
   const control = <FormArray>this.vendorInvoiceForm.get('vendorInvoiceItemsList')['controls'][i].get('copyDetails');
control.removeAt(j);
  }


  // purchase order calculations

  calculatePurchaseOrderPrice(data) {
  let serviceAmt;
  let gstTaxAmount;
  let totalAmount;
 serviceAmt = data.qty * data.unitAmt;
gstTaxAmount = serviceAmt * (data.gstTaxPercntg / 100);
totalAmount = serviceAmt + gstTaxAmount;
return {serviceAmt, gstTaxAmount, totalAmount};
  }

  reset(creds) {
    creds.patchValue({ barcode: '' });
    creds.patchValue({ batchNo: '' });
    creds.patchValue({ expireDt: '' });
    creds.patchValue({ uom: '' });
    creds.patchValue({ qty: '' });
    creds.patchValue({ unitAmt: '' });
    creds.patchValue({ mrp: '' });
    creds.patchValue({ gstTaxPercntg: '' });
    creds.patchValue({ totalAmt: '' });
    creds.patchValue({ location: '' });
    creds.patchValue({ receivedDt: '' });
  }

  // to clear data
  clear() {
    // this.manageFormArray.reset();
    const arr = <FormArray>this.vendorInvoiceForm.controls.vendorInvoiceItemsList;
    arr.controls = [];
    this.vendorInvoiceForm.reset();
    this.purchaseOrderNo = '';
  }
}
