import { Component, OnInit, AfterViewInit, OnDestroy, ChangeDetectionStrategy } from '@angular/core';
import { ActivatedRoute, Router, NavigationExtras } from '@angular/router';
import { AbstractControl } from '@angular/forms';
import { UtilService } from '@core/services';
import { WarehouseService } from '../../warehouse.service';
import { AutoUnsubscribe } from 'ngx-auto-unsubscribe';
@AutoUnsubscribe()
@Component({
  selector: 'app-settings',
  templateUrl: './settings.component.html',
  styleUrls: ['./settings.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class SettingsComponent implements OnInit, AfterViewInit, OnDestroy {
  public productList = false;
  public partList = false;

  constructor(
    private router: Router,
    private activatedroute: ActivatedRoute,
    private utilService: UtilService,
    private warehouseDataService: WarehouseService
  ) {}

  ngOnInit() {}

  ngAfterViewInit() {
    this.warehouseDataService.settingsData.subscribe(val => {
      if ( val === 'products-list') {
        this.partList = true;
        this.productList = false;
      } else if ( val === 'list-of-products') {
        this.partList = false;
        this.productList = true;
      } else {
        this.hideList();
      }
    });
  }

  history() {
    this.router.navigate(['/warehouse/add-product']);
  }

  showPart() {
    this.partList = true;
    this.productList = false;
  }

  showProductList() {
    this.partList = false;
    this.productList = true;
  }

  hideList() {
    this.partList = false;
    this.productList = false;
  }

  historyparts() {
    this.router.navigate(['/warehouse/warehouse-partsList']);
  }
  showCartonBox(){
    this.router.navigate(['warehouse/settings/carton-box'])
  }

  ngOnDestroy() {}
}
