import { Component, OnInit, ChangeDetectorRef } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { WarehouseApiService } from '@core/services/warehouse-api.service';
import { UserSession } from '@core/models/user-session';
import { UserService } from '@core/services';
import { ToastrService } from 'ngx-toastr';
import { Router } from '@angular/router';


@Component({
  selector: 'app-carton-box',
  templateUrl: './carton-box.component.html',
  styleUrls: ['./carton-box.component.scss']
})
export class CartonBoxComponent implements OnInit {
  cartonBoxForm: FormGroup;
  private sessionData: UserSession;
  public cartonboxesList: any;
  public editCartonData: any;
  isUpdatebtn = false;
  showPopUpVal = false;
  width = false;
  length = false;
  height = false;
  cartType: number;
  cartonBoxFormSubmitted = false;
  productDimension = [
    { uom: 'MM' },
    { uom: 'CM' },
    { uom: 'Mtr' },
  ];
  validation_messages = {
    cartonTypeNm: [
      {
        type: 'required', message: '* Carton Name is required'
      }
    ],
    uom: [
      {
        type: 'required', message: '* Carton Dimensions are required'
      }
    ]
  };

  constructor(
    private fb: FormBuilder,
    private warehouseApiService: WarehouseApiService,
    private userService: UserService,
    private toastr: ToastrService,
    private router: Router,
    private changeDetection: ChangeDetectorRef
  ) {
    this.sessionData = userService.getUser();
  }

  ngOnInit() {
    this.initForm();
    this.fetchList();
  }
  /**
   * @description validations for Carton box input fields
   */
  initForm() {

    this.cartonBoxForm = this.fb.group({
      cartonTypeNm: this.fb.control('', Validators.required),
      uom: this.fb.control('', Validators.required),
      cartonLength: this.fb.control('', Validators.required),
      cartonWidth: this.fb.control('', Validators.required),
      cartonHeight: this.fb.control('', Validators.required),
    });
  }

  /**
   * Insert Api Integration Calling for CartonBox
   */
  create() {
     this.cartonBoxFormSubmitted = true;
    if (this.cartonBoxForm.valid) {
      const payload = [
        {
          createdUsrId: this.sessionData.regId,
          orgGrpId: this.sessionData.orgGroupId,
          orgId: this.sessionData.orgId,
          cartonTypeNm: this.cartonBoxForm.value.cartonTypeNm,
          cartonLength: this.cartonBoxForm.value.cartonLength,
          cartonWidth: this.cartonBoxForm.value.cartonWidth,
          cartonHeight: this.cartonBoxForm.value.cartonHeight,
          uom: this.cartonBoxForm.value.uom
        }];
      this.warehouseApiService.insertCartonDetails(payload).subscribe(
        (response) => {
          if (response && response.responseCode === 'E200') {
             this.clearForm();
            this.fetchList();
            this.router.navigate(['/warehouse/settings/carton-box']);
          } else if (response && response.responseCode === 'E400') {
            this.toastr.error('Failure!', response.responseMessage);
          }
        },
        (err) => {
          this.toastr.error('Failure!', 'Something went wrong');
        }
      );
    }
  }
  /**
   *  Carton Box Editing
   */
  editCartonBox(i, val) {
    this.isUpdatebtn = true;
    const payload = {
      cartonTypeId: val,
      createdUsrId: this.sessionData.regId,
      orgGrpId: this.sessionData.orgGroupId,
      orgId: this.sessionData.orgId
    };
    this.warehouseApiService.fetchCartonDetails(payload).subscribe(
      (response) => {
        if (response && response.responseCode === 'E200') {

          this.editCartonData = response.data[0];
          this.cartType = this.editCartonData.cartonTypeId;

          this.cartonBoxForm.patchValue(this.editCartonData);
        } else if (response && response.responseCode === 'E400') {
          this.toastr.error('Failure!', response.responseMessage);
        }
      },
      () => {
        this.toastr.error('Failure!', 'Something went wrong');
      }
    );
  }
  /**
   * Update Api Integration for Carton Box
   */
  update() {
    this.isUpdatebtn = false;
    const payload = {
      createdUsrId: this.sessionData.regId,
      orgGrpId: this.sessionData.orgGroupId,
      orgId: this.sessionData.orgId,
      cartonTypeId: this.cartType,
      cartonTypeNm: this.cartonBoxForm.value.cartonTypeNm,
      cartonLength: this.cartonBoxForm.value.cartonLength,
      cartonWidth: this.cartonBoxForm.value.cartonWidth,
      cartonHeight: this.cartonBoxForm.value.cartonHeight,
      uom: this.cartonBoxForm.value.uom,
    };
    this.warehouseApiService.updateCartonDetails(payload).subscribe(
      (response) => {
        if (response && response.responseCode === 'E200') {
          this.fetchList();
          this.clearForm();
          this.router.navigate(['/warehouse/settings/carton-box']);
        } else if (response && response.responseCode === 'E400') {
          this.toastr.error('Failure!', response.responseMessage);
        }
      },
      () => {
        this.toastr.error('Failure!', 'Something went wrong');
      }
    );
  }

  /**
   * Delete Api Integration for Carton Box
   */
  delete(i, val) {
    const payload = {
      createdUsrId: this.sessionData.regId,
      orgGrpId: this.sessionData.orgGroupId,
      orgId: this.sessionData.orgId,
      cartonTypeId: val
    };
    this.warehouseApiService.deleteCartonDetails(payload).subscribe(
      (res) => {
        if (res && res.responseCode === 'E200') {
          this.toastr.success('Success', res.responseMessage);

        } else if (res && res.responseCode === 'E400') {
          this.toastr.error('Failure!', res.responseMessage);
        }
      },
      () => {
        this.toastr.error('Failure!', 'Something went wrong');
      });
  }

  /**
   * Ftech Api for Cartonbox
   */
  fetchList() {
    const payload = {
      cartonTypeId: 0,
      createdUsrId: this.sessionData.regId,
      orgGrpId: this.sessionData.orgGroupId,
      orgId: this.sessionData.orgId
    };
    this.warehouseApiService.fetchCartonDetails(payload).subscribe(
      (response) => {
        if (response && response.responseCode === 'E200') {
          this.cartonboxesList = response.data;
        } else if (response && response.responseCode === 'E400') {
          this.toastr.error('Failure!', response.responseMessage);
        }
        this.changeDetection.detectChanges();
      },
      () => {
        this.toastr.error('Failure!', 'Something went wrong');
      });
  }
  /**
   * Clear Form Data
   */
  clearForm() {
    this.cartonBoxForm.reset();
   }
}
