import {
  Component,
  OnInit,
  OnDestroy,
  AfterViewInit,
  ChangeDetectionStrategy
} from '@angular/core';
import { Router } from '@angular/router';
import { WarehouseService } from '../../warehouse.service';
import { AutoUnsubscribe } from 'ngx-auto-unsubscribe';

@AutoUnsubscribe()
@Component({
  selector: 'app-warehouse-purchase',
  templateUrl: './warehouse-purchase.component.html',
  styleUrls: ['./warehouse-purchase.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class WarehousePurchaseComponent
  implements OnInit, OnDestroy, AfterViewInit {
  public Raise = true;
  public Vendor = false;
  public Invoice = false;
  public currentDate = new Date();
  constructor(
    private router: Router,
    private warehouseDataService: WarehouseService
  ) {}

  ngOnInit() {
    this.warehouseDataService.purchaseData.subscribe(val => {
      if (val && val === 'purchase-order') {
        this.raise();
      } else if (val && val === 'list-of-vendors') {
        this.vendor();
      } else if (val && val === 'vendor-invoice') {
        this.invoice();
      }
    });
  }

  ngAfterViewInit() {}

  raise() {
    this.Raise = true;
    this.Vendor = false;
    this.Invoice = false;
  }
  vendor() {
    this.Raise = false;
    this.Vendor = true;
    this.Invoice = false;
  }
  invoice() {
    this.Raise = false;
    this.Vendor = false;
    this.Invoice = true;
  }
  newOrder() {
    this.warehouseDataService.setPurchaseOrderFromDate(this.currentDate);
    this.warehouseDataService.setPurchaseOrderToDate(this.currentDate);
    this.router.navigate(['/warehouse/purchase-history']);
  }
  history() {
    this.router.navigate(['/warehouse/add-new-vendor']);
  }
  createNewInvoice() {
    this.router.navigate(['/warehouse/vendor-invoice-history']);
  }

  ngOnDestroy() {}
}
