import { Component, OnInit, AfterViewInit, OnDestroy, ChangeDetectionStrategy } from '@angular/core';
import { Router } from '@angular/router';
import { AbstractControl } from '@angular/forms';
import { UtilService } from '@core/services';
import { WarehouseService } from '../../warehouse.service';
import { Subscription } from 'rxjs';
import { AutoUnsubscribe } from 'ngx-auto-unsubscribe';
@AutoUnsubscribe()
@Component({
  selector: 'app-warehouse-sale',
  templateUrl: './warehouse-sale.component.html',
  styleUrls: ['./warehouse-sale.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class WarehouseSaleComponent implements OnInit, AfterViewInit, OnDestroy {
  public saleOrder = true;
  public newcustomer = false;
  public saleReturns = false;
  public saleRetuenHistory = false;
  saleControl: AbstractControl;
  checked = this.utilService.generateUniqueId();
  saleOrderDataSubscription: Subscription;
  currentDate = new Date();
  constructor(
    private router: Router,
    private utilService: UtilService,
    private warehouseDataService: WarehouseService
    ) {}

  ngOnInit() {}

  ngAfterViewInit() {
   this.saleOrderDataSubscription = this.warehouseDataService.saleOrderData.subscribe(val => {
      if (val && val === 'sale-order') {
        this.showSaleOrder();
      } else if (val && val === 'list-of-customers') {
        this.showCustomer();
      } else if (val && val === 'credit-note-history') {
       this.saleOrder = false;
      }
    });
  }

  showSaleOrder() {
    this.saleOrder = true;
    this.newcustomer = false;
    this.saleReturns = false;
    this.saleRetuenHistory = false;
  }

  showCustomer() {
    this.saleOrder = false;
    this.newcustomer = true;
    this.saleReturns = false;
    this.saleRetuenHistory = false;
  }
  showSaleRetuns() {
    this.saleOrder = false;
    this.newcustomer = false;
    this.saleReturns = true;
    this.saleRetuenHistory = false;
  }
  showSaleRetunsHisory() {
    this.saleOrder = false;
    this.newcustomer = false;
    this.saleReturns = false;
    this.saleRetuenHistory = true;
  }
  showCreditHistory() {
    this.router.navigate(['/warehouse/sale/credit-note-history']);
    this.newcustomer = false;
    this.saleOrder = false;


  }
  addCustomer() {
    this.router.navigate(['/warehouse/warehouse-registration']);
  }

  saleHistory() {
    this.warehouseDataService.setSaleHistoryFromDate(this.currentDate);
    this.warehouseDataService.setSaleHistoryToDate(this.currentDate);
    this.router.navigate(['/warehouse/warehouse-sale-history']);
  }

  ngOnDestroy() {}
}
