import { Pipe, PipeTransform } from '@angular/core';
import { DatePipe } from '@angular/common';
@Pipe({
  name: 'stockHistory'
})
export class StockHistoryPipe implements PipeTransform {

  transform(
    fetchStockHistory: any,
    transferedDate: string,
    partProductName: string,
    barcode: string,
    prevLocation: string,
    quantity: string,
    expirydate: string,
    currentLocation: string,
    )  {
      if (fetchStockHistory && fetchStockHistory.length) {
        return fetchStockHistory.filter(transferStock => {
          if (
            (partProductName && !transferStock.partNmOrNo) ||
            (quantity && !transferStock.qty) ||
            ( transferedDate && !transferStock.transferredDt) ||
            (expirydate && !transferStock.expireDt) ||
            ( barcode && !transferStock.barcode)
          ) {
            return false;
          }
          if (transferStock.partNmOrNo) {
            if (
              partProductName &&
              transferStock.partNmOrNo.toLowerCase().indexOf(partProductName.toLowerCase()) ===
                -1
            ) {
              return false;
            }
          }
          // transfer Date
          if (transferStock.transferredDt) {
            const datePipe = new DatePipe('en-US');
            transferStock.transferredDt = datePipe.transform(transferStock.transferredDt, 'yyyy/MM/dd');
            if (transferedDate && transferStock.transferredDt.toLowerCase().indexOf(transferedDate.toLowerCase()) === -1) {
                return false;
            }
          }
          // expiry date
          if (transferStock.expireDt) {
            const datePipe = new DatePipe('en-US');
            transferStock.expireDt = datePipe.transform(transferStock.expireDt, 'yyyy/MM/dd');
            if (expirydate && transferStock.expireDt.toLowerCase().indexOf(expirydate.toLowerCase()) === -1) {
                return false;
            }
          }
          // barcode no
          if (transferStock.barcode) {
            if (
              barcode &&
              transferStock.barcode.toLowerCase().indexOf(barcode.toLowerCase()) ===
                -1
            ) {
              return false;
            }
          }
          // transferStock qty
          if (transferStock.qty) {
            if (
              quantity &&
              transferStock.qty &&
              transferStock.qty.toString()
                  .toLowerCase()
                  .indexOf(quantity.toLowerCase()) === -1
            ) {
              return false;
            }
          }
        // previous location
          if (transferStock.previousLocation) {
            if (
              prevLocation &&
              transferStock.previousLocation &&
              transferStock.previousLocation.toString()
                  .toLowerCase()
                  .indexOf(prevLocation.toLowerCase()) === -1
          ) {
              return false;
          }
          }
          // current locations
          if (transferStock.currentLocation) {
            if (
              currentLocation &&
              transferStock.currentLocation &&
              transferStock.currentLocation.toString()
                  .toLowerCase()
                  .indexOf(currentLocation.toLowerCase()) === -1
          ) {
              return false;
          }
          }
          return true;
        });
      } else {
          return fetchStockHistory;
      }
    }

}
