import { ToastrService } from 'ngx-toastr';
import { Component, OnInit, ChangeDetectorRef } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { WarehouseApiService } from '@core/services/warehouse-api.service';
import { MatSnackBar } from '@angular/material/snack-bar';
import { DatePipe } from '@angular/common';
import { WarehouseService } from '../../warehouse.service';
import { UtilService, UserService, ReportsService } from '@core/services';
import { Router } from '@angular/router';
import { UserSession } from '@core/models/user-session';
import { HttpInterceptor, HttpHandler, HttpRequest, HttpErrorResponse } from '@angular/common/http';
import { FormControl, FormGroup, FormBuilder, Validators, AbstractControl } from '@angular/forms';
import { MatDatepicker, MatSidenav } from '@angular/material';
import { SaleReturnComformModelComponent } from '../sale-return-comform-model/sale-return-comform-model.component';
import { element } from '@angular/core/src/render3';
@Component({
  selector: 'app-sale-return',
  templateUrl: './sale-return.component.html',
  styleUrls: ['./sale-return.component.scss']
})
export class SaleReturnComponent implements OnInit {
  customerSearchControl: AbstractControl;
  selectAll: AbstractControl;
  returnQuantity: AbstractControl;
  isSelect: AbstractControl;
  returnQty: AbstractControl;
  select: AbstractControl;
  selectAllParts = false;
  public isInvalid = false;
  public selectedArray: any;
  customerData: any;
  searchType: any;
  dateMax = new Date();
  public returnAmt = 0;
  public totalReturnedQty = 0;
  public index = null;
  public indexNum;
  public saleorderId = null;
  public customerId = null;
  public customerNm = null;
  public saleOrderNo = null;
  public invoiveNo = null;
  public partDetailsList: any;
  public placeholderValue: string;
  public IsDisplayBatchDetails = false;
  public selectAllBatch = false;
  public isInvalidQuanity = false;
  public invalidQty = false;
  public gstTaxAmt = 0;
  public orgCustomerId = null;
  public saleOrderId = null;
  public customerOrder: any;
  public totalReturnServiceAmt = 0;
  public saleOrderReturnItemDetailsList: any [] = [];
  public isDisplayBatch = false;
  public batchDetails: any [] = [];
  public returnpartsList: any [] = [];
  public selectedPartDetails: any;
  public isDisplayParts = false;
  public showBatches = false;
  private sessionData: UserSession;
  public toDate = new Date();
  public fromDate = new Date();
  public saleList: any;
  public minDate: any;
  public checked = this.utilService.generateUniqueId();
  public subIndex: any;
  public availableqty = 0;
  constructor( private router: Router,
  userService: UserService,
    private utilService: UtilService,
    private warehouseService: WarehouseService,
    private snackBar: MatSnackBar,
    private datePipe: DatePipe,
    private warehouseApiService: WarehouseApiService,
    public dialog: MatDialog,
    private changeDetection: ChangeDetectorRef,
    private reportsApiService: ReportsService,
    private toaster: ToastrService
  ) {
    this.sessionData = userService.getUser();
   }
    ngOnInit() {
    this.fromDate = this.warehouseService.getSaleReturnsFromDate();
    this.toDate = this.warehouseService.getSaleReturnsToDate();
    this.placeholderValue = 'Search  by Customer Name';
    this.getCustomersList(this.searchType, this.customerData);
    this.customerSearchControl = new FormControl('mobile');
      if (!this.fromDate) {
      this.fromDate = new Date();
      this.toDate = new Date();
    }
    this.customerSearchControl.valueChanges.subscribe(value => {
      this.searchType = value;
      switch (this.searchType) {
        case 'SO':
          this.placeholderValue = 'Search  by Sale Order ID';
          this.customerData = '';
          this.getCustomersList('', '');
          break;
          default:
          this.placeholderValue = 'Search  by Customer Name';
          this.customerData = '';
          this.getCustomersList('', '');
          break;
      }
    });
  }
  getCustomersList( searchType: any, customerData: any) {
  const payload = {
  createdUsrId:  this.sessionData.regId,
  toDt: this.datePipe.transform(this.toDate, 'yyyy-MM-dd'),
  fromDt: this.datePipe.transform(this.fromDate, 'yyyy-MM-dd'),
  orgId: this.sessionData.orgId,
  category: 'I',
  orgGrpId: this.sessionData.orgGroupId,
    };
    if (searchType === 'SO') {
      payload['saleOrderNo'] = customerData;
    } else {
      payload['customerId'] = customerData;
    }
    this.warehouseApiService.fetchSaleOrders(payload).subscribe(
      response => {
        if (response && response.responseCode === 'E200') {
          this.saleList = response.data;
          if (this.saleList.length > 0) {
            this.saleList.forEach(el => {
              el['isShow'] = false;
            });
        }
        } else if (response && response.responseCode === 'E400') {
          this.snackBar.open(response.responseMessage, 'dismiss', {
            duration: 1500
          });
        }
        this.changeDetection.detectChanges();
      },
      (errorResponse: HttpErrorResponse) => {
        if (errorResponse.status > 0) {
          this.snackBar.open(errorResponse.error.responseMessage, 'dismiss', {
            duration: 1500
          });
        } else {
          // network failure
          this.snackBar.open('Network slow', 'dismiss', {
            duration: 1500
          });
        }
      }
    );
  }

  /** fetchPartDetailsForReturn start here */
  fetchPartDetailsForReturn(row, index) {
    // if ( this.saleList[index].isShow === true) {
    //   this.saleList[index].isShow = true;
    // } else {
    //   this.saleList[index].isShow  = false;
    // }
    this.saleList.forEach(el => {
      el['isShow'] = false;
    });
    if (index !== null) {
      this.saleList[index].isShow = true;
    }
    this.invoiveNo = row ? row. invoiveNo : null;
    this.customerOrder = row;
  const payload = {
  toDt: this.datePipe.transform(this.toDate, 'yyyy-MM-dd'),
  fromDt: this.datePipe.transform(this.fromDate, 'yyyy-MM-dd'),
  orgId: this.sessionData.orgId,
  orgGrpId: this.sessionData.orgGroupId,
  saleOrderNo: row ? row.saleOrderNo : null,
  customerNm: row ? row.customerNm : null,
  orgCustomerId: row ? row.orgCustomerId : null,
  saleOrderId: row ? row.saleOrderId : null,
  customerId: row ? row.customerId : null,
    };
    this.warehouseApiService.fetchPartDetailsForReturn(payload).subscribe(
      response => {
        if (response && response.responseCode === 'E200') {
          // this.subIndex = index;
          this.partDetailsList = response.data;
          if (this.partDetailsList && this.partDetailsList.length > 0) {
          this.partDetailsList.forEach (value => {
             value['showBatchDetails'] = false;
            value.batchWiseItemsList.forEach(obj => {
              obj['isQuantityValid'] = false;
            });
          });
        }
        } else if (response && response.responseCode === 'E400') {
          this.snackBar.open(response.responseMessage, 'dismiss', {
            duration: 1500
          });
        }
        this.changeDetection.detectChanges();
      },
      (errorResponse: HttpErrorResponse) => {
        if (errorResponse.status > 0) {
          this.snackBar.open(errorResponse.error.responseMessage, 'dismiss', {
            duration: 1500
          });
        } else {
          // network failure
          this.snackBar.open('Network slow', 'dismiss', {
            duration: 1500
          });
        }
      }
    );
  }
  datePickerToggler(datePicker: MatDatepicker<Date>) {
    datePicker[datePicker.opened ? 'close' : 'open']();
  }
  /** onChange date function start here */
  onChangeDates() {
    if (!this.fromDate || !this.toDate) {
      return false;
    }
    this.minDate = this.fromDate;
    this.getCustomersList(this.searchType, this.customerData);
    this.warehouseService.setSaleReturnsFromDate(this.fromDate);
    this.warehouseService.setSaleReturnsToDate(this.toDate);
  }
  showPartDetails(icon, listDetails, index, obj) {
    this.index = index;
    this.partDetailsList[index].showBatchDetails = ! this.partDetailsList[index].showBatchDetails;
  }
/** Show batch details functions start here */
showBatchDetails (index) {
  this.indexNum = index;
  this.partDetailsList[index].showBatchDetails = ! this.partDetailsList[index].showBatchDetails;
  this.showBatches = true;
}

/** Show selected retun batch details function start here */
showreturnBatchDetails (index) {
  this.indexNum = index;
  this.returnpartsList[index].showBatchDetails = ! this.returnpartsList[index].showBatchDetails;
  this.showBatches = true;
}
/** Show selected retun batch details function end  here */

/** close part list function start here */
closePartList() {
  this.returnpartsList = [];
  this.saleOrderReturnItemDetailsList = [];
  this.selectedArray = {};
  this.availableqty = 0;

}
/** Based on part and bach level amount calculation function start here */
calculationAmount(elmen) {
   this.selectedArray = {...elmen};
    this.selectedArray.batchWiseItemsList =
    this.selectedArray.batchWiseItemsList.filter(val => {
      if (val.isSelectBatch === true && (val.returnQty) / 1 !== 0) {
      // val['returnAmt'] = (val.unitAmt + val.gstTaxAmt / elmen.totalQty) * (val.returnQty) / 1;
      val['returnAmt'] = ((val.unitAmt) * (val.returnQty) / 1);
      val.returnQty = (val.returnQty) / 1;
      this.returnAmt += val.returnAmt;
      this.totalReturnedQty += (val.returnQty) / 1;
      // this.gstTaxAmt = (val.gstTaxAmt / elmen.totalQty) * (val.returnQty) / 1;
      val.gstTaxAmt = (val.gstTaxAmt / elmen.totalQty) * (val.returnQty) / 1;
      this.gstTaxAmt += (val.gstTaxAmt);
      this.totalReturnServiceAmt = (val.unitAmt * (val.returnQty) / 1);
      return val.isSelectBatch;
      } else {
        this.isInvalid = true;
        this.snackBar.open('Please Enter Return Quantity', 'undo', {
          duration: 3000
        });
        }
    });
    this.selectedArray.returnAmt = this.returnAmt;
    this.selectedArray.totalReturnedQty = this.totalReturnedQty;
    this.selectedArray.gstTaxAmt = this.gstTaxAmt;
    this.selectedArray.totalReturnServiceAmt = this.totalReturnServiceAmt;
    this.saleOrderReturnItemDetailsList.push(this.selectedArray);
    this.orgCustomerId = elmen.orgCustomerId;
    this.saleOrderId = elmen.saleOrderId;

}

/** dummayfunction  start here */
dummayfunction(list) {
  this.selectedPartDetails = {...list};
  this.selectedPartDetails.batchWiseItemsList =
  this.selectedPartDetails.batchWiseItemsList.filter(val => {
    if (val.isSelectBatch === true) {
      return val.isSelectBatch;
    }
  });
this.returnpartsList.push(this.selectedPartDetails);
}


checkIsSelected() {
let returnValue = false;
if (this.partDetailsList && this.partDetailsList.length > 0) {
this.partDetailsList.forEach( val => {
  if (!returnValue) {
  if (val.isSelectPart) {
    returnValue = true;
  } else {
    val.batchWiseItemsList.forEach(item => {
      if (item.isSelectBatch) {
        returnValue = true;
      }
    });
  }
}
});
return returnValue;
}
}
/**selected return parts function start here */
returnSelectedParts (selectedList) {
  if (!this.checkIsSelected()) {
    this.snackBar.open('Please Select Sale Order', 'undo', {
      duration: 3000
    });
    return false;
  }
this.partDetailsList.forEach( val => {
  if (val.isSelectPart === true || val.batchesDisplay === true) {
    this.dummayfunction(val);
  }
});
}

/** verifyFnForReturnQty function start here */
verifyFnForReturnQty(obj, i) {
this.availableqty = (obj.qty - (obj.previousRetrunedQty) / 1);
if (Number(obj.returnQty) > this.availableqty || obj.returnQty === 0) {
obj.isInvalidQuanity = true;
this.invalidQty = true;
} else {
obj.isInvalidQuanity = false;
}
}

/** final retuns submit function start here */
finalReturnParts() {
  this.returnpartsList.forEach(val => {
    if (val.isSelectPart || val.batchesDisplay) {
    this.calculationAmount(val);
    }
  });
if (this.isInvalid !== true && this.invalidQty !== true) {
  /** caputing the return parts reasion dialog function strat here */
  const dialogRef = this.dialog.open(SaleReturnComformModelComponent, {
    width: '25%',
    height: '50%',
  });
  dialogRef.afterClosed().subscribe(result => {
 const payload = {
 createdUsrId : this.sessionData.regId,
 customerId : this.customerId,
 isCreditNote : result.isCreditNote, // based on pop up selection
 orgCustomerId : this.orgCustomerId,
 orgGrpId : this.sessionData.orgGroupId,
 orgId : this.sessionData.orgId,
 saleOrderReturnItemDetailsList: this.saleOrderReturnItemDetailsList,
 returnDt : this.datePipe.transform(this.fromDate, 'yyyy-MM-dd'),
 returnRemarks : result.returnRemarks ,
 saleOrderId : this.saleOrderId,
 invoiceNo  :  this.customerOrder.invoiceNo,
 saleOrderNo : this.customerOrder.saleOrderNo
  };
this.warehouseApiService.insertSaleReturnFn(payload).subscribe(
  response => {
    if (response && response.responseCode === 'E200') {
      if (result.isCreditNote) {
        this.creditNoteOrderPrint(this.saleOrderId);
      } else {
        this.returnsOrderPrint(this.saleOrderId);
      }
      this.returnpartsList = [];
      this.fetchPartDetailsForReturn (this.customerOrder, null);
      this.customerOrder = {};
      this.saleOrderReturnItemDetailsList = [];
      this.selectedArray = [];
      this.invoiveNo = '';
    } else if (response && response.responseCode === 'E400') {
      this.snackBar.open(response.responseMessage, 'dismiss', {
        duration: 1500
      });
    }
    this.changeDetection.detectChanges();
  },
  (errorResponse: HttpErrorResponse) => {
    if (errorResponse.status > 0) {
      this.snackBar.open(errorResponse.error.responseMessage, 'dismiss', {
        duration: 1500
      });
    } else {
      this.snackBar.open('Network slow', 'dismiss', {
        duration: 1500
      });
    }
  }
);
});
 } else {
  this.isInvalid = false;
   if (this.invalidQty === true) {
    this.snackBar.open('please Enter Valid Quantity', 'undo', {
      duration: 3000
    });
   }
}
  }

  /** accordian toogle function start here */
  accordionToggle(index: number) {
    this.saleList[index].isShow = ! this.saleList[index].isShow;
  }

/** select all parts based on order selection function start here */
slectAllPartsFn(selectAllPArts) {
if (selectAllPArts === true) {
if (this.partDetailsList && this.partDetailsList.length) {
this.partDetailsList.forEach(val => {
val.isSelectPart = true;
if (val.isSelectPart === true) {
  val.batchWiseItemsList.forEach( batchobj => {
    batchobj.isSelectBatch = true;
    return batchobj;
  });
} else if (val.isSelectPart !== true) {
  val.batchWiseItemsList.forEach( batchobj => {
    batchobj.isSelectBatch = false;
    return batchobj;
  });
}
return val;
});
}
} else if (selectAllPArts !== true) {
  if (this.partDetailsList && this.partDetailsList.length) {
  this.partDetailsList.forEach(val => {
  val.isSelectPart = false;
  val.isSelectPart = false;
  if (val.isSelectPart !== true) {
    val.batchWiseItemsList.forEach( batchobj => {
      batchobj.isSelectBatch = false;
      return batchobj;
    });
  } else if (val.isSelectPart === true) {
    val.batchWiseItemsList.forEach( batchobj => {
      batchobj.isSelectBatch = true;
      return batchobj;
    });
  }
  return val;
  });
}
}
}
  /** select all batches based on part selection  function start her */
  selectAllBatches( part, isBathSelect, index) {
    if (part.isSelectPart === true) {
      part.batchWiseItemsList.forEach(value => {
       return  value.isSelectBatch = true;
      });
    } else if (part.isSelectPart !== true) {
      part.batchWiseItemsList.forEach(value => {
       return  value.isSelectBatch = false;
      });
    }
 }

/**singile batch selection function start here */
selectSingileBatche (batch, isBatchSelect, index) {
if (batch.isSelectBatch === true) {
  batch.isSelectBatch = true;
 this.partDetailsList[index].batchesDisplay = true;
 } else if (batch.isSelectBatch === false) {
  this.partDetailsList[index].batchesDisplay = false;
  this.partDetailsList[index].isSelectPart = false;
  this.selectAllParts = false;
}
}

returnsOrderPrint(saleOrderId) {
  const queryParams = {
    orgId : this.sessionData.orgId,
    orgGrpId : this.sessionData.orgGroupId,
    createdUsrId: this.sessionData.regId,
    saleOrderId: saleOrderId,
    orgTimeZone: this.sessionData.orgTimeZone,
    loggedInUserId: this.sessionData.regId
  };
  this.reportsApiService.fetchWarehouseReturnsOrderPrint(queryParams).subscribe(response => {
    if (response instanceof ArrayBuffer) {
      const returnsOrderReport = new Blob([response], {
        type : 'application/pdf'
      });
      const reportURL = URL.createObjectURL(returnsOrderReport);
      const win = window.open('', '_blank');
      win.location.href = reportURL;
      setTimeout(() => {
        win.print();
      }, 1000);
    } else if (response.responseCode) {
        this.toaster.error('Failure!', 'Something went wrong');
    }
  },
  () => {
    this.toaster.error('Failure!', 'Something went wrong');
  }
  );
}

creditNoteOrderPrint(saleOrderId) {
  const queryParams = {
    orgId : this.sessionData.orgId,
    orgGrpId : this.sessionData.orgGroupId,
    createdUsrId: this.sessionData.regId,
    saleOrderId: saleOrderId,
    orgTimeZone: this.sessionData.orgTimeZone,
    loggedInUserId: this.sessionData.regId
  };
  this.reportsApiService.fetchWarehouseCreditNoteOrderPrint(queryParams).subscribe(response => {
    if (response instanceof ArrayBuffer) {
      const creditNoteOrderReport = new Blob([response], {
        type : 'application/pdf'
      });
      const reportURL = URL.createObjectURL(creditNoteOrderReport);
      const win = window.open('', '_blank');
      win.location.href = reportURL;
      setTimeout(() => {
        win.print();
      }, 1000);
    } else if (response.responseCode) {
        this.toaster.error('Failure!', 'Something went wrong');
    }
  },
  () => {
    this.toaster.error('Failure!', 'Something went wrong');
  }
  );
}
}
