import { Component, OnInit, Input, OnDestroy, AfterViewInit, ChangeDetectorRef } from '@angular/core';
import { MatSidenav } from '@angular/material';
import { Subscription } from 'rxjs';
import { WarehouseService } from '@app/modules/warehouse/warehouse.service';
import { AutoUnsubscribe } from 'ngx-auto-unsubscribe';

@Component({
  selector: 'app-products-view',
  templateUrl: './products-view.component.html',
  styleUrls: ['./products-view.component.scss']
})
export class ProductsViewComponent implements OnInit, OnDestroy, AfterViewInit {

  @Input()
  sideNavRef: MatSidenav;
  private partListDataSubscription: Subscription;
  public partList: any;
  constructor(
    private wareHouseDataService: WarehouseService,
    private cdr: ChangeDetectorRef
  ) { }
  ngOnDestroy() {

  }
  ngOnInit() {
  }
  ngAfterViewInit() {
    this.partListDataSubscription = this.wareHouseDataService.partListData.subscribe(res => {
      if (res) {
        this.partList = res;
        // this.cdr.detectChanges();
      }
    });
  }
  closeSideNav() {
    this.sideNavRef.close();
  }

}
