import {
  Component,
  OnInit,
  OnDestroy,
  AfterViewInit,
  ChangeDetectorRef,
  ViewChild,
  ElementRef
} from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { Subscription } from 'rxjs';
import { AutoUnsubscribe } from 'ngx-auto-unsubscribe';
import { UserSession } from '@core/models/user-session';
import { UserService } from '@core/services/user.service';
import { WarehouseApiService } from '@core/services/warehouse-api.service';
import { WarehouseService } from '@app/modules/warehouse/warehouse.service';
import {
  Router
} from '../../../../../../node_modules/@angular/router';
import {
  MatSnackBar,
  MatDialog,
  MatDialogRef
} from '../../../../../../node_modules/@angular/material';
import { HttpErrorResponse } from '@angular/common/http';
import { PartProductLocationsComponent } from '../part-product-locations/part-product-locations.component';
import { ProfileImageSelectorComponent } from '@core/components/profile-image/profile-image.component';
import { ProfileImageSelectorResult } from '@core/models/profile-image-selector';

@AutoUnsubscribe()
@Component({
  selector: 'app-part-list',
  templateUrl: './part-list.component.html',
  styleUrls: ['./part-list.component.scss']
})
export class PartListComponent implements OnInit, OnDestroy, AfterViewInit {
  dimentionUom = [{ uom: 'MM' }, { uom: 'CM' }, { uom: 'Mtr' }];
  uom: 'MM';
  productUom = [
    { uom: 'ML' },
    { uom: 'KG' },
    { uom: 'Tons' },
    { uom: 'LT' },
    { uom: 'Each' },
    { uom: 'GAL' },
    { uom: 'MG' },
    { uom: 'GM' },
    { weigh: 'Pound' }
  ];
  prodUom: 'ML';
  weightUom = [{ weigh: 'Pound' }, { weigh: 'KG' }, { weigh: 'GM' }];
  weight: 'Pound';
  taxCode: any;
  partsListForm: FormGroup;
  public apiSubscription: Subscription;
  private partListDataSubscription: Subscription;
  localYes = false;
  private sessionData: UserSession;
  public partList: any;
  public isEditable: Boolean;
  sale = false;
  vendorsList: any;
  // partBehaviableType = false;
  discountApplicable = false;
  taxCategory: any;
  isOpenDialog = false;
  storagSheds: any;
  storagRows: any;
  shedNm: string;
  rowNo: string;
  columnNo: string;
  tierLocationNm: string;
  @ViewChild('length')
  lengthRef: ElementRef<HTMLInputElement>;
  @ViewChild('width')
  widthRef: ElementRef<HTMLInputElement>;
  @ViewChild('height')
  heightRef: ElementRef<HTMLInputElement>;
  // public bucket = new S3(this.wareHouseDataService.getBucketsConfig());

  acceptedMimeTypes = ['image/gif', 'image/jpeg', 'image/png'];
  public imageUploadSubscription: Subscription;
  @ViewChild('fileInput') fileInput: ElementRef;
  public url;
  public imageFile;
  vendorTableList = [];
  showPopUp = false;
  width = false;
  length = false;
  height = false;
  shedId: any;
  rowId: any;
  storagColumns: any;
  columnId: any;
  storagShelfs: any;
  tierId: any;
  vendorInfoId: any;
  BehaviableType: string;
  partLifeCycle = false;
  currencyType: {};
  vendor: any;
  constructor(
    private formbuilder: FormBuilder,
    userService: UserService,
    private wareHouseApiService: WarehouseApiService,
    private wareHouseDataService: WarehouseService,
    private router: Router,
    private snackBar: MatSnackBar,
    private dialog: MatDialog,
    private changeDetection: ChangeDetectorRef
  ) {
    this.sessionData = userService.getUser();
  }

  openDialog(): void {
    const dialogRef = this.dialog.open(PartProductLocationsComponent, {
      width: '60%',
      height: '50%',
      data: {
        shedNm: this.shedNm,
        rowNo: this.rowNo,
        columnNo: this.columnNo,
        tierLocationNm: this.tierLocationNm,
        shedId: this.shedId,
        rowId: this.rowId,
        storagColumns: this.storagColumns,
        columnId: this.columnId,
        storagShelfs: this.storagShelfs,
        tierId: this.tierId
      }
    });
    // dialogRef.afterClosed().subscribe(result => {
    // });
    this.wareHouseDataService.locationsData.subscribe(res => {
      if (res.shedNm) {
        this.partsListForm.patchValue({ shedId: res.shedId });
        this.partsListForm.patchValue({ rowId: res.rowId });
        this.partsListForm.patchValue({ columnId: res.columnId });
        this.partsListForm.patchValue({ tierId: res.tierId });
        // this.shedId = res.shedId;
        this.shedNm = res.shedNm;
        // this.rowId = res.rowId;
        this.rowNo = res.rowNo;
        // this.columnId = res.columnId;
        this.columnNo = res.columnNo;
        // this.tierId  = res.tierId;
        this.tierLocationNm = res.tierNo;
        // this.tier
        const loc =
          res.shedNm + '/' + res.rowNo + '/' + res.columnNo + '/' + res.tierNo;
        this.partsListForm.get('inventoryLocation').patchValue(loc);
      }
    });
  }
  ngOnInit() {
    this.fetchCurrencyType();
    this.initForm();
    this.fetchVendors();
    this.fetchTaxCategory();
  }

  ngAfterViewInit() {
    this.partListDataSubscription = this.wareHouseDataService.partListData.subscribe(
      res => {
        if (res) {
          this.isEditable = true;
          this.partList = res;
          this.vendorTableList = res.vendorAndPartMappingList;
          this.vendorTableList.forEach(vendors => {
            if (vendors.vendorInfoId === res.vendorInfoId) {
              this.vendorInfoId = vendors.vendorInfoId;
              vendors['isChecked'] = true;
              this.partsListForm
                .get('defaultVendor')
                .patchValue(vendors.vendorNm);
            }
          });
          this.partsListForm.patchValue(this.partList);
          this.patchingData();
          const dimentionUom = this.partsListForm.get('dimentionUom').value;
          if (dimentionUom) {
            this.lengthRef.nativeElement.readOnly = false;
            this.heightRef.nativeElement.readOnly = false;
            this.widthRef.nativeElement.readOnly = false;
            this.lengthRef.nativeElement.style.cursor = 'initial';
            this.heightRef.nativeElement.style.cursor = 'initial';
            this.widthRef.nativeElement.style.cursor = 'initial';
            this.showPopUp = true;
          }
          this.changeDetection.detectChanges();
        }
      }
    );
  }
  fetchCurrencyType() {
    const payload = {
      orgId: this.sessionData.orgId,
      orgGrpId: this.sessionData.orgGroupId,
      createdUsrId: this.sessionData.regId
    };
    this.wareHouseApiService.fetchCurrencyType(payload).subscribe(
      response => {
        if (response && response.responseCode === 'E200') {
          this.currencyType = response.data;
        } else if (response && response.responseCode === 'E400') {
          this.snackBar.open(response.responseMessage, 'dismiss', {
            duration: 500
          });
        }
        this.changeDetection.detectChanges();
      },
      (errorResponse: HttpErrorResponse) => {
        if (errorResponse.status > 0) {
          this.snackBar.open(errorResponse.error.responseMessage, 'dismiss', {
            duration: 500
          });
        } else {
          // network failure
          this.snackBar.open('Network slow', 'dismiss', {
            duration: 500
          });
        }
      }
    );
  }
  ngOnDestroy() {}

  initForm() {
    this.partsListForm = this.formbuilder.group({
      barcode: '',
      unitAmt: '',
      inventoryLocation: '',
      shedId: '',
      columnId: '',
      tierId: '',
      rowId: '',
      defaultVendor: this.formbuilder.control('', Validators.required),
      partColor: '',
      partHeight: '',
      partLength: '',
      partWidth: '',
      partWeight: '',
      partsPerPacket: '',
      typeOfSale: '',
      uom: this.formbuilder.control(null),
      partNm: this.formbuilder.control('', Validators.required),
      partNo: this.formbuilder.control('', [
        Validators.required,
        Validators.pattern('^[0-9]$')
      ]),
      partDescription: '',
      vendor: '',
      taxCatCode: this.formbuilder.control('', Validators.required),
      dimentionUom: '',
      mrp: '',
      storageLocationType: '',
      weightUom: '',
      isFlammable: '',
      isExpiry: ''
    });
    const width = this.partsListForm.get('partWidth');
    const length = this.partsListForm.get('partLength');
    const height = this.partsListForm.get('partHeight');
    width.valueChanges.subscribe(wi => {
      if (wi) {
        this.width = true;
      }
    });
    length.valueChanges.subscribe(le => {
      if (le) {
        this.length = true;
      }
    });
    height.valueChanges.subscribe(he => {
      if (he) {
        this.height = true;
      }
    });
    this.lengthRef.nativeElement.readOnly = true;
    this.heightRef.nativeElement.readOnly = true;
    this.widthRef.nativeElement.readOnly = true;
    this.lengthRef.nativeElement.style.cursor = 'not-allowed';
    this.heightRef.nativeElement.style.cursor = 'not-allowed';
    this.widthRef.nativeElement.style.cursor = 'not-allowed';
    this.partsListForm.get('dimentionUom').valueChanges.subscribe(res => {
      if (res && (!width.value || !length.value || !height.value)) {
        this.lengthRef.nativeElement.readOnly = false;
        this.heightRef.nativeElement.readOnly = false;
        this.widthRef.nativeElement.readOnly = false;
        this.lengthRef.nativeElement.style.cursor = 'initial';
        this.heightRef.nativeElement.style.cursor = 'initial';
        this.widthRef.nativeElement.style.cursor = 'initial';
        this.showPopUp = true;
      }
    });
    if (this.width || this.length || this.height) {
      this.showPopUp = false;
    }
  }

  add() {
    const data = this.partsListForm.get('vendor').value;
    this.vendorsList.filter(val => {
      if (val.vendorInfoId === Number(data)) {
        const result = this.vendorTableList.find(
          item => item.vendorInfoId === val.vendorInfoId
        );
        if (!result) {
          // this.vendorTableList.push(val);
          val['isChecked'] = true;
          this.vendorTableList.push(val);
          this.vendorTableList.forEach(event => {
            if (event.isChecked) {
              this.vendorInfoId = event.vendorInfoId;
            }
          });
          this.vendor = ' ';
        }
      }
    });
  }
  compareWeigtUom(o1, o2) {
    return o1 && o2 && o1 === o2;
  }
  deleteVendor(index, data) {
    if (data.vendorPartId) {
      this.deleteVendorPartMapping(data, index);
    } else {
      this.vendorTableList.splice(index, 1);
    }
  }
  deleteVendorPartMapping(data, index) {
    const payload = {
      orgId: this.sessionData.orgId,
      orgGrpId: this.sessionData.orgGroupId,
      createdUsrId: this.sessionData.regId,
      partId: data.partId,
      vendorPartId: data.vendorPartId
        };
    this.wareHouseApiService
      .deleteVendorPartMapping(payload)
      .subscribe(response => {
        if (response && response.responseCode === 'E200') {
          // this.taxCategory = response.data;
      this.vendorTableList.splice(index, 1);

        } else if (response && response.responseCode === 'E400') {
          this.snackBar.open(response.responseMessage, 'close', {
            duration: 1500
          });
        }
      });
  }
  fetchTaxCategory() {
    const payload = {
      orgId: this.sessionData.orgId,
      orgGrpId: this.sessionData.orgGroupId,
      createdUsrId: this.sessionData.regId,
      isNotDropDown: false
    };
    this.wareHouseApiService.fetchTaxCategory(payload).subscribe(
      response => {
        if (response && response.responseCode === 'E200') {
          this.taxCategory = response.data;
        } else if (response && response.responseCode === 'E400') {
          this.snackBar.open(response.responseMessage, 'close', {
            duration: 1500
          });
        }
        this.changeDetection.detectChanges();
      },
      (errorResponse: HttpErrorResponse) => {
        if (errorResponse.status > 0) {
          this.snackBar.open(errorResponse.error.responseMessage, 'dismiss', {
            duration: 3000
          });
        } else {
          // network failure
          this.snackBar.open('Network slow', 'dismiss', {
            duration: 3000
          });
        }
      }
    );
  }

  patchingData() {
    const loc =
      this.partList.shedNm +
      '/' +
      this.partList.rowNo +
      '/' +
      this.partList.columnNo +
      '/' +
      this.partList.tierNo;
    if (this.partList.shedNm) {
      this.partsListForm.get('inventoryLocation').patchValue(loc);
    }
    this.partsListForm.patchValue({ isExpiry: this.partList.isExpiry});
    this.url = this.partList.imagePath;
    this.vendorTableList = this.partList.vendorAndPartMappingList;
    this.uom = this.partsListForm.get('dimentionUom').value;
    this.prodUom = this.partsListForm.get('uom').value;
    this.taxCode = this.partsListForm.get('taxCatCode').value;
  }

  clearFormData() {
    this.partsListForm.reset();
    this.isEditable = false;
  }


  submitAddProductForm() {
    const partNm = this.partsListForm.get('partNm').value;
    const partNo = this.partsListForm.get('partNo').value;
    const uom = this.partsListForm.get('uom').value;
    const taxCatCode = this.partsListForm.get('taxCatCode').value;
    const unitAmt = this.partsListForm.get('unitAmt').value;
    const vendor = this.partsListForm.get('vendor').value;
    if (partNm && partNo && taxCatCode) {
      // let behavialType;
      let saleType;
      // if (this.partBehaviableType) {
      //   behavialType = 'Flammable';
      // } else {
      //   behavialType = 'InFlammable';
      // }
      if (this.sale) {
        saleType = 'Individual';
      } else {
        saleType = 'Package';
      }
      const vendorlist = [];
      this.vendorTableList.forEach(el => {
        vendorlist.push({
          vendorInfoId: el.vendorInfoId,
          vendorPartId: el.vendorPartId
        });
        // const {createdUsrId, isChecked, isOrganisation, isactive, partId, vendorInfo, vendorNm, versionId , ...remain} = el;
        // vendorlist.push(remain);
      });
      const payload = {
        orgId: this.sessionData.orgId,
        orgGrpId: this.sessionData.orgGroupId,
        createdUsrId: this.sessionData.regId,
        ...this.partsListForm.value,
        // expiryApplicable: this.localYes,
        typeOfSale: saleType,
        discountApplicable: this.discountApplicable,
        // partBehaviableType: this.BehaviableType,
        vendorAndPartMappingList: vendorlist,
        vendorInfoId: this.vendorInfoId,
        imagePath: this.url,
        partProductInd: 'PA'
        // shedId: this.shedId,
        // rowId: this.rowId,
        // columnId: this.columnId,
        // tierId: this.tierId
      };
      const addPartsList = Array.of(payload);
      this.wareHouseApiService.insertOrUpdateParts(addPartsList).subscribe(
        response => {
          if (response && response.responseCode === 'E200') {
            this.clearFormData();
            this.vendorTableList = [];
            this.router.navigate([
              '/warehouse/settings/warehouse-productsList'
            ]);
          } else if (response && response.responseCode === 'E400') {
            this.snackBar.open(response.responseMessage, 'close', {
              duration: 3000
            });
          }
        },
        (errorResponse: HttpErrorResponse) => {
          if (errorResponse.status > 0) {
            this.snackBar.open(errorResponse.error.responseMessage, 'dismiss', {
              duration: 3000
            });
          } else {
            // network failure
            this.snackBar.open('Network slow', 'dismiss', {
              duration: 3000
            });
          }
        }
      );
    }
  }
  preferredVendor(event, item) {
    if (event.target.checked) {
      this.vendorInfoId = item.vendorInfoId;
    }
  }

  // storageLocation() {
  //   const loc = this.shedNm + '/' + this.rowNo + '/' + this.columnNo + '/' + this.tierLocationNm;
  // this.partsListForm.get('inventoryLocation').patchValue(loc);
  //  this.isOpenDialog = false;
  // }

  saveAndAddNextButton() {
    const partNm = this.partsListForm.get('partNm').value;
    const partNo = this.partsListForm.get('partNo').value;
    const uom = this.partsListForm.get('uom').value;
    const taxCatCode = this.partsListForm.get('taxCatCode').value;
    const unitAmt = this.partsListForm.get('unitAmt').value;
    const vendor = this.partsListForm.get('vendor').value;
    if (partNm && partNo && taxCatCode) {
      // let behavialType;
      let saleType;
      // if (this.partBehaviableType) {
      //   behavialType = 'Flammable';
      // } else {
      //   behavialType = 'InFlammable';
      // }
      if (this.sale) {
        saleType = 'Individual';
      } else {
        saleType = 'Package';
      }
      const vendorlist = [];
      this.vendorTableList.forEach(el => {
        vendorlist.push({
          vendorInfoId: el.vendorInfoId,
          vendorPartId: el.vendorPartId
        });
        // const {createdUsrId, isChecked, isOrganisation, isactive, partId, vendorInfo, vendorNm, versionId , ...remain} = el;
        // vendorlist.push(remain);
      });
      const payload = {
        orgId: this.sessionData.orgId,
        orgGrpId: this.sessionData.orgGroupId,
        createdUsrId: this.sessionData.regId,
        ...this.partsListForm.value,
        // expiryApplicable: this.localYes,
        typeOfSale: saleType,
        discountApplicable: this.discountApplicable,
        // partBehaviableType: behavialType,
        vendorAndPartMappingList: vendorlist,
        vendorInfoId: this.vendorInfoId,
        partProductInd: 'PA',
        imagePath: this.url
        // shedId: this.shedId,
        // rowId: this.rowId,
        // columnId: this.columnId,
        // tierId: this.tierId
      };
      const addPartsList = Array.of(payload);
      this.wareHouseApiService.insertOrUpdateParts(addPartsList).subscribe(
        response => {
          if (response && response.responseCode === 'E200') {
            // this.clearFormData();
            this.vendorTableList = [];
            this.partsListForm.reset();
            this.url = '';
            // this.router.navigate(['/warehouse/warehouse-partsList']);
          } else if (response && response.responseCode === 'E400') {
            this.snackBar.open(response.responseMessage, 'close', {
              duration: 2
            });
          }
        },
        (errorResponse: HttpErrorResponse) => {
          if (errorResponse.status > 0) {
            this.snackBar.open(errorResponse.error.responseMessage, 'dismiss', {
              duration: 3000
            });
          } else {
            // network failure
            this.snackBar.open('Network slow', 'dismiss', {
              duration: 3000
            });
          }
        }
      );
    }
  }

  submitUpdateProductForm() {
    const partNm = this.partsListForm.get('partNm').value;
    const partNo = this.partsListForm.get('partNo').value;
    const uom = this.partsListForm.get('uom').value;
    const taxCatCode = this.partsListForm.get('taxCatCode').value;
    const unitAmt = this.partsListForm.get('unitAmt').value;
    const vendor = this.partsListForm.get('vendor').value;
       if (partNm && partNo && taxCatCode ) {
      const vendorlist = [];
      this.vendorTableList.forEach(el => {
        vendorlist.push({
          vendorInfoId: el.vendorInfoId,
          vendorPartId: el.vendorPartId
        });
      });
      const payload = {
        orgId: this.sessionData.orgId,
        orgGrpId: this.sessionData.orgGroupId,
        createdUsrId: this.sessionData.regId,
        partId: this.partList.partId,
        ...this.partsListForm.value,
        partProductInd: 'PA',
        // shedId: this.shedId,
        // rowId: this.rowId,
        // columnId: this.columnId,
        // tierId: this.tierId,
        vendorAndPartMappingList: vendorlist,
        vendorInfoId: this.vendorInfoId,
        imagePath: this.url
      };
      this.wareHouseApiService.updatePartDetails(payload).subscribe(
        response => {
          if (response && response.responseCode === 'E200') {
            this.clearFormData();
            this.wareHouseDataService.partListData.next(null);
            this.router.navigate([
              '/warehouse/settings/warehouse-productsList'
            ]);
          } else if (response && response.responseCode === 'E400') {
            this.snackBar.open(response.responseMessage, 'close', {
              duration: 2
            });
          }
        },
        (errorResponse: HttpErrorResponse) => {
          if (errorResponse.status > 0) {
            this.snackBar.open(errorResponse.error.responseMessage, 'dismiss', {
              duration: 3000
            });
          } else {
            // network failure
            this.snackBar.open('Network slow', 'dismiss', {
              duration: 3000
            });
          }
        }
      );
    }
  }

  fetchVendors() {
    const payload = {
      orgId: this.sessionData.orgId,
      orgGrpId: this.sessionData.orgGroupId,
      createdUsrId: this.sessionData.regId,
      dropDown: false
    };
    this.wareHouseApiService.fetchVendors(payload).subscribe(
      (response): any => {
        if (response && response.responseCode === 'E200') {
          this.vendorsList = response.data;
          this.changeDetection.detectChanges();
        } else if (response && response.responseCode === 'E400') {
          this.snackBar.open(response.responseMessage, 'close', {
            duration: 2
          });
        }
        this.changeDetection.detectChanges();
      },
      (errorResponse: HttpErrorResponse) => {
        if (errorResponse.status > 0) {
          this.snackBar.open(errorResponse.error.responseMessage, 'dismiss', {
            duration: 3000
          });
        } else {
          // network failure
          this.snackBar.open('Network slow', 'dismiss', {
            duration: 3000
          });
        }
      }
    );
    this.clearFormData();
  }

  onFileChanged($event) {
    const dialogRef: MatDialogRef<
      ProfileImageSelectorComponent,
      ProfileImageSelectorResult
    > = this.dialog.open(ProfileImageSelectorComponent, {
      height: '80vh',
      width: '60vw',
      panelClass: 'user-image-selector-popup'
    });
    dialogRef.afterClosed().subscribe(result => {
      if (result) {
        this.imageFile = result.profileImage;
        // will contain cropper image in blob
        const reader = new FileReader();
        reader.readAsDataURL(this.imageFile);
        reader.onload = () => {
          this.url = reader.result;
        };
        // return result.profileImage;
      }
    });
  }
  removeImage() {
    this.url = null;
  }
  validateFile(file) {
    return this.acceptedMimeTypes.includes(file.type) && file.size < 3000000;
  }

  goToExcelUpload() {
    this.router.navigate(['/warehouse/excel-upload']);
  }

  cancelButton() {
    this.clearFormData();
    this.wareHouseDataService.partListData.next(null);
    this.router.navigate(['/warehouse/settings/warehouse-productsList']);
  }
}
