import { Component, OnInit, ViewChild, ChangeDetectionStrategy, ChangeDetectorRef } from '@angular/core';
import { WarehouseApiService } from '@core/services/warehouse-api.service';
import { UserSession } from '@core/models/user-session';
import { UserService } from '@core/services/user.service';
import { Subscription } from 'rxjs';
import { ToastrService } from 'ngx-toastr';
import { MatDialog, MatSnackBar } from '@angular/material';
import { CommentsDialogComponent } from '@shared/components/comments-dialog/comments-dialog.component';
import { Router } from '../../../../../../node_modules/@angular/router';
import { WarehouseService } from '@app/modules/warehouse/warehouse.service';
import { MatSidenav } from '@angular/material';
import { FormGroup, FormControl, AbstractControl, FormBuilder, Validators } from '../../../../../../node_modules/@angular/forms';
import { UtilService } from '@core/services';
import { HttpErrorResponse } from '../../../../../../node_modules/@angular/common/http';

@Component({
  selector: 'app-products-list',
  templateUrl: './products-list.component.html',
  styleUrls: ['./products-list.component.scss'],
})
export class ProductsListComponent implements OnInit {
  patientSearchForm: FormGroup;
  partsList: any;
  public apiSubscription: Subscription;
  private sessionData: UserSession;
  inputValue: string;
  patientList: any;
  placeholderValue: string;
  patientControl = new FormControl();
  patientData: any;
  patientmobile: any;
  searchType: any;
  patientSearch: any;
  public side = false;
  patientSearchControl: AbstractControl;
  // public side = false;
  checked = this.utilService.generateUniqueId();
  @ViewChild('sidenav')
  sidenav: MatSidenav;
  partChecked: string;
  currencyType: any;
  constructor(
    userService: UserService,
    private toaster: ToastrService,
    private dialog: MatDialog,
    private wareHouseApiService: WarehouseApiService,
    private router: Router,
    private formbuilder: FormBuilder,
    private utilService: UtilService,
    private snackBar: MatSnackBar,
    private warehoueDataService: WarehouseService,
    private changeDetection: ChangeDetectorRef
  ) {
    this.sessionData = userService.getUser();
  }

  ngOnInit() {
    this.warehoueDataService.settingsData.next('products-list');
    this.patientSearchControl = new FormControl('partNm');
    this.placeholderValue = 'Search Part by Name';
    this.searchType = 'partNm';
    this.patientSearchControl.valueChanges.subscribe(value => {
      this.searchType = value;
      switch (this.searchType) {
        case 'partNm':
          this.placeholderValue = 'Search Part by Name';
          this.patientData = '';
          break;
        case 'partNo':
          this.placeholderValue = 'Search Part by Number';
          this.patientData = '';
          break;
        default:
          this.placeholderValue = 'Search Part by Name';
          this.patientData = '';
          break;
      }
    });
   this.partChecked = this.warehoueDataService.getSettingsChecked();
   this.warehoueDataService.setSettingsChecked(this.partChecked);
   this.fetchPartDetails();
   this.fetchCurrencyType();
   this.warehoueDataService.partListData.next(null);
   this.sidenav.closedStart.subscribe(res =>{
    this.warehoueDataService.partListData.next(null);
  });
  }
  initForm() {
    this.patientSearchForm = this.formbuilder.group({
      patientSearchControl: this.formbuilder.control(null, Validators.required),
      serviceTable: ['', Validators.required]
    });
  }
  fetchCurrencyType() {
    const payload = {
      orgId: this.sessionData.orgId,
      orgGrpId: this.sessionData.orgGroupId,
      createdUsrId: this.sessionData.regId,
    };
    this.wareHouseApiService.fetchCurrencyType(payload).subscribe(response => {
      if (response && response.responseCode === 'E200') {
        this.currencyType = response.data;
      } else if (response && response.responseCode === 'E400') {
        this.snackBar.open(response.responseMessage, 'dismiss', {
          duration: 500,
        });
      }
      this.changeDetection.detectChanges();
    }, (errorResponse: HttpErrorResponse) => {
      if (errorResponse.status > 0) {
        this.snackBar.open(errorResponse.error.responseMessage, 'dismiss', {
          duration: 500,
        });
      } else {
        // network failure
        this.snackBar.open('Network slow', 'dismiss', {
          duration: 500,
        });
      }
    });
  }
  fetchPartDetails() {
    const payload = {
      orgId: this.sessionData.orgId,
      orgGrpId: this.sessionData.orgGroupId,
      createdUsrId: this.sessionData.regId,
      partProductInd: 'PA'
    };
    if (this.searchType === 'partNm') {
      payload['partNm'] = this.patientData;
    } else if (this.searchType === 'partNo') {
      payload['partNo'] = this.patientData;
    }
    this.wareHouseApiService.fetchPartDetails(payload).subscribe(response => {
      if (response && response.responseCode === 'E200') {
      this.partsList = response.data;
      } else if (response && response.responseCode === 'E400') {
        this.snackBar.open(response.responseMessage, 'close', {
          duration: 3000,
        });
      }
      this.changeDetection.detectChanges();
    }
      ,
      (errorResponse: HttpErrorResponse) => {
        if (errorResponse.status > 0) {
          this.snackBar.open(errorResponse.error.responseMessage, 'dismiss', {
            duration: 3000,
          });
        } else {
          // network failure
          this.snackBar.open('Network slow', 'dismiss', {
            duration: 3000,
          });
        }
      });
  }
  deletePart(part, index) {
    const dialogRef = this.dialog.open(CommentsDialogComponent, {
      data: {
        title: 'Delete Part',
        data: 'Reason for Deleting Part'
      },
      disableClose: true
    });
    dialogRef.afterClosed().subscribe(result => {
      if (result) {
        const deletePayload = {
          orgId: this.sessionData.orgId,
          orgGrpId: this.sessionData.orgGroupId,
          createdUsrId: this.sessionData.regId,
          partId: part.partId,
          partNm: part.partNm,
          partNo: part.partNo,
          deleteReason: result,
          partProductInd: 'PA'
        };
        this.wareHouseApiService.deletePartDetails(deletePayload).subscribe(
          (response: any) => {
            if (response && response.responseCode === 'E200') {
              this.fetchPartDetails();
            } else if (response && response.responseCode === 'E400') {
              this.snackBar.open(response.responseMessage, 'close', {
                duration: 3000,
              });
            }
          }
          ,
          (errorResponse: HttpErrorResponse) => {
            if (errorResponse.status > 0) {
              this.snackBar.open(errorResponse.error.responseMessage, 'dismiss', {
                duration: 3000,
              });
            } else {
              // network failure
              this.snackBar.open('Network slow', 'dismiss', {
                duration: 3000,
              });
            }
          }
        );
      }
    });

  }

  editPart(part) {
    this.warehoueDataService.partListData.next(part);
    this.router.navigate(['/warehouse/warehouse-partsList']);
  }
  openSidenav(part) {
    this.side = true;
    this.warehoueDataService.partListData.next(part);
    this.sidenav.open();
  }
  // history() {
  //   this.router.navigate(['/warehouse/warehouse-partsList']);
  // }
}
