import { Component, OnInit, Input, OnDestroy, AfterViewInit } from '@angular/core';
import { MatSidenav } from '../../../../../../node_modules/@angular/material';
import { WarehouseService } from '@app/modules/warehouse/warehouse.service';
import { Subscription } from 'rxjs';
@Component({
  selector: 'app-vendors-view',
  templateUrl: './vendors-view.component.html',
  styleUrls: ['./vendors-view.component.scss']
})
export class VendorsViewComponent implements OnInit, OnDestroy, AfterViewInit {

  @Input()
  sideNavRef: MatSidenav;
  private vendorListDataSubscription: Subscription;
  public vendorList: any;
  public isOrganisation: true;
  constructor(
    private wareHouseDataService: WarehouseService,
  ) { }
  ngAfterViewInit() {
    this.vendorListDataSubscription = this.wareHouseDataService.vendorData.subscribe(res => {
      if (res) {
        this.vendorList = res;
        this.isOrganisation = this.vendorList.isOrganisation;
      }
    });
  }
  ngOnDestroy() {

  }
  ngOnInit() {
  }
  closeSideNav() {
    this.sideNavRef.close();
  }
}
