import { Component, OnInit, Inject } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { MatSnackBar } from '@angular/material';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { UserService } from '@core/services/user.service';
import { UserSession } from '@core/models/user-session';
import { WarehouseApiService } from '@core/services/warehouse-api.service';
import { WarehouseService } from '@app/modules/warehouse/warehouse.service';
import { Subscription } from 'rxjs';
import { ILocationDialogData } from '../../models/location-dialog';
import { ILocations } from '../../models/created-locations';
import { HttpErrorResponse } from '@angular/common/http';

@Component({
  selector: 'app-warehouse-create-location-dialogue',
  templateUrl: './warehouse-create-location-dialogue.component.html',
  styleUrls: ['./warehouse-create-location-dialogue.component.scss']
})
export class WarehouseCreateLocationDialogueComponent implements OnInit {
  public storage: any[] = [];
  createLocationForm: FormGroup;
  private sessionData: UserSession;
  public newLocationDetails: any;
  private locationDataSubscription: Subscription;
  public warehouseFormSubmitted: false;
  locationUpdateList: any;
  isEditable = false;
  responseData: {};
  createLocArr: ILocations;
  create_storage_validation_messages = {
    shedNm: [
      { type: 'required', message: '* Shed Name is required' },
      {
        type: 'minlength',
        message: '* Shed Name must be at least 3 characters long'
      },
      {
        type: 'maxlength',
        message: '* Shed Name cannot be more than 20 characters'
      }
    ],
    contactPersonPhoneNo: [
      { type: 'required', message: '* Phone Number is required' },
      {
        type: 'minlength',
        message: '* Phone Numbeer must be at least 10 characters long'
      },
      {
        type: 'maxlength',
        message: '* Phone Number cannot be more than 10 characters'
      },
      { type: 'pattern', message: 'Phone number must contain only Numbers' }
    ],
    alternateContactNo: [
      {
        type: 'minlength',
        message: '* Phone Number must be at least 10 characters long'
      },
      {
        type: 'maxlength',
        message: '* Phone Number cannot be more than 10 characters'
      },
      { type: 'pattern', message: 'Phone number must contain only Numbers' }
    ],
    shedAddress: [{ type: 'required', message: '* Address is required' }],
    shedCityNm: [
      { type: 'required', message: '* Street Name is required' },
      {
        type: 'minlength',
        message: '* City Name must be at least 3 characters long'
      },
      {
        type: 'maxlength',
        message: '*  City Name cannot be more than 20 characters'
      }
    ],
    shedCountryNm: [
      { type: 'required', message: '* Country Name is required' },
      {
        type: 'minlength',
        message: '* Country Name must be at least 3 characters long'
      },
      {
        type: 'maxlength',
        message: '* Country Name must be at least 30 characters long'
      },
      { type: 'pattern', message: 'Country Name must contain only Alphabets' }
    ],
    shedStateNm: [{ type: 'required', message: '* State Name is required' }],
    shedZipcode: [{ type: 'required', message: '* Zip Code is required' }]
  };

  createLocationFormSubmitted = false;
  constructor(
    public dialogRef: MatDialogRef<WarehouseCreateLocationDialogueComponent>,
    private formbuilder: FormBuilder,
    userService: UserService,
    private wareHouseApiService: WarehouseApiService,
    private snackBar: MatSnackBar,
    private wareHouseDataService: WarehouseService,
    @Inject(MAT_DIALOG_DATA)
    public data: ILocationDialogData
  ) {
    this.sessionData = userService.getUser();
  }
  cancelLocation() {
    this.dialogRef.close();
    this.isEditable = false;
  }
  ngOnInit() {
    // this.storageType();
    this.initForm();

    if (this.data.location) {
      this.locationUpdateList = this.data.location;
      this.getEditLocData();
    }
  }
  // storageType() {
  //   this.storage = ['Refrigerator', 'Open Space', 'Secured'];
  // }
  initForm() {
    this.createLocationForm = this.formbuilder.group({
      shedId: this.formbuilder.control(''),
      shedNm: this.formbuilder.control('', [
        Validators.required,
        Validators.minLength(3),
        Validators.maxLength(20)
      ]),
      contactPersonPhoneNo: this.formbuilder.control('', [
        Validators.required,
        Validators.minLength(10),
        Validators.maxLength(10),
        Validators.pattern(/(5|6|7|8|9)\d{9}$/)
      ]),
      alternateContactNo: this.formbuilder.control('', [
        Validators.minLength(10),
        Validators.maxLength(10),
        Validators.pattern(/(5|6|7|8|9)\d{9}$/)
      ]),
      shedAddress: this.formbuilder.control('', [Validators.required]),
      shedCityNm: this.formbuilder.control('', [
        Validators.required,
        Validators.minLength(3),
        Validators.maxLength(20)
      ]),
      shedStateNm: this.formbuilder.control('', [Validators.required]),
      shedCountryNm: this.formbuilder.control('', [
        Validators.required,
        Validators.minLength(3),
        Validators.maxLength(30),
        Validators.pattern(/^[a-zA-Z ]*$/)
      ]),
      shedZipcode: this.formbuilder.control('', [Validators.required])
    });
  }

  confirmLocation() {
    this.createLocationFormSubmitted = true;
    if (this.createLocationForm.valid) {
      const createLocationpayload = {
        orgId: this.sessionData.orgId,
        orgGrpId: this.sessionData.orgGroupId,
        createdUsrId: this.sessionData.regId,
        ...this.createLocationForm.value
      };
      this.newLocationDetails = this.createLocationForm.value;
      this.wareHouseApiService
        .insertOrUpdateShedRef(createLocationpayload)
        .subscribe(response => {
          if (response && response.responseCode === 'E200') {
            this.responseData = response.data;
            this.dialogRef.close(this.newLocationDetails);
          } else if (response && response.responseCode === 'E400') {
            this.snackBar.open(response.responseMessage, 'dismiss', {
              duration: 500
            });
          } else {
            // network failure
            this.snackBar.open('Network slow', 'dismiss', {
              duration: 500
            });
          }
        });
    }
  }

  getEditLocData() {
    this.isEditable = true;
    this.createLocationForm.patchValue({
      ...this.locationUpdateList
    });
  }

  updateLocation() {
    this.createLocationFormSubmitted = true;
    if (this.createLocationForm.valid) {
      const createLocationpayload = {
        orgId: this.sessionData.orgId,
        orgGrpId: this.sessionData.orgGroupId,
        createdUsrId: this.sessionData.regId,
        ...this.createLocationForm.value
      };
      this.wareHouseApiService
        .insertOrUpdateShedRef(createLocationpayload)
        .subscribe(
          response => {
            if (response && response.responseCode === 'E200') {
              // this.fetchShedRef();
              this.dialogRef.close(this.createLocationForm.value);
            } else if (response && response.responseCode === 'E400') {
              console.log('ok');
              this.snackBar.open(response.responseMessage, 'dismiss', {
                duration: 500
              });
            }
          },
          (errorResponse: HttpErrorResponse) => {
            if (errorResponse.status > 0) {
              this.snackBar.open(
                errorResponse.error.responseMessage,
                'dismiss',
                {
                  duration: 500
                }
              );
            } else {
              // network failure
              this.snackBar.open('Network slow', 'dismiss', {
                duration: 500
              });
            }
          }
        );
    }
  }
}
