import { Component, OnInit, OnDestroy, AfterViewInit, ChangeDetectorRef } from '@angular/core';
import { Router } from '../../../../../../node_modules/@angular/router';
import { Subscription } from 'rxjs';
import { WarehouseService } from '@app/modules/warehouse/warehouse.service';
import { UserSession } from '@core/models/user-session';
import { UserService } from '@core/services';
import { WarehouseApiService } from '@core/services/warehouse-api.service';
import { HttpErrorResponse } from '../../../../../../node_modules/@angular/common/http';
import { MatSnackBar } from '../../../../../../node_modules/@angular/material';
import { ToastrService } from 'ngx-toastr';

@Component({
  selector: 'app-purchase-order-invoice-breakdown',
  templateUrl: './purchase-order-invoice-breakdown.component.html',
  styleUrls: ['./purchase-order-invoice-breakdown.component.scss']
})
export class PurchaseOrderInvoiceBreakdownComponent implements OnInit, OnDestroy, AfterViewInit {
  private partListDataSubscription: Subscription;
  public poList: any;
  public partList: any;
  inputPartNo: '';
  inputPartNm: '';
  inputQty: '';
  inputUOM: '';
  inputUnitPrice: '';
  inputTax: '';
  inputTotalAmt: '';

  private sessionData: UserSession;
  constructor(
    private snackBar: MatSnackBar,
    private router: Router,
    private cdr: ChangeDetectorRef,
    private wareHouseDataService: WarehouseService,
    private wareHouseApiService: WarehouseApiService,
    private toaster: ToastrService,
    userService: UserService
  ) {
    this.sessionData = userService.getUser();
  }
  ngOnDestroy() {

  }
  ngOnInit() {
    this.partListDataSubscription = this.wareHouseDataService.poPartsList.subscribe(res => {
      if (res) {
        this.poList = res;
      }
    });
    this.fetchPurchaseOrderItemWiseDetails();
  }
  cancelButton() {
    this.router.navigate(['/warehouse/purchase-history']);
    console.log(this.poList);
  }
  ngAfterViewInit() {


  }
  fetchPurchaseOrderItemWiseDetails() {
    const payload = {
      orgId: this.sessionData.orgId,
      createdUsrId: this.sessionData.regId,
      orgGrpId: this.sessionData.orgGroupId,
      purchaseOrderId: this.poList.purchaseOrderId
    };
    this.wareHouseApiService.fetchPurchaseOrderItemWiseDetails(payload).subscribe(response => {
      this.partList = response.data;

    }
      ,
      (errorResponse: HttpErrorResponse) => {
        if (errorResponse.status > 0) {
          this.snackBar.open(errorResponse.error.responseMessage, 'dismiss', {
            duration: 3000,
          });
        } else {
          // network failure
          this.snackBar.open('Network slow', 'dismiss', {
            duration: 3000,
          });
        }
      }
    );
  }

  poPrint() {
    const queryParams = {
      orgId : this.sessionData.orgId,
      orgGrpId : this.sessionData.orgGroupId,
      createdUsrId: this.sessionData.regId,
      purchaseOrderId : this.poList.purchaseOrderId
    };
    this.wareHouseApiService.purchaseOrderDetailsPrint(queryParams).subscribe(response => {
      if (response instanceof ArrayBuffer) {
        const poReport = new Blob([response], {
          type : 'application/pdf'
        });
        const reportURL = URL.createObjectURL(poReport);
        const win = window.open('', '_blank');
        win.location.href = reportURL;
        setTimeout(() => {
          win.print();
        }, 1000);
      } else if (response.responseCode) {
          this.toaster.error('Failure!', 'Something went wrong');
      }
    },
    () => {
      this.toaster.error('Failure!', 'Something went wrong');
    }
    );
  }
}
