import {
  Component,
  OnInit,
  OnDestroy,
  Inject
} from '@angular/core';
import { Router } from '@angular/router';
import { UtilService } from '@core/services';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { UserSession } from '@core/models/user-session';
import { UserService } from '@core/services/user.service';
import { WarehouseApiService } from '@core/services/warehouse-api.service';
import { MatSnackBar, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { WarehouseService } from '../../warehouse.service';
import { Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';
import { HttpErrorResponse } from '@angular/common/http';
import { DialogData } from '../create-storage-location/create-storage-location.component';

@Component({
  selector: 'app-generate-storage-space',
  templateUrl: './generate-storage-space.component.html',
  styleUrls: ['./generate-storage-space.component.scss']
})
export class GenerateStorageSpaceComponent implements OnInit, OnDestroy {
  public suffix = ['Numerical', 'Alphabet'];
  alleyData: any;
  createnewSpace: FormGroup;
  createType = 'createNew';
  private sessionData: UserSession;
  checked = this.utilService.generateUniqueId();
  generateStorageData: any;
  destroyed$ = new Subject();
  insertPayloadObj: {};
  insertColumnPayloadObj: {};
  alleyResponse;
  storageColumn: any;
  storageRack: any;
  insertRackPayloadObj: {};
  colData: any;
  rackData: any;
  createnewSpaceForm = false;
  create_new_space_validation_messages = {
    categoryNm: [
      { type: 'required', message: '* Location Name is required' },
      { type: 'maxlength', message: '* Location Name cannot be more than 16 characters'}
    ],
    noOfStorageSpaces: [
      { type: 'required', message: '* Storage space is required' },
      { type: 'minlength', message: '* Number Should be greater than Zero'},
      { type: 'maxlength', message: '* Number Can not be more than five digits'}
    ],
    typeOfGeneration: [
      { type: 'required', message: '* Type of generation is required' }
    ],
    rangeStartsFrom: [
      { type: 'required', message: '* Starting Range is required' }
    ]
  };
  public changeInput = true;

  constructor(
    private router: Router,
    private utilService: UtilService,
    userService: UserService,
    private snackBar: MatSnackBar,
    private wareHouseApiService: WarehouseApiService,
    private warehouseDataService: WarehouseService,
    private formbuilder: FormBuilder,
    public dialogRef: MatDialogRef<GenerateStorageSpaceComponent>,
    @Inject(MAT_DIALOG_DATA)
    public data: DialogData
  ) {
    this.sessionData = userService.getUser();
  }

  ngOnInit() {
    this.initForm();
    this.warehouseDataService.createStorage
      .pipe(takeUntil(this.destroyed$))
      .subscribe(res => {
        if (res) {
          this.generateStorageData = res;
          this.storageColumn = 'alley';
        }
      });
    this.warehouseDataService.createStorageColumn
      .pipe(takeUntil(this.destroyed$))
      .subscribe(res => {
        if (res) {
          this.storageColumn = res;
        }
      });
  }
  initForm() {
    this.createnewSpace = this.formbuilder.group({
      categoryNm: this.formbuilder.control('', [Validators.required, Validators.maxLength(16)]),
      noOfStorageSpaces: this.formbuilder.control('', [Validators.required, Validators.minLength(1), Validators.maxLength(5)]),
      rangeStartsFrom: this.formbuilder.control('', [Validators.required]),
      typeOfGeneration: this.formbuilder.control('Numerical', [Validators.required])
    });
    this.createnewSpace.get('typeOfGeneration').valueChanges.subscribe(res => {
      if (res && res === 'Alphabet') {
        this.changeInput = false;
      } else if (res && res === 'Numerical') {
        this.changeInput = true;
      }
    });
  }

  goBack() {
    this.router.navigate(['/warehouse/create-storage-location']);
  }

  // insertion of rows
  insertRow() {
    const insertPayload = [];
    this.alleyData.forEach(element => {
      this.insertPayloadObj = {
        createdUsrId: this.sessionData.regId,
        orgGrpId: this.sessionData.orgGroupId,
        orgId: this.sessionData.orgId,
        shedId: this.generateStorageData.shedId,
        rowNo: element
      };
      insertPayload.push(this.insertPayloadObj);
    });

    this.wareHouseApiService.insertOrUpdateShedRow(insertPayload).subscribe(
      response => {
        if (response && response.responseCode === 'E200') {
          this.warehouseDataService.createStorageColumn.next(null);
          this.router.navigate(['/warehouse/create-storage-location']);
        } else if (response && response.responseCode === 'E400') {
          this.snackBar.open(response.responseMessage, 'dismiss', {
            duration: 500
          });
        } else {
          // network failure
          this.snackBar.open('Network slow', 'dismiss', {
            duration: 500
          });
        }
      },
      (errorResponse: HttpErrorResponse) => {
        if (errorResponse.status > 0) {
          this.snackBar.open(errorResponse.error.responseMessage, 'dismiss', {
            duration: 1500
          });
        } else {
          // network failure
          this.snackBar.open('Network slow', 'dismiss', {
            duration: 1500
          });
        }
      }
    );
  }

  // insertion of columns
  insertColumn() {
    const insertColumnPayload = [];
    this.colData.forEach(element => {
      this.insertColumnPayloadObj = {
        createdUsrId: this.sessionData.regId,
        orgGrpId: this.sessionData.orgGroupId,
        orgId: this.sessionData.orgId,
        shedId: this.generateStorageData.shedId,
        columnNo: element,
        rowId: this.storageColumn.rowId
      };
      insertColumnPayload.push(this.insertColumnPayloadObj);
    });
    this.wareHouseApiService
      .insertOrUpdateShedColumnRef(insertColumnPayload)
      .subscribe(
        response => {
          if (response && response.responseCode === 'E200') {
            this.warehouseDataService.createStorageColumn.next(null);
            this.router.navigate(['/warehouse/create-storage-location']);
          } else if (response && response.responseCode === 'E400') {
            this.snackBar.open(response.responseMessage, 'dismiss', {
              duration: 500
            });
          } else {
            // network failure
            this.snackBar.open('Network slow', 'dismiss', {
              duration: 500
            });
          }
        },
        (errorResponse: HttpErrorResponse) => {
          if (errorResponse.status > 0) {
            this.snackBar.open(errorResponse.error.responseMessage, 'dismiss', {
              duration: 1500
            });
          } else {
            // network failure
            this.snackBar.open('Network slow', 'dismiss', {
              duration: 1500
            });
          }
        }
      );
  }

  insertRack() {
    const insertRackPayload = [];
    this.rackData.forEach(element => {
      this.insertRackPayloadObj = {
        createdUsrId: this.sessionData.regId,
        orgGrpId: this.sessionData.orgGroupId,
        orgId: this.sessionData.orgId,
        shedId: this.generateStorageData.shedId,
        columnId: this.storageColumn.columnId,
        rowId: this.storageColumn.rowId,
        tierNo: element
      };
      insertRackPayload.push(this.insertRackPayloadObj);
    });
    this.wareHouseApiService
      .insertOrUpdateShedTierxref(insertRackPayload)
      .subscribe(
        response => {
          if (response && response.responseCode === 'E200') {
            this.warehouseDataService.createStorageColumn.next(null);
            this.router.navigate(['/warehouse/create-storage-location']);
          } else if (response && response.responseCode === 'E400') {
            this.snackBar.open(response.responseMessage, 'dismiss', {
              duration: 500
            });
          } else {
            // network failure
            this.snackBar.open('Network slow', 'dismiss', {
              duration: 500
            });
          }
        },
        (errorResponse: HttpErrorResponse) => {
          if (errorResponse.status > 0) {
            this.snackBar.open(errorResponse.error.responseMessage, 'dismiss', {
              duration: 1500
            });
          } else {
            // network failure
            this.snackBar.open('Network slow', 'dismiss', {
              duration: 1500
            });
          }
        }
      );
  }

  // generating location number

  generateStorage() {
    this.createnewSpaceForm = true;
    if (this.createnewSpace.valid) {
      const genStrgpayload = {
        createdUsrId: this.sessionData.regId,
        orgGrpId: this.sessionData.orgGroupId,
        orgId: this.sessionData.orgId,
        ...this.createnewSpace.value
      };
      this.wareHouseApiService
        .locationNumberGeneration(genStrgpayload)
        .subscribe(
          response => {
            if (response && response.responseCode === 'E200') {
              if (this.storageColumn === 'alley') {
                this.alleyData = response.data;
                this.dialogRef.close(this.alleyData);
                this.insertRow();
              }
              if (this.storageColumn.clickedOn === 'row') {
                this.colData = response.data;
                this.dialogRef.close(this.colData);
                this.insertColumn();
                this.snackBar.open(response.responseMessage, 'dismiss', {
                  duration: 500
                });
              }
              if (this.storageColumn.clickedOn === 'column') {
                this.rackData = response.data;
                this.dialogRef.close(this.rackData);
                this.insertRack();
                this.snackBar.open(response.responseMessage, 'dismiss', {
                  duration: 500
                });
              }
              this.warehouseDataService.createStorage.next(null);
              this.warehouseDataService.createStorageColumn.next(null);
            } else if (response && response.responseCode === 'E400') {
              this.snackBar.open(response.responseMessage, 'dismiss', {
                duration: 500
              });
            } else {
              // network failure
              this.snackBar.open('Network slow', 'dismiss', {
                duration: 500
              });
            }
          },
          (errorResponse: HttpErrorResponse) => {
            if (errorResponse.status > 0) {
              this.snackBar.open(
                errorResponse.error.responseMessage,
                'dismiss',
                {
                  duration: 1500
                }
              );
            } else {
              // network failure
              this.snackBar.open('Network slow', 'dismiss', {
                duration: 1500
              });
            }
          }
        );
    }
  }

  cancelButton() {
    this.dialogRef.close();
  }

  ngOnDestroy() {
    this.destroyed$.next();
    this.destroyed$.complete();
  }
}
