import { Component, OnInit, AfterViewInit, Output, ChangeDetectorRef } from '@angular/core';
import { Router } from '../../../../../../node_modules/@angular/router';
import {MatDialog, MatDialogRef, MAT_DIALOG_DATA, MatSnackBar} from '@angular/material';
import { WarehouseCreateLocationDialogueComponent } from '../warehouse-create-location-dialogue/warehouse-create-location-dialogue.component';
import { WarehouseService } from '../../warehouse.service';
import { UserSession } from '@core/models/user-session';
import { UserService } from '@core/services/user.service';
import { CommentsDialogComponent } from '@shared/components/comments-dialog/comments-dialog.component';
import { WarehouseApiService } from '@core/services/warehouse-api.service';
import { FormGroup, FormBuilder } from '@angular/forms';
import { ILocations } from '../../models/created-locations';
import { HttpErrorResponse } from '../../../../../../node_modules/@angular/common/http';
import { Subscription } from 'rxjs';

// import { WarehouseService } from '@app/modules/warehouse/warehouse.service';

@Component({
  selector: 'app-warehouse-locations',
  templateUrl: './warehouse-locations.component.html',
  styleUrls: ['./warehouse-locations.component.scss']
})
export class WarehouseLocationsComponent implements OnInit {
  public createLocDetails: any;
  public createLocArr =  [];
  private sessionData: UserSession;
  locationData = [] ;

  constructor(
    private router: Router,
    public dialog: MatDialog,
    userService: UserService,
    private snackBar: MatSnackBar,
    private wareHouseApiService: WarehouseApiService,
    private wareHouseService: WarehouseService,
    private changeDetection: ChangeDetectorRef
  ) {
    this.sessionData = userService.getUser();

   }
  ngOnInit() {
    this.wareHouseService.settingsData.next('warehouse-location');
    this.fetchShedRef();
  }
  fetchShedRef() {
    const payload = {
      orgId: this.sessionData.orgId,
      orgGrpId: this.sessionData.orgGroupId,
      createdUsrId: this.sessionData.regId,
      isLocations: true
    };
    this.wareHouseApiService.fetchShedRef<ILocations>(payload).subscribe(response => {
      if (response && response.responseCode === 'E200') {
        const data = response.data;
        this.createLocArr = response.data;
        this.changeDetection.detectChanges();
      } else if (response && response.responseMessage === 'E400') {
        this.snackBar.open(response.responseMessage, 'dismiss', {
          duration: 500,
        });
        this.changeDetection.detectChanges();
      } else {
        this.snackBar.open('Network slow', 'dismiss', {
          duration: 500,
        });
        this.changeDetection.detectChanges();
      }
    });
  }

   createLocations(location?) {
    const dialogRef = this.dialog.open(WarehouseCreateLocationDialogueComponent, {
      width: '500px',
      height: 'auto',
      data: {
        location
      }
    });

    dialogRef.afterClosed().subscribe(result => {
      if (result) {
        this.createLocDetails = result;
        this.fetchShedRef();
        if (this.createLocDetails && this.createLocDetails.shedId) {
          const createdLocIndex = this.createLocArr.findIndex(val => val.shedId === this.createLocDetails.shedId);
          if (createdLocIndex > -1) {
            this.createLocArr[createdLocIndex] = { ...this.createLocArr[createdLocIndex], ...this.createLocDetails};
           }
        } else {
          this.createLocArr.push(this.createLocDetails);
        }
      }
    });
  }

  editLocation(location) {
    this.createLocations(location);
  }
  deleteLocation(location) {
    const dialogRef = this.dialog.open(CommentsDialogComponent, {
      data: {
        title: 'Delete Warehouse',
        data: 'Reason for Deleting Warehouse'
      },
      disableClose: true
    });
    dialogRef.afterClosed().subscribe(result => {
    if (result) {
    const  deleteLocationPayload = {
      createdUsrId: this.sessionData.regId,
      orgGrpId: this.sessionData.orgGroupId,
      orgId: this.sessionData.orgId,
      locationId: location.shedId
    };
    this.wareHouseApiService.deleteShedRef(deleteLocationPayload).subscribe(
      (response: any) => {
        if (response && response.responseCode === 'E200') {
          this.fetchShedRef();
          // this.snackBar.open(response.responseMessage, 'close', {
          //   duration: 3000,
          // });
        } else if (response && response.responseCode === 'E400') {
          this.snackBar.open(response.responseMessage, 'close', {
            duration: 3000,
          });
        }
      }
      ,
      (errorResponse: HttpErrorResponse) => {
        if (errorResponse.status > 0) {
          this.snackBar.open(errorResponse.error.responseMessage, 'dismiss', {
            duration: 3000,
          });
        } else {
          // network failure
          this.snackBar.open('Network slow', 'dismiss', {
            duration: 3000,
          });
        }
      }
    );
    }
  });
  }
  goToStoragePage(location) {
    this.wareHouseService.createStorage.next(location);
    this.router.navigate(['/warehouse/create-storage-location']);
  }

}

