import { Component, OnInit, ElementRef, ViewChild, Inject } from '@angular/core';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import * as S3 from 'aws-sdk/clients/s3';
import { WarehouseService } from '@app/modules/warehouse/warehouse.service';
import { UtilService } from '@core/services';
import { ToastrService } from 'ngx-toastr';
import { from, Subscription } from 'rxjs';
import { DatePipe } from '@angular/common';
@Component({
  selector: 'app-upload-doc',
  templateUrl: './upload-doc.component.html',
  styleUrls: ['./upload-doc.component.scss']
})
export class UploadDocComponent implements OnInit {
  public bucket = new S3(this.warehouseService.getBucketsConfig());
  @ViewChild('fileInput') fileInput: ElementRef;
  public url;
  public fileUpload: any;
  fileList: any;
  editUpload = false;
  changeDecRef: any;
  obj: any;
  documentNm: any;
  public fileUploadSubscription: Subscription;
  index: any;
  strsubstring: any;
  constructor(
    public dialogRef: MatDialogRef<UploadDocComponent>,
    private warehouseService: WarehouseService,
    private utilService: UtilService,
    private datePipe: DatePipe,
    private toaster: ToastrService,
    @Inject(MAT_DIALOG_DATA) public data: any
  ) {}

  ngOnInit() {
    if (this.data) {
    if (this.data.data.typeCheck === 'edit') {
      this.editUpload = true;
      // this.obj = {};
    }
  }
  }
  // method for uploding file
  uploadfile(fileData) {
    if (this.data.typeCheck === 'edit') {
      // this.editUpload = true;
      this.obj = {};
    }
      this.fileList = fileData;
      const bucketDetails = this.warehouseService.getBucketName();
      const currentDate = this.datePipe.transform(new Date(), 'yyyy-MM-dd');
      const key =
        this.utilService.generateUniqueId() + '-' + this.fileUpload.name;
      const params = {
        Bucket: bucketDetails.bucketURL,
        Key: key,
        Body: this.fileList
      };
      this.obj = {
        documentNm: this.fileUpload[0].name,
       documentUrl: bucketDetails.url + key,
       documentUploadDt: currentDate
      };
      const awsfileUpload = this.bucket.putObject(params).promise();
      this.fileUploadSubscription = from(awsfileUpload).subscribe(() => {
        const documentUrl = bucketDetails.url + key;
      });
  }
  pushToTable() {
    this.dialogRef.close(this.obj);
  }

  // file upload
  onFileChanged($event) {
    const file = this.fileInput.nativeElement.files[0];
    this.fileUpload = this.fileInput.nativeElement.files;
    this.documentNm = this.fileInput.nativeElement.files[0].name;
    if (this.fileUpload.length > 0) {
    for (let i = 0; i < this.fileUpload.length; i++) {
      const reader = new FileReader();
      reader.readAsDataURL(this.fileUpload[i]);
      reader.onload = () => {
        this.url = reader.result;
      };
      this.uploadfile(this.fileUpload[i]);
  }
    }
  }

  // for continue upload files
  cancel() {
   this.dialogRef.close();
  }

  //  fileUploadProgress() {
  //   const percentDone = Math.round(100 * event.loaded / event.total);
  //   return { status: 'progress', message: percentDone };
  // }
}
