import { Component, OnInit, ViewChild, ChangeDetectorRef, Inject } from '@angular/core';
import { UserSession } from '@core/models/user-session';
import { WarehouseApiService } from '@core/services/warehouse-api.service';
import { UserService } from '@core/services/user.service';
import { MatSnackBar, MatSidenav, MatDialog } from '@angular/material';
import { FormControl, AbstractControl, FormGroup, FormBuilder, Validators } from '@angular/forms';
import { OpticalStoreApiService, JSBARCODE } from '@core/services';
import { WarehouseService } from '@app/modules/warehouse/warehouse.service';
import { CommentsDialogComponent } from '@shared/components/comments-dialog/comments-dialog.component';
import { Router } from '@angular/router';
import { HttpErrorResponse } from '@angular/common/http';
import { UtilService } from '@core/services';
import { Subject, concat, of, Observable } from 'rxjs';
import { debounceTime, distinctUntilChanged, filter, switchMap, map, catchError } from 'rxjs/operators';

@Component({
  selector: 'app-inventory-list',
  templateUrl: './inventory-list.component.html',
  styleUrls: ['./inventory-list.component.scss'],
})
export class InventoryListComponent implements OnInit {
  settingBarcodeForm: FormGroup;
  private sessionData: UserSession;
  partSearchControl: AbstractControl;
  placeholderValue: string;
  partData: any;
  partList: any;
  inputpartName: any;
  public brandNameListSubject: Subject<any>;
  brandNameList$: Observable<any>;
  brandNameListinput$ = new Subject<string>();
  inputbarcode: any;
  inputexpireDate: any;
  inputQty: any;
  inputUom: any;
  inputUnitcost: any;
  barcodeSettingsFlag: Boolean = false;
  fetchedSettingData: Boolean = false;
  barcodeList: any[] = [];
  barcodeImg: any[] = [];
  settingObj: any;
  searchType: any;
  isFetchingData = true;
  public side = false;
  inputbatchNo;
  inputmrp;
  public currencyType: any;
  checked;
  selected = this.utilService.generateUniqueId();
  @ViewChild('sidenav')
  sidenav: MatSidenav;
  constructor(
    userService: UserService,
    private warehouseService: WarehouseService,
    private formbuilder: FormBuilder,
    private wareHouseApiService: WarehouseApiService,
    private opticalService: OpticalStoreApiService,
    private snackBar: MatSnackBar,
    private utilService: UtilService,
    private dialog: MatDialog,
    private router: Router,
    private changeDetection: ChangeDetectorRef,
    @Inject(JSBARCODE) private jsbarcode
  ) {
    this.sessionData = userService.getUser();
  }



  ngOnInit() {
    this.warehouseService.vendorData.next(null);
    this.fetchCurrencyType();
    this.fetchBrandNames();
    this.initForm();
    this.fetchSettingNames();
    this.getPartList(this.searchType, this.partData);
    this.partSearchControl = new FormControl('mobile');
    this.placeholderValue = 'Search  by Part Name';
    this.searchType = 'N';
    this.partSearchControl.valueChanges.subscribe(value => {
      this.searchType = value;
      switch (this.searchType) {
        case 'I':
          this.placeholderValue = 'Search  by Part Number';
          this.partData = '';
          this.getPartList('', '');
          break;
        // case 'Y':
        //   this.placeholderValue = 'Search  by Location';
        //   this.partData = '';
        //   this.getPartList('', '');
        //   break;
        default:
          this.placeholderValue = 'Search  by Part Name';
          this.partData = '';
          this.getPartList('', '');
          break;
      }
    });
  }

  // form init func
  initForm() {
    this.settingBarcodeForm = this.formbuilder.group({
      labelTypeNm: this.formbuilder.control('', Validators.required),
      labelWidth: this.formbuilder.control('', [
        Validators.required,
        Validators.minLength(70),
        Validators.maxLength(73)
      ]),
      labelHeight: this.formbuilder.control('', Validators.required),
      labelLeftMargin: this.formbuilder.control('', Validators.required),
      labelRightMargin: this.formbuilder.control('', Validators.required),
      labelTopMargin: this.formbuilder.control('', Validators.required),
      labelBottomMargin: this.formbuilder.control('', Validators.required)
    });

  }
  // for setting barcode details
  fetchSettingNames() {
    const payload = {
      orgId: this.sessionData.orgId,
      orgGrpId: this.sessionData.orgGroupId,
      createdUsrId: this.sessionData.regId,
      labelTypeNm: 'recommended'
    };
    this.wareHouseApiService.fetchBarCodeDetails(payload).subscribe(
      (response: any) => {
        if (response.responseCode === 'E200') {
          this.settingObj = response.data[0];
          this.settingBarcodeForm.patchValue(this.settingObj);
        } else if (response.response === 'E400') {
          this.snackBar.open(response.responseMessage, 'dismiss', {
            duration: 3000,
          });
        }
      },
      err => {
        this.snackBar.open('Something went wrong', 'dismiss', {
          duration: 3000,
        });
      }
    );
  }

  // for setting barcode name

  fetchBrandNames() {
    this.brandNameListSubject = new Subject();
    this.brandNameList$ = concat(
      of([]), // default items
      this.brandNameListinput$.pipe(
        debounceTime(200),
        distinctUntilChanged(),
        filter(checkStringCount => {
          if (checkStringCount.length > 1) {
            return true;
          }
        }),
        //  tap(() => this.people3Loading = true),
        switchMap((term: string) => {
          const payload = {
            createdUsrId: this.sessionData.regId,
            orgGrpId: this.sessionData.orgGroupId,
            orgId: this.sessionData.orgId,
            labelTypeNm: term
          };
          return this.opticalService.fetchBarCodeDetails(payload).pipe(
            map((response: any) => {
              if (response.responseCode === 'E200') {
                return response.data;
              } else if (response.response === 'E400') {
                // return (this.manufacturersList = []);
              }
            }),
            catchError(error => {
              return of([]);
            })
          );
        })
      )
    );
    this.brandNameList$.subscribe(this.brandNameListSubject);
  }

  fetchCurrencyType() {
    const payload = {
      orgId: this.sessionData.orgId,
      orgGrpId: this.sessionData.orgGroupId,
      createdUsrId: this.sessionData.regId,
    };
    this.wareHouseApiService.fetchCurrencyType(payload).subscribe(response => {
      if (response && response.responseCode === 'E200') {
        this.currencyType = response.data;
      } else if (response && response.responseCode === 'E400') {
        this.snackBar.open(response.responseMessage, 'dismiss', {
          duration: 500,
        });
      }
      this.changeDetection.detectChanges();
    }, (errorResponse: HttpErrorResponse) => {
      if (errorResponse.status > 0) {
        this.snackBar.open(errorResponse.error.responseMessage, 'dismiss', {
          duration: 500,
        });
      } else {
        // network failure
        this.snackBar.open('Network slow', 'dismiss', {
          duration: 500,
        });
      }
    });
  }
// to get part related list
  getPartList(searchType, partData) {
    const payload = {
      orgId: this.sessionData.orgId,
      orgGrpId: this.sessionData.orgGroupId,
      createdUsrId: this.sessionData.regId,
      partNo: '',
      partNm: ''
    };
    if (searchType === 'I') {
     payload.partNo = partData;
    } else if (searchType === 'N') {
      payload.partNm = partData;
    }
    this.wareHouseApiService.fetchWarehouseInventory(payload).subscribe(response => {
      if (response && response.responseCode === 'E200') {
        this.partList = response.data;
        if (this.partList.length) {
          this.isFetchingData = true;
        } else {
          this.isFetchingData = false;
        }
      } else if (response && response.responseCode === 'E400') {
        this.snackBar.open(response.responseMessage, 'dismiss', {
          duration: 500,
        });
      }
      this.changeDetection.detectChanges();
    },
    (errorResponse: HttpErrorResponse) => {
      if (errorResponse.status > 0) {
        this.snackBar.open(errorResponse.error.responseMessage, 'dismiss', {
          duration: 500,
        });
      } else {
        // network failure
        this.snackBar.open('Network slow', 'dismiss', {
          duration: 500,
        });
      }
    }
    );
  }
  // for barcode generation
  barcodeBtnDis() {
    const result = this.partList.find(element =>
      element.selected === false
    );
    if (!result) {
    this.checked = true;
    } else {
      this.checked = false;
    }
    // if (this.partList) {
    //  this.partList.forEach(val => {
    //    if (val.selected === false) {
    //      this.checked = false;
    //    }
    //  });
    // }
    this.barcodeList = this.partList.filter((val) => {
      if (val.barcode) {
        return val.selected;
      }
    });
  }

  barcodeGenerate() {
    let currencyType;
    currencyType = this.currencyType;
    let totalcostPrice = [];
    this.barcodeImg = [];
    totalcostPrice = this.barcodeList.reduce((acc, val) => {
      return [
        ...acc,
        ...Array.from(new Array(val.qty)).map(() => val)
      ];
    }, []);

    for (let i = 0; i < totalcostPrice.length; i++) {
      const barcodeObj: any = {
        ...this.settingObj
      };
      const canvas = document.createElement('canvas');
     this.jsbarcode(canvas, totalcostPrice[i].barcode, {
        format: 'code39',
        height: 1000,
        displayValue: false
      });
      barcodeObj.barcode = totalcostPrice[i].barcode.toUpperCase();
      barcodeObj.partNm = totalcostPrice[i].partNm;
      barcodeObj.imageObj = canvas.toDataURL('image/jpeg', 1);
      barcodeObj.itemBrandNm = totalcostPrice[i].partNm;
      barcodeObj.currencyType = this.currencyType;
      barcodeObj.mrp = totalcostPrice[i].mrp;
      barcodeObj.batchNo = totalcostPrice[i].batchNo;

      this.barcodeImg.push(barcodeObj);
    }
    this.warehouseService.getBarcodePrints(this.barcodeImg);
    this.barcodeList = [];
  this.partList.forEach(val => {
    return (val.selected = false);
  });
  this.checked = false;
  }
  // view function
  viewPart(part) {
    this.side = true;
    this.warehouseService.vendorData.next(part);
    this.sidenav.open();
    this.side = true;
  }
  // edit function
  editPart(part) {
    this.warehouseService.vendorData.next(part);
    this.router.navigate(['/warehouse/inventory/add-to-list']);
  }
  // delete function
  deletePart(part, i) {
    const dialogRef = this.dialog.open(CommentsDialogComponent, {
      data: {
        title: 'Delete Inventory',
        data: 'Reason for Deleting Inventory'
      },
      disableClose: true
    });
    dialogRef.afterClosed().subscribe(result => {
      if (result) {
        const deletePayload = {
          orgId: this.sessionData.orgId,
          orgGrpId: this.sessionData.orgGroupId,
          createdUsrId: this.sessionData.regId,
          inventoryId: part.inventoryId,
          remarks: result
        };
        this.wareHouseApiService.deleteItemInInventory(deletePayload).subscribe(
          (response: any) => {
            if (response.responseCode === 'E200') {
              this.partList.splice(i, 1);
              this.getPartList(null, null);
              this.snackBar.open(response.responseMessage, 'dismiss', {
                duration: 1000,
              });
            } else if (response.response === 'E400') {
              this.snackBar.open(response.responseMessage, 'dismiss', {
                duration: 1000,
              });
            }
          },
          err => {
            this.snackBar.open('Something went wrong', 'dismiss', {
              duration: 1000,
            });
          }
        );
      }
    });
  }

  // to open and close barcode settings page
  barcodeSettings() {
    this.barcodeSettingsFlag = true;
  }
  cancelOrgGroupForm() {
    this.barcodeSettingsFlag = false;
  }
  onAddBrand(brand) {
    this.settingBarcodeForm.patchValue(brand);
    if (brand.barCodeSettingsId) {
      this.fetchedSettingData = true;
    } else {
      this.settingObj = {};
      this.fetchedSettingData = false;
    }
  }
  // to save barcode settings
  saveBarcodeSetting() {
    if (this.fetchedSettingData || this.settingObj.barCodeSettingsId) {
      this.settingObj = this.settingBarcodeForm.value;
      this.snackBar.open('Successfully changed your settings', 'dismiss', {
        duration: 3000,
      });
      return;
    }
    const payload = {
      orgId: this.sessionData.orgId,
      orgGrpId: this.sessionData.orgGroupId,
      createdUsrId: this.sessionData.regId,
      noOfRollColumns: 1,
      ...this.settingBarcodeForm.value
    };
    this.settingObj = this.settingBarcodeForm.value;
    this.wareHouseApiService.insertOrUpdateBarCodeDetails(payload).subscribe(
      (response: any) => {
        if (response.responseCode === 'E200') {
          this.snackBar.open(response.responseMessage, 'dismiss', {
            duration: 3000,
          });
        } else if (response.response === 'E400') {
          this.snackBar.open(response.responseMessage, 'dismiss', {
            duration: 3000,
          });
        }
      },
      err => {
        this.snackBar.open('Something went wrong', 'dismiss', {
          duration: 3000,
        });
      }
    );
  }

  // for selecting all checkboxes
  selectAll(event) {
    if (event.checked === true) {
      this.partList.forEach(element => {
        element.selected = true;
      });
    } else if (event.checked === false) {
      this.partList.forEach(element => {
        element.selected = false;
      });
    }
    this.barcodeBtnDis();
  }

}
