import { Component, OnInit, Inject } from '@angular/core';
import { WarehouseApiService } from '@core/services/warehouse-api.service';
import { MatSnackBar, MAT_DIALOG_DATA, MatDialogRef } from '@angular/material';
import { HttpErrorResponse } from '@angular/common/http';
import { UserSession } from '@core/models/user-session';
import { UserService } from '@core/services';
import { Subject, Observable, concat, of } from 'rxjs';
import { debounceTime, distinctUntilChanged, filter, switchMap, map } from 'rxjs/operators';

@Component({
  selector: 'app-location-dialog',
  templateUrl: './location-dialog.component.html',
  styleUrls: ['./location-dialog.component.scss']
})
export class LocationDialogComponent implements OnInit {
  storagSheds: any;
  private sessionData: UserSession;
  public partName: any;
  public alleys: any;
  public selectedShed: any;
  public selectedAllay: any;
  public selectedTier: any;
  public selectedRack: any;
  public columnNo: any;
  public rowNo:  any;
  public product$: Observable<any>;
  public searchedData$ = new Subject<string>();
  public shelfData$ = new Subject<string>();
  public shelf$: Observable<any>;
  locationData = [];
  selectedTierData: any;
  shelves: {};
  constructor(
    private wareHouseApiService: WarehouseApiService,
    private snackBar: MatSnackBar,
    private userService: UserService,
    public dialogRef: MatDialogRef<LocationDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public data
  ) {
    this.sessionData = userService.getUser();
  }

  ngOnInit() {
    this.partName = this.data;
    this.fetchShedRef();
  }

  fetchShedRef() {
    const payload = {
      orgId: this.sessionData.orgId,
      orgGrpId: this.sessionData.orgGroupId,
      createdUsrId: this.sessionData.regId,
    };
    this.wareHouseApiService.fetchShedRef(payload).subscribe(
      response => {
        if (response && response.responseCode === 'E200') {
          this.storagSheds = response.data;
        } else if (response && response.responseCode === 'E400') {
          this.snackBar.open(response.responseMessage, 'close', {
            duration: 2,
          });
        }
      },
      (errorResponse: HttpErrorResponse) => {
        if (errorResponse.status > 0) {
          this.snackBar.open(errorResponse.error.responseMessage, 'dismiss', {
            duration: 3000,
          });
        } else {
          // network failure
          this.snackBar.open('Network slow', 'dismiss', {
            duration: 3000,
          });
        }
      }
    );
  }

  fetchAlleys() {
    const payload = {
      orgId: this.sessionData.orgId,
      orgGrpId: this.sessionData.orgGroupId,
      createdUsrId: this.sessionData.regId,
      shedId: this.selectedShed
    };
    this.wareHouseApiService.fetchShedRowxref(payload).subscribe(
      response => {
        if (response && response.responseCode === 'E200') {
          this.alleys = response.data;
        } else if (response && response.responseCode === 'E400') {
          this.snackBar.open(response.responseMessage, 'close', {
            duration: 1500,
          });
        }
      },
      (errorResponse: HttpErrorResponse) => {
        if (errorResponse.status > 0) {
          this.snackBar.open(errorResponse.error.responseMessage, 'dismiss', {
            duration: 3000,
          });
        } else {
          // network failure
          this.snackBar.open('Network slow', 'dismiss', {
            duration: 3000,
          });
        }
      }
    );
  }

  fetchRacks() {
    const payload = {
      orgId: this.sessionData.orgId,
      orgGrpId: this.sessionData.orgGroupId,
      createdUsrId: this.sessionData.regId,
      shedId: this.selectedShed,
      rowId: this.selectedAllay
    };
    this.wareHouseApiService.fetchShedColumnRef(payload).subscribe(
      response => {
        if (response && response.responseCode === 'E200') {
          this.shelves = response.data;
        } else if (response && response.responseCode === 'E400') {
          this.snackBar.open(response.responseMessage, 'close', {
            duration: 1500,
          });
        }
      },
      (errorResponse: HttpErrorResponse) => {
        if (errorResponse.status > 0) {
          this.snackBar.open(errorResponse.error.responseMessage, 'dismiss', {
            duration: 3000,
          });
        } else {
          // network failure
          this.snackBar.open('Network slow', 'dismiss', {
            duration: 3000,
          });
        }
      }
    );
  }
  fetchShelfs() {
    const payload = {
      orgId: this.sessionData.orgId,
      orgGrpId: this.sessionData.orgGroupId,
      createdUsrId: this.sessionData.regId,
      shedId: this.selectedShed,
      rowId: this.selectedAllay,
      columnId: this.selectedRack
    };
    this.wareHouseApiService.fetchShedTierxref(payload).subscribe(
      response => {
        if (response && response.responseCode === 'E200') {
          this.selectedTierData = response.data;
        } else if (response && response.responseCode === 'E400') {
          this.snackBar.open(response.responseMessage, 'close', {
            duration: 1500,
          });
        }
      },
      (errorResponse: HttpErrorResponse) => {
        if (errorResponse.status > 0) {
          this.snackBar.open(errorResponse.error.responseMessage, 'dismiss', {
            duration: 3000,
          });
        } else {
          // network failure
          this.snackBar.open('Network slow', 'dismiss', {
            duration: 3000,
          });
        }
      }
    );
  }

  fetchlocationData(selectTier) {
   this.selectedTierData.forEach(element => {
    if ( element.tierId === selectTier) {
      this.locationData.push(element);
    }
   });
  }
  confirmData() {
  this.dialogRef.close(this.locationData[0]);
  }
  close() {
    this.dialogRef.close();
  }
}
