import { Component, OnInit, OnDestroy, AfterViewInit, Input, ChangeDetectorRef } from '@angular/core';
import { Subscription, Observable, concat, of, Subject } from 'rxjs';
import { WarehouseService } from '@app/modules/warehouse/warehouse.service';
import { UserSession } from '@core/models/user-session';
import { WarehouseApiService } from '@core/services/warehouse-api.service';
import { UserService } from '@core/services';
import { AutoUnsubscribe } from 'ngx-auto-unsubscribe';
import { MatSidenav } from '@angular/material/sidenav';
import { ToastrService } from 'ngx-toastr';
import { MatSnackBar } from '@angular/material/snack-bar';
import { debounceTime, distinctUntilChanged, tap, filter, switchMap, catchError } from 'rxjs/operators';
@AutoUnsubscribe()
@Component({
  selector: 'app-parts-mapping',
  templateUrl: './parts-mapping.component.html',
  styleUrls: ['./parts-mapping.component.scss']
})
export class PartsMappingComponent implements OnInit, OnDestroy, AfterViewInit {

  @Input()
  sideNavRef: MatSidenav;
  private partListDataSubscription: Subscription;
  public partList = [];
  productsList$: Observable<any>;
  private sessionData: UserSession;
  partsList = [];
  selectedParts = [];
  checkedParts = [];
  public isListEmpty: Boolean;
  productsListinput$ = new Subject<string>();
  checked = false;
  isSelectedListEmpty = true;
  dropdownSettings = {};
  selectedItems = [];

  constructor(
    private wareHouseDataService: WarehouseService,
    private cdr: ChangeDetectorRef,
    private toaster: ToastrService,
    private wareHouseApiService: WarehouseApiService,
    userService: UserService,
    private snackBar: MatSnackBar
  ) {
    this.sessionData = userService.getUser();
  }
  ngOnDestroy() {

  }
  ngOnInit() {
    this.partsList = [];
    this.fetchPartDetails();
    this.fetchParts();
    this.dropdownSettings = {
      singleSelection: false,
      idField: 'partId',
      textField: 'partNm',
      selectAllText: 'Select All',
      unSelectAllText: 'UnSelect All',
      itemsShowLimit: 0,
      allowSearchFilter: true,
      defaultOpen: false
    };
    // this.onItemSelect();
    // this.onSelectAll('item');
    this.wareHouseDataService.productMappingDataSource.subscribe(res => {
      if (res) {
        this.selectedParts = res;
        this.selectedItems = res;
      }
      console.log(this.selectedItems);
      console.log(this.selectedParts);
    });
  }
  onItemSelect(item: {partId: string, partNm: string}) {
    if (this.selectedParts && this.selectedParts.length > 0) {
      const part = this.selectedParts.find(ele => ele.partId === item.partId);
      if (!part) {
        this.selectedParts.push(item);
      }
    } else {
      this.checkedParts.push(item);
    }

  }
  onSelectAll(items: any) {
    this.selectedParts = items;
  }
  onItemDeSelect(item: {partId: string, partNm: string}) {
    let index;
    this.selectedParts.forEach(res => {
      if (res.partId === item.partId) {
        index = this.selectedParts.indexOf(res);
      }
    });
    if (index) {
      this.selectedParts.splice(index, 1);
    }
  }

  unMapPartsFromProduct(parts, index) {
    if (parts.associatedPartId) {
      const payload = {
        orgId: this.sessionData.orgId,
        associatedPartId: parts.associatedPartId,
        partNm: parts.partNm,
        productPartId: parts.productPartId
      };
      this.wareHouseApiService.unMapPartsFromProduct(payload).subscribe(response => {
        if (response && response.responseCode === 'E200') {
         this.selectedParts.splice(index, 1);
        } else if (response && response.responseCode === 'E400') {
          this.snackBar.open(response.responseMessage, 'close', {
            duration: 1500,
          });
        }
      });
    } else {
      this.checkedParts.splice(index, 1);
    }
    this.selectedItems = this.selectedParts;
    console.log(this.selectedItems);
  }
  ngAfterViewInit() {
    this.partListDataSubscription = this.wareHouseDataService.partListData.subscribe(res => {
      if (res) {
        this.partList = res;
        this.cdr.detectChanges();
      }
    });
    // this.wareHouseDataService.productMappingDataSource.subscribe(res => {
    //   if (res) {
    //     this.selectedParts = res;
    //   }
    // });
  }

  closeSideNav() {
    //this.partsList = [];
    this.isSelectedListEmpty = false;
    this.sideNavRef.close();
  }
  fetchPartDetails() {
    this.productsList$ = concat(
      of([]), // default items
      this.productsListinput$.pipe(
        debounceTime(200),
        distinctUntilChanged(),
        tap(console.log),
        filter(checkStringCount => {
          if (checkStringCount && checkStringCount.length > 1) {
            return true;
          }
        }),
        switchMap(this.fetchParts.bind(this))
      )
    );
  }
  fetchParts() {
    const payload = {
      orgId: this.sessionData.orgId,
      orgGrpId: this.sessionData.orgGroupId,
      createdUsrId: this.sessionData.regId,
      // partNmOrNo: partName,
      partProductInd: 'PA'
    };
     this.wareHouseApiService.fetchPartDetails(payload).
     subscribe((response: any) => {
        if (response && response.responseCode === 'E200') {
          this.partsList = response.data;
          if (this.partsList.length > 1) {
            this.isListEmpty = true;
          } else {
            this.isListEmpty = false;
          }
        } else if (response && response.responseCode === 'E400') {
          this.toaster.error(response.responseMessage);
        }
      }),
      catchError(error => {
        return of([]);
      });
  }

  mapParts() {
    const data  = [];
    this.partsList.forEach(parts => {
      this.checkedParts.forEach(selected => {
        if (parts.partId === selected.partId) {
          data.push(parts);
        }
      });
    });
    this.wareHouseDataService.selectedPartsList.next(data);
    this.fetchParts();
    this.closeSideNav();
  }
}
