import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { WarehouseApiService } from '@core/services/warehouse-api.service';
import { WarehouseService } from '@app/modules/warehouse/warehouse.service';
import { UtilService } from '@core/services';
import { UserSession } from '@core/models/user-session';
import { UserService } from '@core/services/user.service';
import { LoginApiService } from '@core/services';
import { MatDatepicker } from '@angular/material';
import { DatePipe } from '@angular/common';
import { Subscription } from 'rxjs';
import {MatSnackBar} from '@angular/material';
import { Router } from '@angular/router';
import { HttpErrorResponse } from '@angular/common/http';
@Component({
  selector: 'app-vendor-payment-mode',
  templateUrl: './vendor-payment-mode.component.html',
  styleUrls: ['./vendor-payment-mode.component.scss']
})
export class VendorPaymentModeComponent implements OnInit {
  private sessionData: UserSession;
  private customerDataSubscription: Subscription;
  menuType = 'paymentMode';
  disabledButn: Boolean = false;
  paymentModesList = [];
  currentPaymentModesList = [];
  paymentModesForm: FormGroup;
  vendorList: any;
  totalAmount: any;
  payAndPrint = true;
  maxDate = new Date();
  cardNumber =  false;
  transactionDetails: any;
  totalDueAmount: any;
  public currencyType: any;

  constructor(
    userService: UserService,
    private utilService: UtilService,
    private warehouseService: WarehouseService,
    private datePipe: DatePipe,
    private warehouseApiService: WarehouseApiService,
    private loginService: LoginApiService,
    private snackBar: MatSnackBar,
    private router: Router,
    private formbuilder: FormBuilder,
  ) {
    this.sessionData = userService.getUser();
   }

   initForm() {
    this.paymentModesForm = this.formbuilder.group({
      paymentMode: this.formbuilder.control(null, Validators.required),
      issuedBank: this.formbuilder.control(null, Validators.required),
      chequeDdNo: this.formbuilder.control(null,  [
        Validators.required,
        // Validators.minLength(9),
        // Validators.maxLength(18),
        Validators.pattern('^[0-9]*$')
      ]),
      chequeDdDate: this.formbuilder.control(null, Validators.required),
      paidAmountByMode: this.formbuilder.control(null, Validators.required)
    });
    const currentDate = this.datePipe.transform(new Date(), 'yyyy-MM-dd');
    this.paymentModesForm.controls['chequeDdDate'].setValue(currentDate);
  }
  ngOnInit() {
    this.fetchCurrencyType();
    this.fetchPaymentModes();
    this.initForm();
     this.warehouseService.paymentsList.subscribe(res => {
      if (res) {
        this.vendorList = res;
        this.totalDueAmount = Math.round(this.vendorList.totalDueAmt);
      }
    });
    this.getTransactionDetails();
    // this.paymentModesForm
    // .get('paidAmountByMode')
    // .valueChanges.subscribe(val => {
    //   this.OnKeyUpPayingAmount(Number(val));
    // });
  }

  fetchCurrencyType() {
    const payload = {
      orgId: this.sessionData.orgId,
      orgGrpId: this.sessionData.orgGroupId,
      createdUsrId: this.sessionData.regId,
    };
    this.warehouseApiService.fetchCurrencyType(payload).subscribe(response => {
      if (response && response.responseCode === 'E200') {
        this.currencyType = response.data;
      } else if (response && response.responseCode === 'E400') {
        this.snackBar.open(response.responseMessage, 'dismiss', {
          duration: 500,
        });
      }
    }, (errorResponse: HttpErrorResponse) => {
      if (errorResponse.status > 0) {
        this.snackBar.open(errorResponse.error.responseMessage, 'dismiss', {
          duration: 500,
        });
      } else {
        // network failure
        this.snackBar.open('Network slow', 'dismiss', {
          duration: 500,
        });
      }
    });
  }
  // ngAfterViewInit() {

  // }
  // date picker
  datePickerToggler(datePicker: MatDatepicker<Date>) {
    datePicker[datePicker.opened ? 'close' : 'open']();
  }
  fetchPaymentModes() {
    const payload = {
      orgId: this.sessionData.orgId,
      masterType: 'PAYMENT_MODE',
      authenticatedUserId: this.sessionData.regId
    };

    this.loginService.fetchListOfCommonValues(payload).subscribe(
      (response: any) => {
        if (response.responseCode === 'E200') {
          this.paymentModesList = response.data;
          this.paymentModesList = this.paymentModesList.filter(function(value) {
      return (value.attributeCode !== 'DD') && (value.attributeCode !== 'INSURANCE') && (value.attributeCode !== 'ONLINE');

       });
          this.paymentModesList.forEach(function (val) {
            if (val.attributeCode === 'CHEQUE' || val.attributeCode === 'DD') {
              val.modeValidation = true;
            }
          });
        } else if (response.response === 'E400') {
        }
      },
      err => {
      }
    );
  }
  // add payment modes
  addPaymentModes() {
    this.totalAmount = this.vendorList.totalDueAmt;
    const mode = { ...this.paymentModesForm.value };
    this.paymentModesList = this.paymentModesList.filter(function(value) {
      return value.attributeCode !== mode.paymentMode;
    });
    this.currentPaymentModesList.push({
      ...this.paymentModesForm.value,
      chequeDdDt: this.datePipe.transform(
        this.paymentModesForm.value.chequeDdDate,
        'yyyy-MM-dd'
      ),
    });
    if (this.currentPaymentModesList.length) {
      this.payAndPrint = false;
    }
    let decreaseAmt = 0;
    if (this.currentPaymentModesList.length > 0) {
      for (let i = 0; i < this.currentPaymentModesList.length; i++) {
        decreaseAmt +=
          Number(this.currentPaymentModesList[i].paidAmountByMode) || 0;

      }
    }
    this.totalDueAmount = Math.round(this.totalAmount - decreaseAmt);

    this.paymentModesForm.reset();
    // if (this.showBillingFromPending === 'pointofsale') {
    //   this.totalDueAmount = Math.round(this.totalInvoiceAmount - decreaseAmt);
    //   this.storeValue = decreaseAmt;
    //   this.paymentModesForm.reset();
    // } else if (this.showBillingFromPending === 'pendingOrders') {
    //   this.totalDueAmount = Math.round(
    //     this.pendingFinalPayableAmount - decreaseAmt
    //   );
    // this.getTotalPaidAmtIncAdv(this.currentPaymentModesList);
    // this.paymentModesForm.reset();

    // }
  }
  // remove payment modes
  removePaymentMode = function (index, mode) {
    mode.attributeCode = mode.paymentMode;
    this.paymentModesList.push(mode);
    this.currentPaymentModesList.splice(index, 1);
    let decreaseAmt = 0;
    if (this.currentPaymentModesList.length > 0) {
      for (let i = 0; i < this.currentPaymentModesList.length; i++) {
        decreaseAmt += Number(this.currentPaymentModesList[i].paidAmountByMode);
      }
    }
    if (this.currentPaymentModesList.length) {
      this.payAndPrint = false;
    } else {
      this.payAndPrint = true;
    }
    this.totalDueAmount = Math.round(this.totalAmount - decreaseAmt);
    this.storeValue = decreaseAmt;
  };
  // on change payment mode
  onChangePaymentMode(type) {
    if (type === 'paymentModeChange') {
      // Here we are removing already entered dd or check and bank name
    this.paymentModesForm.patchValue({issuedBank: null}) ;
    this.paymentModesForm.patchValue({chequeDdNo: null}) ;
    }
    const currentDate = this.datePipe.transform(new Date(), 'yyyy-MM-dd');
    this.paymentModesForm.controls['chequeDdDate'].setValue(currentDate);
    const modeVal = this.paymentModesForm.get('paymentMode').value;
    this.paymentModesForm.patchValue({
      paidAmountByMode: Math.round(this.totalDueAmount) || 0
    });
    if (modeVal === 'CHEQUE') {
      this.cardNumber = false;
    }
    if (modeVal === 'DD' || modeVal === 'CHEQUE') {
      this.paymentModesForm.get('issuedBank').enable();
      this.paymentModesForm.get('chequeDdNo').enable();
      this.paymentModesForm
        .get('issuedBank')
        .setValidators(Validators.required);
      this.paymentModesForm
        .get('chequeDdNo')
        .setValidators(Validators.required);
    } else if (modeVal === 'CARD') {
      this.cardNumber = true;
      this.paymentModesForm.get('issuedBank').enable();
      this.paymentModesForm
      .get('issuedBank')
      .setValidators(Validators.required);
      this.paymentModesForm.get('chequeDdNo').enable();

    } else {
      this.paymentModesForm.get('issuedBank').disable();
      this.paymentModesForm.get('chequeDdNo').disable();
      this.paymentModesForm.get('issuedBank').clearValidators();
      this.paymentModesForm.get('chequeDdNo').clearValidators();
    }


  }
  // on amount entering'
  OnKeyUpPayingAmount = function (val) {
    // this.totalAmount = this.vendorList.totalAmt;
    if (val <= Math.round(this.totalAmount)) {
      this.disabledButn = false;
    }
      if (val > Math.round(this.totalAmount)) {
        this.snackBar.open('Given amount is more than required payable amount', 'dismiss', {
          duration: 3000,
        });
        this.disabledButn = true;

      // if (this.storeValue) {
      //   const addValue = this.storeValue + (val || 0);
      //   if (addValue > this.vendorList.totalAmt) {
      //     this.toaster.warning(
      //       'Failure!',
      //       'Given amount is more than required payable amount'
      //     );
      //     this.disabledButn = true;
      //   }
      // }
    }
    let decreaseAmt = 0;
    if (this.currentPaymentModesList.length > 0) {
      for (let i = 0; i < this.currentPaymentModesList.length; i++) {
        decreaseAmt +=
          Number(this.currentPaymentModesList[i].paidAmountByMode) || 0;
      }
      if (this.vendorList.totalAmount < this.decreaseAmt) {
        this.toaster.warning(
          'Failure!',
          'Given amount is more than required payable amount'
        );
        this.disabledButn = true;
      }
    }
  };
  // payment submit function
  paymentForm() {
    const totalPaidAmt = this.currentPaymentModesList.reduce(
      (acc, val) => (acc += val.paidAmountByMode),
      0
    );
    const vendorPaymentTransList = this.mapPaymentModes();

    const payload = {
      createdUsrId: this.sessionData.regId,
      orgGrpId: this.sessionData.orgGroupId,
      orgId: this.sessionData.orgId,
      paidAmt: totalPaidAmt,
      vendorInvoiceNo: this.vendorList.vendorInvoiceNo,
      vendorInvoiceId: this.vendorList.vendorInvoiceId,
      vendorPaymentTransList: []
    };
    if (this.currentPaymentModesList) {
      for (let i = 0; i < this.currentPaymentModesList.length; i++) {
        this.currentPaymentModesList[
          i
        ].paymentSource = this.currentPaymentModesList[i].paymentMode;
      }
    }
    // if (this.currentPaymentModesList.length > 0) {
    //   this.currentPaymentModesList.forEach(item => {
    //     payload.paidAmt += Number(item.paidAmountByMode);
    //   });
    // }
    payload.vendorPaymentTransList = vendorPaymentTransList;
    this.warehouseApiService.duePaymentForVendorInvoice(payload).subscribe((response): any => {
      if (response && response.responseCode === 'E200') {
      this.warehouseService.paymentsList.next(this.vendorList);
      this.router.navigate(['/warehouse/vendor-invoice-history']);

      } else if (response && response.responseCode === 'E400') {
        this.snackBar.open(response.responseMessage, 'dismiss', {
          duration: 800,
        });
      }
    });
  }
  mapPaymentModes() {
    return this.currentPaymentModesList.map(val => {
      return {
        orgId: this.sessionData.orgId,
        orgGrpId: this.sessionData.orgGroupId,
        createdUsrId: this.sessionData.regId,
        vendorInvoiceId: this.vendorList.vendorInvoiceId,
        vendorInvoiceNo: this.vendorList.vendorInvoiceNo,
        vendorId: this.vendorList.vendorId,
        paymentMode: val.paymentMode,
        paymentDt: val.paymentDt,
        transactionDt: val.transactionDt || null,
        transactionAmt: val.paidAmountByMode,
        paidAmountByMode: val.paidAmountByMode,
        chequeDdNo: val.chequeDdNo || null,
        issuedBank: val.issuedBank || null,
        chequeDdDt: val.chequeDdDate || null,
        isCheque: val.paymentMode === 'CHEQUE' ? true : false
      };
    });
  }
// payment transaction details
getTransactionDetails() {
  const payload = {
    orgId: this.sessionData.orgId,
    orgGrpId: this.sessionData.orgGroupId,
    createdUsrId: this.sessionData.regId,
    vendorInvoiceId: this.vendorList.vendorInvoiceId
  };
  this.warehouseApiService.fetchVendorTransactions(payload).subscribe(response => {
    if (response && response.responseCode === 'E200') {
      this.transactionDetails = response.data;
    } else if (response && response.responseCode === 'E400') {
      this.snackBar.open(response.responseMessage, 'dismiss', {
        duration: 800,
      });
    }
  });
}

cancel() {
  this.warehouseService.paymentsList.next(this.vendorList);
  this.router.navigate(['/warehouse/vendor-invoice-history']);
}
}

