import { Component, OnInit } from '@angular/core';
import { MatDialogRef } from '@angular/material/dialog';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { WarehouseApiService } from '@core/services/warehouse-api.service';
import { UserSession } from '@core/models/user-session';
import { UserService } from '@core/services';
import { ToastrService } from 'ngx-toastr';
import { MatSnackBar } from '@angular/material';
import { WarehouseService } from '../../warehouse.service';

@Component({
  selector: 'app-carton-dialog',
  templateUrl: './carton-dialog.component.html',
  styleUrls: ['./carton-dialog.component.scss']
})
export class CartonDialogComponent implements OnInit {
  public isCreate = false;
  cartonBoxForm: FormGroup;
  selectCartonForm: FormGroup;
  private sessionData: UserSession;
  allCartons: any;
  genCode: any;
  cartonData: any;
  invalidUniq = false;
  uniqueId = [];
  selectCartonFormSubmitted = false;
  productDimension = [{ uom: 'MM' }, { uom: 'CM' }, { uom: 'Mtr' }];
  previousCartonData: any;
  constructor(
    private dialogRef: MatDialogRef<CartonDialogComponent>,
    private warehouseApiService: WarehouseApiService,
    private userService: UserService,
    private fb: FormBuilder,
    private toastr: ToastrService,
    private snackBar: MatSnackBar,
    private warehouseService: WarehouseService
  ) {
    this.sessionData = this.userService.getUser();
  }

  validation_messages = {
    cartonTypeNm: [
      {
        type: 'required',
        message: '* Carton Type is required'
      }
    ],
    uom: [
      {
        type: 'required',
        message: '* Carton Dimensions are required'
      }
    ]
  };
  validation_messages_selctCarton = {
    cartonNmAndDimension: [
      {
        type: 'required',
        message: '* Carton is required'
      }
    ],
    cartonNo: [
      {
        type: 'required',
        message: '* Unique Identification is required'
      }
    ]
  };

  ngOnInit() {
    this.fetchCartons();
    this.initForm();
    this.warehouseService.cartonData.subscribe(res => {
      if (res) {
        this.previousCartonData = res;
      }
    });
  }
  close() {
    this.dialogRef.close();
  }
  createCartn() {
    this.isCreate = true;
  }
  initForm() {
    this.cartonBoxForm = this.fb.group({
      cartonTypeNm: this.fb.control('', Validators.required),
      uom: this.fb.control('', Validators.required),
      cartonLength: this.fb.control('', Validators.required),
      cartonWidth: this.fb.control('', Validators.required),
      cartonHeight: this.fb.control('', Validators.required)
    });
    this.selectCartonForm = this.fb.group({
      cartonNmAndDimension: this.fb.control('', Validators.required),
      cartonNo: this.fb.control('', Validators.required),
      barCode: this.fb.control('')
    });
  }

  /**
   * Fetch Cartons in Select box
   */
  fetchCartons() {
    const payload = {
      cartonTypeId: 0,
      createdUsrId: this.sessionData.regId,
      orgGrpId: this.sessionData.orgGroupId,
      orgId: this.sessionData.orgId
    };
    this.warehouseApiService.fetchCartonDetails(payload).subscribe(
      response => {
        if (response && response.responseCode === 'E200') {
          this.allCartons = response.data;
        } else if (response && response.responseCode === 'E400') {
        }
      },
      () => {}
    );
  }
  /**
   * Insert Api for carton Box
   */
  createCarton() {
    if (this.cartonBoxForm.valid) {
      const payload = [
        {
          createdUsrId: this.sessionData.regId,
          orgGrpId: this.sessionData.orgGroupId,
          orgId: this.sessionData.orgId,
          cartonTypeNm: this.cartonBoxForm.value.cartonTypeNm,
          cartonLength: this.cartonBoxForm.value.cartonLength,
          cartonWidth: this.cartonBoxForm.value.cartonWidth,
          cartonHeight: this.cartonBoxForm.value.cartonHeight,
          uom: this.cartonBoxForm.value.uom
        }
      ];
      this.warehouseApiService.insertCartonDetails(payload).subscribe(
        response => {
          if (response && response.responseCode === 'E200') {
            this.toastr.success('Success', response.responseMessage);
            this.clearForm();
            this.isCreate = false;
            this.fetchCartons();
          } else if (response && response.responseCode === 'E400') {
            this.toastr.error('Failure!', response.responseMessage);
          }
        },
        (err) => {
          this.toastr.error('Failure!', 'Something went wrong');
        }
      );
    }
  }
  /**
   * Clearing Data form the Form
   */
  clearForm() {
    this.cartonBoxForm.reset();
  }
  /**
   * Fetch API Integration for Barcode
   */
  genBarcode(event) {
    if (event.target.checked === true) {
      const payload = {
        createdUsrId: this.sessionData.regId,
        orgGrpId: this.sessionData.orgGroupId,
        orgId: this.sessionData.orgId
      };
      this.warehouseApiService.generateBarcode(payload).subscribe(
        response => {
          if (response && response.responseCode === 'E200') {
            this.genCode = response.data;
            this.selectCartonForm
              .get('cartonNo')
              .patchValue(this.genCode.barcode);
          }
        },
        () => {}
      );
    } else {
      this.selectCartonForm.get('cartonNo').reset();
    }
  }

  saveCartonDtls() {
    const uniqId = this.selectCartonForm.get('cartonNo').value;
    if (this.previousCartonData) {

     const cartonNo = this.previousCartonData.find(ele => ele.cartonNo === uniqId);
     if (cartonNo) {
      this.snackBar.open('unique identity already exists', 'close', {
        duration: 2000
      });
     } else {
      this.selectCartonFormSubmitted = true;
      if (this.selectCartonForm.valid) {
        this.cartonData = this.selectCartonForm.value;
        this.warehouseService.selectCarton.next(this.cartonData);
        this.dialogRef.close();
      }
     }
    }
  }
}
