import { Pipe, PipeTransform } from '@angular/core';
import { DatePipe } from '@angular/common';

@Pipe({
  name: 'invoiceHistoryFilter'
})
export class InvoiceHistoryFilterPipe implements PipeTransform {

  patientNm: any;
  private datePipe: DatePipe;
  transform(
    vendorList: any,
    inputinvoiceNumber: string,
    inputpoNumber: string,
    inputvendorName: string,
    inputinvoiceDate: string,
    inputtotalAmount: string,
    inputpaidAmount: string,
    inputdueAmount: string,
    inputmodeDelivery: string,
  ) {
    if (vendorList && vendorList.length) {
      return vendorList.filter(vendor => {
        if (
          (inputinvoiceNumber && !vendor.vendorInvoiceNo) ||
          (inputpoNumber && !vendor.purchaseOrderNo)  ||
          ( inputvendorName && !vendor.vendorNm) ||
          (inputinvoiceDate && !vendor.vendorInvoiceDt) ||
          ( inputtotalAmount && !vendor.totalAmt) ||
          (inputpaidAmount && !vendor.paidAmt) ||
          (inputdueAmount && !vendor.totalDueAmt) ||
          (inputmodeDelivery && !vendor.shippingType)
        ) {
          return false;
        }

         // vendor invoice no
         if (vendor.vendorInvoiceNo) {
          if (
            inputinvoiceNumber &&
            vendor.vendorInvoiceNo.toLowerCase().indexOf(inputinvoiceNumber.toLowerCase()) ===
              -1
          ) {
            return false;
          }
        }

         // purchase order no

         if (vendor.purchaseOrderNo) {
          if (
            inputpoNumber &&
            vendor.purchaseOrderNo.toLowerCase().indexOf(inputpoNumber.toLowerCase()) ===
              -1
          ) {
            return false;
          }
        }

        // vendor Name
        if (vendor.vendorNm) {
          if (
            inputvendorName &&
            vendor.vendorNm.toLowerCase().indexOf(inputvendorName.toLowerCase()) ===
              -1
          ) {
            return false;
          }
        }

        // invoice date


        // order Date
        if (vendor.vendorInvoiceDt) {
          const datePipe = new DatePipe('en-US');
          vendor.vendorInvoiceDt = datePipe.transform(vendor.vendorInvoiceDt, 'yyyy/MM/dd');
          if (inputinvoiceDate && vendor.vendorInvoiceDt.toLowerCase().indexOf(inputinvoiceDate.toLowerCase()) === -1) {
              return false;
          }
        }

        // total Amt
        if (vendor.totalAmt) {

          if (
            inputtotalAmount &&
            vendor.totalAmt &&
            vendor.totalAmt.toString()
                .toLowerCase()
                .indexOf(inputtotalAmount.toLowerCase()) === -1
        ) {
            return false;
        }
        }

         // paid Amt
         if (vendor.paidAmt) {

          if (
            inputpaidAmount &&
            vendor.paidAmt &&
            vendor.paidAmt.toString()
                .toLowerCase()
                .indexOf(inputpaidAmount.toLowerCase()) === -1
        ) {
            return false;
        }
        }

        // due amount

        if (vendor.totalDueAmt) {

          if (
            inputdueAmount &&
            vendor.totalDueAmt &&
            vendor.totalDueAmt.toString()
                .toLowerCase()
                .indexOf(inputdueAmount.toLowerCase()) === -1
        ) {
            return false;
        }
        }

          // mode of delivery
          if (vendor.shippingType) {
            if (
              inputmodeDelivery &&
              vendor.shippingType.toLowerCase().indexOf(inputmodeDelivery.toLowerCase()) ===
                -1
            ) {
              return false;
            }
          }

        return true;
      });
    } else {
        return vendorList;
    }
  }

}
