import { Component, OnInit, Inject, ChangeDetectorRef } from '@angular/core';
import { MatDatepicker } from '@angular/material/datepicker';
import { MOMENT, UserService } from '@core/services';
import { UserSession } from '@core/models/user-session';
import { WarehouseApiService } from '@core/services/warehouse-api.service';
import { MatSnackBar } from '@angular/material/snack-bar';
import { HttpErrorResponse } from '@angular/common/http';
import { WarehouseService } from '../../warehouse.service';

@Component({
  selector: 'app-tax-category',
  templateUrl: './tax-category.component.html',
  styleUrls: ['./tax-category.component.scss']
})
export class TaxCategoryComponent implements OnInit {
  public sessionData: UserSession;
  public today = new Date();
  public categoryNm: string;
  public updateCategoryNm: string;
  public percentage: string;
  public updatePercentage: string;
  public date = this.today;
  public updateDate = this.today;
  public isEditCurrentCategory: number;
  public isEditFutureCategory: number;
  public categoryData: any;
  public currentCategoryData = [];
  public futureCategoryData = [];
  public updateTaxId: number;
  public isEnableAdd = true;
  public isEnableUpdate = true;
  constructor(
    userService: UserService,
    @Inject(MOMENT) private moment,
    private snackBar: MatSnackBar,
    private wareApiService: WarehouseApiService,
    private warehouseDataService: WarehouseService,
    private changeDetection: ChangeDetectorRef
  ) {
    this.sessionData = userService.getUser();
  }

  ngOnInit() {
    this.warehouseDataService.settingsData.next('tax-category');
    this.fetchTaxCategory();
  }

  validate() {
    if (this.categoryNm.length > 0 && this.categoryNm.length <= 10
      && Number(this.percentage) > 0 && Number(this.percentage) <= 100) {
      this.isEnableAdd = false;
    } else {
      this.isEnableAdd = true;
    }
  }

  updateValidate() {
    if (this.updateCategoryNm.length > 0 && this.updateCategoryNm.length < 10
      && Number(this.updatePercentage) > 0 && Number(this.updatePercentage) <= 100) {
      this.isEnableUpdate = false;
    } else {
      this.isEnableUpdate = true;
    }
  }

  fetchTaxCategory() {
    this.currentCategoryData = [];
    this.futureCategoryData = [];
    this.categoryData = [];
    const payload = {
      createdUsrId: this.sessionData.regId,
      isNotDropDown: true,
      orgGrpId: this.sessionData.orgGroupId,
      orgId: this.sessionData.orgId
    };
    this.wareApiService.fetchTaxCategory(payload).subscribe(
      response => {
        if (response && response.responseCode === 'E200') {
          this.categoryData = response.data;
          this.categoryData.forEach(element => {
            const startDt = new Date(element.startDt);
            if (startDt <= this.today) {
              this.currentCategoryData.push(element);
            } else {
              this.futureCategoryData.push(element);
            }
          });
        } else if (response && response.responseCode === 'E400') {
          this.snackBar.open(response.responseMessage, 'dismiss', {
            duration: 500
          });
        }
        this.changeDetection.detectChanges();
      },
      (errorResponse: HttpErrorResponse) => {
        if (errorResponse.status > 0) {
          this.snackBar.open(errorResponse.error.responseMessage, 'dismiss', {
            duration: 1000
          });
        } else {
          // network failure
          this.snackBar.open('Network slow', 'dismiss', {
            duration: 1000
          });
        }
      }
    );
  }

  addTax(categoryNm) {
    // if(this.currentCategoryData.length>0 ){
    //   this.currentCategoryData.forEach(element => {
    //     if(element.taxCatCode === categoryNm.toUppercase()){
    //       this.snackBar.open('Category Already Exists', 'dismiss', {
    //         duration: 2000
    //       });
    //     }
    //   });
    // }
    const startDt = this.moment(this.date).format('YYYY-MM-DD');
    const payload = {
      orgId: this.sessionData.orgId,
      orgGrpId: this.sessionData.orgGroupId,
      createdUsrId: this.sessionData.regId,
      startDt,
      taxCatCode: this.categoryNm,
      taxPercntg: this.percentage
    };
    this.wareApiService.insertTaxCategory(payload).subscribe(
      response => {
        if (response && response.responseCode === 'E200') {
          this.fetchTaxCategory();
          this.categoryNm = '';
          this.percentage = '';
          this.date = this.today;
          this.isEnableAdd = true;
        } else if (response && response.responseCode === 'E400') {
          this.snackBar.open(response.responseMessage, 'dismiss', {
            duration: 1000
          });
        }
      },
      (errorResponse: HttpErrorResponse) => {
        if (errorResponse.status > 0) {
          this.snackBar.open(errorResponse.error.responseMessage, 'dismiss', {
            duration: 1000
          });
        } else {
          // network failure
          this.snackBar.open('Network slow', 'dismiss', {
            duration: 1000
          });
        }
      }
    );
  }

  delete(i: number, data) {
    let taxCatId;
    if (data === 'current') {
      taxCatId = this.currentCategoryData[i].taxCatId;
    } else {
      taxCatId = this.futureCategoryData[i].taxCatId;
    }
    const payload = {
      createdUsrId: this.sessionData.regId,
      orgGrpId: this.sessionData.orgGroupId,
      orgId: this.sessionData.orgId,
      taxCatId
    };
    this.wareApiService.deactivateTaxCategory(payload).subscribe(
      response => {
        if (response && response.responseCode === 'E200') {
          if (data === 'current') {
            this.currentCategoryData.splice(i, 1);
            this.fetchTaxCategory();
          } else {
            this.futureCategoryData.splice(i, 1);
            this.fetchTaxCategory();
          }
        } else if (response && response.responseCode === 'E400') {
          this.snackBar.open(response.responseMessage, 'dismiss', {
            duration: 500
          });
        }
      },
      (errorResponse: HttpErrorResponse) => {
        if (errorResponse.status > 0) {
          this.snackBar.open(errorResponse.error.responseMessage, 'dismiss', {
            duration: 1000
          });
        } else {
          // network failure
          this.snackBar.open('Network slow', 'dismiss', {
            duration: 1000
          });
        }
      }
    );
  }

  cancelEdit() {
    this.isEditCurrentCategory = null;
    this.isEditFutureCategory = null;
  }

  editCategory(i: number, data) {
    if (data === 'current') {
      this.isEditCurrentCategory = i;
      this.isEditFutureCategory = null;
      this.updateCategoryNm = this.currentCategoryData[i].taxCatCode;
      this.updatePercentage = this.currentCategoryData[i].taxPercntg;
      this.updateDate = this.currentCategoryData[i].startDt;
      this.updateTaxId = this.currentCategoryData[i].taxCatId;
    } else {
      this.isEditFutureCategory = i;
      this.isEditCurrentCategory = null;
      this.updateCategoryNm = this.futureCategoryData[i].taxCatCode;
      this.updatePercentage = this.futureCategoryData[i].taxPercntg;
      this.updateDate = this.futureCategoryData[i].startDt;
      this.updateTaxId = this.futureCategoryData[i].taxCatId;
    }
    this.isEnableUpdate = false;
  }

  datePickerToggler(datePicker: MatDatepicker<Date>) {
    datePicker[datePicker.opened ? 'close' : 'open']();
  }

  updateTaxCategory(i: number, data) {
    const startDt = this.moment(this.updateDate).format('YYYY-MM-DD');
    const payload = {
      orgId: this.sessionData.orgId,
      orgGrpId: this.sessionData.orgGroupId,
      createdUsrId: this.sessionData.regId,
      startDt,
      taxCatCode: this.updateCategoryNm,
      taxCatId: this.updateTaxId,
      taxPercntg: this.updatePercentage
    };

    this.wareApiService.updateTaxCategory(payload).subscribe(
      response => {
        if (response && response.responseCode === 'E200') {
          if (data === 'current') {
             this.currentCategoryData[i].taxCatCode = this.updateCategoryNm;
            this.currentCategoryData[i].taxPercntg = this.updatePercentage;
            this.currentCategoryData[i].startDt = startDt;
            this.snackBar.open(response.responseMessage, 'done', {
              duration: 2000});

          } else {
            this.futureCategoryData[i].taxCatCode = this.updateCategoryNm;
            this.futureCategoryData[i].taxPercntg = this.updatePercentage;
            this.futureCategoryData[i].startDt = startDt;
            this.snackBar.open(response.responseMessage, 'done', {
              duration: 2000});
          }

          this.isEnableUpdate = true;
          this.fetchTaxCategory();
        } else if (response && response.responseCode === 'E400') {
          this.snackBar.open(response.responseMessage, 'dismiss', {
            duration: 500
          });
        }
      },
      (errorResponse: HttpErrorResponse) => {
        if (errorResponse.status > 0) {
          this.snackBar.open(errorResponse.error.responseMessage, 'dismiss', {
            duration: 1000
          });
        } else {
          // network failure
          this.snackBar.open('Network slow', 'dismiss', {
            duration: 1000
          });
        }
      }
    );
    this.isEditCurrentCategory = null;
    this.isEditFutureCategory = null;
  }
}
