import { Component, OnInit, OnDestroy, AfterViewInit, ViewChild, ElementRef, ChangeDetectorRef, Inject } from '@angular/core';
import { MatSidenav, MatSnackBar, MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '../../../../../../node_modules/@angular/material';
import { WarehouseApiService } from '@core/services/warehouse-api.service';
import { WarehouseService } from '@app/modules/warehouse/warehouse.service';
import { UserSession } from '@core/models/user-session';
import { UserService } from '@core/services';
import { DialogData } from '../add-product/add-product.component';
import { HttpErrorResponse } from '@angular/common/http';
@Component({
  selector: 'app-part-product-locations',
  templateUrl: './part-product-locations.component.html',
  styleUrls: ['./part-product-locations.component.scss']
})
export class PartProductLocationsComponent implements OnInit {
  private sessionData: UserSession;
    storagSheds: any;
    storagRows: any;
    shedNm: string;
    rowNo: string;
    columnNo: string;
    tierLocationNm: string;
    shedId: any;
    rowId: any;
    storagColumns: any;
    columnId: any;
    storagShelfs: any;
    tierId: any;
    closeValue: any;
    confirmBtn = false;
    constructor(
        userService: UserService,
        private warehouseService: WarehouseService,
        private wareHouseApiService: WarehouseApiService,
        private snackBar: MatSnackBar,
        public dialogRef: MatDialogRef<PartProductLocationsComponent>,
        private dialog: MatDialog,
      @Inject(MAT_DIALOG_DATA) public data: DialogData) {
        this.sessionData = userService.getUser();
      }
      ngOnInit() {
          this.fetchShedRef();
      }
  onNoClick(): void {
    this.dialogRef.close();
  //   console.log('clseda data', this.data);
  }
  fetchShedRef() {
      const payload = {
        orgId: this.sessionData.orgId,
        orgGrpId: this.sessionData.orgGroupId,
        createdUsrId: this.sessionData.regId,
      };
      this.wareHouseApiService.fetchShedRef(payload).subscribe(
        response => {
          if (response && response.responseCode === 'E200') {
            // console.log(response.data);
        //     // const shedName = response.shedNm;
        //     const sheds = [];
        //    this.data.forEach(el => {
        //    sheds.push({shedName : el.shedNm});
        //    console.log(this.data);
        // });
            this.storagSheds = response.data;
            // console.log(this.storagSheds);
            // for ( i = 0; i <= this.storagSheds.length; i++) {
            //  const sheds = this.storagSheds.shedNm;
            //  console.log(sheds);
            // }
          }
        });
    }
    locationSearch1(value) {
      this.shedNm = value.shedNm;
      this.shedId = value.shedId;
      this.fetchRowRef();
      this.enableConformBtn();
     }
  fetchRowRef() {
    this.rowNo = null;
    this.columnNo = null;
    this.tierLocationNm = null;		
    this.storagRows = [];
    this.storagColumns = [];
    this.storagShelfs = [];

      const payload = {
        orgId: this.sessionData.orgId,
        orgGrpId: this.sessionData.orgGroupId,
        createdUsrId: this.sessionData.regId,
        shedId: this.shedId
      };
      this.wareHouseApiService.fetchShedRowxref(payload).subscribe(
        response => {
          if (response && response.responseCode === 'E200') {
            this.storagRows = response.data;
          } else if (response && response.responseCode === 'E400') {
            this.snackBar.open(response.responseMessage, 'close', {
              duration: 2,
            });
          }
        },
          (errorResponse: HttpErrorResponse) => {
            if (errorResponse.status > 0) {
              this.snackBar.open(errorResponse.error.responseMessage, 'dismiss', {
                duration: 3000,
              });
            } else {
              // network failure
              this.snackBar.open('Network slow', 'dismiss', {
                duration: 3000,
              });
            }
          }
        );
    }
    locationSearch2 (value) {
      this.rowNo = value.rowNo;
      this.rowId = value.rowId;
      this.fetchShedColumnRef();
      this.enableConformBtn();
    }
   fetchShedColumnRef() {
    this.columnNo = null;		
    this.tierLocationNm = null;		
    this.storagColumns = [];
    this.storagShelfs = [];
    
    const payload = {
      orgId: this.sessionData.orgId,
      orgGrpId: this.sessionData.orgGroupId,
      createdUsrId: this.sessionData.regId,
      shedId: this.shedId,
      rowId: this.rowId
    };
    this.wareHouseApiService.fetchShedColumnRef(payload).subscribe(
      response => {
        if (response && response.responseCode === 'E200') {
          this.storagColumns = response.data;
        } else if (response && response.responseCode === 'E400') {
          this.snackBar.open(response.responseMessage, 'close', {
            duration: 2,
          });
        }
      },
        (errorResponse: HttpErrorResponse) => {
          if (errorResponse.status > 0) {
            this.snackBar.open(errorResponse.error.responseMessage, 'dismiss', {
              duration: 3000,
            });
          } else {
            // network failure
            this.snackBar.open('Network slow', 'dismiss', {
              duration: 3000,
            });
          }
        }
      );
  }
  locationSearch3(value) {
    this.columnNo = value.columnNo;
    this.columnId = value.columnId;
    this.fetchShedTierxref();
    this.enableConformBtn();
  }
  fetchShedTierxref() {
    this.tierLocationNm = null;		
    this.storagShelfs = [];

    const payload = {
      orgId: this.sessionData.orgId,
      orgGrpId: this.sessionData.orgGroupId,
      createdUsrId: this.sessionData.regId,
      shedId: this.shedId,
      rowId: this.rowId,
      columnId: this.columnId
    };
    this.wareHouseApiService.fetchShedTierxref(payload).subscribe(
      response => {
        if (response && response.responseCode === 'E200') {
          this.storagShelfs = response.data;
        } else if (response && response.responseCode === 'E400') {
          this.snackBar.open(response.responseMessage, 'close', {
            duration: 2,
          });
        }
      },
        (errorResponse: HttpErrorResponse) => {
          if (errorResponse.status > 0) {
            this.snackBar.open(errorResponse.error.responseMessage, 'dismiss', {
              duration: 3000,
            });
          } else {
            // network failure
            this.snackBar.open('Network slow', 'dismiss', {
              duration: 3000,
            });
          }
        }
      );
  }
  locationSearch4(value) {
    // this.columnId = value.columnId;
    this.warehouseService.dataFromLocations(value);
  //   this.closeValue = value;
    // console.log('shelf details', value);
    this.tierLocationNm = value.tierNo;
    this.tierId = value.tierId;
    this.enableConformBtn();
  }
  // storageLocation() {
  //   const loc = this.shedNm + '/' + this.rowNo + '/' + this.columnNo + '/' + this.tierLocationNm;
  //   this.partsListForm.get('inventoryLocation').patchValue(loc);
  //   // this.isOpenDialog = false;
  // }

  enableConformBtn() {
    if (this.shedNm && this.rowNo && this.columnNo && this.tierLocationNm) {
      this.confirmBtn = true;
    } else {
      this.confirmBtn = false;
    }
  }
}
