import { Component, OnInit, AfterViewInit, ChangeDetectionStrategy, ChangeDetectorRef } from '@angular/core';
import { Router } from '@angular/router';
import { AbstractControl } from '@angular/forms';
import { WarehouseService } from '../../warehouse.service';
import { UtilService } from '@core/services';

@Component({
  selector: 'app-add-to-inventory',
  templateUrl: './add-to-inventory.component.html',
  styleUrls: ['./add-to-inventory.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class AddToInventoryComponent implements OnInit, AfterViewInit {
  InventoryControl: AbstractControl;
  public Raise = false;
  checked = this.utilService.generateUniqueId();
  constructor(
    private router: Router,
    private utilService: UtilService,
    private warehouseDataService: WarehouseService,
    private changeDetection: ChangeDetectorRef
  ) {}

  ngOnInit() {}
  ngAfterViewInit() {
    this.warehouseDataService.purchaseData.subscribe(val => {
      if (val && val === 'add-to-list') {
        this.Raise = true;
      } else {
        this.Raise = false;
      }
      // this.changeDetection.detectChanges();
    });
  }

  addToList() {
    this.warehouseDataService.vendorData.next(null);
    this.Raise = true;
  }

  hideExcelUpload() {
    this.Raise = false;
  }
  openExcel() {
    this.router.navigate(['/warehouse/inventory-excel-upload']);
  }
}
