import { Component, OnInit } from '@angular/core';
import { WarehouseService } from '@app/modules/warehouse/warehouse.service';
import { WarehouseApiService } from '@core/services/warehouse-api.service';
import { MatSnackBar } from '@angular/material';
import { UserService, ReportsService } from '@core/services';
import { UserSession } from '@core/models/user-session';
import { Router } from '@angular/router';

@Component({
  selector: 'app-pick-items',
  templateUrl: './pick-items.component.html',
  styleUrls: ['./pick-items.component.scss']
})
export class PickItemsComponent implements OnInit {
  CusNm_SoNum = {
    customerNm: '',
    saleOrderNo: '',
    saleOrderId: ''
  };
  dataFromPickIcon = [];
  showPrintButton: boolean;
  index: any;
  private sessionData: UserSession;
  saleOrderId: number;
  showDetails = false;
  fetcLocations = [];
  saleOrderDetailedId: any;
  PicklistDetails: any;
  showEditPicklisst = false;
  stockTobePicked: any;
  dateToSaleHistory = new Date();
  headings = {
    PicklistHeading: '',
    collectStockText: ''
  };

  constructor(
    private warehouseService: WarehouseService,
    private warehouseApiService: WarehouseApiService,
    private snackBar: MatSnackBar,
    private userService: UserService,
    private router: Router,
    private reportsApiService: ReportsService
  ) {
    this.sessionData = this.userService.getUser();
  }

  ngOnInit() {
    this.saleOrderId = this.warehouseService.getSaleOrderId();

    this.warehouseService.saleOrderData.subscribe(res => {
      let data = [];
      data = res;
      this.CusNm_SoNum.customerNm = res.customerNm;
      this.CusNm_SoNum.saleOrderNo = res.saleOrderNo;
      this.CusNm_SoNum.saleOrderId = res.saleOrderId;
      this.dataFromPickIcon = data[0];
      if (this.dataFromPickIcon.length) {
        this.dataFromPickIcon.forEach(element => {
          if (element.collectedQty === element.orderQty) {
            this.showPrintButton = true;
          } else {
            this.showPrintButton = false;
          }
          if (element.picklistInfoId !== null) {
            element['shouPlusIcon'] = true;
          } else {
            element['shouPlusIcon'] = false;
          }
        });
      }
    });
    this.dateToSaleHistory = this.warehouseService.getSaleHistoryFromDate();
    this.picklistDetails();
  }
  fetchPickListLocation(data, index) {
    const i = this.dataFromPickIcon.findIndex(dat => dat === data);
    // data['fetchLocations'] = this.fetcLocations[0];
    this.index = index;
    if (index === i) {
      this.showDetails = !this.showDetails;
      const payload = {
        createdUsrId: this.sessionData.regId,
        orgGrpId: this.sessionData.orgGroupId,
        orgId: this.sessionData.orgId,
        saleOrderId: this.saleOrderId,
        saleOrderDetailedId: data.saleOrderDetailedId
      };
      this.warehouseApiService.fetchPickListLocations(payload).subscribe(
        response => {
          if (response.responseCode === 'E200') {
            data['fetchLocations'] = response.data;
            this.fetcLocations.push(response.data);
            this.fetcLocations[0].forEach(element => {
              const loc =
                element.storageShed +
                '/' +
                element.rowLocation +
                '/' +
                element.columnLocation +
                '/' +
                element.tierLocation;
              element['location'] = loc;
            });
          } else if (response.responseCode === 'E400') {
            this.snackBar.open('NO LOCATIONS FOUND IN INVENTORY', 'undo', {
              duration: 3000
            });
          }
        },
        () => {
          this.snackBar.open('something went wrong', 'undo', {
            duration: 3000
          });
        }
      );
    }
  }
  picklistDetails() {
    const payload = {
      createdUsrId: this.sessionData.regId,
      orgGrpId: this.sessionData.orgGroupId,
      orgId: this.sessionData.orgId,
      saleOrderId: this.saleOrderId,
      saleOrderDetailedId: this.saleOrderDetailedId
    };
    this.warehouseApiService.fetchPickListDetails(payload).subscribe(
      response => {
        if (response.responseCode === 'E200') {
          const data = [];
          data.push(response.data);
          this.warehouseService.editPickIcon(data);
          this.dataFromPickIcon = data[0];
        } else if (response.responseCode === 'E400') {
          this.snackBar.open(response.responseMessage, 'undo', {
            duration: 3000
          });
        }
      },
      () => {
        this.snackBar.open('something went wrong', 'undo', { duration: 3000 });
      }
    );
  }
  // open picklist locations table
  dataFromSelTablets(data, index) {
    this.index = index;
    this.showDetails = !this.showDetails;
  }
  // when click on plus icon it shows picklist screen
  showPicklist(data, listDetails, index, items?) {
    this.index = index;
    if (data === 'picklist') {
      this.saleOrderDetailedId = listDetails.saleOrderDetailedId;
      this.warehouseService.setSaleOrderDetailId(this.saleOrderDetailedId);
      this.PicklistDetails = listDetails;
      this.stockTobePicked = this.PicklistDetails.stockToBePicked;
      this.showEditPicklisst = true;
      const { previousQty, picklistInfoId, ...remaining } = listDetails;
      this.headings.PicklistHeading = 'PICK LIST';
      this.headings.collectStockText = 'COLLECT STOCK';
      this.warehouseService.setPickItemsHeadings(this.headings);
      this.warehouseService.fetchPicklistDetailsData(remaining);
    } else if (data === 'editPicklist' && this.fetcLocations) {
      if (this.fetcLocations.length > 0) {
        listDetails['index'] = this.index;
      this.warehouseService.setSaleOrderDetailId(this.saleOrderDetailedId);
        listDetails.partId = items.partId;
        this.showEditPicklisst = true;
        this.headings.PicklistHeading = 'UPDATE PICK LIST';
        this.headings.collectStockText = 'UPDATE COLLECT STOCK';
        this.warehouseService.setPickItemsHeadings(this.headings);
        this.warehouseService.fetchPicklistDetailsData(listDetails);
      }
    }
    this.warehouseService.pickListIndex.next(this.index);
    this.router.navigate(['warehouse/picklist']);
  }
  backToSaleHistory() {
    this.warehouseService.setSaleHistoryFromDate(this.dateToSaleHistory);
    this.router.navigate(['warehouse/warehouse-sale-history']);
  }

  // Pick list print
  picklistPrint(saleOrderId) {
    const queryParams = {
      orgId : this.sessionData.orgId,
      saleOrderId : saleOrderId
    };
    this.reportsApiService.fetchWarehouseOrderPicklistPrint(queryParams).subscribe(response => {
      if (response instanceof ArrayBuffer) {
        const picklistReport = new Blob([response], {
          type : 'application/pdf'
        });
        const reportURL = URL.createObjectURL(picklistReport);
        const win = window.open('', '_blank');
        win.location.href = reportURL;
        setTimeout(() => {
          win.print();
        }, 1000);
      } else if (response.responseCode) {
        this.snackBar.open('Something went wrong', 'dismiss', {
          duration: 800,
        });
      }
    },
    () => {
      this.snackBar.open('Something went wrong', 'dismiss', {
        duration: 800,
      });
    }
    );
  }
}
