import { Component, OnInit, Inject } from '@angular/core';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { MatDatepicker, MatSnackBar } from '@angular/material';
import { ActivatedRoute, Router, NavigationExtras } from '@angular/router';
import { UtilService } from '@core/services';
import { WarehouseService } from '@app/modules/warehouse/warehouse.service';
import { UserSession } from '@core/models/user-session';
import { UserService } from '@core/services/user.service';
import { WarehouseApiService } from '@core/services/warehouse-api.service';
import { DatePipe } from '@angular/common';
import { HttpErrorResponse } from '@angular/common/http';


@Component({
  selector: 'app-picklist-order-status',
  templateUrl: './picklist-order-status.component.html',
  styleUrls: ['./picklist-order-status.component.scss']
})
export class PicklistOrderStatusComponent implements OnInit {
  private sessionData: UserSession;
  public remarks;
  constructor(
    public dialog: MatDialog,
    private router: Router,
    userService: UserService,
    private utilService: UtilService,
    private warehouseService: WarehouseService,
    private snackBar: MatSnackBar,
    private datePipe: DatePipe,
    private warehouseApiService: WarehouseApiService,
    private dialogRef: MatDialogRef<PicklistOrderStatusComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any

  ) {
    this.sessionData = userService.getUser();
   }

  ngOnInit() {

  }
  // date picker

  changingOrderStatus() {
    const payload = {
      orgId: this.sessionData.orgId,
      orgGrpId: this.sessionData.orgGroupId,
      createdUsrId: this.sessionData.regId,
      saleOrderId: this.data.saleOrderId,
      remarks: this.remarks,
    };
    this.warehouseApiService.changeSaleOrderStatus(payload).subscribe(response => {
      if (response && response.responseCode === 'E200') {
         if (response.data === true) {
         this.dialogRef.close(response.data);
      }
      } else if (response && response.responseCode === 'E400') {
        this.snackBar.open(response.responseMessage, 'dismiss', {
          duration: 500,
        });
      }
    },
    (errorResponse: HttpErrorResponse) => {
      if (errorResponse.status > 0) {
        this.snackBar.open(errorResponse.error.responseMessage, 'dismiss', {
          duration: 500,
        });
      } else {
        // network failure
        this.snackBar.open('Network slow', 'dismiss', {
          duration: 500,
        });
      }
    }
    );
  }
  close() {
    this.dialogRef.close();
  }
}
