import { Component, OnInit, ChangeDetectorRef } from '@angular/core';
import { Observable, concat, of, Subject, from, Subscription } from 'rxjs';
import { UserSession } from '@core/models/user-session';
import { UserService } from '@core/services/user.service';
import { UtilService, ReportsService } from '@core/services';
import { WarehouseApiService } from '@core/services/warehouse-api.service';
import { WarehouseService } from '@app/modules/warehouse/warehouse.service';
import { debounceTime, distinctUntilChanged, filter, switchMap, map, catchError, tap, multicast, share } from 'rxjs/operators';
import { Router } from '@angular/router';
import { MatSnackBar } from '@angular/material';
import { objectEach } from 'highcharts';
import { HttpErrorResponse } from '@angular/common/http';
import { BarcodeComponent } from '../barcode/barcode.component';
import { MatDatepicker, MatDialog, MatDialogRef } from '@angular/material';

@Component({
  selector: 'app-sale-order',
  templateUrl: './sale-order.component.html',
  styleUrls: ['./sale-order.component.scss']
})
export class SaleOrderComponent implements OnInit {
  public customerShippingAddressId;
  public customerNm;
  public partNmOrNo;
  public qty;
  public unitPrice;
  public price;
  public gstTaxPercntg;
  public taxAmount;
  public totalAmt;
  public totalTaxAmt;
  public totalServiceAmt;
  public grossAmt;
  public costPrice;
  public barcode;
  taxError = false;
  public currencyType: any;
  customerOrorgList: any;
  addressList: any;
  isOrder = true;
  // orgList: any;
  // customerNameList: any;
  customerName$: Observable<any>;
  customerNameListinput$ = new Subject<string>();
  partName$: Observable<any>;
  partNameListinput$ = new Subject<string>();
  private sessionData: UserSession;
  partList: any;
  selectedPartList: any;
  partTableList = [];
  customerList: any;
  public table = false;
  constructor(
    private utilService: UtilService,
    private userService: UserService,
    private wareHouseApiService: WarehouseApiService,
    public dialog: MatDialog,
    private wareHouseDataService: WarehouseService,
    private snackBar: MatSnackBar,
    private router: Router,
    private changeDetection: ChangeDetectorRef,
    private reportsApiService: ReportsService
  ) {
  this.sessionData = userService.getUser();
  }

  ngOnInit() {
    this.fetchCurrencyType();
    this.wareHouseDataService.saleOrderData.next('sale-order');
    this.fetchCustomerDetails();
    this.fetchPartDetails();
  }

  fetchCurrencyType() {
    const payload = {
      orgId: this.sessionData.orgId,
      orgGrpId: this.sessionData.orgGroupId,
      createdUsrId: this.sessionData.regId,
    };
    this.wareHouseApiService.fetchCurrencyType(payload).subscribe(response => {
      if (response && response.responseCode === 'E200') {
        this.currencyType = response.data;
      } else if (response && response.responseCode === 'E400') {
        this.snackBar.open(response.responseMessage, 'dismiss', {
          duration: 500,
        });
      }
    }, (errorResponse: HttpErrorResponse) => {
      if (errorResponse.status > 0) {
        this.snackBar.open(errorResponse.error.responseMessage, 'dismiss', {
          duration: 500,
        });
      } else {
        // network failure
        this.snackBar.open('Network slow', 'dismiss', {
          duration: 500,
        });
      }
    });
  }

  /** navigate to barcode */
  openBarcode(data) {
    // this.router.navigate(['/warehouse/barcode']);
    const dialogRef = this.dialog.open(BarcodeComponent, {
      width: '700px',
      height: '500px',
    });
    dialogRef.afterClosed().subscribe(result => {
      if (result) {
        // data.patchValue({barcode: result });
        this.fetchBarcodePartDetails(result);
      }
    });
  }

  // to get list of customer names
  fetchCustomerDetails() {
    this.customerName$ = concat(
      of([]), // default items
      this.customerNameListinput$.pipe(
        debounceTime(200),
        distinctUntilChanged(),
        tap(console.log),
        filter(checkStringCount => {
          if (checkStringCount && checkStringCount.length > 1) {
            return true;
          }
        }),
        //  tap(() => this.people3Loading = true),
        switchMap(this.fetchcustomers.bind(this))
      )
    );
  }
  fetchcustomers(string) {
    const payload = {
      createdUsrId: this.sessionData.regId,
      orgGrpId: this.sessionData.orgGroupId,
      orgId: this.sessionData.orgId,
      customerNmOrNo: string,
      dropDown: true
    };
    return this.wareHouseApiService.fetchCustomerRegList(payload).pipe(
      map((response: any) => {
        if (response.responseCode === 'E200') {
          this.customerOrorgList = response.data;
          return response.data;
        } else if (response.response === 'E400') {
        }
      }),
      catchError(error => {
        return of([]);
      })
    );
  }
  // to get address details

  fetchAddressDetails() {
    const payload = {
      orgId: this.sessionData.orgId,
      orgGrpId: this.sessionData.orgGroupId,
      createdUsrId: this.sessionData.regId,
      orgCustomerId: this.customerList.orgCustomerId
    };
    this.wareHouseApiService.fetchCustomerShippingAddress(payload).subscribe(
      response => {
        if (response && response.responseCode === 'E200') {
          this.addressList = response.data;
        } else if (response && response.responseCode === 'E400') {
          this.snackBar.open(response.responseMessage, 'close', {
            duration: 2,
          });
        }
      }
    );
  }
  // to get part details
  fetchPartDetails() {
    this.partName$ = concat(
      of([]), // default items
      this.partNameListinput$.pipe(
        debounceTime(200),
        distinctUntilChanged(),
        tap(console.log),
        filter(checkStringCount => {
          if (checkStringCount && checkStringCount.length > 1) {
            return true;
          }
        }),
        //  tap(() => this.people3Loading = true),
        switchMap(this.fetchParts.bind(this))
      )
    );
  }
  fetchParts(string) {
    const payload = {
      createdUsrId: this.sessionData.regId,
      orgGrpId: this.sessionData.orgGroupId,
      orgId: this.sessionData.orgId,
      partNmOrNo: string,
      isSaleOrder: true
    };
    return this.wareHouseApiService.fetchPartDetails(payload).pipe(
      map((response: any) => {
        if (response.responseCode === 'E200') {
          return response.data;
        } else if (response.response === 'E400') {
        }
      }),
      catchError(error => {
        return of([]);
      })
    );
  }
  onAddcustomer(customer) {
    this.customerList = customer;
    this.fetchAddressDetails();

  }
  // to get part details in the table

  onAddPart(part) {
    const result = this.partTableList.find(val =>
      val.partId === part.partId
      );
      if (!result) {
        this.partTableList.push(part);
        this.table = true;
        this.partNmOrNo = null;
        this.isOrder = true;
      } else {
        this.snackBar.open('Part Name already exists', 'dismiss', {
          duration: 800,
        });
      }
  }

  /**
   * @description function for getting part details either by entering part Name or barcode manually
   * @param barcode
   */
  getPartDetails(barcode) {
    if (barcode) {
      this.fetchBarcodePartDetails(barcode);
    }
    this.isOrder = true;
  }


  calculatePrice(part) {
    const unitPrice = part.mrp || 0;
    if (!part.qty) {
      this.isOrder = true;
    } else {
      this.isOrder = false;
    }
    const qty = part.qty || 0;
    part.serviceAmt = qty * unitPrice;
    part.serviceAmt = Number((part.serviceAmt).toFixed(2));
    if (part.gstTaxPercntg >= 0) {
      this.calculateTaxAmount(part);
    }
  }
  calculateTaxAmount(part) {
    if (part.serviceAmt >= 0) {
      this.totalAmt = 0;
      this.totalTaxAmt = 0;
      this.totalServiceAmt = 0;
      //  const price = part.serviceAmt;
      //  const qty = part.qty;
      const gstTaxPercntg = part.gstTaxPercntg;
      if (part.gstTaxPercntg > 100) {
        this.snackBar.open('Tax should not be greater than 100', 'dismiss', {
          duration: 800,
        });
        part.gstTaxPercntg = null;
      }
      //  part.gstTaxAmt = Number((price * (gstTaxPercntg / 100)).toFixed(2));
      //  part.serviceAmt = qty * price;
      part.gstTaxAmt = part.serviceAmt * (gstTaxPercntg / 100);
      part.totalAmt = part.serviceAmt + part.gstTaxAmt;
      this.partTableList.forEach(val => {
        if (!val.qty) {
          this.isOrder = true;
        }
        this.totalAmt += val.totalAmt;
        this.totalTaxAmt += val.gstTaxAmt;
        this.totalServiceAmt += val.serviceAmt;
        this.grossAmt = this.totalAmt - this.totalTaxAmt;
      });
    }
  }


  /**
     @description to get part details which are present in inventory through barcode
     @param barcode
     @function fetchBarcodePartDetails
  */

  fetchBarcodePartDetails(barcode) {
    const payload = {
      orgId: this.sessionData.orgId,
      orgGrpId: this.sessionData.orgGroupId,
      createdUsrId: this.sessionData.regId,
      barcode: barcode
    };
    this.wareHouseApiService.fetchWarehouseInventory(payload).subscribe((response: any) => {
      if (response && response.responseCode === 'E200') {
        if (response.data.length > 0) {
          const part = response.data[0];
          this.table = true;
          this.partTableList.push(part);
        } else {
          this.snackBar.open('No Data Found', 'dismiss', {
            duration: 2000,
          });

        }
      } else if (response && response.responseCode === 'E400') {
      }
    this.changeDetection.detectChanges();

    });

  }
  insertOrder() {
    this.taxError = false;
 if (this.partTableList) {
  this.partTableList.forEach(val => {
    if (val.gstTaxPercntg <= 0 ) {
      this.snackBar.open('Please Enter Tax%', 'dismiss', {
        duration: 2000,
      });
      this.taxError = true;
    }
  }
    );
 } if (!this.customerShippingAddressId || (this.taxError)) {
      this.snackBar.open('Please Enter Mandatory fields', 'dismiss', {
        duration: 2000,
      });
    } else if (this.grossAmt <= 0 || !this.grossAmt) {
      this.snackBar.open('Please Enter Valid Quantity', 'dismiss', {
        duration: 2000,
      });
    } else {
      const payload = {
        createdUsrId: this.sessionData.regId,
        orgGrpId: this.sessionData.orgGroupId,
        orgId: this.sessionData.orgId,
        customerShippingAddressId: this.addressList[0].customerShippingAddressId,
        saleOrderItemDetails: this.partTableList,
        customerNm: this.customerList ? this.customerList.firstNm : null,
        orgCustomerId: this.customerList ? this.customerList.orgCustomerId : null,
        totalServiceAmt: this.totalServiceAmt,
        totalTaxAmt: this.totalTaxAmt,
        totalAmt: this.totalAmt,
        totalDueAmt: this.totalAmt,
        paidAmt: null
      };
      this.wareHouseApiService.insertSaleOrder(payload).subscribe((response): any => {
        if (response && response.responseCode === 'E200') {
          this.table = false;
          this.partTableList = [];
          this.customerNm = null;
          this.partNmOrNo = null;
          this.grossAmt = null;
          this.totalTaxAmt = null;
          this.totalAmt = null;
          this.customerShippingAddressId = null;
          this.isOrder = true;
          this.barcode = null;
          // this.snackBar.open(response.responseMessage, 'dismiss', {
          //   duration: 1000,
          // });
          this.saleOrderPrint(response.data);
        } else if (response && response.responseCode === 'E400') {
          this.snackBar.open(response.responseMessage, 'dismiss', {
            duration: 1000,
          });
        }
        this.changeDetection.detectChanges();
      });
    }
  }

  //Below function is used to Print Sale order
saleOrderPrint(saleOrderId) {
  const queryParams = {
    orgId : this.sessionData.orgId,
    loggedInUserId : this.sessionData.regId,
    saleOrderId : saleOrderId
  };
  this.reportsApiService.fetchWarehouseSaleOrderPrint(queryParams).subscribe(response => {
    if (response instanceof ArrayBuffer) {
      const saleOrderReport = new Blob([response], {
        type : 'application/pdf'
      });
      const reportURL = URL.createObjectURL(saleOrderReport);
      const win = window.open('', '_blank');
      win.location.href = reportURL;
      setTimeout(() => {
        win.print();
      }, 1000);
    } else if (response.responseCode) {
      this.snackBar.open('Something went wrong', 'dismiss', {
        duration: 800,
      });
    }
  },
  () => {
    this.snackBar.open('Something went wrong', 'dismiss', {
      duration: 800,
    });
  }
  );
}
  // delete operation
  deletePart(i) {
    const part = this.partTableList[i];
    this.grossAmt = this.grossAmt - part.serviceAmt;
    this.totalTaxAmt = this.totalTaxAmt - part.gstTaxAmt;
    this.totalAmt = this.grossAmt + this.totalTaxAmt;
    this.partTableList.splice(i, 1);
    if (!this.partTableList.length) {
      this.clear();
    }
  }

  // cancel
  clear() {
    this.partTableList = [];
    this.customerNm = null;
    this.partNmOrNo = null;
    this.grossAmt = null;
    this.totalTaxAmt = null;
    this.totalAmt = null;
    this.customerShippingAddressId = null;
    this.table = false;
    this.isOrder = true;
  }
}
