import { Component, OnInit, ElementRef, ViewChild, ChangeDetectorRef } from '@angular/core';
import { FormGroup, FormBuilder, FormArray } from '@angular/forms';
import { Router } from '@angular/router';
import { UserService } from '@core/services';
import { UserSession } from '@core/models/user-session';
import { WarehouseApiService } from '@core/services/warehouse-api.service';
import { MatSnackBar } from '@angular/material';
import { HttpErrorResponse } from '@angular/common/http';
import { WarehouseService } from '@app/modules/warehouse/warehouse.service';
import { Observable, Subject } from 'rxjs';
@Component({
  selector: 'app-manage-stock',
  templateUrl: './manage-stock.component.html',
  styleUrls: ['./manage-stock.component.scss']
})
export class ManageStockComponent implements OnInit {
  public date = new Date();
  manageStockForm: FormGroup;
  productLocation: number;
  sessionData: UserSession;
  dataFromStockTransfer: any;
  showLocationOrPart: string;
  inventoryLocations: any;
  partName$: Observable<any>;
  partNameListinput$ = new Subject<string>();
  shedId: any;
  shedNm: any;
  locName2$: Observable<any>;
  locNameListinput2$ = new Subject<string>();

  locName3$: Observable<any>;
  locNameListinput3$ = new Subject<string>();

  locName4$: Observable<any>;
  locNameListinput4$ = new Subject<string>();

  tierId: any;
  tierLocationNm: any;
  columnId: any;
  columnNo: any;
  rowId: any;
  rowNo: any;
  searchLocationKey: any;
  transferToListqty: '';
  previousHistory: any;
  showerror = false;
  @ViewChild('locaiton1')
  locationRef1: ElementRef<HTMLInputElement>;
  @ViewChild('location2')
  locationRef2: ElementRef<HTMLInputElement>;
  @ViewChild('location3')
  locationRef3: ElementRef<HTMLInputElement>;
  @ViewChild('location4')
  locationRef4: ElementRef<HTMLInputElement>;
  @ViewChild('list.qty')
  requiredStock: ElementRef<HTMLInputElement>;
  storagSheds: any;
  storagRows: any;
  storagColumns: any;
  storagShelfs: any;
  trsData = [];
  trnsQtyIndex: any;
  addClick: boolean;
  qty: number;
  transferToList = [];
  constructor(
    private _fb: FormBuilder,
    private router: Router,
    private userService: UserService,
    private warehouseApiService: WarehouseApiService,
    private snackBar: MatSnackBar,
    private warehouseService: WarehouseService,
    private changeDetection: ChangeDetectorRef
  ) {
    this.sessionData = this.userService.getUser();
  }

  ngOnInit() {
    this.initForm();
    this.warehouseService.stockTransferParts.subscribe(res => {
      if (res) {
        this.inventoryLocations = res;
        res.forEach(re => {
          this.dataFromStockTransfer = re;
        });
      }
    });
    this.fetchShedRef();
  }
  initForm() {
    this.manageStockForm = this._fb.group({
      assignLocation: this._fb.array([this.addGroup()])
    });
  }
  addGroup() {
    return this._fb.group({
      shedlocation: '',
      rowlocation: '',
      columlocation: '',
      tierlocation: '',
      transferQty: '',
      Previousqty: ''
    });
  }
  get manageFormArray() {
    return <FormArray>this.manageStockForm.get('assignLocation');
  }
  addNew(i) {
    this.addClick = false;
    this.manageFormArray.push(this.addGroup());
    this.trsData = [];
  }
  remove(i) {
    this.manageFormArray.removeAt(i);
    this.trnsQtyIndex = i; // using as a key for display addnew button
  }
  transferQty(event, index) {
    this.addClick = true;
    this.trnsQtyIndex = index;
    event.target.value = Math.abs(event.target.value);
    const number = Number(event.target.value);
    this.qty = number;
    if (this.qty === this.previousHistory.qty) {
      this.previousHistory['addButtonError'] = true;
    }
    if (number > this.previousHistory.qty) {
      this.showerror = true;
    } else {
      this.showerror = false;
    }
  }
  indexForAddIcon(i) {
    this.trnsQtyIndex = i;
  }
  showAssignLocation(_index, data) {
    this.productLocation = _index;
    this.previousHistory = data;
  }
  transferStock() {
    this.router.navigate(['warehouse/Stock-transfer']);
  }

  insertStockTransferRequest() {
    this.trsData = this.manageFormArray.value;
    this.trsData.forEach(re => {
      this.transferToList.push({
        currentColumnId: re.columlocation.columnId,
        currentRowId: re.rowlocation.rowId,
        currentTierId: re.tierlocation.tierId,
        currentShedId: re.shedlocation.shedId,
        qty: re.transferQty
      });
    });

    const payload = {
      createdUsrId: this.sessionData.regId,
      orgGrpId: this.sessionData.orgGroupId,
      orgId: this.sessionData.orgId,
      previousColumnId: this.previousHistory.columnId,
      previousRowId: this.previousHistory.rowId,
      previousShedId: this.previousHistory.shedId,
      previousTierId: this.previousHistory.tierId,
      previousInventoryId: this.previousHistory.inventoryId,
      transferToList: this.transferToList,
      partId: this.previousHistory.partId,
      qty: this.previousHistory.qty,
      receivedDt: this.previousHistory.receivedDt,
      expireDt: this.previousHistory.expireDt
    };
    this.warehouseApiService.insertStockTransferRequest(payload).subscribe(
      response => {
        if (response.responseCode === 'E200') {
          this.manageStockForm.reset();
          // this.router.navigate(['warehouse/inventory/Stock-transfer']);
        }
        if (response.responseCode === 'E400') {
          this.snackBar.open(response.responseMessage, 'undo', {
            duration: 3000
          });
        }
      },
      () => {
        this.snackBar.open('something went wrong', 'undo', { duration: 3000 });
      }
    );
  }

  // to fetch part details by entering specific location
  fetchShedRef() {
    const payload = {
      orgId: this.sessionData.orgId,
      orgGrpId: this.sessionData.orgGroupId,
      createdUsrId: this.sessionData.regId
    };
    this.warehouseApiService.fetchShedRef(payload).subscribe(response => {
      if (response && response.responseCode === 'E200') {
        this.storagSheds = response.data;
      }
    });
  }
  locationSearch1(value) {
    if (value) {
      this.shedNm = value.shedNm;
      this.shedId = value.shedId;
      this.fetchRowRef();
    }
  }
  fetchRowRef() {
    const payload = {
      orgId: this.sessionData.orgId,
      orgGrpId: this.sessionData.orgGroupId,
      createdUsrId: this.sessionData.regId,
      shedId: this.shedId
    };
    this.warehouseApiService.fetchShedRowxref(payload).subscribe(
      response => {
        if (response && response.responseCode === 'E200') {
          this.storagRows = response.data;
        } else if (response && response.responseCode === 'E400') {
          this.snackBar.open(response.responseMessage, 'close', {
            duration: 2
          });
        }
      },
      (errorResponse: HttpErrorResponse) => {
        if (errorResponse.status > 0) {
          this.snackBar.open(errorResponse.error.responseMessage, 'dismiss', {
            duration: 3000
          });
        } else {
          // network failure
          this.snackBar.open('Network slow', 'dismiss', {
            duration: 3000
          });
        }
      }
    );
  }
  locationSearch2(value) {
    if (value) {
      this.rowNo = value.rowNo;
      this.rowId = value.rowId;
      this.fetchShedColumnRef();
    }
  }
  fetchShedColumnRef() {
    const payload = {
      orgId: this.sessionData.orgId,
      orgGrpId: this.sessionData.orgGroupId,
      createdUsrId: this.sessionData.regId,
      shedId: this.shedId,
      rowId: this.rowId
    };
    this.warehouseApiService.fetchShedColumnRef(payload).subscribe(
      response => {
        if (response && response.responseCode === 'E200') {
          this.storagColumns = response.data;
        } else if (response && response.responseCode === 'E400') {
          this.snackBar.open(response.responseMessage, 'close', {
            duration: 2
          });
        }
      },
      (errorResponse: HttpErrorResponse) => {
        if (errorResponse.status > 0) {
          this.snackBar.open(errorResponse.error.responseMessage, 'dismiss', {
            duration: 3000
          });
        } else {
          // network failure
          this.snackBar.open('Network slow', 'dismiss', {
            duration: 3000
          });
        }
      }
    );
  }
  locationSearch3(value) {
    if (value) {
      this.columnNo = value.columnNo;
      this.columnId = value.columnId;
      this.fetchShedTierxref();
    }
  }
  fetchShedTierxref() {
    const payload = {
      orgId: this.sessionData.orgId,
      orgGrpId: this.sessionData.orgGroupId,
      createdUsrId: this.sessionData.regId,
      shedId: this.shedId,
      rowId: this.rowId,
      columnId: this.columnId
    };
    this.warehouseApiService.fetchShedTierxref(payload).subscribe(
      response => {
        if (response && response.responseCode === 'E200') {
          this.storagShelfs = response.data;
        } else if (response && response.responseCode === 'E400') {
          this.snackBar.open(response.responseMessage, 'close', {
            duration: 2
          });
        }
      },
      (errorResponse: HttpErrorResponse) => {
        if (errorResponse.status > 0) {
          this.snackBar.open(errorResponse.error.responseMessage, 'dismiss', {
            duration: 3000
          });
        } else {
          // network failure
          this.snackBar.open('Network slow', 'dismiss', {
            duration: 3000
          });
        }
      }
    );
  }

  locationSearch4(event) {
    if (event) {
      this.tierLocationNm = event.tierNo;
      this.tierId = event.tierId;
      event['showLocationOrPart'] = 'locations';
    }
  }
}
