import { Injectable, Inject } from '@angular/core';
import { BehaviorSubject } from 'rxjs';
import { APP_CONSTANTS } from '@app/app.constant';
import { PDFMAKE } from '@core/services';
import { AppConstant } from '@core/models/app-constant';

@Injectable({
  providedIn: 'root'
})
export class WarehouseService {
  historyFromDate = new Date();
  saleReturnFromDt = new Date();
  saleReturnToDt = new Date();
  historyToDate = new Date();
  purchaseOrderFromDate = new Date();
  purchaseOrderToDate = new Date();
  checkedSettings = '';
  private organizationInfo = {};
  pickItemsHeadings: any;
  // saleorderId for picklist
  saleOrderId: number;
  // saleorderDetailId for picklist component from pick items
  saleOrderDetailedId: number;
  // storing data from salehistory , while clicking pick icon we are sending data to pick list
  private pickIconDataSource = new BehaviorSubject<any>([]);
  pickIconData = this.pickIconDataSource.asObservable();
  // storing data from stock transfer to managestock component
  public stockTransferParts = new BehaviorSubject(null);
  // stockTransferPartsdata = this.stockTransferParts.asObservable();

  // sending data to add Product after locations search
  private locationsSourceData = new BehaviorSubject<any>([]);
  locationsData = this.locationsSourceData.asObservable();

  // sending data from add product to parts mapping
  public productMappingDataSource = new BehaviorSubject(null);

  // sending response data from stock-transfer to transfer-history component
  public transferHistorySource = new BehaviorSubject(null);
// transferHistory = this.transferHistorySource.asObservable();

// sending data from pick items component to picklist for fetchpicklistDetails function
private fetchpicklistDetailsSource  = new BehaviorSubject<any>([]);
fetchpicklistDetails = this.fetchpicklistDetailsSource.asObservable();

  // routing data
  public saleOrderData = new BehaviorSubject(null);
  public settingsData = new BehaviorSubject(null);
  public purchaseData = new BehaviorSubject(null);
  currencyType: {};

  constructor(@Inject(APP_CONSTANTS) private appConstants: AppConstant,
  @Inject(PDFMAKE) private pdfmake,

  ) {

  }
  createLocationData: any;
  public createStorage = new BehaviorSubject(null);
  public createLocation = new BehaviorSubject(null);
  public createStorageColumn = new BehaviorSubject(null);
  public partListData = new BehaviorSubject(null);
  public vendorData = new BehaviorSubject(null);
  public selectedPartsList = new BehaviorSubject(null);
  public poPartsList = new BehaviorSubject(null);
  public paymentsList = new BehaviorSubject(null);
  public pickListIndex = new BehaviorSubject(null);
  public creditData = new BehaviorSubject(null);

  // saleHistory data sending to packing list
  public saleHistory = new BehaviorSubject(null);
  // picklist data dending to Packing list
  public pickListData = new BehaviorSubject(null);
  // sending cartonNo from packing list component to carton Dialog
  public cartonData = new BehaviorSubject(null);
  // sending carton dialog data to packing list
  public selectCarton = new BehaviorSubject(null);
  // sending date from sale history component to pick list
  setSaleHistoryFromDate(date) {
    this.historyFromDate = date;
  }
  getSaleHistoryFromDate() {
    return this.historyFromDate;
  }
  setSaleReturnsFromDate(date) {
    this.saleReturnFromDt = date;
  }
  getSaleReturnsFromDate() {
    return this.saleReturnFromDt;
  }
  setSaleReturnsToDate(date) {
    this.saleReturnToDt = date;
  }
  getSaleReturnsToDate() {
    return this.saleReturnToDt;
  }
  setSaleHistoryToDate(date) {
    this.historyToDate = date;
  }
  getSaleHistoryToDate() {
    return this.historyToDate;
  }
  setPurchaseOrderFromDate(data) {
    this.purchaseOrderFromDate = data;
  }
  setPurchaseOrderToDate(data) {
    this.purchaseOrderToDate = data;
  }
  getPurchaseOrderFromDate() {
    return this.purchaseOrderFromDate;
  }
  getPurchaseOrderToDate() {
    return this.purchaseOrderToDate;
  }
  setPickItemsHeadings(data) {
    this.pickItemsHeadings = data;
  }
  getPickItemsHeadins() {
    return this.pickItemsHeadings;
  }
  // sending key based on checked state in settings page
  setSettingsChecked(component) {
    this.checkedSettings = component;
  }
  getSettingsChecked() {
    return this.checkedSettings;
  }
  // sending saleOrderDetailedId
  setSaleOrderDetailId(id) {
    this.saleOrderDetailedId = id;
  }
  getSaleOrderDetailId() {
    return this.saleOrderDetailedId;
  }
  //  sending sale orderId from sale history component to picklist
  setSaleOrderId(id) {
    this.saleOrderId = id;
  }
  getSaleOrderId() {
    return this.saleOrderId;
  }
  editPickIcon(data: any[]) {
    this.pickIconDataSource.next(data);
  }
  dataInStockTransfer(data: any[]) {
    this.stockTransferParts.next(data);
  }
  dataFromLocations(data: any[]) {
    this.locationsSourceData.next(data);
  }

  getBucketsConfig() {
    return {
      accessKeyId: this.appConstants.buckets.warehouse.accessKeyId,
      secretAccessKey: this.appConstants.buckets.warehouse.secretAccessKey,
      region: this.appConstants.buckets.warehouse.region
    };
  }
  stockTransferHistory(data: any[]) {
    this.transferHistorySource.next(data);
  }
  fetchPicklistDetailsData(data: any[]) {
    this.fetchpicklistDetailsSource.next(data);
  }


  // barcode

  getBarcodePrints(data) {
    console.log(data, '-=-=-=');
    const dd = {
      content: [],
      pageMargins: [
        parseInt(data[0].labelLeftMargin, 10),
        parseInt(data[0].labelTopMargin, 10),
        parseInt(data[0].labelRightMargin, 10),
        parseInt(data[0].labelBottomMargin, 10)
      ],
      // 45.5197
      pageSize: { height: 45.5197, width: 260.787 }
    };
    for (let i = 0; i < data.length; i++) {
      dd.content.push({
        table: {
          widths: ['55%', '45%'],
          body: [
            [
              {
                table: {
                  widths: ['55%', '45%'],
                  body: [
                    [
                      {
                        stack: [
                          {
                            text: data[i].partNm,
                            fontSize: 7,
                            bold: true,
                            noWrap: false
                          },
                          {
                            text: data[i].currencyType + data[i].mrp,
                            fontSize: 8,
                            bold: true,
                            noWrap: false
                          },
                          {
                            text: data[i].batchNo,
                            fontSize: 7,
                            bold: true,
                            noWrap: false,
                            margin: [0, 0, 0, 1.5]
                          },
                          // {
                          //   // colSpan: 2,
                          //   text: this.organizationInfo[0].orgName,
                          //   fontSize: 7,
                          //   bold: true,
                          //   noWrap: true
                          // }
                        ]
                      },
                      {
                        stack: [
                          {
                            image: data[i].imageObj,
                            height: parseInt(data[i].labelHeight, 10),
                            width: parseInt(data[i].labelWidth, 10),
                          },
                          {
                            text: data[i].barcode,
                            fontSize: 6,
                            bold: true,
                            alignment: 'center'
                          }
                        ]
                      }
                    ]
                    // [
                    //   {
                    //     colSpan: 2,
                    //     text: data[i].name.slice(0, 15) + (data[i].name.length > 15 ? '...' : ''),
                    //     fontSize: 4,
                    //     bold: true,
                    //     noWrap: true,
                    //   },
                    //   ''
                    // ]
                  ]
                },
                layout: {
                  hLineWidth: () => 0,
                  vLineWidth: () => 0,
                  paddingTop: () => 2,
                  paddingBottom: () => 2
                }
              }
            ]
          ]
        },
        layout: 'noBorders'
      });
    }
    this.pdfmake.createPdf(dd).print();
  }
  getBucketName() {
    // return {
    //   bucketURL: this.appConstants.buckets.warehouse.BucketURL,
    //   url: this.appConstants.buckets.warehouse.url
    // };
    return {
      bucketURL: this.appConstants.buckets.profileBuckt.BucketURL,
      url: this.appConstants.buckets.profileBuckt.url
    };
  }
}
