import { Pipe, PipeTransform } from '@angular/core';
import { DatePipe } from '@angular/common';

@Pipe({
  name: 'saleFilter'
})
export class SaleFilterPipe implements PipeTransform {

  patientNm: any;
  private datePipe: DatePipe;
  transform(
    saleList: any,
    inputDate: string,
    inputSONumber: string,
    inputCustomerName: string,
    inputPhoneNumber: string,
    inputOrderStatus: string,
    inputTotalAmt: string,
    inputPaidAmt: string,
    inputDueAmt: string,
  ) {
    if (saleList && saleList.length) {
      return saleList.filter(inventory => {
        if (
          (inputCustomerName && !inventory.customerNm) ||
          (inputOrderStatus && !inventory.orderStatus) ||
          ( inputDate && !inventory.orderDt) ||
          (inputSONumber && !inventory.saleOrderNo) ||
          ( inputPhoneNumber && !inventory.mobileNo)
        ) {
          return false;
        }
        if (inventory.customerNm) {
          if (
            inputCustomerName &&
            inventory.customerNm.toLowerCase().indexOf(inputCustomerName.toLowerCase()) ===
              -1
          ) {
            return false;
          }
        }

        // order Date
        if (inventory.orderDt) {
          const datePipe = new DatePipe('en-US');
          inventory.orderDt = datePipe.transform(inventory.orderDt, 'yyyy/MM/dd');
          if (inputDate && inventory.orderDt.toLowerCase().indexOf(inputDate.toLowerCase()) === -1) {
              return false;
          }
        }

        // sale order no
        if (inventory.saleOrderNo) {
          if (
            inputSONumber &&
            inventory.saleOrderNo.toLowerCase().indexOf(inputSONumber.toLowerCase()) ===
              -1
          ) {
            return false;
          }
        }

        // mobile no

        if (inventory.mobileNo) {
          if (
            inputPhoneNumber &&
            inventory.mobileNo.toLowerCase().indexOf(inputPhoneNumber.toLowerCase()) ===
              -1
          ) {
            return false;
          }
        }



        // inventory status
        if (inventory.orderStatus) {
          if (
            inputOrderStatus &&
            inventory.orderStatus.toLowerCase().indexOf(inputOrderStatus.toLowerCase()) === -1
          ) {
            return false;
          }
        }
      // paid Amt
        if (inventory.paidAmt) {

          if (
            inputPaidAmt &&
            inventory.paidAmt &&
            inventory.paidAmt.toString()
                .toLowerCase()
                .indexOf(inputPaidAmt.toLowerCase()) === -1
        ) {
            return false;
        }
        }

        // due amt

        if (inventory.totalDueAmt) {

          if (
            inputDueAmt &&
            inventory.totalDueAmt &&
            inventory.totalDueAmt.toString()
                .toLowerCase()
                .indexOf(inputDueAmt.toLowerCase()) === -1
        ) {
            return false;
        }
        }

        // total Amt
        if (inventory.totalAmt) {
          if (
            inputTotalAmt &&
            inventory.totalAmt &&
            inventory.totalAmt.toString()
                .toLowerCase()
                .indexOf(inputTotalAmt.toLowerCase()) === -1
        ) {
            return false;
        }
        }

        return true;
      });
    } else {
        return saleList;
    }
  }
}
