import { Component, OnInit, ChangeDetectorRef } from '@angular/core';
import { FormGroup, FormBuilder } from '@angular/forms';
import { MatDatepicker, MatSnackBar } from '@angular/material';
import { WarehouseApiService } from '@core/services/warehouse-api.service';
import { UserService } from '@core/services';
import { UserSession } from '@core/models/user-session';
import { Observable } from 'rxjs';
import { DatePipe } from '@angular/common';
import { WarehouseService } from '../../warehouse.service';
import { HttpErrorResponse } from '@angular/common/http';
@Component({
  selector: 'app-stock-transfer',
  templateUrl: './stock-transfer.component.html',
  styleUrls: ['./stock-transfer.component.scss']
})
export class StockTransferComponent implements OnInit {
  stockTransferForm: FormGroup;
  showRadioButtons = true;
  fromDate = '';
  toDate = '';
  sessionData: UserSession;
  searchLocationKey: string;
  errInSearch = {
    Parts: true,
    Locations: false
  };
  shedId: any;
  shedNm: any;
  tierId: any;
  tierLocationNm: any;
  columnId: any;
  columnNo: any;
  rowId: any;
  rowNo: any;
  saleHistoryForm: FormGroup;
  product$: Observable<any>;
  date = new Date();
  productInout: any;
  partOrProductNm: any;
  transferHistory = false;
  fetchStockHistory = [];
  blurScreen = false;
  storagSheds: any;
  storagRows: any;
  storagColumns: any;
  storagShelfs: any;
  fetchSaleHistory = false;
  searchDate = [];
  showHistory: boolean;
  searchIcon = true;
  productSearchRes: any;
  locationsSearchRes: any;
  partsSearch = true;
  locationsSearch: boolean;
  public maxDt = new Date();
  public partData: any;
  public inventoryParts: any;
  public isMangeStock = false;
  constructor(
    private warehouseApiService: WarehouseApiService,
    private snackBar: MatSnackBar,
    private userService: UserService,
    private fb: FormBuilder,
    private datePipe: DatePipe,
    private warehouseService: WarehouseService,
    private changeDetection: ChangeDetectorRef
  ) {
    this.sessionData = this.userService.getUser();
    this.fromDate = this.datePipe.transform(new Date(), 'yyyy-MM-dd');
    this.toDate = this.datePipe.transform(new Date(), 'yyyy-MM-dd');
  }

  ngOnInit() {
    this.inItForm();
  }

  inItForm() {
    this.saleHistoryForm = this.fb.group({
      search1: 'shed',
      search2: 'row',
      search3: 'column Location',
      search4: 'tier'
    });
  }

  // filter by selecting date to show stock history
  fetchHistoryByDate(items) {
    this.searchDate = this.searchDate.filter(res => {
      if (res.date === items.date) {
        return res;
      }
    });
  }

  errorValuesInPart(val) {
    this.transferHistory = false;
    if (val === 'parts') {
      this.errInSearch.Parts = true;
      this.errInSearch.Locations = false;
      this.partsSearch = true;
      this.locationsSearch = false;
    }
    if (val === 'locations') {
      this.errInSearch.Locations = true;
      this.errInSearch.Parts = false;
      this.partsSearch = false;
      this.locationsSearch = true;
    }
    this.inventoryParts = null;
    this.storagSheds = null;
    this.storagRows = null;
    this.storagColumns = null;
    this.storagShelfs = null;
  }

  transferStock() {
    this.showHistory = false;
    this.showRadioButtons = true;
    this.inventoryParts = null;
  }

  saleHistory() {
    this.showHistory = true;
    this.fetchStockTansferHistory('dummy');
    this.showRadioButtons = false;
    this.inventoryParts = null;
    this.isMangeStock = false;
  }

  datePickerToggler(datePicker: MatDatepicker<Date>) {
    datePicker[datePicker.opened ? 'close' : 'open']();
  }

  fetchHistory() {
    this.fromDate = this.datePipe.transform(this.fromDate, 'yyyy-MM-dd');
    this.toDate = this.datePipe.transform(this.toDate, 'yyyy-MM-dd');
    this.fetchStockTansferHistory('date');
  }

  fetchStockTansferHistory(data, event?) {
    const payload = {
      createdUsrId: this.sessionData.regId,
      fromDt: this.fromDate,
      orgGrpId: this.sessionData.orgGroupId,
      orgId: this.sessionData.orgId,
      toDt: this.toDate
    };
    if (data === 'location') {
      payload['rowLocation'] = this.rowNo;
      payload['columnLocation'] = this.columnNo;
      payload['tierLocation'] = this.tierLocationNm;
      payload['shedLocation'] = this.shedNm;
    }
    if (data === 'part') {
      payload['partId'] = event.partId;
    }
    if (this.shedId || this.partOrProductNm) {
      delete payload.fromDt;
      delete payload.toDt;
    }
    if (data === 'date') {
      payload['fromDt'] = this.fromDate;
      payload['toDt'] = this.toDate;
    }
    this.warehouseApiService.fetchStockTansferHistory(payload).subscribe(
      response => {
        if (response.responseCode === 'E200') {
          const arr = [];
          arr.push(response.data);
          this.fetchStockHistory = arr[0];
          this.searchDate = arr[0];
          this.warehouseService.transferHistorySource.next(this.fetchStockHistory);
        }
        if (response.responseCode === 'E400') {
          this.snackBar.open(response.responseMessage, 'Cancel', {
            duration: 3000
          });
        }
      },
      () => {
        this.snackBar.open('something went wrong', 'Cancel', { duration: 3000 });
      }
    );
  }

  fetchInventoryParts(event) {
    if (event.term.length > 2) {
      const payload = {
        createdUsrId: this.sessionData.regId,
        orgGrpId: this.sessionData.orgGroupId,
        orgId: this.sessionData.orgId,
        partNmOrNo: event.term
      };
      this.warehouseApiService.fetchInventoryParts(payload).subscribe(
        response => {
          if (response.responseCode === 'E200') {
            this.inventoryParts = response.data;
          } else if (response.responseCode === 'E400') {
            this.snackBar.open(response.responseMessage, 'Cancel', {
              duration: 2500
            });
          }
          this.changeDetection.detectChanges();
        },
        (errorResponse: HttpErrorResponse) => {
          if (errorResponse.status > 0) {
            this.snackBar.open(errorResponse.error.responseMessage, 'Cancel', {
              duration: 2500
            });
          } else {
            this.snackBar.open('Something Went Wrong', 'Cancel', {
              duration: 2500
            });
          }
        }
      );
    }
  }

  // part or product search
  fetchWarehouseInventory(data, event?) {
    const payload = {
      createdUsrId: this.sessionData.regId,
      orgGrpId: this.sessionData.orgGroupId,
      orgId: this.sessionData.orgId
    };
    if (data === 'part') {
      payload['partId'] = event.partId;
    }
    if (data === 'location') {
      payload['shedId'] = this.locationsSearchRes.shedId;
      payload['rowId'] = this.locationsSearchRes.rowId;
      payload['columnId'] = this.locationsSearchRes.columnId;
      payload['tierId'] = this.locationsSearchRes.tierId;
    }
    this.warehouseApiService
      .fetchWarehouseInventory(payload)
      .subscribe(response => {
        if (response && response.responseCode === 'E200') {
          this.productInout = response.data;
          this.warehouseService.stockTransferParts.next(this.productInout);
          if (!this.showHistory) {
            this.isMangeStock = true;
          }
        } else if (response && response.responseCode === 'E400') {
          this.snackBar.open(response.responseMessage, 'Cancel', {
            duration: 2500
          });
        }
        this.changeDetection.detectChanges();
      },
      (errorResponse: HttpErrorResponse) => {
        if (errorResponse.status > 0) {
          this.snackBar.open(errorResponse.error.responseMessage, 'Cancel', {
            duration: 2500
          });
        } else {
          this.snackBar.open('Something Went Wrong', 'Cancel', {
            duration: 2500
          });
        }
      });
  }

  productSearch(event) {
    if (event) {
      if (this.showHistory) {
        this.fetchStockTansferHistory('part', event);
      } else {
        this.fetchWarehouseInventory('part', event);
      }
    }
  }

  // to fetch part details by entering specific location
  fetchShedRef() {
    const payload = {
      orgId: this.sessionData.orgId,
      orgGrpId: this.sessionData.orgGroupId,
      createdUsrId: this.sessionData.regId
    };
    this.warehouseApiService.fetchShedRef(payload).subscribe(response => {
      if (response && response.responseCode === 'E200') {
        this.storagSheds = response.data;
      }
    });
  }

  locationSearch1(value) {
    if (value) {
      this.shedNm = value.shedNm;
      this.shedId = value.shedId;
      this.transferHistory = false;
      this.fetchSaleHistory = true;
      this.partOrProductNm = null;
      this.fetchRowRef();
    }
  }

  fetchRowRef() {
    const payload = {
      orgId: this.sessionData.orgId,
      orgGrpId: this.sessionData.orgGroupId,
      createdUsrId: this.sessionData.regId,
      shedId: this.shedId
    };
    this.warehouseApiService.fetchShedRowxref(payload).subscribe(
      response => {
        if (response && response.responseCode === 'E200') {
          this.storagRows = response.data;
        } else if (response && response.responseCode === 'E400') {
          this.snackBar.open(response.responseMessage, 'close', {
            duration: 2
          });
        }
      },
      (errorResponse: HttpErrorResponse) => {
        if (errorResponse.status > 0) {
          this.snackBar.open(errorResponse.error.responseMessage, 'dismiss', {
            duration: 3000
          });
        } else {
          // network failure
          this.snackBar.open('Network slow', 'dismiss', {
            duration: 3000
          });
        }
      }
    );
  }

  locationSearch2(value) {
    if (value) {
      this.rowNo = value.rowNo;
      this.rowId = value.rowId;
      this.fetchShedColumnRef();
    }
  }

  fetchShedColumnRef() {
    const payload = {
      orgId: this.sessionData.orgId,
      orgGrpId: this.sessionData.orgGroupId,
      createdUsrId: this.sessionData.regId,
      shedId: this.shedId,
      rowId: this.rowId
    };
    this.warehouseApiService.fetchShedColumnRef(payload).subscribe(
      response => {
        if (response && response.responseCode === 'E200') {
          this.storagColumns = response.data;
        } else if (response && response.responseCode === 'E400') {
          this.snackBar.open(response.responseMessage, 'close', {
            duration: 2500
          });
        }
      },
      (errorResponse: HttpErrorResponse) => {
        if (errorResponse.status > 0) {
          this.snackBar.open(errorResponse.error.responseMessage, 'dismiss', {
            duration: 3000
          });
        } else {
          // network failure
          this.snackBar.open('Network slow', 'dismiss', {
            duration: 3000
          });
        }
      }
    );
  }

  locationSearch3(value) {
    if (value) {
      this.columnNo = value.columnNo;
      this.columnId = value.columnId;
      this.fetchShedTierxref();
    }
  }

  fetchShedTierxref() {
    const payload = {
      orgId: this.sessionData.orgId,
      orgGrpId: this.sessionData.orgGroupId,
      createdUsrId: this.sessionData.regId,
      shedId: this.shedId,
      rowId: this.rowId,
      columnId: this.columnId
    };
    this.warehouseApiService.fetchShedTierxref(payload).subscribe(
      response => {
        if (response && response.responseCode === 'E200') {
          this.storagShelfs = response.data;
        } else if (response && response.responseCode === 'E400') {
          this.snackBar.open(response.responseMessage, 'close', {
            duration: 2
          });
        }
      },
      (errorResponse: HttpErrorResponse) => {
        if (errorResponse.status > 0) {
          this.snackBar.open(errorResponse.error.responseMessage, 'dismiss', {
            duration: 3000
          });
        } else {
          // network failure
          this.snackBar.open('Network slow', 'dismiss', {
            duration: 3000
          });
        }
      }
    );
  }

  locationSearch4(event) {
    if (event) {
      this.tierLocationNm = event.tierNo;
      this.tierId = event.tierId;
      event['showLocationOrPart'] = 'locations';
      this.locationsSearchRes = event;
      if (this.showHistory) {
        this.fetchStockTansferHistory('location');
      } else {
        this.fetchWarehouseInventory('location');
      }
    }
  }

  checkPart(event) {
    if (event) {
      this.searchIcon = false;
    } else {
      this.searchIcon = true;
    }
  }
}
