import { Pipe, PipeTransform } from '@angular/core';
import { DatePipe } from '@angular/common';

@Pipe({
  name: 'inventoryfilter'
})
export class InventoryfilterPipe implements PipeTransform {

  patientNm: any;
  private datePipe: DatePipe;
  transform(
    partList: any,
    inputpartName: string,
    inputbarcode: string,
    inputbatchNo: string,
    inputexpireDate: string,
    inputQty: string,
    inputUom: string,
    inputUnitcost: string,
    inputmrp: string
  ) {
    if (partList && partList.length) {
      return partList.filter(part => {
        if (
          (inputpartName && !part.partNmOrNo) ||
          (inputbarcode && !part.barcode) ||
          ( inputbatchNo && !part.batchNo) ||
          ( inputexpireDate && !part.expireDt) ||
          (inputQty && !part.qty) ||
          ( inputUom && !part.uom) ||
          ( inputUnitcost && !part.unitAmt) ||
          ( inputmrp && !part.mrp)

        ) {
          return false;
        }
        if (part.partNmOrNo) {
          if (
            inputpartName &&
            part.partNmOrNo.toLowerCase().indexOf(inputpartName.toLowerCase()) ===
              -1
          ) {
            return false;
          }
        }



        // sale order no
        if (part.barcode) {
          if (
            inputbarcode &&
            part.barcode.toLowerCase().indexOf(inputbarcode.toLowerCase()) ===
              -1
          ) {
            return false;
          }
        }

          // batch no
          if (part.batchNo) {
            if (
              inputbatchNo &&
              part.batchNo.toLowerCase().indexOf(inputbatchNo.toLowerCase()) ===
                -1
            ) {
              return false;
            }
          }
        // mobile no

        if (part.expireDt) {
          const datePipe = new DatePipe('en-US');
          part.expireDt = datePipe.transform(part.expireDt, 'yyyy/MM/dd');
        let date = part.expireDt.split('/');
        const  filterDate = date[2] + '/' + date[1] + '/' + date[0];
          if (inputexpireDate && filterDate.toLowerCase().indexOf(inputexpireDate.toLowerCase()) === -1) {
              return false;
          }
        }


        // part status
        if (part.qty) {

          if (
            inputQty &&
            part.qty &&
            part.qty.toString()
                .toLowerCase()
                .indexOf(inputQty.toLowerCase()) === -1
        ) {
            return false;
        }
        }
        if (part.uom) {
          if (
            inputUom &&
            part.uom.toLowerCase().indexOf(inputUom.toLowerCase()) === -1
          ) {
            return false;
          }
        }

        if (part.unitAmt) {

          if (
            inputUnitcost &&
            part.unitAmt &&
            part.unitAmt.toString()
                .toLowerCase()
                .indexOf(inputUnitcost.toLowerCase()) === -1
        ) {
            return false;
        }
        }

        // mrp
        if (part.mrp) {

          if (
            inputmrp &&
            part.mrp &&
            part.mrp.toString()
                .toLowerCase()
                .indexOf(inputmrp.toLowerCase()) === -1
        ) {
            return false;
        }
        }
        return true;
      });
    } else {
        return partList;
    }
  }
}
