import { Component, OnInit, ViewChild } from '@angular/core';
import { CommentsDialogComponent } from '@shared/components/comments-dialog/comments-dialog.component';
import { Validators, FormControl, FormBuilder, AbstractControl, FormGroup } from '../../../../../../node_modules/@angular/forms';
import { WarehouseService } from '@app/modules/warehouse/warehouse.service';
import { UtilService, UserService } from '@core/services';
import { Router } from '../../../../../../node_modules/@angular/router';
import { WarehouseApiService } from '@core/services/warehouse-api.service';
import { MatDialog, MatSidenav, MatDatepicker, MatSnackBar } from '../../../../../../node_modules/@angular/material';
import { ToastrService } from '../../../../../../node_modules/ngx-toastr';
import { UserSession } from '@core/models/user-session';
import { Subscription } from 'rxjs';
import { HttpErrorResponse } from '../../../../../../node_modules/@angular/common/http';
import { DatePipe } from '../../../../../../node_modules/@angular/common';


@Component({
  selector: 'app-purchase-history',
  templateUrl: './purchase-history.component.html',
  styleUrls: ['./purchase-history.component.scss']
})
export class PurchaseHistoryComponent implements OnInit {
  patientSearchForm: FormGroup;
  partsList: any;
  public apiSubscription: Subscription;
  private sessionData: UserSession;
  inputValue: string;
  patientList: any;
  placeholderValue: string;
  patientControl = new FormControl();
  patientData: any;
  patientmobile: any;
  public fromDate = new Date;
  public toDate = new Date;
  public maxDate = new Date;
  searchType: any;
  orderDt: '';
  inputPoNum: '';
  inputVendorNm: '';
  inputMobileNum: '';
  inputOrderStatus: '';
  inputTotalAmt: '';
  expDeliveryDt: '';
  patientSearch: any;
  patientSearchControl: AbstractControl;
  checked = this.utilService.generateUniqueId();
  start_time = new Date();
   poList: any;
  constructor(
    userService: UserService,
    private toaster: ToastrService,
    private dialog: MatDialog,
    private wareHouseApiService: WarehouseApiService,
    private router: Router,
    private formbuilder: FormBuilder,
    private datePipe: DatePipe,
    private utilService: UtilService,
    private snackBar: MatSnackBar,
    private warehoueDataService: WarehouseService
  ) {
    this.sessionData = userService.getUser();
  }

  ngOnInit() {
    this.patientSearchControl = new FormControl('purchaseOrderNo');
    this.placeholderValue = 'Search by PO Number';
    this.searchType = 'purchaseOrderNo';
    this.patientSearchControl.valueChanges.subscribe(value => {
      this.searchType = value;
      switch (this.searchType) {
        case 'purchaseOrderNo':
          this.placeholderValue = 'Search by PO Number';
          this.patientData = '';
          break;
        case 'vendorNm':
          this.placeholderValue = 'Search by Vendor Name';
          this.patientData = '';
          break;
        case 'orderStatus':
          this.placeholderValue = 'Search by Order Status';
          this.patientData = '';
          break;
        default:
          this.placeholderValue = 'Search by PO Number';
          this.patientData = '';
          break;
      }
    });
    this.fetchPurchaseOrders();
  }
  initForm() {
    this.patientSearchForm = this.formbuilder.group({
      patientSearchControl: this.formbuilder.control(null, Validators.required),
      fromDt: '',
      toDt: '',
      orderDt: '',
      serviceTable: ['', Validators.required]
    });
  }
  // newOrder() {
  //   this.router.navigate(['/warehouse/purchase-history']);
  // }
  datePickerToggler(datePicker: MatDatepicker<Date>) {
    datePicker[datePicker.opened ? 'close' : 'open']();
  }
  fetchPurchaseOrders() {
    this.fromDate = this.warehoueDataService.getPurchaseOrderFromDate();
    this.toDate = this.warehoueDataService.getPurchaseOrderToDate();
    const payload = {
      orgId: this.sessionData.orgId,
      orgGrpId: this.sessionData.orgGroupId,
      createdUsrId: this.sessionData.regId,
      fromDt: this.datePipe.transform(
        this.fromDate,
        'yyyy-MM-dd'
      ),
      toDt: this.datePipe.transform(
        this.toDate,
        'yyyy-MM-dd'
      )
    };

    if (this.searchType === 'purchaseOrderNo') {
      payload['purchaseOrderNo'] = this.patientData;
    } else if (this.searchType === 'vendorNm') {
      payload['vendorNm'] = this.patientData;
    } else if (this.searchType === 'orderStatus') {
      payload['orderStatus'] = this.patientData;
    }
    this.wareHouseApiService.fetchPurchaseOrders(payload).subscribe(response => {
      this.poList = response.data;
      if (response && response.responseCode === 'E200') {
        // this.toaster.success(response.responseMessage);
      } else if (response && response.responseCode === 'E400') {
        this.toaster.error(response.responseMessage);
      }
    });
  }

  onChangeDates() {
    if (!this.fromDate || !this.toDate) {
      return false;
    }
    this.warehoueDataService.setPurchaseOrderFromDate(this.fromDate);
    this.warehoueDataService.setPurchaseOrderToDate(this.toDate);
    this.fetchPurchaseOrders();
  }

  cancellingPurchaseOrder(po, index) {
    const dialogRef = this.dialog.open(CommentsDialogComponent, {
      data: {
        title: 'Delete Purchase Order',
        data: 'Reason for deleting Purchase Order'
      },
      disableClose: true
    });
    dialogRef.afterClosed().subscribe(result => {
      if (result) {
        const deletePayload = {
          orgId: this.sessionData.orgId,
          orgGrpId: this.sessionData.orgGroupId,
          createdUsrId: this.sessionData.regId,
          purchaseOrderId: po.purchaseOrderId,

          cancelReason: result,
          partProductInd: 'PA'
        };
        this.wareHouseApiService.cancellingPurchaseOrders(deletePayload).subscribe(
          (response: any) => {
            if (response && response.responseCode === 'E200') {
              this.fetchPurchaseOrders();
            } else if (response && response.responseCode === 'E400') {
              this.snackBar.open(response.responseMessage, 'close', {
                duration: 3000,
              });
            }
          }
          ,
          (errorResponse: HttpErrorResponse) => {
            if (errorResponse.status > 0) {
              this.snackBar.open(errorResponse.error.responseMessage, 'dismiss', {
                duration: 3000,
              });
            } else {
              // network failure
              this.snackBar.open('Network slow', 'dismiss', {
                duration: 3000,
              });
            }
          }
        );
      }
    });

  }
  navigation(po) {
    this.warehoueDataService.poPartsList.next(po);
    this.router.navigate(['/warehouse/purchase-order-invoice-breakdown']);
  }

  backtoPO() {
    this.router.navigate(['/warehouse/purchase/purchase-order']);
  }

  getPOList() {
    if (!this.patientData) {
      this.fetchPurchaseOrders();
    }
  }

  printPo(purchaseOrderId) {
    const queryParams = {
      orgId : this.sessionData.orgId,
      orgGrpId : this.sessionData.orgGroupId,
      createdUsrId: this.sessionData.regId,
      purchaseOrderId : purchaseOrderId
    };
    this.wareHouseApiService.purchaseOrderDetailsPrint(queryParams).subscribe(response => {
      if (response instanceof ArrayBuffer) {
        const poReport = new Blob([response], {
          type : 'application/pdf'
        });
        const reportURL = URL.createObjectURL(poReport);
        const win = window.open('', '_blank');
        win.location.href = reportURL;
        setTimeout(() => {
          win.print();
        }, 1000);
      } else if (response.responseCode) {
          this.toaster.error('Failure!', 'Something went wrong');
      }
    },
    () => {
      this.toaster.error('Failure!', 'Something went wrong');
    }
    );
  }
}
