import { Component, OnInit, Input } from '@angular/core';
import { MatSidenav, MatDialog } from '@angular/material';
import { CartonDialogComponent } from '../carton-dialog/carton-dialog.component';
import { WarehouseApiService } from '@core/services/warehouse-api.service';
import { WarehouseService } from '../../warehouse.service';
import { UserSession } from '@core/models/user-session';
import { UserService, ReportsService } from '@core/services';
import { MatSnackBar } from '@angular/material';
import { CartonListResponse } from './models/cartonListRespone';
import { PartsForPackingResponse } from './models/partsForPckingRes';
import { Request } from './models/requestInterface';
import { SavePackListRequest } from './models/insertPackListReq';
import { HttpErrorResponse } from '@angular/common/http';
@Component({
  selector: 'app-packing-list',
  templateUrl: './packing-list.component.html',
  styleUrls: ['./packing-list.component.scss']
})
export class PackingListComponent implements OnInit {
  @Input()
  sideNavRef: MatSidenav;
  resData: any;
  soNum: number;
  public packedQty;
  packedData = [];
  cartonDetails = [];
  cartonTypeId: any;
  cartonTypeNm: any;
  cartonResData = [];
  uniqueKey: any;
  pickListData = [];
  partNm: any;
  isPickedShow = false;
  isCartSelected: number;
  private sessionData: UserSession;
  cartonData: any[];
  saleOrderId: any;
  cartonNo: any;
  btnShow = false;
  partReturnData: any;
  batchItemList: any;
  _index: any;
  saleOrderNo: any;
  batchDataObj: {};
  pickedquantity: any;
  public cartonList = [];
  public partList: any;
  public batchIndex: number;
  public pickedItemIndex: number;
  public isBatchOpen = false;
  public isSave = true;
  public editedPackedQty: any;
  public editedPicklistInfoId: any;
  public cartonsInfoList: any;
  public qty: any;

  constructor(
    private dialog: MatDialog,
    private warehouseService: WarehouseService,
    private warehouseApiService: WarehouseApiService,
    private userService: UserService,
    private snackBar: MatSnackBar,
    private reportsApiService: ReportsService
  ) {
    this.sessionData = this.userService.getUser();
  }

  ngOnInit() {
    // Sale History data
    this.warehouseService.saleHistory.subscribe(res => {
      if (res) {
        this.resData = res;
        this.soNum = res.saleOrderNo;
        this.saleOrderId = res.saleOrderId;
        this.saleOrderNo = res.saleOrderNo;
        // this.fetchPartReturn();
        this.fetchPackedCartonsList();
        this.fetchPartsForPacking();
      }
    });
    // picklistData
    this.warehouseService.pickListData.subscribe(res => {
      if (res) {
        this.pickListData = res;
      }
    });
    // carton dialog data
    this.warehouseService.selectCarton.subscribe(result => {
      if (result) {
        result['partListInfoDto'] = [];
        this.cartonResData.push(result);
        this.cartonNo = this.cartonResData[0].cartonNo;
        this.isCartSelected = 0;
        this.getCortonList(0);
        this.warehouseService.cartonData.next(this.cartonResData);
      }
    });
  }

/**
 * @description Thsi method used to get the packed qty when to be packed table batch qty changed.
 * @param data
 * @param part
 */
  qtyChange(data, part) {
    if (this.cartonList.length) {
      this.cartonList.forEach(ele => {
        if (ele.partId === part.partId) {
          ele.batchPackingInfoDto.forEach(res => {
            if (res.picklistInfoId === data.picklistInfoId) {
              this.qty = res.qty;
            }
          });
        }
      });
    }
  }

  /**
   * @description This method is used to add the qty and batch to Carton batch.
   * @param i
   * @param j
   */
  addPackedCartonList(i, j) {
    if (this.cartonResData.length) {
      const part = this.partList[i];
      const batchList = part.batchWiseList[j];
      if (batchList.packedQty && batchList.packedQty <= batchList.toBePackedQty) {
        this.packedQty = batchList.packedQty;
        batchList['qty'] = this.packedQty;
        batchList.toBePackedQty = batchList.toBePackedQty - this.packedQty;
        const batchPackingObj = {
          batchPackingInfoDto: [batchList],
          orderQty: part.orderQty,
          partId: part.partId,
          partNm: part.partNm,
          partNo: part.partNo,
          saleOrderDetailedId: part.saleOrderDetailedId,
          saleOrderId: part.saleOrderId,
          toBePackedQty: part.toBePackedQty
        };
        if (this.cartonList.length) {
          const packedPart = this.cartonList.find(
            item => item.partId === part.partId
          );
          if (packedPart) {
            const packedBatch = packedPart.batchPackingInfoDto.find(
              ele => ele.picklistInfoId === batchList.picklistInfoId
            );
            if (packedBatch) {
              if (this.qty) {
                packedBatch.packedQty = this.qty + this.packedQty;
              } else {
                packedBatch.packedQty += this.packedQty;
              }
              packedBatch.toBePackedQty = batchList.toBePackedQty;
            } else {
              packedPart.batchPackingInfoDto.push(batchList);
            }
          } else {
            this.cartonList.push(batchPackingObj);
          }
        } else {
          this.cartonList.push(batchPackingObj);
        }
        this.isSave = false;
      } else {
        this.snackBar.open('Please Enter Correct Quentity', 'Cancel', {
          duration: 2500
        });
      }
    } else {
      this.snackBar.open('Please Add/Select Carton Box', 'Cancel', {
        duration: 2500
      });
    }
  }


  /**
   * @description This method is used to close the sidenav.
   */
  closeSideNav() {
    this.cartonNo = null;
    this.btnShow = false;
    this.partList = null;
    this.pickedItemIndex = null;
    this.batchIndex = null;
    this.cartonList = [];
    this.cartonsInfoList = [];
    this.cartonResData = [];
    this.sideNavRef.close();
  }

/**
 * @description This method i used to cancel the edited qty
 * @param data
 */

  closeEdit(data) {
    data.packedQty = this.editedPackedQty;
    this.editedPicklistInfoId = null;
  }

/**
 * @deprecated This method is used to delete the packed batch under the part based on packingInfoId by using API call.
 * @param data
 * @param item
 * @param j
 */
  deletePackingDetails(data, item, j) {
    if (data.packingInfoId) {
      const payload = {
        createdUsrId: this.sessionData.regId,
        orgId: this.sessionData.orgId,
        orgGrpId: this.sessionData.orgGroupId,
        packingInfoId: data.packingInfoId,
        saleOrderDetailedId: item.saleOrderDetailedId,
        saleOrderId: this.resData.saleOrderId
      };
      this.warehouseApiService.deletePackingDetails(payload).subscribe(
        response => {
          if (response && response.responseCode === 'E200') {
            this.snackBar.open(response.responseMessage, 'Cancel', {
              duration: 2500
            });
          } else if (response.responseCode === 'E400') {
            this.snackBar.open(response.responseMessage, 'Cancel', {
              duration: 2500
            });
            this.isSave = true;
          }
          this.partList = null;
          this.cartonResData = [];
          this.cartonList = [];
          this.cartonNo = null;
          this.btnShow = false;
          this.batchIndex = null;
          this.pickedItemIndex = null;
          this.fetchPackedCartonsList();
          this.fetchPartsForPacking();
        },
        (errorResponse: HttpErrorResponse) => {
          if (errorResponse.status > 0) {
            this.snackBar.open(errorResponse.error.responseMessage, 'Cancel', {
              duration: 2500
            });
          } else {
            this.snackBar.open('Please Contact Adminstrator', 'Cancel', {
              duration: 2500
            });
          }
        }
      );
    } else {
      item.batchPackingInfoDto.splice(j, 1);
    }
  }

  /**
   * @description This method is used to save the edited qty.
   */
  saveEdit() {
    this.editedPicklistInfoId = null;
    this.isSave = false;
  }

  /**
   * @description This method is used to edit the packed qty.
   * @param data
   */
  editBatchPackedList(data) {
    this.editedPackedQty = data.packedQty;
    this.editedPicklistInfoId = data.picklistInfoId;
  }

/**
 * @description This method is used to open the carton dialog.
 */
  openDialog() {
    this.warehouseService.cartonData.next(this.cartonResData);
    this.dialog.open(CartonDialogComponent, {
      width: '600px',
      height: '500px'
    });
  }

  /**
   * Insert Or Update Packinglist lifo Api integration
   */
  savePackList() {
    if (!this.btnShow) {
      this.snackBar.open('Please Add/Select Carton', 'Cancel', {
        duration: 3000
      });
      return;
    }
    const payload = [];
    this.cartonList.forEach(ele => {
      const obj = {
        batchesForPacking: [],
        cartonNo: this.cartonNo,
        cartonTypeId: this.cartonTypeId,
        createdUsrId: this.sessionData.regId,
        orgGrpId: this.sessionData.orgGroupId,
        orgId: this.sessionData.orgId,
        saleOrderId: this.resData.saleOrderId,
        totalPackedQty: ele.toBePackedQty
      };
      ele.batchPackingInfoDto.forEach(res => {
        const batchObj = {
          partId: ele.partId,
          picklistInfoId: res.picklistInfoId,
          packingInfoId: res.packingInfoId,
          qty: res.packedQty,
          saleOrderDetailedId: ele.saleOrderDetailedId
        };
        obj.batchesForPacking.push(batchObj);
      });
      payload.push(obj);
    });
    this.warehouseApiService.insertOrUpdatePackingInfo(payload).subscribe(
      response => {
        if (response && response.responseCode === 'E200') {
        } else if (response.responseCode === 'E400') {
          this.snackBar.open(response.responseMessage, 'Cancel', {
            duration: 2500
          });
          this.isSave = true;
        }
        this.partList = null;
        this.cartonResData = [];
        this.cartonList = [];
        this.cartonsInfoList = [];
        this.batchIndex = null;
        this.cartonNo = null;
        this.btnShow = false;
        this.pickedItemIndex = null;
        this.fetchPackedCartonsList();
        this.fetchPartsForPacking();
      },
      (errorResponse: HttpErrorResponse) => {
        if (errorResponse.status > 0) {
          this.snackBar.open(errorResponse.error.responseMessage, 'Cancel', {
            duration: 2500
          });
        } else {
          this.snackBar.open('Please Contact Adminstrator', 'Cancel', {
            duration: 2500
          });
        }
      }
    );
  }

  /**
   * @description This method is used to open the batch under the part in to be packed parts table
   * @param i
   */
  openBatchDetails(i) {
    this.batchIndex = i;
  }

  /**
   * @description This method is for Get the selected carton detais like carton number etc.
   * @param i
   * @param val
   */

  selectedCart(i, val) {
    this.isCartSelected = i;
    this.cartonDetails.push(val.cartonNmAndDimension);
    this.btnShow = true;
    const obj = {
      cartonNo: val.cartonNo,
      cartonTypeNm: this.cartonDetails[0].cartonTypeNm,
      cartonTypeId: this.cartonDetails[0].cartonTypeId,
      partListInfoDto: []
    };
    this.cartonsInfoList.push(obj);
    this.getCortonList(i);
  }

  /**
   * Show & Hiding Picked items
   */
  showPickedItem(i) {
    this.pickedItemIndex = i;
  }

  /**
   * @description Get the packed carton list based on selected carton index.
   * @param i
   */

  getCortonList(i) {
    this.cartonNo = this.cartonResData[i].cartonNo;
    this.btnShow = true;
    this.isCartSelected = i;
    this.cartonTypeId = this.cartonResData[i].cartonNmAndDimension.cartonTypeId;
    this.cartonList = this.cartonResData[i].partListInfoDto;
  }

/**
 * @description This method is for getting the paked list based on the carton.
 * @function fetchPackedCartonsList
 */
  fetchPackedCartonsList() {
    this.cartonResData = [];
    const payload: Request = {
      createdUsrId: this.sessionData.regId,
      orgId: this.sessionData.orgId,
      orgGrpId: this.sessionData.orgGroupId,
      saleOrderId: this.resData.saleOrderId
    };
    this.warehouseApiService.fetchPackedCartonsList(payload).subscribe(
      response => {
        if (response && response.responseCode === 'E200') {
          const data = response.data;
          this.cartonsInfoList = data['cartonsInfoList'];
          if (this.cartonsInfoList.length) {
            this.cartonsInfoList.forEach(ele => {
              const obj = {
                barCode: true,
                cartonNo: ele.cartonNo,
                partListInfoDto: ele.partListInfoDto,
                cartonNmAndDimension: {
                  cartonTypeId: ele.cartonTypeId,
                  cartonTypeNm: ele.cartonTypeNm
                }
              };
              this.cartonResData.push(obj);
            });
            this.getCortonList(0);
          }
        } else if (response && response.responseCode === 'E400') {
          this.snackBar.open(response.responseMessage, 'Cancel', {
            duration: 2500
          });
        }
      },
      (errorResponse: HttpErrorResponse) => {
        if (errorResponse.status > 0) {
          this.snackBar.open(errorResponse.error.responseMessage, 'Cancel', {
            duration: 2500
          });
        } else {
          this.snackBar.open('Network Slow', 'Cancel', {
            duration: 2500
          });
        }
      }
    );
  }

/**
 * @description This method for get the packing list from API
 * @function fetchPartsForPacking
 */
  fetchPartsForPacking() {
    const payload: Request = {
      createdUsrId: this.sessionData.regId,
      orgId: this.sessionData.orgId,
      orgGrpId: this.sessionData.orgGroupId,
      saleOrderId: this.resData.saleOrderId
    };
    this.warehouseApiService
      .fetchPartsForPacking<PartsForPackingResponse>(payload)
      .subscribe(
        response => {
          if (response && response.responseCode === 'E200') {
            this.partList = response.data;
          } else if (response && response.responseCode === 'E400') {
            this.snackBar.open(response.responseMessage, 'Cancel', {
              duration: 2500
            });
          }
        },
        (errorResponse: HttpErrorResponse) => {
          if (errorResponse.status > 0) {
            this.snackBar.open(errorResponse.error.responseMessage, 'Cancel', {
              duration: 2500
            });
          } else {
            this.snackBar.open('Network Slow', 'Cancel', {
              duration: 2500
            });
          }
        }
      );
  }

  // Below function is used to Print Sale order
  saleOrderPrint() {
    const queryParams = {
      orgId: this.sessionData.orgId,
      loggedInUserId: this.sessionData.regId,
      saleOrderId: this.partList.saleOrderId
    };
    this.reportsApiService.fetchWarehouseSaleOrderPrint(queryParams).subscribe(
      response => {
        if (response instanceof ArrayBuffer) {
          const saleOrderReport = new Blob([response], {
            type: 'application/pdf'
          });
          const reportURL = URL.createObjectURL(saleOrderReport);
          const win = window.open('', '_blank');
          win.location.href = reportURL;
          setTimeout(() => {
            win.print();
          }, 1000);
        } else if (response.responseCode) {
          this.snackBar.open('Something went wrong', 'dismiss', {
            duration: 2500
          });
        }
      },
      () => {
        this.snackBar.open('Something went wrong', 'dismiss', {
          duration: 2500
        });
      }
    );
  }
}
